import axiosInstance from '../middleware/Interceptor'


function subscribeToThreatIntel () {
    return axiosInstance.get('/siem-core/user/threat-intel/subscribe')
}
function unSubscribeToThreatIntel(){
    return axiosInstance.get('/siem-core/user/threat-intel/stop')
}



const threatIntelService = {
    subscribeToThreatIntel,
    unSubscribeToThreatIntel,
    
}

export default threatIntelService