import axiosInstance from '../middleware/Interceptor'


function saveBrandConfiguration (data) {
    return axiosInstance.post('/siem-core/users/brand-mointor/save-configuration',data)
}
function getConfiguration(){
    return axiosInstance.get('/siem-core/users/brand-mointor/get-coniguration')
}

function getCategoryByCount(data){
    return axiosInstance.post('/siem-core/users/brand-mointor/get-category-by-count',data)
}

function getBrandFeed(data){
    return axiosInstance.post('/siem-core/users/brand-mointor/get-brand-feed',data)
}

function getTopInfluencers(data){
    return axiosInstance.post('/siem-core/users/brand-mointor/get-top-influencers',data)
}


const brandMointoring = {
    saveBrandConfiguration,
    getConfiguration,
    getCategoryByCount,
    getBrandFeed,
    getTopInfluencers

}

export default brandMointoring