import React,{useState} from 'react';
import {
    Card,
      CardBody, Nav, NavItem, NavLink, TabContent, TabPane,CardTitle,Col,Row,CardText, Badge
  } from 'reactstrap';
import '../../../../node_modules/react-splitter-layout/lib/index.css';
import Moment from 'react-moment';
import moment from 'moment'
import { useDispatch, useSelector } from 'react-redux';
import * as widgetHelper  from "../../../shared/helpers/widget_helper"
import Collapse from '../../../shared/components/Collapse';
import DateRangePicker from 'react-bootstrap-daterangepicker'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-daterangepicker/daterangepicker.css';
import ReactJson from 'react-json-view'
const CaseEntityView = (props) =>{

    const evidenceDetails = useSelector((state) => state.cases.evidenceDetails);
    const playbookResult = useSelector((state) => state.cases.playbookResult);
    const fields = useSelector((state) => state.cases.logFields);
  
    
    const evidences = [];
    if(evidenceDetails && evidenceDetails.entities){

        for (const [key, value] of Object.entries(evidenceDetails.entities)) {
            
            if(widgetHelper.entityTypeMap[fields[key]]){
                let object = new Object();
                object['image'] = widgetHelper.entityTypeMap[fields[key]];  //fields[widgetHelper[evidenceDetails.alertEvidences.entities[i]]];
                object['field'] = value;
                evidences.push(object);
            }
           
          }

       
    }
    const hasJsonStructure  = (str) =>{
        if (typeof str !== 'string') return false;
        try {
            const result = JSON.parse(str);
            const type = Object.prototype.toString.call(result);
            return type === '[object Object]' 
                || type === '[object Array]';
        } catch (err) {
            return false;
        }
    }
    

    return (<div className = "context-details-container">
            <div className = "context-details-container__title">
                <div className = "icon-box icon smp-Alerts">
                         <span class="lnr lnr-flag"></span> 
                </div>

                <p>Alert</p>
                <span className = "description">{evidenceDetails.alertName}</span>      

            </div>





        <Collapse title="Alert Entites" className="with-shadow">

            {evidences.map((value, index) => {

                        return (
                            <div className = "inbox__list-button">
                            <div className = "inbox__mailbox">
                                <img className = "imgContainer" src = {value.image}/> <span className = "entityUser u-ellipsis">{value.field}</span> 
                            </div>

                        </div>
                        )

            })}
                        
                      

            </Collapse> 
            <Collapse title="Alert Details" className="with-shadow">

             <div className = "ui-table-scrollable-wrapper ng-star-inserted">
                 <div className = "ui-table-scrollable-view">
                    <table className = "ui-table table">
                        <thead>
                            <tr>
                                <th>Field Name</th>
                                <th>Value</th>
                            </tr>
                        </thead>
                        <tbody>
                            

                            {evidenceDetails && Object.keys(evidenceDetails).filter(key => key!=='entities').map((value, index) => {
                              
                              return (<tr>
                                <td>{value}</td>
                                  {value==='createdDate' && evidenceDetails[value] && <td className = "u-ellipsis"><Moment format="MM/DD/YYYY HH:mm:ss" date={evidenceDetails[value]}/></td>}
                                  {value!='createdDate' && evidenceDetails[value] &&  <td className = "u-ellipsis">{evidenceDetails[value].toString()}</td>}

                                </tr>)
                            })}
                           
                        </tbody>
                    </table>
                 </div>
            </div>   
                        

            </Collapse> 
            <Collapse title="Threat" className="with-shadow">

            </Collapse> 
            <Collapse title="Case" className="with-shadow">

            <div className = "ui-table-scrollable-wrapper ng-star-inserted">
                 <div className = "ui-table-scrollable-view">
                    <table className = "ui-table table">
                        <thead>
                            <tr>
                                <th>Field Name</th>
                                <th>Value</th>
                            </tr>
                        </thead>
                        <tbody>
                            

                            {evidenceDetails && Object.keys(evidenceDetails).filter(key => key!=='entities').map((value, index) => {
                              
                              return (<tr>
                                <td>{value}</td>
                                  {value==='createdDate' && evidenceDetails[value] && <td className = "u-ellipsis">
                                    <Moment format="MM/DD/YYYY HH:mm:ss" date={evidenceDetails[value]}/>
                                  </td>}
                                  {value!='createdDate' && evidenceDetails[value] &&  <td className = "u-ellipsis">{evidenceDetails[value].toString()}</td>}
                                </tr>)
                            })}
                           
                        </tbody>
                    </table>
                 </div>
            </div>             


            </Collapse> 

            <Collapse title="Playbook" className="with-shadow">
                 {playbookResult && playbookResult.runDate &&
                    <React.Fragment>
                            <Row>
                                <Col xs ={12} md = {12} lg= {12} style = {{paddingBottom:'12px'}}>
                                    <h6 className = "bold-text">Run Date : <Moment format="MM/DD/YYYY HH:mm:ss" date={playbookResult.runDate}/></h6>
                                </Col>
                                <Col xs ={12} md = {12} lg= {12}  style = {{paddingBottom:'12px'}} >
                                     <h6 className = "bold-text" >Status : <Badge color={playbookResult.runStatus === 'Success' ? 'success':'danger'}>{playbookResult.runStatus}</Badge></h6>
                                </Col>
                            </Row>
                           
                           
                    </React.Fragment>
                 
                 }
                  
                
                   {playbookResult && playbookResult.playbookResult 
                   
                   && !hasJsonStructure(playbookResult.playbookResult)
                   && <h6>{playbookResult.playbookResult}</h6>
                   }

                    {playbookResult && playbookResult.playbookResult  
                   && hasJsonStructure(playbookResult.playbookResult)
                   && <ReactJson displayDataTypes = {false}  src={JSON.parse(playbookResult.playbookResult)} />
                   } 

            </Collapse>

    </div>)

}

export default CaseEntityView;