import { takeLatest, put } from "redux-saga/effects";
import dashboardService from "../../api/dashboardService";
import * as ActionTypes from "../actions/dashboardLayoutActions";
import * as DashboardActions from "../actions/dashboardActions";
import _ from 'lodash'
function* saveDashboardLayout(action) {
  let filterData = [];
  if (action.payload.type === "add") {
    filterData = action.payload.layout.concat([action.payload.dashboard]);
  } else {
    filterData = action.payload.layout.filter(
      (item) => item.id != action.payload.dashboardId
    );
  }

  const data = {
    layout: JSON.stringify(filterData),
    dashboardId: action.payload.dashboardId
    
  };
  const dashboadLayoutResponse = yield dashboardService.saveGridLayout(data);
  if (dashboadLayoutResponse.status === 200) {
    const finalData = {
      filterData: filterData,
      value: filterData.length - 1,
      index: filterData.length - 1,
      type:action.payload.type
    };
   
    yield put({
      type: ActionTypes.DELETE_DASHBOARD,
      payload: finalData,
    });

    yield put({
      type: DashboardActions.FETECH_SINGLE_DASHBOARD,
      id: filterData[finalData.index].id,
    });
  }
}

function* fetchDashboardLayout(action) {
  const dashboadLayoutResponse = yield dashboardService.getGridLayout(0);

  const fieldsResponse = yield dashboardService.getAllLogFields();

  if (dashboadLayoutResponse.status == 200) {
    const tempField = fieldsResponse.data.map((value) => {
      var object = new Object();
      object[value.fieldName] = value.dataType;
      return object;
    });
    const finalData = {
        dashboardLayout: [],
        fields: tempField,
        index: 0,
        value: 0,
    };
    const tempData = JSON.parse(dashboadLayoutResponse.data.layout);
    var isDashbaordExits =  _.find(tempData, function(o) { return o.id.toString() === action.payload.id.toString() });
    if(tempData.length===0 || !isDashbaordExits){
        const dashboardResponseData = yield dashboardService.getAllDashboards();
        if(dashboardResponseData.status === 200){
            const exitingDashboard = _.find(dashboardResponseData.data, function(o) { return o.id.toString() === action.payload.id.toString() });
            const currentDashboard = {
                "type": "tab",
                "id": exitingDashboard.id,
                "name": exitingDashboard.name,
                "component": "dashboard"
            }
            tempData.push(currentDashboard)
        }
        const data = {
            layout: JSON.stringify(tempData),
            dashboardId: action.payload.id
            
          };
        yield dashboardService.saveGridLayout(data);
    }
    finalData.dashboardLayout = tempData;

    for (var i = 0; i < tempData.length; i++) {
      if (action.payload.id.toString() === tempData[i].id.toString()) {
        finalData.index = i;
        finalData.value = i;
      }
    }

    yield put({
      type: ActionTypes.LOAD_LAYOUT,
      payload: finalData,
    });

    yield put({
      type: DashboardActions.FETECH_SINGLE_DASHBOARD,
      id: action.payload.id,
    });
  }
}

export function* fetchDashbardLayoutWatcher() {
  yield takeLatest(ActionTypes.FETCH_LAYOUT, fetchDashboardLayout);
}

export function* saveDashboardLayoutWatcher() {
  yield takeLatest(ActionTypes.SAVE_DASHBOARD_LAYOUT, saveDashboardLayout);
}
