import EyeIcon from 'mdi-react/EyeIcon';
import React,{useState} from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter ,Row,Col} from 'reactstrap';
import { Field, reduxForm } from 'redux-form';
import userService from '../../../api/userService';
 const DisplayChangePasswordModel = (props)=>{

    return <ChangePasswordModel handleSubmit = {props.handleSubmit} toggle = {props.toggle} isModelOpen = {props.isModelOpen}/>
}

const ChangePasswordModel = (props) => {
  
    const [formData,setFormData] = useState({newPassword:'',confirmPassword:''});
    const [showNewPassword,setShowNewPassword] = useState(false);
    const [showConfirmPassword,setShowConfirmPassword] = useState(false);
    const changeEventHandler = (event)=>{
        const tempFormData = formData;
        tempFormData[event.target.name] = event.target.value;
        setFormData(tempFormData);
    }
    const setShowPassword = (event,passwordType)=>{
        if(passwordType === 'new'){
            setShowNewPassword(!showNewPassword)
        }else{
            setShowConfirmPassword(!showConfirmPassword)
        }
    }
    const submit = ()=>{
        if(formData.newPassword=== '' || formData.confirmPassword === ''){
            alert("Please Enter new password and confirm password");
            return false;
        }
        if(formData.newPassword!=formData.confirmPassword){
            alert("New Password and Confirm Password doesn't match");
            return false;
        }
        userService.changePassword(formData).then((responseData)=>{
            console.log(responseData);
        })
    }
    
  
    return (
      <div>
        <form className="form form--horizontal"  >
        <Modal isOpen={props.isModelOpen} >
          <ModalHeader >Change Password</ModalHeader>
          <ModalBody>
          
          <Row>
                        <Col xs = {12} lg={6} md = {6} sm={12}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">New Password</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="newPassword"
                                        component="input"
                                        type={showNewPassword ? 'text' : 'password'}
                                        className = "form-control"
                                        onChange = {event=>changeEventHandler(event)}
                                    />
                                    <button  type="button" className={`form__form-group-button${showNewPassword ? ' active' : ''}`} onClick={e => setShowPassword(e,'new')}
                                    ><EyeIcon /></button>
                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Confirm Password</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="confirmPassword"
                                        component="input"
                                        type="text"
                                        type={showConfirmPassword ? 'text' : 'password'}
                                        className = "form-control"
                                        onChange = {event=>changeEventHandler(event)}
                                    />
                                     <button  type="button" className={`form__form-group-button${showConfirmPassword ? ' active' : ''}`} onClick={e => setShowPassword(e,'confirm')}
                                    ><EyeIcon /></button>
                               </div>
                            </div>
                          </Col>
            </Row>
            



          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick = {submit}>Change Password</Button>{' '}
            <Button color="secondary" onClick = {(event)=>props.toggle(event)}>Cancel</Button>
          </ModalFooter>
        </Modal>
        </form>
        
      </div>
    );
  }

  export default reduxForm({
    form: 'change_password_form', // a unique identifier for this form
  })(DisplayChangePasswordModel);
