
import {fetchIntergrationsWatcher,saveIntegrations} from './integrationsSaga'
import {fetchDashboardWatcher,openDashboardWatcher} from './dashboardSaga'
import {fetchAllCasesWatcher,fetchPlaybookWatcher,fetchFilterCasesWatcher} from './caseSaga'
import {fetchExploreDataWatcher,fetchEntityDataWatcher} from './exploreSaga'
import {fetchDashbardLayoutWatcher,saveDashboardLayoutWatcher} from './dashboardLayoutSaga'
import { all } from 'redux-saga/effects'

export default function* rootSaga() {
    yield all([
      fetchIntergrationsWatcher(),
      saveIntegrations(),
      fetchDashboardWatcher(),
      fetchAllCasesWatcher(),
      fetchPlaybookWatcher(),
      fetchFilterCasesWatcher(),
      fetchExploreDataWatcher(),
      fetchEntityDataWatcher(),
     
      openDashboardWatcher(),
      fetchDashbardLayoutWatcher(),
      saveDashboardLayoutWatcher()
    ])
  }