import React,{useState,useEffect    } from 'react';
import { Col, Container, Row,ButtonToolbar,Button,Card,CardBody,ButtonGroup,InputGroup,InputGroupAddon,Input,Popover, PopoverHeader, PopoverBody,Form, FormGroup ,Label } from 'reactstrap';
import { useDispatch, useSelector } from 'react-redux';
import  '../Filter/Filter.css'
import {addFilters,deleteFilters} from  '../../../../redux/actions/dashboardActions'



export const FilterBar = (props) =>{

    const {filters,fields} = props

    
  
    const [popoverOpen, setPopoverOpen] = useState(false);
    const toggle = () => setPopoverOpen(!popoverOpen);
    const dispatch = useDispatch()
    const [fieldName,setFieldName] = useState("");
    const [operator,setOperator] = useState("");
    const [value,setValue] = useState("");

    const applyFiltersClick = () => {
        let data = {key:fieldName,operator:operator,value:value};
        dispatch(addFilters(data))
        toggle();
       
    }
    const removeFilters = (index) =>{
        dispatch(deleteFilters(index))
    }

    const changeHanlder = (event) =>{
        if(event.target.name === 'fieldName'){
            setFieldName(event.target.value);
        }
        if(event.target.name === 'operator'){
            setOperator(event.target.value);
        } 
        if(event.target.name === 'value'){
            setValue(event.target.value);
        }     
    }

    return (

            <div className = "filterContainer">

            <div className = "euiFlexGroup euiFlexGroup--alignItemsFlexStart euiFlexGroup--directionRow globalFilterGroup">
            <div className = "globalFilterGroup__filterBar">
                <div className = "euiFlexItem globalFilterGroup__filterFlexItem">
                    <div className = "euiFlexGroup euiFlexGroup--gutterExtraSmall euiFlexGroup--alignItemsCenter euiFlexGroup--directionRow euiFlexGroup--wrap globalFilterBar globalFilterGroup__filterBar">
                    {filters && filters.map((filter,index) =>
                        <div key = {filter.key} className = "euiFlexItem euiFlexItem--flexGrowZero globalFilterBar__flexItem">
                            <div className = "euiPopover euiPopover--anchorDownLeft euiPopover--withTitle globalFilterItem__popover">
                            <span className = "euiBadge euiBadge--hollow euiBadge--iconRight globalFilterItem">
                                <span className = "euiBadge__content">
                                <button className = "euiBadge__childButton">
                                    <span><span>{filter.key}: <span class="globalFilterLabel__value">{filter.value}</span></span></span>
                                </button>
                                <button className = "euiBadge__childButton" onClick = {removeFilters.bind(this,index)}>
                                     <span class="lnr lnr-cross"></span>
                                </button>
                               
                                </span>
                                
                             </span>
                               
                            </div>
                        </div>
                     )}
                     <div className ="euiFlexItem euiFlexItem--flexGrowZero">
                            <div className = "euiPopover euiPopover--anchorDownLeft euiPopover--withTitle">
                                <div className = "euiPopover__anchor">
                                    <div className = "euiButtonEmpty euiButtonEmpty--primary euiButtonEmpty--xSmall globalFilterBar__addButton">
                                        <span className = "euiButtonEmpty__content">
                                         <span class="euiButtonEmpty__text" id="Popover1">+ Add filter</span>
                                         <Popover placement="bottom" isOpen={popoverOpen} target="Popover1" toggle={toggle}>
        <PopoverHeader>Filter</PopoverHeader>
        <PopoverBody>
        
        <Form>
            <Row>
                <Col md = {6}>
                <FormGroup>
                    <Label for="exampleEmail">Field</Label>
                    <Input type="select" onChange = {changeHanlder} value ={fieldName} name="fieldName">
                     {fields.map((field) =>
                        <option value = {field.fieldName}>{field.fieldName}</option>
                     )}
                    </Input>
                </FormGroup> 
                </Col>
                <Col md = {6}>
                <FormGroup>
                    <Label for="Operator">Operator</Label>
                    <Input  onChange = {changeHanlder} value ={operator} type="select" name="operator">
                        <option value = "=">Equals</option>
                        <option value = "!=">Not Equals</option>
                        <option value = "<">Greater Than</option>
                        <option value = ">">Less Than</option>
                        <option value = "=~">Contains</option>
                        <option value = "!~">Not Contains</option>
                        
                    </Input>   
                </FormGroup> 
                </Col>
            </Row>
            <Row>
                <Col md = {6}>
                <FormGroup>
                    <Label for="value">Value</Label>
                    <Input onChange = {changeHanlder} value ={value} type="text" name="value"  />
                </FormGroup> 
                </Col>
                
            </Row> 
            <hr/>

            <Button color="primary" onClick = {applyFiltersClick.bind(this)}> Apply</Button>    

         </Form>            
        </PopoverBody>
      </Popover>
                                       
                                        </span>
                                    </div>
                                </div>

                            </div>

                     </div>

                    </div>
                </div>
            </div>
            
            </div>

            </div>

           


           



           

    );
}

function filterMemo(prevProps, nextProps) {
    return _.isEqual(prevProps.filters, nextProps.filters);
  }
  

export default React.memo(FilterBar,filterMemo);