import {
    OPEN_CASE,
    OPEN_EVIDENCE,
    OPEN_PLAYBOOK,
    CLEAR_CAESES,
    LOAD_ALL_CASES,
    OPEN_ALL_CASES,
    LOAD_PLAYBOOK,
    OPEN_FILTERED_CASES,
    LOAD_FILTERED_CASES,
    OPEN_PLAYBOOK_RESULT
  } from '../actions/casesAction';
  
  const initialState = {
    caseDetails : {},
    loading:false,
    cases:[],
    evidenceDetails : {type : 'case'},
    playbookResults:[],
    logFields:[],
    users:[],
    playbookResult:{}
  };
  
  const caseReducer = (state = initialState, action) => {

      if(action.type === LOAD_ALL_CASES ){
        return Object.assign({}, state, { loading: true,cases:[],caseDetails:{},playbookResults:[],logFields:[],users:[]  });
      }
      
      if(action.type === LOAD_PLAYBOOK){
         return Object.assign({}, state, { loading: true});
      }
     if(action.type === LOAD_FILTERED_CASES){
        return Object.assign({}, state, { loading: true});
     }
     if(action.type === OPEN_FILTERED_CASES){
         return Object.assign({}, state, { 
           loading: false,
           cases:action.payload.cases,
           caseDetails:action.payload.currentCase
          
          });
     }
     if(action.type === OPEN_ALL_CASES ){
          return Object.assign({}, state, { 
          
            loading: false,
            cases:action.payload.cases,
            caseDetails:action.payload.currentCase,
            logFields:action.payload.logFields,
            users:action.payload.users,
           
          
          });
      }


    if(action.type === OPEN_CASE ){
      return Object.assign({}, state, { caseDetails: action.payload  });
    }

    if(action.type === OPEN_EVIDENCE ){
      return Object.assign({}, state, { evidenceDetails: action.payload  });
    }
    if(action.type === OPEN_PLAYBOOK){
        return Object.assign({}, state, { loading:false,playbookResults: action.payload.playbookResults,playbookDetails : action.payload.playbookDetails  });
    }

    if(action.type === OPEN_PLAYBOOK_RESULT){
      return Object.assign({}, state, { loading:false,playbookResult: action.payload});
    }

    if(action.type === OPEN_PLAYBOOK){
      return Object.assign({}, state, { loading:false,playbookResults: action.payload.playbookResults,playbookDetails : action.payload.playbookDetails  });
  }
    if(action.type === CLEAR_CAESES){
      return Object.assign({}, state, { caseDetails: {},evidenceDetails: {type : 'case'},playbookResults:[] });
  }

    return state;

   

  };
  
  export default caseReducer;
  