import axiosInstance from '../middleware/Interceptor'


function getAllIntegrations () {
    return axiosInstance.get('/siem-core/user/integrations/get-all-integrations')
}
function enableIntegrationForCompany(data){
    return axiosInstance.post('/siem-core/user/integrations/assignee-integration-to-org',data)
}



const integrationService = {
    getAllIntegrations,
    enableIntegrationForCompany
   
}

export default integrationService