import React from "react";
import { ReflexContainer, ReflexSplitter, ReflexElement } from "react-reflex";
import "react-reflex/styles.css";

import RenderSideBar from "./components/Aside";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { IconButton } from "@material-ui/core";
import CloseIcon from "mdi-react/CloseIcon";
import dashboardService from "../../../api/dashboardService";
import DashboardGrid from "../../Dashboards/components/DashboardGrid";
import { fetchSingleDashboardAction } from "../../../redux/actions/dashboardActions";
import {
  changeDashboard,
  saveDashboardLayout,
  loadFetchLayout
} from "../../../redux/actions/dashboardLayoutActions";
import { connect } from "react-redux";
import _ from "lodash";
import { BarLoader } from "react-spinners";
import RenderBottom from "./components/BottomBar";
class DashboardLayoutNew extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      currenId: "",
      dashboardData: {},
      secondaryInitialSize: 20,
      sideBarToggle: false,
      value: 1,
      dashboardList: [],
      index: 1,
      fields: [],
      loading: false,
      bottom: 0,
      bootBarToogle: false,
      fieldMaps: {},
      type: "dashboards",
      middleMinSize:900,
      middelMaxSize:900
    };
    this.openDashboardsOrReports = this.openDashboardsOrReports.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.fetchSingleDashboard = this.fetchSingleDashboard.bind(this);
    // this.renderDashboardLayout = this.renderDashboardLayout.bind(this);
  }
  openDashboardsOrReports = (type) => {
    if (this.state.sideBarToggle) {
      this.setState((state, props) => ({
        sideBarToggle: false,
        secondaryInitialSize: 20,
        type: type,
      }));
      console.log("trigger");
      window.dispatchEvent(new Event("resize"));
    } else {
      this.setState((state, props) => ({
        secondaryInitialSize: 300,
        sideBarToggle: true,
      }));
    }
  };

  addDashboardItemToList = (event) => {
    const { dispatch } = this.props;
    var isDashboardExists = this.props.layout.filter((value) => {
      return value.id === event.id;
    });

    if (isDashboardExists.length === 0) {
        const data = { layout: this.props.layout, dashboardId: event.id.replace("#",""),type:"add",dashboard:event };
        dispatch(saveDashboardLayout(data));     
    } else {
        alert("Dashboard Already Exists!!!");
    }
  };
  componentDidMount(prevPorps){
    const currentId = this.props.match.params.id;
    const { dispatch } = this.props;
   
        //console.log(currentId+">>>>"+this.props.layout[this.props.index].id);
        const data = {id:currentId}
        dispatch(loadFetchLayout(data));
    
   
 }

  handleChange = (event, newValue) => {
    const { dispatch } = this.props;
    console.log("testin")
    if (newValue != this.props.value) {
      if (event.target.tagName === "svg") {
        const currentDashboard = this.props.layout[newValue];
        const data = { layout: this.props.layout, dashboardId: currentDashboard.id.toString().replace("#","") };
        dispatch(saveDashboardLayout(data));
        
      } else {
        const currentDashboard = this.props.layout[newValue];
        dispatch(changeDashboard(newValue));
        this.fetchSingleDashboard(currentDashboard);
      }
    }
  };

  fetchSingleDashboard = (event) => {
    const { dispatch } = this.props;
    dispatch(fetchSingleDashboardAction(event.id.toString().replace("#", "")));
  };

  toggleBottom = () => {
    if (this.state.bootBarToogle) {
      this.setState((state, props) => ({
        bootBarToogle: false,
        bottom: 0,
        middelMinSize:900,
        middelMaxSize:900
      }));
      console.log("trigger");
      window.dispatchEvent(new Event("resize"));
    } else {
      this.setState((state, props) => ({
        bootBarToogle: true,
        bottom: 400,
        middleMinSize:0,
        middelMaxSize:500
      }));
    }
  };

  handleClose = (id) => {
    const { dispatch } = this.props;
    const data = { layout: this.props.layout, dashboardId: id };
    //dispatch(saveDashboardLayout(data))
    //this.saveDashboardLayout();
  };

  render() {
    const { layout, index, value, fields, loading } = this.props;

    return (
      <div style={{ paddingTop: "50px", height: "100vh" }}>
        {loading && <BarLoader width="100%" color="#0066CC" />}
        <ReflexContainer orientation="vertical">
          <ReflexElement
            id="leftPane"
            minSize={this.state.secondaryInitialSize}
            size={this.state.secondaryInitialSize}
            className="left-pane"
          >
            <RenderSideBar
              type={this.state.type}
              title={this.state.sideBarToggle}
              openDashboardsOrReports={this.openDashboardsOrReports}
              addDashboardToLayout={this.addDashboardItemToList}
            />
          </ReflexElement>
          <ReflexSplitter propagate={true} />

          <ReflexElement>
            <ReflexContainer orientation="horizontal">
              <ReflexElement
                 minSize={this.state.middleMinSize}
                 size={this.state.middelMaxSize}
                 maxSize = {900}
                id="middlePane"
                className="m-paniddele"
              >
                <Memotab
                  value={value}
                  dashboardList={layout}
                  handleClose={this.handleClose}
                  handleChange={this.handleChange}
                />

                {layout.length > 0 && (
                  <TabPanel value={value} index={index}>
                    <DashboardGrid fields={fields} />
                  </TabPanel>
                )}
              </ReflexElement>
              <ReflexSplitter />

              <ReflexElement
                id="bottomPane"
                minSize={this.state.bottom}
                size={this.state.bottom}
                maxSize={Number.MAX_VALUE}
                className="bottom-pane"
              >
                <RenderBottom
                  fieldMaps={this.state.fieldMaps}
                  fields={this.state.fields}
                  status={this.state.bootBarToogle}
                  toggleBottom={this.toggleBottom}
                />
              </ReflexElement>
            </ReflexContainer>
          </ReflexElement>
        </ReflexContainer>
      </div>
    );
  }
}

const DisplayTab = (props) => {
  const { value, dashboardList, handleClose, handleChange } = props;

  return (
    <AppBar position="static" color="default">
      <Tabs
        style={{ minHeight: "30px" }}
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        variant="scrollable"
        scrollButtons="auto"
        
      >
       {dashboardList &&
        dashboardList.map(function (name, index) {
          return (
            <Tab
              style={{ minHeight: "30px" }}
              id={`scrollable-auto-tabpanel-${index}`}
              aria-labelledby={`scrollable-auto-tab-${index}`}
              label={
                <span style={{ fontSize: "12px" }}>
                  {" "}
                  {name.name}{" "}
                  <IconButton
                    component="span"
                    style={{ height: "12px", fontSize: 10 }}
                    fontSize="small"
                    onClick={(event) => handleClose(name.id)}
                  >
                    <CloseIcon style={{ height: "12px", fontSize: 10 }} />
                  </IconButton>{" "}
                </span>
              }
            />
          );
        })}
      </Tabs>
    </AppBar>
  );
};
function areDisplayTabsAreEqual(props, nextProps) {
  return (
    props.value === nextProps.value &&
    _.isEqual(props.dashboardList, nextProps.dashboardList)
  );
}




const Memotab = React.memo(DisplayTab, areDisplayTabsAreEqual);

const TabPanel = (props) => {
  const { children, value, index, ...other } = props;
  console.log("in table ");
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <div>{children}</div>}
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    layout: state.dashboardLayoutReducer.layout,
    index: state.dashboardLayoutReducer.index,
    value: state.dashboardLayoutReducer.value,
    fields: state.dashboardLayoutReducer.fields,
    loading: state.dashboardLayoutReducer.loading,
  };
};

export default connect(mapStateToProps)(DashboardLayoutNew);
