  
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import shallowCompare from 'react-addons-shallow-compare'; 
import $ from 'jquery';
import 'jstreetable/jstreetable.min';
import 'jstree/dist/jstree.min';
import 'jstree/dist/themes/default/style.css';
import _ from 'lodash';

class TreeView extends Component {

    
    static propTypes = {
      treeData: PropTypes.object.isRequired,
      treeSearchData: PropTypes.object.isRequired,
      onChange: PropTypes.func,
      deleteCategory:PropTypes.func,
      changeCategory:PropTypes.func
    };
  
    static defaultProps = {
      onChange: () => false,
    };

    shouldComponentUpdate(nextProps) {
         return (nextProps.treeData !== this.props.treeData);
    } 
    
  
   
  
    componentDidMount() {
      const { treeData } = this.props;
      if (treeData) {

        treeData.contextmenu.items = this.customMenu($(this.treeContainer),this.props);
        $(this.treeContainer).jstree(treeData);
        $(this.treeContainer).on('changed.jstree', (e, data) => {
          this.props.onChange(e, data);
        });
        $(this.treeContainer).on('rename_node.jstree', (e, data) => {
            this.props.changeCategory(e, data);
        });
      }
    }
  
    componentDidUpdate() {
      const { treeData, treeSearchData } = this.props;
      if (treeData) {
          
        $(this.treeContainer).jstree(true).settings = treeData;
        $(this.treeContainer).jstree(true).refresh();
        // if(treeSearchData) {
        
      }
    }

    customMenu(node,props){
		var items = {
				"rename" : {
					"separator_before"	: false,
					"separator_after"	: false,
					"_disabled"			: false, 
					"label"				: "Rename",
					"action"			: function (data) {
						var inst = $(node).jstree(true);
						var obj = inst.get_node(data.reference);
						if(obj.type!="Dashboard"){
                            console.log("redname");
							inst.edit(obj);
						}
					}
				},
				"delete" : {
					"separator_before"	: false,
					"separator_after"	: false,
					"_disabled"			: false, 
					"label"				: "Delete Category",

					"action"			: function (data) {
						var inst = $(node).jstree(true);
						var obj = inst.get_node(data.reference);
						if(obj.type!=='Dashboard'){
                            props.deleteCategory(obj.id);
                        }

					}
				},
                "openDashboard" : {
					"separator_before"	: false,
					"separator_after"	: false,
					"_disabled"			: false, 
					"label"				: "Open Dashboard",

					"action"			: function (data) {
						var inst = $(node).jstree(true);
						var obj = inst.get_node(data.reference);
						if(obj.type==='Dashboard'){
                            props.openDashboard(obj);
                        }

					}
				},
		}



		return items;
	}
  
    render() {
      return (
        <div ref={div => this.treeContainer = div} />
      );
    }
  }
  
  export default TreeView;