import React from 'react';
import SplitterLayout from 'react-splitter-layout';
import queryString from 'query-string'

import AlertView from '../Explore/components/AlertView'
import GraphView from '../Explore/components/GraphView'


import EventsView from '../Explore/components/EventsView'
import EntityView from '../Explore/components/EntityView'
import { connect } from 'react-redux';
import {loadExploreData} from '../../redux/actions/exploreActions'
class Explore extends React.Component {



    constructor(props) {
        super(props);
        this.state = { alerts: [],graphData : {},alertDetails:{} };
        this.highlightNodes = this.highlightNodes.bind(this);
    }

      

    componentDidMount() {
        const {dispatch} = this.props;
        const values =  queryString.parse(this.props.location.search)
        const id = values.id;
        const investigationId = values.investigationId;
        if(investigationId){
            const data = {id:investigationId,type:'alert'}
            dispatch(loadExploreData(data))
        }else{
            const data = {id:id,type:'case'}
            dispatch(loadExploreData(data))
        }
           
    }

    highlightNodes = (alertDetails) =>{
        const {dispatch} = this.props;
        this.setState((state, props) => ({
            alertDetails:alertDetails
        }));
        let payloayd = {alertDetails:alertDetails,caseDetails:this.state.alerts};
        dispatch(openAlert(payloayd))
         
    }


    render() {
        return (
        <div style = {{paddingTop:'50px'}}>

<SplitterLayout primaryIndex={1} secondaryInitialSize={250}>
        <AlertView highlightNodes = {this.highlightNodes} alertData = {this.state.alerts}/>
      <SplitterLayout secondaryInitialSize={250}>
        <SplitterLayout vertical secondaryInitialSize={250}>
          <GraphView graphData = {this.state.graphData}/>
           
        </SplitterLayout>
          <EntityView/>
        
      </SplitterLayout>
    </SplitterLayout>


         </div>
        )
    }

}

export default connect()(Explore)
