export const OPEN_ALERT = 'OPEN_ALERT';

export const OPEN_ENTITY= 'OPEN_ENTITY';

export const LOAD_EXPLORE_DATA = 'LOAD_EXPLORE_DATA';

export const OPEN_EXPLORE_DATA = 'OPEN_EXPLORE_DATA';

export const LOAD_ENTITY_VIEW = 'LOAD_ENTITY_VIEW'


export function loadEntityView(action){
  return {
    type: LOAD_ENTITY_VIEW,
    payload: action,
  };
}

export function loadExploreData(action){
  return {
    type: LOAD_EXPLORE_DATA,
    payload: action,
  };
}

export function openExploreData(action){
  return {
    type: OPEN_EXPLORE_DATA,
    payload: action,
  };
}

export function openAlert(action) {
  return {
    type: OPEN_ALERT,
    payload: action,
  };
}

export function openEntityView(action){
  return {
    type: OPEN_ENTITY,
    payload: action,
  };
}



