import axiosInstance from '../middleware/Interceptor'


 

function getFields () {
  return axiosInstance.get('/siem-core/widget/get-mapping-fields')
}

function showPanelPreivew (data) {
  return axiosInstance.post('/siem-core/user/panel/show-panel-preview',data)
}
function showPanelPreivewForReport(data){
  return axiosInstance.post('/siem-core/user/panel/load-single-widget',data)
}

function savePanel(data){
  return axiosInstance.post('/siem-core/user/panel/save-panel',data)
}

function getPanels(){
  return axiosInstance.get('/siem-core/user/panel/')
}


function loadLogviewerData(data){
 
  return axiosInstance.post('/siem-core/widget/load-events',data)

}
function getAllLogFields(){
  return axiosInstance.get('/siem-core/user/logField/getAllLogFields')
}

function getDashboardCategories(type){
  return axiosInstance.get('/siem-core/user/panel/dashboard/categories?type='+type)
}
function saveDashboard(data){
  return axiosInstance.post('/siem-core/user/panel/dashboard-save',data)
}

function getAllDashboards(){
  return axiosInstance.get('/siem-core/widget/load_all_dahsboards')
}
function getAllDashboardsWithCategoris(type){
  return axiosInstance.get('/siem-core/widget/dashboard/getAllCategories?type='+type)
}
function getSingleDashboard(id){
  return axiosInstance.get('/siem-core/user/panel/dashboard/'+id)
}

function getSingleDashboardForReport(id,token){
  return axiosInstance.get('/siem-core/user/panel/get-report/'+id+"?token="+token)
}

function saveDashboardLayout(data){
  return axiosInstance.post('/siem-core/user/panel/update-dashboard-layout',data)
}

function deleteWidget(id){
  return axiosInstance.delete('/siem-core/widget/delete_widget/'+id)
}

function saveGridLayout(data){
  return axiosInstance.post('/siem-core/user/panel/dashboard/save-layout',data)
}
function getGridLayout(id){
  return axiosInstance.get('/siem-core/user/panel/dashboard/load-layout/'+id)
}
function getFieldsWithDataTypes(){
  return axiosInstance.get('/siem-core/user/logField/get-fields-with-data-types')
}
function  getSingleWidget (id){
    return axiosInstance.get('/siem-core/user/panel/load-single-panel/'+id)
}
function getSingleWidgetForReport(id,token,dashboardId){
  return axiosInstance.get('/siem-core/user/panel/load-single-panel-for-report/'+id+"?token="+token+"&dashboardId="+dashboardId); 
}
function getRawDocumentQuery(data){
    return axiosInstance.post('/siem-core/user/panel/get-historical-data',data)
}
function deleteDashboard(id){
  return axiosInstance.delete('/siem-core/user/panel/delete_dashboard/'+id)
}

function saveCategoryData(data){
  return axiosInstance.post('/siem-core/widget/dashboard/saveCategory',data)
}

function deleteCategory(id){
  return axiosInstance.delete('/siem-core/widget/dashboard/categories/'+id)
}





const dashboardService = {
    getFields,
    showPanelPreivew,
    savePanel,
    getPanels,
    loadLogviewerData,
    getAllLogFields,
    getDashboardCategories,
    saveDashboard,
    getAllDashboards,
    getAllDashboardsWithCategoris,
    getSingleDashboard,
    saveDashboardLayout,
    deleteWidget,
    saveGridLayout,
    getGridLayout,
    getFieldsWithDataTypes,
    getSingleWidget,
    getRawDocumentQuery,
    deleteDashboard,
    saveCategoryData,
    deleteCategory,
    getSingleDashboardForReport,
    getSingleWidgetForReport,
    showPanelPreivewForReport
}

export default dashboardService