import React, {Component, useEffect} from 'react'

import {Row,Card,CardBody,Col,Button,Modal,ModalHeader,ModalBody,ModalFooter} from 'reactstrap';

import dashboardService from "../../../api/dashboardService";
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-jsx";
import "ace-builds/src-noconflict/theme-github";
import "ace-builds/src-noconflict/mode-mysql";
import "ace-builds/src-noconflict/snippets/mysql"
import "ace-builds/src-min-noconflict/ext-searchbox";
import "ace-builds/src-min-noconflict/ext-language_tools";
import { addCompleter } from 'ace-builds/src-noconflict/ext-language_tools';
import histroicalService from "../../../api/histroicalService";
import ThreatHuntingTable from "./ThreatHutingTable";
import {Field, reduxForm,SubmissionError} from "redux-form";
import {withTranslation} from "react-i18next";
import renderSelectField from '../../../shared/components/form/Select';
import {toast, ToastContainer} from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import BarLoader from "react-spinners/BarLoader";
class ThreatHutingEditor extends  Component{

    constructor(props) {
        super(props);
        this.state = {
            query:'',
            rows:[],
            cols:[],
            openModel:false,
            updateModel:false,
            savedQuries:[],
            options:[],
            currentFormData:{queryName:'',description: ''},
            newFormData:{queryName:'',description: ''},
            loading:false
        }
        this.submit = this.submit.bind(this);
    }

    componentDidMount() {

        this.fetechFields();

    }
    changeQueryHandler = (event) =>{

        this.setState({query:event});

    }
    fetechFields = () =>{
        dashboardService.getFields().then((responseData) =>{
           let data = responseData.data;
            const keys = Object.keys(data).map((value)=>{
                return ({
                    value:value.replace(".keyword",""),
                    score:1000,
                    meta: "column"

                });
            })
            keys.push({value:'technominds-events-*',score:1000,meta:'table'})
            addCompleter({
                getCompletions: function(editor, session, pos, prefix, callback) {
                    callback(null, keys);
                },
            });
            this.fetchSavedQuries();


        })
    }

    fetchSavedQuries(){
        this.setState({
            loading:true
        })
        histroicalService.getThreatHuntingSaveQuries().then((responsedata) =>{
            this.setState({
                loading:false
            })
            const tempOptions = [];
            for(var i=0;i<responsedata.data.length;i++){
                tempOptions.push({value:responsedata.data[i].id,label:responsedata.data[i].queryName})
            }
            this.setState({savedQuries:responsedata.data});
            this.setState({options:tempOptions});
        })
    }

    executeQuery =() =>{
        const query = this.refs.aceEditor.editor.getSelectedText();
        if(query===''){
            toast.error('Please Highlight the query', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return false;
        }
      
        this.setState({

            loading:true
        })
        let data = {sql:query}
        histroicalService.getThreatHuntingQuries(data).then((responseData) =>{
            const cols = [];
            this.setState({
                loading: false
            });
            if(responseData.data.status !=200){
                toast.error(responseData.data.error.reason, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
           else if(responseData.data.schema){
                let schema = responseData.data.schema;
                let rows = responseData.data.datarows;
                const finalRows = [];
                for(var i=0;i<schema.length;i++){
                    cols.push({ headerName: schema[i].name, field: schema[i].name })
                }

                for(var j=0;j<rows.length;j++){
                    let object = new Object();
                    for(var i=0;i<schema.length;i++){
                        object[schema[i].name] = rows[j][i];

                    }
                    finalRows.push(object);
                }


                this.setState({
                    cols:cols,
                    rows:finalRows
                })
            }
        })

    }

    openModel = () =>{

        this.setState({

            openModel:true

        })
    }
    openUpdateModel = () =>{
        this.setState({
            updateModel:true
        })
    }
    onFormChageHandler = (event) =>{
        let currentState = this.state.currentFormData;
        currentState[event.target.name] = event.target.value;
        this.setState({currentFormData:currentState});
    }

    onNewFormChageHandler =(event) =>{
    let currentState = this.state.newFormData;
    currentState[event.target.name] = event.target.value;
    this.setState({newFormData:currentState});
}

    submit = () =>{

        const data = {id:this.state.currentFormData.id,queryName:this.state.currentFormData.queryName,huntingQuery:this.state.query,description:this.state.currentFormData.description}

        histroicalService.saveThreatHuntingQuries(data).then((responseData)=>{
            console.log(responseData)
        })
        this.fetchSavedQuries();
        this.closeUpdateModel();

    }

    saveNew = () =>{

        if(this.state.newFormData.queryName ===''){
            toast.error("Enter Valid Query Name", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return false;
        }
        if(this.state.newFormData.description ===''){
            toast.error("Enter Valid Descirption", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return false;
        }

        const data = {id:0,queryName:this.state.newFormData.queryName,huntingQuery:this.state.query,description:this.state.newFormData.description}

        histroicalService.saveThreatHuntingQuries(data).then((responseData)=>{
            console.log(responseData)
        })
        this.fetchSavedQuries();
        this.closeNewModel();

    }
    closeNewModel  = () =>{
        this.setState({

            openModel:false

        })
    }

    closeUpdateModel= () =>{
        this.setState({

            updateModel:false

        })
    }

    openHunting = (event) =>{
        const existingSearches = this.state.savedQuries;
        for(var i=0;i<existingSearches.length;i++){
            if(existingSearches[i].id === event.value){
                console.log(existingSearches[i].huntingQuery)

                this.setState({query:existingSearches[i].huntingQuery})
                this.setState({currentFormData:existingSearches[i]});
                this.executeQueryFromDB(existingSearches[i].huntingQuery);
            }
        }
    }

   executeQueryFromDB = (query) =>{

       this.setState({
           loading:true
       })

       let data = {sql:query}
       histroicalService.getThreatHuntingQuries(data).then((responseData) =>{
           const cols = [];
           this.setState({
               loading:false
           })
           if(responseData.data.schema){
               let schema = responseData.data.schema;
               let rows = responseData.data.datarows;
               const finalRows = [];
               for(var i=0;i<schema.length;i++){
                   cols.push({ headerName: schema[i].name, field: schema[i].name })
               }

               for(var j=0;j<rows.length;j++){
                   let object = new Object();
                   for(var i=0;i<schema.length;i++){
                       object[schema[i].name] = rows[j][i];

                   }
                   finalRows.push(object);
               }


               this.setState({
                   cols:cols,
                   rows:finalRows
               })
           }
       })

   }

    renderField = ({ input, label, type, meta: { touched, error } }) => (
        <div>
            <label>{label}</label>
            <div>
                <input className="form-control" {...input} placeholder={label} type={type} />
                {touched && error && <span>{error}</span>}
            </div>
        </div>
    )
    renderTextAreaField = ({ input, label, type, meta: { touched, error } }) => (
        <div>
            <label>{label}</label>
            <div>
                <textarea className="form-control" {...input} placeholder={label} type={type} />
                {touched && error && <span>{error}</span>}
            </div>
        </div>
    )

    render(){
        return(

            <div className="queryContainer">
                <Row style = {{paddingTop:"12px",marginBottom:'14px'}}>
                    {this.state.loading && <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
                    <Col xs={10}>
                        <p><b>{this.state.currentFormData.description}</b></p>
                    </Col>
                    <Col>
                        <div className="form__form-group" style={{width: "200px",float: "right" }}>

                            <div className="form__form-group-field">



                                <Field
                                    name="existingQuery"
                                    component={renderSelectField}
                                    type="text"
                                    onChange={this.openHunting}
                                    placeholder="Name of Query"
                                    options={this.state.options}

                                />
                            </div>
                        </div>
                    </Col>

                </Row>

                <ToastContainer
                    position="top-right"
                    hideProgressBar={false}
                    autoClose={false}
                    newestOnTop={true}
                    closeOnClick={false}
                    draggable={false}
                    rtl={false}
                />

                        <Card style = {{paddingTop:'12px'}}>


                            <CardBody>
                                <AceEditor ref="aceEditor"
                                    placeholder="Run Threat Hunting "
                                    mode="mysql"
                                    theme="github"
                                    name="blah2"
                                    height="300px"
                                    width="100%"
                                    fontSize={14}
                                    onChange={this.changeQueryHandler}
                                    showPrintMargin={true}
                                    showGutter={true}
                                    highlightActiveLine={true}
                                    value={this.state.query}
                                    setOptions={{
                                        enableBasicAutocompletion: true,
                                        enableLiveAutocompletion: true,
                                        enableSnippets: true,
                                        showLineNumbers: true,
                                        tabSize: 2,
                                    }}/>


                            </CardBody>
                            <div style={{borderTop: "1px solid"}}>
                                <Row>
                                    <Col xs={6}>
                                        <div style = {{marginTop:'12px',marginLeft:'14px'}}><b>N.o Of Records Found : {this.state.rows.length}</b></div>

                                    </Col>
                                    <Col xs={6}>

                                        <Button onClick={this.openModel} style = {{marginTop:'12px',float:'right',marginLeft:'14px'}} color="primary" >Save</Button>

                                        {this.state.currentFormData.queryName!='' && <Button onClick={this.openUpdateModel} style = {{marginTop:'12px',float:'right',marginLeft:'14px'}} color="primary" >Update</Button>
                                        }
                                        <Button onClick={this.executeQuery} style = {{marginTop:'12px',float:'right'}} color="primary" >Execute</Button>
                                    </Col>
                                </Row>

                            </div>

                        </Card>

                <Card>

                    <ThreatHuntingTable rows = {this.state.rows} cols = {this.state.cols}/>

                </Card>

                <Modal isOpen={this.state.openModel}  >
                    <ModalHeader >Save New Query</ModalHeader>
                    <form style={{width:'100%'}} className="form form--horizontal">
                    <ModalBody>

                            <div className="form__form-group">
                                <span className="form__form-group-label">Name:</span>
                                <div className="form__form-group-field">
                                    <input type="text"  name = "queryName" className="form-control" value={this.state.newFormData.queryName} onChange={this.onNewFormChageHandler}/>

                                </div>
                            </div>
                            <div className="form__form-group">
                            <span className="form__form-group-label">Description:</span>
                            <div className="form__form-group-field">

                                <textarea  name="description" value={this.state.newFormData.description} onChange={this.onNewFormChageHandler} className="form-control"></textarea>


                            </div>
                        </div>

                    </ModalBody>
                    <ModalFooter style = {{width:'100%'}}>
                        <Button type="button" onClick={this.saveNew}  color="primary" >Save</Button>{' '}

                        <Button type="button" color="secondary" onClick = {this.closeNewModel}>Cancel</Button>
                    </ModalFooter>
                    </form>
                </Modal>

                <Modal isOpen={this.state.updateModel}  >
                    <ModalHeader >Update Query</ModalHeader>
                    <form style={{width:'100%'}} className="form form--horizontal">
                        <ModalBody>

                            <div className="form__form-group">
                                <span className="form__form-group-label">Name:</span>
                                <div className="form__form-group-field">
                                    <input type="text"  name = "queryName" className="form-control" value={this.state.currentFormData.queryName} onChange={this.onFormChageHandler}/>

                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Description:</span>
                                <div className="form__form-group-field">

                                    <textarea  name="description" value={this.state.currentFormData.description} onChange={this.onFormChageHandler} className="form-control"></textarea>


                                </div>
                            </div>

                        </ModalBody>
                        <ModalFooter style = {{width:'100%'}}>
                            <Button type="button" onClick={this.submit}  color="primary" >Save</Button>{' '}

                            <Button type="button" color="secondary" onClick = {this.closeUpdateModel}>Cancel</Button>
                        </ModalFooter>
                    </form>
                </Modal>

            </div>
           )
    }

}

export default reduxForm({
    form: 'save_threat_hunting_form',
    enableReinitialize : true
})(withTranslation('common')(ThreatHutingEditor));

