export const OPEN_THERAT_EXPLORE_ENTITY = 'OPEN_THERAT_EXPLORE_ENTITY';
export const OPEN_THERAT_TIME_LINE = 'OPEN_THERAT_TIME_LINE';



export function openEntity(action) {
    return {
        type: OPEN_THERAT_EXPLORE_ENTITY,
        payload: action,
    };
}
export function opeTimeLine(action) {
    return {
        type: OPEN_THERAT_TIME_LINE,
        payload: action,
    };
}

