export const ADD_DASHBOARD = 'ADD_DASHBOARD';
export const UPDATE_DASHBOARD = 'UPDATE_DASHBOARD';
export const UPDATE_LAYOUT = 'UPDATE_LAYOUT';
export const FETCH_LAYOUT = "FETCH_LAYOUT"
export const LOAD_LAYOUT = "LOAD_LAYOUT";
export const CHANGE_DASHBOARD = "CHANGE_DASHBOARD";
export const DELETE_DASHBOARD = "DELETE_DASHBOARD";
export const SAVE_DASHBOARD_LAYOUT = "SAVE_DASHBOARD_LAYOUT";


export function saveDashboardLayout(action){
  return {
    type: SAVE_DASHBOARD_LAYOUT,
    payload:action
  }
}

export function deleteDashboard(action){
  return {
    type: DELETE_DASHBOARD,
    payload:action
  }
}

export function changeDashboard(action){
  return {
    type: CHANGE_DASHBOARD,
    payload:action
  }
}

export function loadFetchLayout (action){

  return {
    type: FETCH_LAYOUT,
    payload:action
  };

}

export function openLayout(paylaod){
  return {
    type: LOAD_LAYOUT,
    paylaod:paylaod
  };
}

export function addDasboard(payload) {
    return {
      type: ADD_DASHBOARD,
      layout: payload,
    };
  }
  
  export function updateDashboard(payload){
    return {
        type: UPDATE_DASHBOARD,
        layout: payload,
      };
  }

  export function updateLayout(payload){
    return {
      type: UPDATE_LAYOUT,
      layout: payload,
    };
  }
