import React, {useEffect, useState} from 'react';

import { useDispatch, useSelector } from 'react-redux';
import {Button, Card, CardBody, Col, Modal, ModalBody, ModalFooter, ModalHeader, Popover, PopoverHeader, PopoverBody, Row} from 'reactstrap';
import {Field, reduxForm,reset} from "redux-form";
import 'react-toastify/dist/ReactToastify.css';
import { toast,ToastContainer } from 'react-toastify';
import renderSelectField from '../../shared/components/form/Select';
import dashboardService from "../../api/dashboardService";
import userService from "../../api/userService";
import caseService from "../../api/caseService";
import BarLoader from "react-spinners/BarLoader";
import Emitter from "../../events/Events";

const LogDetailView = (props) =>{

    console.log(props.fields)
    const data = useSelector((state) => state.panelReducer.currentEventData);

    const [isModelOpen,setiSModelOpen] = useState(false)
    const [isCaseModelOpen,setIsCaseModelOpen] = useState(false)

    const [isInvestigationModelOpen,setIsInvestigationModelOpen] = useState(false)

    const toggleCaseView = (event) =>{
        if(event){
            event.preventDefault();
        }

        setIsCaseModelOpen(!isCaseModelOpen);
    }
    const toggleInvestigationView = (event) =>{
        if(event){
            event.preventDefault();
        }

        setIsInvestigationModelOpen(!isInvestigationModelOpen);
    }

    const openEventView = (event) =>{
        event.preventDefault();
        setiSModelOpen(true);
    }


    const closeEventView = () =>{

        setiSModelOpen(false);
    }
    if(data.type === 'events'){
        return (
            <Card style ={{height:'auto',overflow:'auto'}}>
                <CardBody style = {{height:'1000px',overflow:'auto'}}>
                   
                   <table class = 'table'>
                        <tbody>
                       {
                           Object.keys(data.data).map((key, id) =>
                           <tr key = {id}>
                                 <td className= "td-field-label">{key}</td>
                                {props.fields[key]==="IP" && 
                                  <td>
                                    <DisplayPopover entityType = 'IP' fieldName = {key} fieldValue = {data.data[key]} />
                                     
                                </td>}
                                {(props.fields[key]==="text" || props.fields[key]==="numeric") && 
                                  <td>{data.data[key]}</td>
                                }
                                {props.fields[key]==="Hash" && 
                                  <td>
                                       <DisplayPopover entityType = 'hash' fieldName = {key} fieldValue = {data.data[key]} />

                                  </td>
                                }
                                {props.fields[key]==="Domain" && 
                                  <td>
                                       <DisplayPopover entityType = 'Threat_IOC_DOMAIN' fieldName = {key} fieldValue = {data.data[key]} />

                                  </td>
                                }
                                {props.fields[key]==="User" && 
                                  <td> <DisplayPopover entityType = 'User' fieldName = {key} fieldValue = {data.data[key]} /></td>
                                }
                                 {!props.fields[key] && 
                                  <td>{data.data[key]}</td>
                                }
                                 
                            </tr>
                           )
                       }
                    </tbody>
                    </table>       
                       
                   
                   

                 </CardBody>
            </Card>

        )
    }else if(data.type === 'alerts' && data.data['rule_data']){

        return ( <Card style = {{height:'auto',overflow:'auto'}}>

                <CardBody>

                    <Row >
                        <Col>
                            <div className="card-link">
                                <Button style = {{height:"27px",float:'right'}} color="link"  onClick={toggleCaseView}>Create Case</Button> {'  '}
                                
                            </div>
                        </Col>
                    </Row>

                    <table class = 'table'>
                        <tbody>


                        {
                            Object.keys(data.data).filter(key => key!='rule_data' && key!= 'investigation_name' && key!= 'investigation_id' && key!= 'case_id' && key!= 'case_name').map((key, id) =>

                                <tr key = {id}>
                                    <td className= "td-field-label">{key}</td>
                                    <td>{data.data[key]}</td>
                                </tr>
                            )
                        }
                        {data.data['investigation_name'] &&
                        <tr>
                            <td  className= "td-field-label">Investigation</td>
                            <td><a target="_blank" href={`/explore?id=${data.data['investigation_id']}`} >{data.data['investigation_name']}</a> </td>
                        </tr>
                        }
                        {data.data['case_name'] &&
                        <tr>
                            <td  className= "td-field-label">Case</td>
                            <td><a target="_blank" href={`/cases?id=${data.data['case_id']}`} >{data.data['case_name']}</a> </td>
                        </tr>
                        }
                        {data.data['rule_data'] &&
                        <tr>
                            <td  className= "td-field-label">Event Count</td>
                            <td><a href="#" onClick = {openEventView}>{data.data['rule_data']['event_data'].length}</a> </td>
                        </tr>
                        }



                        </tbody>
                    </table>


                    <ModelAlertView  close = {closeEventView} type = {data.type} eventData={data.data} isModelOpen = {isModelOpen}/>

                    <ModelCaseView close={toggleCaseView} resetForm = {props.resetForm} handleSubmit = {props.handleSubmit} selectedRows = {data.selectedRows} isCaseModelOpen = {isCaseModelOpen}/>

                    <ModelInvestigationView close={toggleInvestigationView} handleSubmit = {props.handleSubmit} selectedRows = {data.selectedRows} isCaseModelOpen = {isInvestigationModelOpen}/>

                </CardBody>
            </Card>
        )
    }else{
        return (<div>No Data</div>)
    }

    

}
const DisplayPopover = (props)=>{
    const currenData = new Date();
    const startDate = currenData.setDate(currenData.getDate()-30)
     
    const {fieldName,fieldValue,entityType} = props;
    const [popoverOpen,setPopoverOpen] = useState(false);
    const [data,setData] = useState({});
    const [loading,setLoading] = useState(false);


    const toggle = ()=>{
        if(!popoverOpen && Object.keys(data).length===0){
            setPopoverOpen(!popoverOpen);
            setLoading(true)
            const data = {fieldType:entityType,fieldValue:fieldValue,startDate:startDate,endDate:currenData.getTime(),malwareSearchTye:'malwareSearchType'}
    
            caseService.getInfo(data).then((responseData) =>{
                setData(responseData.data)
                setLoading(false)
               
            })
        }else{
            setPopoverOpen(!popoverOpen);
            setLoading(false)
        }
        
       
    }
    return <React.Fragment>
        <a onClick = {toggle} id={'Popover' + fieldName}  block style={{ whiteSpace: "normal" }} href = "#" class ="btn-link"> {fieldValue} </a>
        <Popover placement="bottom" isOpen={popoverOpen} target={'Popover' + fieldName} toggle={toggle}>
        <PopoverHeader>Information : <a target = "_blank" href = {`/threat-explore?entityType=${entityType}&entityValue=${fieldValue}`}> {fieldName} - {fieldValue}</a></PopoverHeader>
        <PopoverBody>
            {loading && <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
            {Object.keys(data).length > 0 && <DiplayIpEntity entity = {data}/>}

        </PopoverBody>
      </Popover>
    </React.Fragment>
}

const DiplayIpEntity = (props)=>{
    const {entity} = props;
    return <div className="" style = {{height:'300px',overflowY:'auto'}}>

<table class = 'table' >
                        <tbody>
                       {
                           Object.keys(entity.userContext).map((key, id) =>
                           <tr key = {id}>
                                 <td className= "td-field-label"><b>{key.toUpperCase()}</b></td>
                               
                                  <td>{`${entity.userContext[key]}`}</td>
                                
                                 
                            </tr>
                           )
                       }
                    </tbody>
                    </table>  
    
</div>
}

const ModelInvestigationView = ( props) =>{
    const {isCaseModelOpen,handleSubmit,selectedRows} = props;
  
    const [loading,setLoading] = useState(false);
     const dispatch = useDispatch();
    useEffect(()=>{
      
        dispatch(reset('case_form'));
       
    },[isCaseModelOpen])

    const showError = (message) =>{
        toast.error(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    const showSuccess = (message) =>{
        toast.success(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }

    const createCase = (values) =>{

        if(!values.name){
            showError("Please Enter Investigation Name")
            return false;
        }

        if(!values.priority){
            showError("Please Select Priority")
            return false;
        }
        if(!values.description){
            showError("Please Enter Description")
            return false;
        }

        const tempAlertIds = [];
        for(let i=0;i<selectedRows.length;i++){
            tempAlertIds.push(selectedRows[i].id);
        }

        const data = {
            alertId:tempAlertIds.join(','),
            workBenchName:values.name,
            status:'New',
            description:values.description,
            priority:values.priority.value,
            id:0

        }

        setLoading(true)
        caseService.createInvestigation(data).then((responseData)=>{
            if(responseData.status===200 && responseData.data.id){
                attachInvestigation(responseData.data.id,values.name)

            }



        }).catch()
    }

    

    const attachInvestigation = (id,name)=>{

        const tempAlertIds = [];
        for(let i=0;i<selectedRows.length;i++){
            tempAlertIds.push(selectedRows[i].id);
        }

        const data = {investigationId:id,investigationName:name,alertId:tempAlertIds.join(',')}



        caseService.updateInvestigationWithAlerts(data).then((responseData) =>{
            if(responseData.status===200){
                showSuccess("Successfully Created the Investigation")
                const payload   = {type:'investigation',data:name,id:id}
                Emitter.emit('UPDATE_CASE',payload);
                setLoading(false);
                props.close();

            }
        })
    }

  
    return ( <Modal isOpen={isCaseModelOpen} >
        {loading &&  <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
        <ModalHeader >Create Investigation</ModalHeader>
        <form className="form" onSubmit={handleSubmit(createCase)} style = {{display:'table-row'}}>
            <ModalBody>
                <ToastContainer
                    position="top-right"
                    hideProgressBar={false}
                    autoClose={false}
                    newestOnTop={true}
                    closeOnClick={false}
                    draggable={false}
                    rtl={false}
                />

                <div className="form__form-group" >
                    <span className="form__form-group-label">Name</span>
                    <div className="form__form-group-field">
                        <Field
                            name="name"
                            component="input"
                            type="text"
                            placeholder="Enter Investigation Name"
                        />
                    </div>
                </div>

                <div className="form__form-group">
                    <span className="form__form-group-label">Priority</span>
                    <div className="form__form-group-field">
                        <Field
                            name="priority"
                            component={renderSelectField}
                            type="select"
                            placeholder="Enter Priority"
                            options={[
                                { value: 'Critical', label: 'Critical' },
                                { value: 'High', label: 'High' },
                                { value: 'Medium', label: 'Medium' },
                                { value: 'Low', label: 'Low' },

                            ]}
                        />
                    </div>
                </div>
                <div className="form__form-group">
                    <span className="form__form-group-label">Description</span>
                    <div className="form__form-group-field">
                        <Field
                            name="description"
                            component="textarea"
                            type="text"
                            placeholder="Enter Description"

                        />
                    </div>
                </div>




            </ModalBody>
            <ModalFooter>
                <Button color="primary" type = "submit" >Create Investigation</Button>{' '}
                <Button color="secondary" type = "button" onClick = {props.close}>Close</Button>{' '}      
            </ModalFooter>
        </form>
    </Modal>);




}


const ModelCaseView = ( props) =>{
    const {isCaseModelOpen,handleSubmit,selectedRows,resetForm} = props;
    const [users,setUsers] = useState([])
    const [loading,setLoading] = useState(false);
    const dispatch = useDispatch();
    useEffect(()=>{
        console.log("test resting");
        dispatch(reset('case_form'));
        fetchUsers()
    },[isCaseModelOpen])

    const showError = (message) =>{
        toast.error(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    const showSuccess = (message) =>{
        toast.success(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }

  const createCase = (values) =>{
      if(!values.assignee){
          showError("Please Select Assignee")
          return false;
      }
      if(!values.caseTitle){
          showError("Please Enter Case Title")
          return false;
      }
      if(!values.caseType){
          showError("Please Select Case Type")
          return false;
      }
      if(!values.priority){
          showError("Please Select Priority")
          return false;
      }
      if(!values.summary){
          showError("Please Enter Summary")
          return false;
      }

      const data = {
          alertIds:JSON.stringify(selectedRows),
          assingee:values.assignee.value,
          caseType:values.caseType.value,
          description:values.summary,
          priority:values.priority.value,
          title:values.caseTitle
      }

      setLoading(true)
     caseService.createCase(data).then((responseData)=>{
            if(responseData.status===200 && responseData.data.id){
                attachCase(responseData.data.id,values.caseTitle)

            }



     }).catch()
  }

  const attachCase = (id,caseTitle)=>{
      const tempAlertIds = [];
      for(let i=0;i<selectedRows.length;i++){
          tempAlertIds.push(selectedRows[i].id);
      }

      const alertData = {caseName:caseTitle,caseId:id,alertId:tempAlertIds.join(',')}
      caseService.attachCaseToAlerts(alertData).then((responseData) =>{
          if(responseData.status===200){
              showSuccess("Successfully Created the Ticket")
              const payload   = {type:'case',data:caseTitle,id:id}

              Emitter.emit('UPDATE_CASE',payload);
              setLoading(false);
              props.close();

          }
      })
  }

    const fetchUsers = () =>{
        userService.getAllUsers().then((responseData) =>{
            if(responseData.data){
            let tempData =    responseData.data.map((value)=>{
                    return {label:value.userName,value:value.userName};
                })
                setUsers(tempData);
            }
        })
    }

    return ( <Modal isOpen={isCaseModelOpen} >
        {loading &&  <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
        <ModalHeader >Case View</ModalHeader>
        <form className="form" onSubmit={handleSubmit(createCase)} style = {{display:'table-row'}}>
        <ModalBody>
            <ToastContainer
                position="top-right"
                hideProgressBar={false}
                autoClose={false}
                newestOnTop={true}
                closeOnClick={false}
                draggable={false}
                rtl={false}
            />

                <div className="form__form-group" >
                    <span className="form__form-group-label">Case Title</span>
                    <div className="form__form-group-field">
                        <Field
                            name="caseTitle"
                            component="input"
                            type="text"
                            placeholder="Enter Case Title"
                        />
                    </div>
                </div>
                <div className="form__form-group">
                    <span className="form__form-group-label">Case Type</span>
                    <div className="form__form-group-field">
                        <Field
                            name="caseType"
                            component={renderSelectField}
                            type="select"
                            placeholder="Enter Case Type"
                            options={[
                                { value: 'Port Scanning Activity', label: 'Port Scanning Activity' },
                                { value: 'Malware Infection', label: 'Malware Infection' },
                                { value: 'Distributed Denial Of Service', label: 'Distributed Denial Of Service' },
                                { value: 'Distributed Denial Of Service Diversion', label: 'Distributed Denial Of Service Diversion' },
                                { value: 'Insider Breach', label: 'Insider Breach' },
                                { value: 'Unauthorized Privilege Escalation', label: 'Unauthorized Privilege Escalation' },
                                { value: 'Destructive Attack"', label: 'Destructive Attack"' },
                                { value: 'Advanced Persistent Threat Or Multistage Attack', label: 'Advanced Persistent Threat Or Multistage Attack' },
                                { value: 'False Alarms', label: 'False Alarms' },
                                { value: 'Other', label: 'Other' },
                            ]}
                        />
                    </div>
                </div>
                <div className="form__form-group">
                    <span className="form__form-group-label">Priority</span>
                    <div className="form__form-group-field">
                        <Field
                            name="priority"
                            component={renderSelectField}
                            type="select"
                            placeholder="Enter Priority"
                            options={[
                                { value: 'Critical', label: 'Critical' },
                                { value: 'High', label: 'High' },
                                { value: 'Medium', label: 'Medium' },
                                { value: 'Low', label: 'Low' },

                            ]}
                        />
                    </div>
                </div>
                <div className="form__form-group">
                    <span className="form__form-group-label">Summary</span>
                    <div className="form__form-group-field">
                        <Field
                            name="summary"
                            component="textarea"
                            type="text"
                            placeholder="Enter Summary"

                        />
                    </div>
                </div>
                <div className="form__form-group">
                    <span className="form__form-group-label">Assignee</span>
                    <div className="form__form-group-field">
                        <Field
                            name="assignee"
                            component={renderSelectField}
                            type="select"
                            placeholder="Select Assignee"
                            options={users}
                        />
                    </div>
                </div>



        </ModalBody>
        <ModalFooter>
            <Button color="primary" type = "submit" >Create Case</Button>{' '}
            <Button color="secondary" type = "button" onClick = {props.close}>Close</Button>{' '}                        
        </ModalFooter>
        </form>
    </Modal>);




}



const ModelAlertView = (props) =>{
    const {eventData,isModelOpen,close} = props;

    const [eventInfo,setEventInfo] = useState({})
    const showEventInfo = (data) =>{
        setEventInfo(data)
    }

    useEffect(()=>{

        if(eventData && eventData['rule_data']){
            setEventInfo(eventData['rule_data']['event_data'][0]);
        }
    })

    return ( <Modal style = {{maxWidth:'90%'}} isOpen={isModelOpen} className="add-widget-model" >
        <ModalHeader >Alert View</ModalHeader>
        <ModalBody>

            <form className="form form--horizontal">
                <Row style = {{width:'100%'}}>

                    <Col xs = {12} md = {6} lg={6} xl={6} sm={12}>
                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Timestamp :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['@timestamp']}</span>
                            </div>
                        </div>


                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Description :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['description']}</span>
                            </div>
                        </div>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Category :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['rule_category']}</span>
                            </div>
                        </div>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Tactics :</b></span>
                            <div className="form__form-group-field">


                                {eventData['techniques'] && eventData['tactics'] != 'low' &&
                                <div className="badge badge-success" > {eventData['tactics']}</div>
                                }
                            </div>
                        </div>







                    </Col>

                    <Col xs = {12} md = {6} lg={6} xl={6} sm={12}>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Name :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['name']}</span>
                            </div>
                        </div>



                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Priority :</b></span>
                            <div className="form__form-group-field">

                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'high' &&
                                <div className="badge badge-danger" >{eventData['priority']}</div>
                                }
                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'medium' &&
                                <div className="badge badge-warning" >{eventData['priority']}</div>
                                }
                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'critical' &&
                                <div className="badge badge-danger" >{eventData['priority']}</div>
                                }
                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'low' &&
                                <div className="badge badge-success" >{eventData['priority']}</div>
                                }
                            </div>
                        </div>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Techniques :</b></span>
                            <div className="form__form-group-field">


                                {eventData['techniques'] && eventData['techniques'] != 'low' &&
                                <div className="badge badge-success" > {eventData['techniques']}</div>
                                }
                            </div>
                        </div>





                    </Col>


                </Row>
                <Card>
                    <Row>
                        <table className="table table-striped">
                            <thead>
                            <tr>
                                <th>Timestamp</th>
                                <th>Event Name</th>
                                <th>Event Description</th>
                                <th>Event Category</th>
                                <th>Log Device</th>
                            </tr>
                            </thead>
                            <tbody>
                            {eventData && eventData['rule_data'] && eventData['rule_data']['event_data'].map((event,index)=>{
                                return( <tr onClick = {()=>{showEventInfo(eventData['rule_data']['event_data'][index])}} style={{cursor:"pointer"}}>
                                    <td>{event['@timestamp']}</td>
                                    <td>{event['event_name']}</td>
                                    <td>{event['event_description']}</td>
                                    <td>{event['event_category']}</td>
                                    <td>{event['log_device']}</td>
                                </tr>)
                            }) }
                            </tbody>

                        </table>



                        <Card style = {{paddingLeft:'12px'}}>
                            <div className ="card-title" style={{borderBottom:'1px solid #e7e2e2'}}>
                                <h4 style={{textAlign:'left'}}>Event Information</h4>
                            </div>

                            <form className="form form--horizontal">


                                <Row>
                                    {
                                        Object.keys( eventInfo).map((singleEvent)=>{
                                            return( <Col xs = {12} lg = {6} md = {6} sm={12}>
                                                    <div className="form__form-group">
                                                        <span className="form__form-group-label" style={{width:'180px'}}><b>{singleEvent} :</b></span>
                                                        <div className="form__form-group-field" style={{width: "calc(100% - 202px)"}}>

                                                            {eventInfo[singleEvent]}


                                                        </div>
                                                    </div>
                                                </Col>
                                            )
                                        })

                                    }
                                </Row>
                            </form>

                        </Card>

                    </Row>
                </Card>

            </form>

        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={props.close} >Close</Button>{' '}

        </ModalFooter>
    </Modal>)

}

export default reduxForm({
    form: 'case_form',
})(LogDetailView);
