import React, {Component, useEffect, useState} from 'react';
import {Badge, Button, ButtonToolbar, Card, CardBody, Col, Container, Input, Row} from "reactstrap";
import {Field, reduxForm} from "redux-form";
import {withTranslation} from "react-i18next";
import DeleteIcon from '@material-ui/icons/Delete';
import dashboardService from "../../../api/dashboardService";
import {ElasticQueryBuilder} from "../../../utils/ElasticQueryBulider";
import detectionsService from "../../../api/detectionsService";
import {toast, ToastContainer} from "react-toastify";
import BarLoader from "react-spinners/BarLoader";
import Moment from "react-moment";
import JSONPretty from 'react-json-prettify';
import {atomOneLight} from 'react-json-prettify/dist/themes';
import { Link } from 'react-router-dom';
export  class CreateAd extends Component{

    state  = {fields:[],id:""}

    constructor() {
        super();
    }

    componentDidMount() {

        const { match: { params } } = this.props;
        if(params.id){
            this.setState({id:params.id})
        }

        dashboardService.getAllLogFields().then((responseData)=>{
            if(responseData.status===200){
                let finalData = responseData.data;
                let tempFields = [];
                for(let i=0;i<finalData.length;i++){
                   if(finalData[i].dataType === 'numeric'){
                       tempFields.push({label:finalData[i].fieldName,value:finalData[i].fieldName})
                   }else{
                       tempFields.push({label:finalData[i].fieldName,value:finalData[i].fieldName+".keyword"})
                   }

                }
                this.setState({fields:tempFields});
            }
        })

    }


    render() {
        return (<Container>
                <div className="" style={{paddingTop:'80px'}}>
                    <AdModel initialize = {this.props.initialize} id ={this.state.id} fields = {this.state.fields}/>
                </div>

        </Container>)
    }


}

const AdModel = (props) =>{

    const [rows,setRows] = useState([]);
    const [filter,setFilter] = useState([])
    const [loading,setLoading] = useState(false);

    const [features,setFeatures] = useState([])

    const [running,setRunning] = useState(false);

    const [formData,setFormData] = useState({
        id:'',
        name:'',
        description:'',
        time_field:'@timestamp',
        feature_attributes:[],
        filter_query:[],
        detection_interval:{period:{interval:1,unit:'Minutes'}},
        window_delay:{period:{interval:1,unit:'Minutes'}},
        shingle_size:8,
        category_field:[]
    });

    useEffect(()=>{
        if(props.id!=''){
            detectionsService.getSingleDetector(props.id).then((responseBody)=>{
                if(responseBody.status === 200 && (responseBody.data.state === 'RUNNING' || responseBody.data.state === 'INIT')){
                    setRunning(true);
                    let object = new Object();

                    object.name = responseBody.data.anomaly_detector.name
                    object.description = responseBody.data.anomaly_detector.description
                    object.interval = responseBody.data.anomaly_detector.detection_interval.period.interval
                    object.windowDelay = responseBody.data.anomaly_detector.window_delay.period.interval
                    object.windowSize = responseBody.data.anomaly_detector.shingle_size
                    setFilter(responseBody.data.anomaly_detector.filter_query);
                    const existingfeatures = responseBody.data.anomaly_detector.feature_attributes;
                    const tempFeatures = [];
                    for(var i=0;i<existingfeatures.length;i++){
                        const currentFeature = existingfeatures[i];

                        const featureName = Object.keys(currentFeature.aggregation_query).join('')

                        const aggType = Object.keys(currentFeature.aggregation_query[featureName]).join('')

                        const field =  currentFeature.aggregation_query[featureName][aggType]
                        tempFeatures.push({feature_id:existingfeatures[i].feature_id, featureName:existingfeatures[i].feature_name,status:true,aggMethod:aggType,aggField:field.field})
                    }
                    setFeatures(tempFeatures);
                    const currentFormData = formData;
                    currentFormData.id  = props.id;
                    currentFormData.name  = object.name;
                    currentFormData.description  = object.description;
                    currentFormData.window_delay.period.interval =   object.interval;
                    currentFormData.detection_interval.period.interval=   object.windowDelay;


                    currentFormData.shingle_size=   object.windowSize;
                    setFormData(currentFormData);
                    props.initialize(object)
                }else if(responseBody.data.anomaly_detector){
                    let object = new Object();

                    object.name = responseBody.data.anomaly_detector.name
                    object.description = responseBody.data.anomaly_detector.description
                    object.interval = responseBody.data.anomaly_detector.detection_interval.period.interval
                    object.windowDelay = responseBody.data.anomaly_detector.window_delay.period.interval
                    object.windowSize = responseBody.data.anomaly_detector.shingle_size
                    setFilter(responseBody.data.anomaly_detector.filter_query);
                    const existingfeatures = responseBody.data.anomaly_detector.feature_attributes;
                    const tempFeatures = [];
                    for(var i=0;i<existingfeatures.length;i++){
                        const currentFeature = existingfeatures[i];

                        const featureName = Object.keys(currentFeature.aggregation_query).join('')

                        const aggType = Object.keys(currentFeature.aggregation_query[featureName]).join('')

                        const field =  currentFeature.aggregation_query[featureName][aggType]
                        tempFeatures.push({feature_id:existingfeatures[i].feature_id, featureName:existingfeatures[i].feature_name,status:true,aggMethod:aggType,aggField:field.field})
                    }
                    setFeatures(tempFeatures);
                    const currentFormData = formData;
                    currentFormData.id  = props.id;
                    currentFormData.name  = object.name;
                    currentFormData.description  = object.description;
                    currentFormData.window_delay.period.interval =   object.interval;
                    currentFormData.detection_interval.period.interval=   object.windowDelay;
                    currentFormData.shingle_size=   object.windowSize;
                    setFormData(currentFormData);
                    props.initialize(object)
                }
            })
        }

    },[props.id])

    const {fields} = props;

    const clearFilter = ()=>{
        setFilter([]);
    }

    const addRow = () =>{
        const array = {key:'',operator:'',value:''}
        setRows([...rows,array]);
    }

    const addFeature = () =>{
        const array = {featureName:'',status:'',aggMethod:'',aggField:''}
        setFeatures([...features,array]);
    }
    const removeFeature = (e,index) =>{
        e.preventDefault();
        var array = [...features];
        array.splice(index, 1);
        setFeatures(array);
    }

    const removeRow = (e,index) =>{
        e.preventDefault();
        var array = [...rows];
        array.splice(index, 1);
        setRows(array);
    }

    const onChangeEventHandler = (event,index)=>{

        let newArr = rows.map((item, i) => {
            if (index == i) {
                return { ...item, [event.target.name]: event.target.value };
            } else {
                return item;
            }
        });


       setRows(newArr);
    }
    const onChangeEventFeatureHandler = (event,index)=>{

        let newArr = features.map((item, i) => {
            if (index == i) {
                return { ...item, [event.target.name]: event.target.value };
            } else {
                return item;
            }
        });


        setFeatures(newArr);
    }

    const onChangeHandlerForOriginalData = (event,fieldName) =>{
        const currentFormData = formData;

        if(fieldName === 'interval'){
            currentFormData.detection_interval.period.interval = parseInt(event.target.value) ;
        }
        if(fieldName === 'window_delay'){
            currentFormData.window_delay.period.interval = parseInt(event.target.value) ;
        }else if(fieldName === 'category_field'){
            currentFormData[fieldName] = [event.target.value];
        }else{
            currentFormData[fieldName] = event.target.value;
        }
        setFormData(currentFormData);
    }


    const submitData = () =>{


        if(rows.length!=0){
            const queryBuilder = new ElasticQueryBuilder({
                timeField: '@timestamp',
                esVersion: '7.9',
            });
            const query =   queryBuilder.build([],rows)

            const tempQuery = query.query;
            delete tempQuery.bool.filter
            formData.filter_query = tempQuery;
        }else if(filter.length!=0){
            formData.filter_query = filter;
        }

        if(props.id){
            formData["id"] = props.id
        }

      let tempFeatures = [];
      for(let i=0;i<features.length;i++){
          const aggQuery = new Object();
          aggQuery[features[i].featureName.replace(" ","_")] = {};
          aggQuery[features[i].featureName.replace(" ","_")][features[i].aggMethod] = {field:features[i].aggField};
          const data = {
              feature_name:features[i].featureName,
              feature_enabled:true,
              aggregation_query:aggQuery
          }
          tempFeatures.push(data)
      }

      formData.feature_attributes =  tempFeatures;

      const tempFormData = formData;
      if(formData.filter_query.length ==0 ){
         delete tempFormData.filter_query
      }
      if(formData.category_field.length ==0 ){
        delete tempFormData.category_field
     }

      const data = {data:JSON.stringify(tempFormData)};
        setLoading(true);
      detectionsService.createAd(data).then((responseData)=>{
          setLoading(false);
          if(responseData.status === 200){
              toast.success('Model was created successfully', {
                  position: "top-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
              });
          }else{
              toast.error('unable to create detector', {
                  position: "top-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
              });
          }
          console.log(responseData.data)
      })


    }

    const stopDetector = ()=>{
        const data = {id:props.id,state:'Running'};
        detectionsService.startOrStop(data).then((responseData)=>{
            if(responseData.status===200){
                detectionsService.getSingleDetector(props.id).then((responseBody)=> {
                    if (responseBody.status === 200 && responseBody.data.state != 'RUNNING') {
                        setRunning(false);
                    }
                });
            }
        })
    }


    return (
        <form className="form">
            <ToastContainer
                position="top-right"
                hideProgressBar={false}
                autoClose={false}
                newestOnTop={true}
                closeOnClick={false}
                draggable={false}
                rtl={false}
            />
            {loading && <BarLoader color= "#53c8b7" height ={4} width = {800}/>}
        <Card style={{backgroundColor:'transparent'}}>
            <CardBody>
                <div className="card__title">
                    <Row>
                        <Col xs = {8} lg= {8} md = {8}>
                            <h5 className="bold-text">Name and description

                                {running  &&
                                <React.Fragment>
                        <span style = {{fontSize: '16px',
                            paddingLeft: '12px'}}>
                            <Badge  color = "success">Running</Badge>

                           <h6 style={{paddingTop: '12px',color:'#2466cc'}}> Can't edit the editor while running</h6>

                        </span>

                                </React.Fragment>
                                }


                            </h5>
                        </Col>
                        <Col xs = {4} lg= {4} md = {4}>
                            {running &&
                                 <Button style={{float:'right'}} color = "danger" onClick={stopDetector} >Stop Detector  </Button>
                            }

                            <Link to = "/detections" style={{float:'right'}} color = "primary"  >Detectors</Link>

                        </Col>
                    </Row>

                </div>
                <Row>
                    <Col xs = {12} lg = {12} md= {12}>

                            <div className="form__form-group">
                                <span className="form__form-group-label">Name</span>
                                <div className="form__form-group-field">
                                    <Field disabled={running} onChange={(event)=>onChangeHandlerForOriginalData(event,'name')} label="Name" name="name" component="input" type="text"/>


                                </div>
                                <span className="form__form-group-description">
                            Specify a unique and descriptive name that is easy to recognize. Detector name must contain 1-64 characters. Valid characters are a-z, A-Z, 0-9, -(hyphen) and _(underscore)
                         </span>
                          </div>

                            <div className="form__form-group">
                                <span className="form__form-group-label">Description</span>
                                <div className="form__form-group-field">
                                    <Field disabled={running} label="Description" onChange={(event)=>onChangeHandlerForOriginalData(event,'description')} name="description" component="input"
                                           type="text"/>


                                </div>
                                <span className="form__form-group-description">
                                    Describe the purpose of the detector.
                                </span>
                            </div>






                    </Col>
                </Row>

            </CardBody>
    </Card>
     <br/>

     <Card style={{backgroundColor:'transparent'}}>
         <CardBody>
         <Row style={{width:'100%'}}>
             <Col xs = {11}>
                
             </Col>
             
             </Row>

             <Row>
             <Col xs = {4} lg = {4} md= {4}>


            <div className="form__form-group">
                <span className="form__form-group-label">Category Field</span>
                <div className="form__form-group-field">
                    
                <select  className="form-control" value = {formData.category_field[0]}  onChange={(event)=>onChangeHandlerForOriginalData(event,'category_field')}>
                  <option value = "-1">Select Category Field</option>
                  {fields.map((value=>{
                      return <option value={value.value}>{value.label}</option>
                  }))}
                </select>

                </div>
                <span className="form__form-group-description">
                        You can only apply the category field to the 'ip' and 'keyword' Elasticsearch data types.
                </span>
            </div>






</Col>
             </Row>


         </CardBody>
     </Card>
     <Card style={{backgroundColor:'transparent'}}>
         <CardBody>
             <Row style={{width:'100%'}}>
             <Col xs = {11}>
                 <h4>Filter Configuration</h4>
             </Col>
             <Col xs = {1}>
                 {filter.length===0 &&
                    <Button style={{float: 'right'}}  color="primary" onClick={addRow}>Add Filter</Button>
                 }
                 {filter.length != 0 &&
                     <Button style={{float: 'right'}}  color="primary" onClick={clearFilter}>Clear</Button>
                 }

                     </Col>
             </Row>

             <Row style={{width:'100%'}}>

                 {filter.length !==0 &&
                     <Col>
                         <JSONPretty style = {{width:'100%'}}  theme = {atomOneLight} json={filter}  padding={4} />
                     </Col>

                 }
                 {filter.length === 0 &&
                 <table className="table">
                     <tbody>
                     {rows.map((value,index)=>{
                         return (<tr key = {index}>
                             <td style={{width:'20%'}}>
                                 <select disabled={running} className="form-control" value={value.key} name = "key" onChange={(event)=>onChangeEventHandler(event,index)}>
                                     {fields.map((value=>{
                                         return <option value={value.value}>{value.label}</option>
                                     }))}
                                 </select> </td>

                             <td  style={{width:'20%'}}>
                                 <select disabled={running} className="form-control" value={value.operator} onChange={(event)=>onChangeEventHandler(event,index)} name = "operator">
                                     <option value = "=">Equals</option>
                                     <option value = "!=">Not Equals</option>
                                     <option value = "<">Greater Than</option>
                                     <option value = ">">Less Than</option>
                                     <option value = "=~">Contains</option>
                                     <option value = "!~">Not Contains</option>
                                     <option value = "exists">Exists</option>
                                     <option value = "notexists">Not Exists</option>


                                 </select>
                             </td>
                             <td style={{width:'20%'}}><input type="text" value={value.value} onChange={(event)=>onChangeEventHandler(event,index)} className="form-control" name="value"/> </td>
                             <td><a href="#" onClick={(e)=>{removeRow(e,index)}}><DeleteIcon color="secondary" fontSize="small"/></a>  </td>
                         </tr>)
                     })}
                     </tbody>

                 </table>
                 }

             </Row>

         </CardBody>
     </Card>

            <Card style={{backgroundColor:'transparent'}}>
                <CardBody>
                    <Row style={{width:'100%'}}>
                        <Col xs = {11}>
                            <h4>Detector operation settings</h4>
                        </Col>

                    </Row>

                    <Row style={{width:'100%',marginTop:'12px'}}>
                        <br/>
                        <Col xs = {12} lg = {12} md= {12}>
                        <div className="form__form-group">
                            <span className="form__form-group-label">Detector interval</span>
                            <div className="form__form-group-field">
                                <Field disabled={running} label="Interval" onChange={(event)=>onChangeHandlerForOriginalData(event,'interval')} name="interval" component="input"
                                       type="number"/>


                            </div>
                            <span className="form__form-group-description">
                                Define how often the detector collects data to generate anomalies. The shorter the interval is, the more real time the detector results will be, and the more computing resources the detector will need. The detector interval, together with data ingestion speed, also determines the preparation time of the detector. Long intervals and slow ingestion speed means the detector takes longer time to collect sufficient data to generate anomalies.
                         </span>
                        </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Window Delay</span>
                                <div className="form__form-group-field">
                                    <Field disabled={running} onChange={(event)=>onChangeHandlerForOriginalData(event,'window_delay')}  label="Window Delay" name="windowDelay" component="input"
                                           type="number"/>


                                </div>
                                <span className="form__form-group-description">
                                Specify a window of delay for a detector to fetch data, if you need to account for extra processing time.
                         </span>
                            </div>
                        </Col>

                    </Row>

                </CardBody>
            </Card>


            <Card style={{backgroundColor:'transparent'}}>
                <CardBody>
                    <Row style={{width:'100%'}}>
                        <Col xs = {11}>
                            <h4>Model Configuration</h4>
                        </Col>
                        <Col xs = {1}>
                            <Button style={{float: 'right'}}  color="primary" onClick={addFeature}>Add Model</Button>
                        </Col>
                    </Row>

                    <Row style={{width:'100%'}}>
                        <table className="table">

                            <thead>
                                <tr>
                                    <th>Feature Name</th>
                                    <th>Aggregation method</th>
                                    <th>Field</th>
                                </tr>
                            </thead>

                            <tbody>
                            {features.map((value,index)=>{
                                return (<tr key = {index}>
                                    <td style={{width:'20%'}}>
                                        <input disabled={running} type="text" className="form-control" value={value.featureName} onChange={(event)=>onChangeEventFeatureHandler(event,index)} name = "featureName" />
                                    </td>

                                    <td  style={{width:'20%'}}>
                                        <select disabled={running} className="form-control" value={value.aggMethod} onChange={(event)=>onChangeEventFeatureHandler(event,index)} name = "aggMethod">
                                            <option value = "sum">Sum</option>
                                            <option value = "value_count">Count</option>
                                            <option value = "average">Average</option>
                                            <option value = "min">Min</option>
                                            <option value = "max">Max</option>


                                        </select>
                                    </td>
                                    <td style={{width:'20%'}}><select className="form-control" value={value.aggField} name = "aggField" onChange={(event)=>onChangeEventFeatureHandler(event,index)}>
                                        {fields.map((value=>{
                                            return <option value={value.value}>{value.label}</option>
                                        }))}
                                    </select> </td>
                                    <td><a href="#" onClick={(e)=>{removeFeature(e,index)}}><DeleteIcon color="secondary" fontSize="small"/></a>  </td>
                                </tr>)
                            })}
                            </tbody>

                        </table>
                    </Row>

                </CardBody>
            </Card>

            <Card style={{backgroundColor:'transparent'}}>
                <CardBody>
                    <Row style={{width:'100%'}}>
                        <Col xs = {11}>
                            <h4>Advanced Settings</h4>
                        </Col>

                    </Row>

                    <Row style={{width:'100%',marginTop:'12px'}}>
                        <br/>
                        <Col xs = {12} lg = {12} md= {12}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Window Size</span>
                                <div className="form__form-group-field">
                                    <Field disabled={running} label="WindowSize" name="windowSize" component="input"
                                           type="number"/>


                                </div>
                                <span className="form__form-group-description">
                                Set the number of intervals to consider in a detection window. We recommend you choose this value based on your actual data. If you expect missing values in your data or if you want the anomalies based on the current interval, choose 1. If your data is continuously ingested and you want the anomalies based on multiple intervals, choose a larger window size.
                         </span>
                            </div>

                        </Col>

                    </Row>

                </CardBody>
            </Card>

            <ButtonToolbar className="form__button-toolbar">
                <Button disabled={running} color="primary" type="button" onClick={submitData}>Save and Start</Button>

            </ButtonToolbar>



        </form>)

}

export default reduxForm({
    form: 'ad_form',
    enableReinitialize:true
})(CreateAd);