import { takeEvery,put } from 'redux-saga/effects'
import caseService from '../../api/caseService'
import * as ActionTypes from '../actions/exploreActions';


function* getEntityViewData(action){

    const exploreData = yield caseService.getEntityInfo(action.payload);

    const finalData = {entityType:action.payload.entityType,processData:exploreData.data }

    yield put({ type: ActionTypes.OPEN_ENTITY, payload:finalData});

}

function* getExploreData(action){

    const exploreData = yield caseService.getSingleCase(action.payload.id);
    exploreData.data['type'] = action.payload.type;

    const graphData =  yield caseService.getGraphData(action.payload.id,action.payload.type);
    let finalData = {nodes:[],edges:[]}
    if(graphData.data.nodes.length>0){
        var tempNodes =[];
          const nodes  = graphData.data.nodes;
            for(var i=0;i<nodes.length;i++){
               if(nodes[i].isThreatIntelNode){
                   nodes[i]['color'] = {border:'red'};
                   nodes[i]['borderWidth'] = 3;

                 }
             tempNodes.push(nodes[i]);
           }
        finalData.nodes = tempNodes;
        finalData.edges = graphData.data.edges;
    }
    const payloadData = {alerts:exploreData.data,graphData:finalData};
    yield put({ type: ActionTypes.OPEN_EXPLORE_DATA, payload:payloadData});

}

export function* fetchEntityDataWatcher(){
    yield takeEvery(ActionTypes.LOAD_ENTITY_VIEW, getEntityViewData)
}

export function* fetchExploreDataWatcher(){
    yield takeEvery(ActionTypes.LOAD_EXPLORE_DATA, getExploreData)
}