import React, {useEffect, useState} from 'react';
import detectionsService from "../../../api/detectionsService";
import {Badge, Button, Card, CardBody} from "reactstrap";
import { DataGrid } from '@material-ui/data-grid';
import { Link } from 'react-router-dom';

const RenderBadege = (props)=>{
     const {label} = props
    if(label === 'RUNNING'){
        return <Badge color={"success"}>RUNNING</Badge>
    }
    if(label === 'DISABLED'){
        return <Badge color={"warning"}>DISABLED</Badge>
    }
    return <Badge color={"warning"}>{label}</Badge>

}


const ViewDetectors =(props) =>{

    const [rows,setRows] = useState([]);

    useEffect(()=>{
        detectionsService.getDetections().then((responseData)=>{
            setRows(responseData.data);
            console.log(rows);
        })
    },[])

    const openDetectionModel = (event) =>{
        event.preventDefault();
    }

    const columns = [
        { field: 'id', headerName: 'ID',flex: 0.3,hide: true},
        { field: 'name', headerName: 'Name',flex: 0.3, renderCell: (params) =>(
                <a href ={`/detections/${params.getValue('id')}`}>{params.getValue('name')}</a>),},
        { field: 'state', headerName: 'State',flex: 0.3,
            renderCell: (params) =>(
            <RenderBadege label ={params.getValue('state')}/>),

        },
        { field: 'count', headerName: 'Anomalies last 24 hours',flex: 0.3 },
        {
            field: 'date',
            headerName: 'Last anomaly occurrence',
            flex: 0.3

        },

    ];

    return (
        <Card className="panel">

        <CardBody >
           <div className="panel__btns" style={{top:'14px',right:'25px'}}>
                <Link to = "/create-ad" className = "button" color="primary"> Create detector</Link>
               
           </div>
            <div className="panel__title">
                <h5>Detectors ({rows.length})</h5>

            </div>
            <div style={{ height: 400, width: '100%' }}>
                <DataGrid rows={rows} columns={columns} pageSize={5} rowHeight = {40} checkboxSelection={false} />
            </div>

        </CardBody>

    </Card>);
}



export  default ViewDetectors;