import axiosInstance from '../middleware/Interceptor'


 

function getAccessList () {
  return axiosInstance.get('/siem-core/user/getAccessList')

}

function getAllUsers(){
    return axiosInstance.get("/siem-core/user/getAllUsersWithinCompany?company=Technominds")
}

function getCurrentLoginedUserDetails(){
    return axiosInstance.get("/siem-core/user/get-logined-user-details")
}

function changePassword(data){
    return axiosInstance.post("/siem-core/user/change-password",data)
} 

function savePreferneces(data){
    return axiosInstance.post("/siem-core/user/change-user-settings",data)
}







const userService = {
    getAccessList,
    getAllUsers,
    getCurrentLoginedUserDetails,
    changePassword,
    savePreferneces
}

export default userService