
import * as queryHelper  from "../shared/helpers/query_helper"
import _ from 'lodash';
export default class PannelModel{

  
    id:any;
    panelTitle:any;
    panelDescription:any;
    gridOptions:any;
    query:any;
    configuration:any;
    chartConfig:any;
    chartData:any;
    modalbool:any
    filters:any;
    type:any;

    constructor(data){
        this.id = data.id;
        this.type = data.type;
        this.panelTitle = data.panelTitle
        this.panelDescription = data.panelDescription;
        this.gridOptions  = data.gridOptions;
        this.query = data.query;
        this.configuration = data.configuration;
        this.chartConfig = data.chartConfig;
        this.chartData = data.chartData;
        this.chartType = data.chartType;
        this.modalbool = false;
        this.showCreatePanel = false;
        this.filters = data.filters;
        this.dashboardLink = data.dashboardLink;
    }
   
    addQueryData(query){

        this.query = query;
    }

    updateGridPos(newPos) {
        let sizeChanged = false;
    
        if (this.gridOptions.w !== newPos.w || this.gridOptions.h !== newPos.h) {
          sizeChanged = true;
        }
    
        this.gridOptions.x = newPos.x;
        this.gridOptions.y = newPos.y;
        this.gridOptions.w = newPos.w;
        this.gridOptions.h = newPos.h;
    
        
      }

      updateSize = (event,metric,currnetIndex,parentIndex,type,aggType) => {
          this.query[currnetIndex]['bucket'][parentIndex]['settings']['size']  = parseInt(event.target.value);
      }

    updateQuery = (event,metric,currnetIndex,parentIndex,type,aggType) => {
        
       
        if(!this.query[0][type][currnetIndex]){
            this.query[0][type].push({});
        }

        this.query[currnetIndex][type][parentIndex]['pipelineAgg'] = {};
        
        if(aggType === 'agg'){
            const aggDef = _.find(queryHelper.metricAggTypes, { value: event.target.value });
            aggDef['size'] = 10;
            this.query[currnetIndex][type][parentIndex]['aggDef'] = aggDef;
            this.query[currnetIndex][type][parentIndex][metric] = event.target.value;
        }else if(aggType==='metric_field'){
            let currentMetric = this.query[currnetIndex][type][parentIndex].type;
            if(currentMetric === 'derivative'){
                let existingFields = this.query[currnetIndex][type];
                let currentFieldName = this.query[currnetIndex][type][parentIndex].field;
                let currentId = "0";
                for(let i=0;i<existingFields.length;i++){
                    if(existingFields[i].field===currentFieldName 
                        && (existingFields[i].type === 'count' 
                        || existingFields[i].type === 'sum'
                        || existingFields[i].type === 'avg' 
                        || existingFields[i].type === 'min'
                        ||  existingFields[i].type === 'max')){
                        currentId = existingFields[i].id;
                    }
                }
    
                this.query[currnetIndex][type][parentIndex]['pipelineAgg']['model'] = event.target.value;
                this.query[currnetIndex][type][parentIndex]['pipelineAgg']['id'] = currentId;
            }
          
            this.query[currnetIndex][type][parentIndex]['field'] = event.target.value
        }
        else if(aggType==='bucketAgg'){
            this.query[currnetIndex][type][parentIndex][metric] = event.target.value;
        }else if(aggType==='bucketField'){
            this.query[currnetIndex][type][parentIndex]['field'] = event.target.value;
        }
       
       
        var fields = [];
        if(event.target.value === 'moving_avg'){
            for(var i=0;i<this.query.length;i++){
                for(var j=0;j<this.query[i].metric.length;j++){
                    var value = this.query[i].metric[j].id ;
                    var label = this.query[i].metric[j][metric]; 
                    if(label!='moving_avg'){
                        fields.push({label:label,value:value});
                    }
                }
            }
        }
     
        if(fields.length==0){
            fields.push({label:'@timestamp',value:'@timestamp'});
            fields.push({label:'event_name',value:'event_name.keyword'});
            fields.push({label:'doc_szie',value:'doc_szie'});
        } 

        this.query[currnetIndex][type][parentIndex]['id'] = parentIndex.toString();
        
        this.query[currnetIndex][type][parentIndex]['settings'] = {collapse:false};
        
        this.query[currnetIndex][type][parentIndex]['options'] = fields;
       
       // dispatch(updateAggData(currentPanel));
    }

    updateSettingsForQuery = (event,metric,currnetIndex,parentIndex,type) =>{

        if(event.target){
            var label = event.target.name;
            if(label === 'percents'){
                var temArray = event.target.value.split(",");
                const tempArray1 = [];
                for(var i=0;i<temArray.length;i++){
                    if(temArray[i].trim()!="") {
                        tempArray1.push(parseInt(temArray[i].trim()))
                    }  
                   
                }
               
                this.query[currnetIndex][type][parentIndex]['settings'][label]= tempArray1;
                this.query[currnetIndex][type][parentIndex]['settings'][label]["percents_value"]= event.target.value
            }else{
                this.query[currnetIndex][type][parentIndex]['settings'][label]=event.target.value;
            }
            
        }else if(event.length>0){
            let object = new Object();
            for(var i=0;i<event.length;i++){
                object[event[i].value] =true;
            }
            
            //this.query[currnetIndex][type][parentIndex]['settings'][metric]=nameArray.join(',');


            this.query[currnetIndex][type][parentIndex]['meta'] = object
    
        }else if(metric === 'model' ){
            let existingFields = this.query[currnetIndex][type];
            let currentFieldName = this.query[currnetIndex][type][parentIndex].field;
            let currentId = "0";
            for(let i=0;i<existingFields.length;i++){
                if(existingFields[i].field===currentFieldName 
                    && (existingFields[i].type === 'count' 
                    || existingFields[i].type === 'sum'
                    || existingFields[i].type === 'avg' 
                    || existingFields[i].type === 'min'
                    ||  existingFields[i].type === 'max')){
                    currentId = existingFields[i].id;
                }
            }
            this.query[currnetIndex][type][parentIndex]['pipelineAgg']['id'] = currentId;
            this.query[currnetIndex][type][parentIndex]['pipelineAgg'][metric] = event.value;
           
        }

    }

    changeAggs = (index,type,parentIndex,opreatoinType) =>{
        const randNumber = Math.floor((Math.random() * 100) + 1);
        //const length =  this.query[parentIndex].metric.length;
        const metricKey = type+"."+randNumber;

       
        const fieldKey = "field."+randNumber;
        if(type === 'metric' && opreatoinType === 'add'){
            const payload = {metricName:metricKey,fieldName:"metric"+fieldKey}
            this.query[index][type].push(payload);
        }
        if(type === 'bucket' && opreatoinType === 'add'){
            const payload = {bucketName:metricKey,fieldName:"bucket"+fieldKey,settings:{size:10}}
            this.query[index][type].push(payload);
        }
        if(opreatoinType === 'remove'){
            this.query[index][type].splice(parentIndex,1);
        }
      
    }

    updateChartType =(chartType) =>{
        this.chartType = chartType;
    }

    

    

    udatePanelTitle = (panelTitle) =>{
        this.panelTitle = panelTitle;
    }

    udatePanelDescription = (panelDescription)=>{
        this.panelDescription = panelDescription;
    }

    updateChartConfig = (chartType,name,value) => {
        this.chartConfig[chartType][name] = value;
    }

    updateChartData = (chartData) => {
        this.chartData = chartData;
    }
    getChart = () =>{
       return this.chartData;
    }
    getPanelId(){
        return this.id;
    }

   
}

