export const VIEW_RULES = 'VIEW_RULES';
export const CREATE_RULES = 'CREATE_RULES'
export const BACK_TO_RULES  = 'BACK_TO_RULES'

export const ADD_SELECTED_ROWS = "ADD_SELECTED_ROWS";

export const OPEN_EDIT_VIEW = "OPEN_EDIT_VIEW"

export function viewRules(action) {
    return {
        type: VIEW_RULES,
        payload:action
    };
}

export function backToRulesPage(action) {
    return {
        type: BACK_TO_RULES,
        payload:action
    };
}

export  function addSelectedRows(action) {
    return {
        type: ADD_SELECTED_ROWS,
        payload:action
    };
}

export function openEditRulesForm(action) {
    return {
        type: OPEN_EDIT_VIEW,
        payload:action
    };
}


export function createRules(action) {
    return {
        type: CREATE_RULES,
        payload:action
    };
}


