import {
 
    ADD_QUERY,
    ADD_METRIC,
    REMOVE_METRIC,
    ADD_CHART_DATA,
    UPDATE_CHART_DATA,
    OPEN_SETTINGS_MODEL,
    ClOSE_SETTINGS_MODEL,
    ADD_CREATED_PANEL,
    HIDE_CREATED_PANEL,
    CHANGE_DATE_SETTINGS,
    APPLY_FILTERS,
    DELETE_FILTERS,
    SHOW_SINGLE_EVENT,
    UPDATE_SETTINGS,
    CHANGE_AGGS,
    UPDATE_QUERY,
    UPDATE_PANEL,
    UPDATE_CHART_TYPE,
    UPDATE_CHART_CONFIG,
    ADD_PANEL_FILTER,
    DELETE_PANEL_FILTER,
    UPATE_PANEL_FILTER,
    CHANGE_SIZE,
    UPDATE_TTHRESHOLD,
    ADD_THREASHOLD
  } from '../actions/panelActions';
  import cloneDeep from 'lodash/cloneDeep';
  import moment from 'moment';
  import PanelModel from '../../models/PanelModel'
  import * as widgetHelper  from "../../shared/helpers/widget_helper"
  import * as queryHelper  from "../../shared/helpers/query_helper"
  import _ from 'lodash'
  let queryData = {collapse:true,metric:[{metricName:'metric.0',fieldName:'field.0',type:'avg',field:'event_name.keyword'}],bucket:[{bucketName:'bucket.0',bucketField:'bucketfield.0',type:'filters',field:'event_category.keyword',settings:{size:10}}]}
       

  const intialPanel = {id:0,title:'',panelDescription:'',gridOptions:{ x: 0, y: 0, h: 3, w: 6 },chartType:'line'};
  intialPanel['query'] = [queryData];
  intialPanel['chartConfig'] = widgetHelper.chartOptions;
  const initialState = {
    panels:[],
    currentPanel:new PanelModel(intialPanel),
    date:{startDate:moment().subtract(24, 'hours').valueOf(),endDate:moment().valueOf(),label:"Past 1 Day"},
    currentEventData:{data:{},type:''},
    filters : []
  };
  
  const panelReducer = (state = initialState, action) => {
    switch (action.type) {

      case ADD_CREATED_PANEL:
       const panelModel = new PanelModel(action.panel);
       const currentOpen =  panelModel.showCreatePanel

       panelModel.showCreatePanel = true;
      return Object.assign({}, state, { currentPanel: panelModel  })

        case HIDE_CREATED_PANEL:
            const panelModel1 = new PanelModel(action.panel);


            panelModel1.showCreatePanel = false;
            return Object.assign({}, state, { currentPanel: panelModel1  })


      case ADD_PANEL_FILTER:
        const currentDashboard5 = cloneDeep(state.currentPanel);
        currentDashboard5.filters.push({key:'',operator:'',value:''});
        return Object.assign({}, state, { currentPanel: currentDashboard5  });
      
      case CHANGE_SIZE:
      const currentDashboard8 = cloneDeep(state.currentPanel);
      currentDashboard8.query[action.currnetIndex]['bucket'][action.parentIndex]['settings']['size']  = parseInt(action.event.target.value);
      
      return Object.assign({}, state, { currentPanel: currentDashboard8  });  

      case UPATE_PANEL_FILTER:
             const currentDashboard7 = cloneDeep(state.currentPanel);
             currentDashboard7.filters[action.payload.index][action.payload.fieldName] =action.payload.value ;
       return Object.assign({}, state, { currentPanel: currentDashboard7  });

    case DELETE_PANEL_FILTER:
       const currentDashboard6 = cloneDeep(state.currentPanel);
       currentDashboard6.filters.splice(action.payload,1);
      return Object.assign({}, state, { currentPanel: currentDashboard6  });

      case UPDATE_CHART_TYPE:
      
      const currentDashboard3 = cloneDeep(state.currentPanel);
      currentDashboard3.chartType =action.chartType;
      currentDashboard3.chartConfig.chartType = action.chartType;
      return Object.assign({}, state, { currentPanel: currentDashboard3  });
     
      case UPDATE_CHART_CONFIG:
      
        const currentDashboard4 = cloneDeep(state.currentPanel);
       
        currentDashboard4.chartConfig[action.chartType][action.name] = action.value;
         return Object.assign({}, state, { currentPanel: currentDashboard4  });
        case ADD_THREASHOLD:
            const currentDashboard10 = cloneDeep(state.currentPanel);
            currentDashboard10.chartConfig[action.chartType]['thresholds'] = action.value;
            return Object.assign({}, state, { currentPanel: currentDashboard10  });
        case UPDATE_QUERY:

        const currentDashboard1 = cloneDeep(state.currentPanel);
      //  const currentDashboard = currentDashboard1.currentPanel

      if(!currentDashboard1.query[0][action.metricType][action.parentIndex]){
        currentDashboard1.query[0][action.metricType].push({});
       }

        currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg'] = {};
          
          if(action.aggType === 'agg'){
              const aggDef = _.find(queryHelper.metricAggTypes, { value: action.event.target.value });
              currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['aggDef'] = aggDef;
              currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex][action.metric] = action.event.target.value;
          }else if(action.aggType==='metric_field'){
              let currentMetric = currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex].type;
              if(currentMetric === 'derivative'){
                  let existingFields = currentDashboard1.query[action.currnetIndex][action.metricType];
                  let currentFieldName = currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex].field;
                  let currentId = "0";
                  for(let i=0;i<existingFields.length;i++){
                      if(existingFields[i].field===currentFieldName 
                          && (existingFields[i].type === 'count' 
                          || existingFields[i].type === 'sum'
                          || existingFields[i].type === 'avg' 
                          || existingFields[i].type === 'min'
                          ||  existingFields[i].type === 'max')){
                          currentId = existingFields[i].id;
                      }
                  }
      
                  currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg']['model'] = action.event.target.value;
                  currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg']['id'] = currentId;
              }
            
              currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['field'] =action.event.target.value
          }
          else if(action.aggType==='bucketAgg'){
            currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex][action.metric] = action.event.target.value
          }else if(action.aggType==='bucketField'){
            currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['field'] = action.event.target.value
          }
         
         
          var fields = [];
          if(action.event.target.value === 'moving_avg'){
              for(var i=0;i<currentDashboard1.query.length;i++){
                  for(var j=0;j<currentDashboard1.query[i].metric.length;j++){
                      var value = currentDashboard1.query[i].metric[j].id ;
                      var label = currentDashboard1.query[i].metric[j][action.metric]; 
                      if(label!='moving_avg'){
                          fields.push({label:label,value:value});
                      }
                  }
              }
          }
            if(action.event.target.value === 'cumulative_sum'){
               // currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['model'] = action.event.target.value
                currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg'] =  {id:action.currnetIndex.toString()};

            }
       
          if(fields.length==0){
              fields.push({label:'@timestamp',value:'@timestamp'});
              fields.push({label:'event_name',value:'event_name.keyword'});
              fields.push({label:'doc_szie',value:'doc_szie'});
          } 
  
          currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['id'] = action.parentIndex.toString();
          
          currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['settings'] = {collapse:false};
          
          currentDashboard1.query[action.currnetIndex][action.metricType][action.parentIndex]['options'] = fields;
    
          return Object.assign({}, state, { currentPanel: currentDashboard1  });
      

      case UPDATE_SETTINGS:
        const currentPanel = cloneDeep(state.currentPanel);
        if(!action.event.length){


















          var label = action.event.target.name;
          if(label === 'percents'){
            var temArray = action.event.target.value.split(",");
            const tempArray1 = [];
            for(var i=0;i<temArray.length;i++){
                if(temArray[i].trim()!="") {
                    tempArray1.push(parseInt(temArray[i].trim()))
                } 
               
            }
              currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings'][label]= tempArray1
              currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings'][label]["percents_value"]= action.event.target.value
          }else if(label === 'moving_avg'){
              //action.parentIndex.toString()
              currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['model'] = action.event.target.value
              currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg'] =  {id:action.currnetIndex.toString(),model:action.event.target.value};
          }else  if(label === 'window' || label === 'predict'  ){
              if(!isNaN(parseInt(action.event.target.value))){
                  if(currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['settings']){
                      delete currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['settings']
                  }
                  currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings'][label] = parseInt(action.event.target.value);
              }
          }else if(label === 'alpha' || label === 'beta' || label === 'gamma' || label === 'period'){
              if(!isNaN(parseInt(action.event.target.value))){
                  currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['settings'] = {};
                  currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['settings'][label] = parseInt(action.event.target.value);
              }
          }else if(label === 'cumulative_sum'){
              //action.parentIndex.toString()
             // currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['settings']['model'] = action.event.target.value
              currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg'] =  {id:action.currnetIndex.toString(),model:action.event.target.value};
          }

          
      }else if(action.event.length>0){
          let object = new Object();
          for(var i=0;i<action.event.length;i++){
              object[action.event[i].value] =true;
          }
         currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['meta'] = object
  
      }else if(action.metric === 'extended_stats_sigma'){
            if( currentPanel.query[action.currnetIndex]['extended_stats'][action.parentIndex]['meta']){
                currentPanel.query[action.currnetIndex]['extended_stats'][action.parentIndex]['meta'] = {sigma:action.event.value}
            }else{
                currentPanel.query[action.currnetIndex]['extended_stats'][action.parentIndex]['meta']['sigma']=action.event.value
            }

      }

        else if(action.metric === 'model' ){
          let existingFields = currentPanel.query[action.currnetIndex][action.metricType];
          let currentFieldName = currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex].field;
          let currentId = "0";
          for(let i=0;i<existingFields.length;i++){
              if(existingFields[i].field===currentFieldName 
                  && (existingFields[i].type === 'count' 
                  || existingFields[i].type === 'sum'
                  || existingFields[i].type === 'avg' 
                  || existingFields[i].type === 'min'
                  ||  existingFields[i].type === 'max')){
                  currentId = existingFields[i].id;
              }
          }
          currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg']['id'] = currentId;
          currentPanel.query[action.currnetIndex][action.metricType][action.parentIndex]['pipelineAgg'][action.metric] = action.event.value;
         
      }
  
        
        
      
         return Object.assign({}, state, { currentPanel: currentPanel  });


         case UPDATE_PANEL:
          const currentDashboard2 = cloneDeep(state.currentPanel);
          currentDashboard2.chartData = action.panel;
        return Object.assign({}, state, { currentPanel: currentDashboard2  });
      
      case CHANGE_AGGS:
        const currentDashboard = cloneDeep(state.currentPanel);
        const randNumber = Math.floor((Math.random() * 100) + 1);
       
        const metricKey = action.type+"."+randNumber;
       const fieldKey = "field."+randNumber;
        if(action.metricType === 'metric' && action.opreatoinType === 'add'){
            const payload = {metricName:metricKey,fieldName:"metric"+fieldKey}
            currentDashboard.query[action.index][action.metricType].push(payload);
        }
        if(action.metricType === 'bucket' && action.opreatoinType === 'add'){
          const payload = {bucketName:metricKey,fieldName:"bucket"+fieldKey,settings:{size:10}}
            currentDashboard.query[action.index][action.metricType].push(payload);
        }
        if(action.opreatoinType === 'remove'){
          currentDashboard.query[action.index][action.metricType].splice(action.parentIndex,1);
        }
        
       return Object.assign({}, state, { currentPanel: currentDashboard  });
       


    

     

      case OPEN_SETTINGS_MODEL:
      const panelModel2 = new PanelModel(action.panel);
      
      return Object.assign({}, state, { currentPanel: panelModel2  })
      
      case ClOSE_SETTINGS_MODEL:
        return state.map((widget) => {
          if (widget.id == action.panel.id) {
              return Object.assign({}, widget, { modalbool: false })
          } else {
              return widget
          }
      })
      case SHOW_SINGLE_EVENT:
          return {...state,currentEventData:action.payload};
    case ADD_CHART_DATA:
       
      return  { ...state, currnetPanel: action.panel };

      case UPDATE_CHART_DATA:
        return state.map((widget) => {
          if (widget.id == action.panel.id) {
              return Object.assign({}, widget, { chartData:action.panel.getChart()  })
          } else {
              return widget
          }
      })



      

        case ADD_QUERY:
          let addQuery = state.currnetPanel;
          addQuery['query'] = [action.panel];
        return { ...state, currnetPanel: addQuery };
        case ADD_METRIC:
          const updatedItems = cloneDeep(state.currnetPanel);
          updatedItems.query[action.index].metric.push(action.payload);
          return {...state,currnetPanel:updatedItems};
       case REMOVE_METRIC:
            const removedItems = cloneDeep(state.currnetPanel);
            removedItems.query[action.mainIndex].metric.splice(action.childeIndex,1);
         return { ...state, currnetPanel: removedItems };  

       
        default:
          return state;
      }
    };
  export default panelReducer;
  