import React, { Component } from 'react';
import DownIcon from 'mdi-react/ChevronDownIcon';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { Collapse } from 'reactstrap';

import userService  from "../../../api/userService";
import { Link } from 'react-router-dom';
import {
  changeThemeToDark, changeThemeToLight,
} from '../../../redux/actions/themeActions';
const Ava = `${process.env.PUBLIC_URL}/img/ava.png`;


class TopbarProfile extends Component {
  constructor() {
    super();
    this.state = {
      collapse: false,
      userName :'',
      theme:'',
      loading:false
    };
  }

  changeToDark = () => {
    const { dispatch } = this.props;
    dispatch(changeThemeToDark());
  };

  changeToLight = () => {
    const { dispatch } = this.props;
    dispatch(changeThemeToLight());
  };

  componentDidMount() {
      userService.getCurrentLoginedUserDetails().then((responseData)=>{

        if(responseData.status===200){
            if(responseData.data.theme && responseData.data.theme === 'dark'){
                this.changeToDark();
            }
            console.log(responseData.data.userDisplayName)
            this.setState({
              userName:responseData.data.userDisplayName
            })
        }
      })
  }

  toggle = () => {
    this.setState(prevState => ({ collapse: !prevState.collapse }));
  };

  render() {
    const { collapse,userName } = this.state;

    return (
      <div className="topbar__profile">
        <button className="topbar__avatar" type="button" onClick={this.toggle}>
          
          <span className="topbar__avatar-name">{userName}</span>
          <DownIcon className="topbar__icon" />
        </button>
        {collapse && <button className="topbar__back" type="button" onClick={this.toggle} />}
        <Collapse isOpen={collapse} className="topbar__menu-wrap">
          <div className="topbar__menu">
          <Link className="topbar__link" to="/profile" >
              <span className={`topbar__link-icon lnr lnr-home`} />
                 <p className="topbar__link-title topbar__profile_link">Profile</p>
          </Link>

          <Link className="topbar__link" to="/plan/billing" >
              <span className={`topbar__link-icon lnr lnr-home`} />
              <p className="topbar__link-title topbar__profile_link">Billing</p>
          </Link>

          </div>
        </Collapse>
      </div>
    );
  }
}

export default withRouter(connect(state => ({
  customizer: state.customizer,
  sidebar: state.sidebar,
  theme: state.theme,
  rtl: state.rtl,
  user: state.user,
}))(TopbarProfile));
