 export const OPEN_EDIT_DASHBOARD = "OPEN_EDIT_DASHBOARD";

 export const CLOSE_EDIT_DASHBOARD = "CLOSE_EDIT_DASHBOARD";

 export const ADD_CREATED_PANEL = "ADD_CREATED_PANEL";

