export const DELETE_PANEL = "DELETE_PANEL"

export const APPLY_BULK_FILTERS = "APPLY_BULK_FILTERS"

export const APPLY_BULK_FILTERS_WITHOUT_DATE = "APPLY_BULK_FILTERS_WITHOUT_DATE"

export const DELETE_ALL_FILTERS = "DELETE_ALL_FILTERS"

export const FETECH_SINGLE_DASHBOARD = "FETECH_SINGLE_DASHBOARD";

export const LOAD_LAYOUT = "LOAD_LAYOUT";

export const OPEN_LAYOUT = "OPEN_LAYOUT";

export const CREATE_DASHBOARD = "CREATE_DASHBOARD";

export const OPEN_DASHBOARD_MODAL = "OPEN_DASHBOARD_MODAL";



export function openEditDashboard(action){
  return {
    type: OPEN_EDIT_DASHBOARD,
    payload:action
  }
}

export function openDashboardModel(action){

  return {
    type: OPEN_DASHBOARD_MODAL,
    payload:action
  }

}

export function createDashboard(action){
  return {
    type: CREATE_DASHBOARD,
    payload:action
  }
}




export function openLayout(action){
  return {
    type: OPEN_LAYOUT,
    payload:action,
  }
}

export function loadLayout(action){
  return {
    type: LOAD_LAYOUT,
    payload:action,
  }
}

 export function applyBulkFilters (payload,date){
     return {
         type: APPLY_BULK_FILTERS,
         payload:payload,
         date:date
     }
 }

 export function applyBulkFiltersWithOutDate (payload,date){
  return {
      type: APPLY_BULK_FILTERS_WITHOUT_DATE,
      payload:payload,
      date:date
  }
}

 export function deleteAllFilters (payload,date){
  return {
      type: DELETE_ALL_FILTERS,
      payload:payload,
      date:date
  }
}

export function addNewDashboard(id){
  return {
    type: ADD_NEW_DASHBOARD,
    id:id
  }
}

export function applySearch(searchString){
  return {
    type: APPLY_SEARCH,
    searchString:searchString
  };

}


export function changeDateSettings(start,end,value){
  return {
    type: CHANGE_DATE_SETTINGS,
    start:start,
    end:end,
    value:value
  
  };
}

export function deleteFilters(index){
  return {
    type: DELETE_FILTERS,
    index:index
  };
}
export function editPanel(payload){
  return {
    type: EDIT_PANEL,
    payload:payload
  };
}



export function addFilters(payload){

  return {
    type:APPLY_FILTERS,
    payload:payload
  };
  
  }

export function openSingleDashboard(payload){
    return {
        type: OPEN_SIGNLE_DAHSBOARD,
        payload:payload

      };
}
export function deletePannel(payload){
    return {
        type: DELETE_PANEL,
        payload:payload

    };
}

export function addCreatedPanelToDashboard(payload){
return {
        type: ADD_CREATED_PANEL_TO_DASHBOARD,
        payload:payload
     };
}



export function addPanel(payload) {
  return {
    type: ADD_PANNEL,
    payload: payload,
  };
}


export function createPanel(payload) {
  return {
    type: CREATE_PANEL,
    dashboard: payload,
  };
}


export function closeEditDashboard(payload){
  return {
    type: CLOSE_EDIT_DASHBOARD,
    dashboard: payload,
  };
}

export function showWidgetsPanel(){
  return {
    type: SHOW_WIDGETS_PANEL
   
  };
}
