export function openSideBar(payload){
  return {
    type: OPEN_SIDE_BAR,
    panel: payload,
  };
}
export function closeSideBar(payload){
  return {
    type: CLOSE_SIDE_BAR,
    panel: payload,
  };
}

export function fetchSingleDashboardAction(id){
    return {
      type: FETECH_SINGLE_DASHBOARD,
      id: id,
    };
}



