import React, {useState} from 'react'
import {Row,Col,Button} from 'reactstrap'
import {useDispatch, useSelector} from "react-redux";
import {createSelector} from "reselect";
import { AgGridReact, AgGridColumn,ChangeDetectionStrategyType } from '@ag-grid-community/react';
import { ServerSideRowModelModule } from '@ag-grid-enterprise/server-side-row-model';
import { RowGroupingModule } from '@ag-grid-enterprise/row-grouping';
import { MenuModule } from '@ag-grid-enterprise/menu';
import { ColumnsToolPanelModule } from '@ag-grid-enterprise/column-tool-panel';
import { AllModules } from '@ag-grid-enterprise/all-modules';
import moment from 'moment'
import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';
import {createRules, backToRulesPage, addSelectedRows, viewRules,openEditRulesForm} from '../../../redux/actions/ruleActions'
import Collapse from '../../../shared/components/Collapse';
import {Field, reduxForm} from "redux-form";
import CreateRuleBody from '../components/RuleEditorForm'
import ruleService from "../../../api/ruleService";
import {toast, ToastContainer} from "react-toastify";
import BarLoader from "react-spinners/BarLoader";
import playbookService from '../../../api/playbookService';


export const RuleHeader = (props) =>{

    const {isView,isCreate} = props;

    const [showNewRule,setShowNewRule]  = useState(true)

    const [loading,setLoading] = useState(false);

    const selectedRows = useSelector((state) => state.ruleReducer.selectedRows);

    const [showEditButton,setShowEditButton] = useState(true);


    const dispatch = useDispatch();

    const openRuleEditor = () =>{
        dispatch(createRules())
        setShowNewRule(false)
    }
    const backToView = () =>{
        dispatch(backToRulesPage())
        setShowNewRule(true)
        setShowEditButton(true)
    }

    const openEditRule = () =>{
        const exitingData = selectedRows[0];
        setLoading(true)

        ruleService.loadSingleRule(exitingData.id).then((responseData) =>{
            setLoading(false)
            dispatch(addSelectedRows([]));
            if(responseData.status===200){
                setShowEditButton(false)
                const ruleData = responseData.data;
                const reference = {value:ruleData.reference,label:ruleData.reference}
                ruleData['reference'] = reference;
                ruleData['description'] = ruleData.eventDescription;
                ruleData['description'] = ruleData.eventDescription;
                ruleData['eventStatus'] = ruleData.eventStatus;
                ruleData['severity'] = {value:ruleData.severity,label:ruleData.severity}
                ruleData['ruleGroup'] = {value:ruleData.groupName,label:ruleData.groupName}
                if( ruleData.eventTags){
                    ruleData['eventTags'] = ruleData.eventTags.split(",").map((value)=>{
                        return {label:value,value:value}
                    });
                }


                const notifications = JSON.parse(ruleData.alert);

                ruleData['triggerCondition'] = {value:notifications.triggerCondition,label:notifications.triggerCondition} ;
                ruleData['triggerConditionOperator'] = {value:notifications.triggerConditionOperator,label:notifications.triggerConditionOperator} ;
                ruleData['triggerConditionValue'] = notifications.triggerConditionValue;

                //email

                const triggers = JSON.parse(ruleData.notification);

                ruleData['to'] = triggers[0].to;
                ruleData['subject'] = triggers[0].subject;
                ruleData['message'] = triggers[0].message;

                ruleData['title'] = triggers[1].title;
                ruleData['description'] = triggers[1].description;
                ruleData['priority'] = triggers[1].priority;
                ruleData['assignee'] = triggers[1].assignee;
                ruleData['type'] = triggers[1].type;
                ruleData['eventMetaData'] = JSON.parse(ruleData.eventMetaData);
                if(triggers[2] && triggers[2].lookupContext){
                    ruleData['lookupContext'] = triggers[2].lookupContext.toString();
                }


               //ruleData['huntingQuery'] = {label:ruleData.threatHuntingQuery,value:ruleData.threatHuntingQuery}

                playbookService.getPlayBookDetails().then((responseData1)=>{
                   if(responseData.status === 200){
                       if(ruleData.threatHuntingQuery){
                        let playbookDetails = responseData1.data.filter((value)=>{
                            return value.id.toString() === ruleData.threatHuntingQuery.toString();
                        })
                        if(playbookDetails.length>0) {
                             ruleData['huntingQuery'] = {label:playbookDetails[0].playBookName,value:ruleData.threatHuntingQuery}
                        }
                       }
                       
                   }
                     const tempTechniques = [];
                        if(ruleData.techniques){
                            for(let i=0;i<ruleData.techniques.split(",").length;i++){
                                tempTechniques.push({value:ruleData.techniques.split(",")[i],label:ruleData.techniques.split(",")[i]})
                            }
                            ruleData['techniques'] = tempTechniques;
                        }


                    dispatch(openEditRulesForm(ruleData))
                })

                
                setShowNewRule(false)
            }
        })

    }

    const deleteRules = () =>{
        let tempRows = [];
        for(let i=0;i<selectedRows.length;i++){
            tempRows.push(selectedRows[i].id);
        }
        setLoading(true);
        ruleService.deleteRules(tempRows).then((responseData) =>{
            if(responseData.status==200){
                showSuccess('Successfully deleted the selected rows')
                fetchAllEvents()
            }
        })
    }

    const showError = (message) =>{
        toast.error(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    const showSuccess = (message) =>{
        toast.success(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    const fetchAllEvents = () =>{

        ruleService.getAllEvents().then((responseData)=>{
            setLoading(false);
            const rules = [];
            for(let i=0;i<responseData.data.custom.length;i++){
                if(responseData.data.custom[i].eventName!=null){
                    rules.push(responseData.data.custom[i])
                }
            }

            dispatch(viewRules(rules));
            dispatch(addSelectedRows([]));

        })
    }
    return (
        <React.Fragment>

            <ToastContainer
                position="top-right"
                hideProgressBar={false}
                autoClose={false}
                newestOnTop={true}
                closeOnClick={false}
                draggable={false}
                rtl={false}
            />

            {loading && <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}

           <Row>

               <Col xs = {6} lg={8} sm={6} md={8}>
                   <div className="panel__title">
                       <h5 className="bold-text">Rules</h5>
                   </div>
               </Col>
               <Col xs = {6} lg={4} sm={6} md={4}>

                   {selectedRows.length===1 && showEditButton && <Button style = {{float:'right',marginRight:'26px'}} onClick={openEditRule} color="primary">
                       Edit
                   </Button>}

                   {selectedRows.length>0 && <Button style = {{float:'right',marginRight:'26px'}} onClick={deleteRules} color="danger">
                      Delete
                   </Button>}
                   {showNewRule && <Button style = {{float:'right',marginRight:'26px'}} onClick={openRuleEditor} color="primary">
                       New Rule
                   </Button>}

                   {!showNewRule && <Button style = {{float:'right',marginRight:'26px'}}  onClick={backToView} color="primary">
                       Back
                   </Button>}
               </Col>

           </Row>


        </React.Fragment>
       )
}

export const RuleBody = (props) => {
    const rowData = useSelector((state) => state.ruleReducer.open);
    console.log(rowData)
    if(rowData.isOpen){
        return (<CreateRuleBody/>)
    }else{
        return (<RuleView/>)
    }
}



const getColsFields = () =>{

    const columns = [
        {headerName: "Event Name", field: "eventName", width: 150, sort: 'asc', checkboxSelection: true, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true}},
        {headerName: "Group", field: "ruleGroup", width: 150, enableRowGroup: true,  hide: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }},
        {headerName: "Rule Type", field: "ruleType", width: 150,  hide: true, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }},
        {headerName: "Description", field: "eventDescription", width: 150,  hide: true, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true,  hide: true }},
        {headerName: "Tags", field: "tags",  hide: true,cellRenderer: 'deltaIndicator', width: 150, enableRowGroup: true,sortable:false,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true,  hide: true, }},
        {headerName: "Status", field: "eventStatus", width: 150, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }},
        {headerName: "Created By", field: "createdBy", width: 150,  hide: true, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true,  hide: true }},
        {headerName: "Severity", field: "severity", width: 150, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }},
        {headerName: "Updated By", field: "updatedBy", width: 150,  hide: true, enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true,  hide: true }},
        {headerName: "Updated At", field: "updatedAt", width: 150,  hide: true, enableRowGroup: true,sortable:true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true,  hide: true }, valueGetter: function(params) {
                if(params.data != undefined && params.data.updatedAt != null){
                    return moment(params.data.updatedAt).format("L LT");
                }
            }}
    ]
    return columns;

}

const getDefaultColDef = () =>{
    const def = {
        flex: 1,
        minWidth: 100,
        filter: false,
        sortable: true,
        resizable: true,
        floatingFilter: true
    }
    return def;
}

const RuleView = (props) =>{

    const rowData = useSelector((state) => state.ruleReducer.ruleData);
    const dispatch = useDispatch();
    const [gridApi, setGridApi] = useState(null);
    const [gridColumnApi, setGridColumnApi] = useState(null);
    const onGridReady = (params) => {
        setGridApi(params.api);
        setGridColumnApi(params.columnApi);

        var columnToolPanel = params.api.getToolPanelInstance('columns');
        columnToolPanel.setPivotModeSectionVisible(false);
        columnToolPanel.setRowGroupsSectionVisible(false);
        columnToolPanel.setValuesSectionVisible(false);
        params.api.closeToolPanel();


    }

    const onSelectionChanged = () =>{
        let selectedRows = gridApi.getSelectedRows();
        dispatch(addSelectedRows(selectedRows));
    }



    return (<Row>
            <Col xs = {12} lg={12} md = {12}>
                <div

                    id="myGrid"
                    style={{
                        height: '500px',
                        width: '100%',
                    }}
                    className="ag-theme-alpine"
                >
                    <AgGridReact
                        rowHeight={30}
                        rowSelection={'multiple'}
                        modules={[ClientSideRowModelModule,RowGroupingModule,MenuModule,ColumnsToolPanelModule]}
                        onSelectionChanged={onSelectionChanged}
                        sideBar={{
                            toolPanels: [
                                {
                                    id: 'columns',
                                    labelDefault: 'Columns',
                                    labelKey: 'columns',
                                    iconKey: 'columns',
                                    toolPanel: 'agColumnsToolPanel',
                                    toolPanelParams: {
                                        suppressRowGroups: true,
                                        suppressValues: true,
                                        suppressPivots: true,
                                        suppressPivotMode: true,
                                        suppressSideButtons: true,
                                        suppressColumnFilter: true,
                                        suppressColumnSelectAll: true,
                                        suppressColumnExpandAll: true,
                                    },
                                },
                            ],
                            defaultToolPanel: 'columns',
                        }}
                        sideBar={true}

                        columnDefs={getColsFields()}
                        pivotMode={false}
                        rowGroupPanelShow= {'always'}
                        defaultColDef={getDefaultColDef()}

                        animateRows={true}
                        onGridReady={onGridReady}
                        rowData={rowData}

                    />
                </div>
            </Col>


    </Row>)
}