import ZoomInIcon from '@material-ui/icons/ZoomIn';
import ZoomOutIcon from '@material-ui/icons/ZoomOut';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';

const HistoircalLayout = (props) =>{

    return (

        <HistoricalTable tableMetaData = {props.tableState} type = {props.type}
                         startDate = {props.startDate} endDate = {props.endDate}
                         filters= {props.filters}
                         searchString = {props.searchString}
                         fields = {props.fields}

        />



   )
}





const HistoricalTable = (props) =>{

    const {type,startDate,endDate,filters,searchString,tableMetaData} = props;
    console.log(searchString);
    const saveFilters = useSelector((state) => state.historicalReducer.saveFilters);
    const [rows,setRows] = useState([]);
    const [cols,setCols] = useState([]);
    useEffect(()=>{

        if(tableMetaData.cols){
            setCols(tableMetaData.cols);
        }

        const metrics = [{type:"raw_document"}]
        let queryBuilder = new ElasticQueryBuilder({
            timeField: '@timestamp',
            esVersion: '7.9',
        })
        let duration = widgetHelper.calculateTimeRange( startDate,endDate);

        var query = queryBuilder.build(metrics,filters,searchString);
        delete query['aggs'];
        query.size = 500;
        let payload = JSON.stringify(query);
        let finalData =  payload.replaceAll("$timeFrom", startDate);
        let fainlData1 = finalData.replaceAll("$timeTo", endDate);
        let finalData2 = fainlData1.replaceAll("$__interval",duration);

       const data = {elsQuery:finalData2,type:type}
       dashboardService.getRawDocumentQuery(data).then((responseData) =>{
           if(responseData.data && responseData.data.hits && responseData.data.hits.hits){
               setRows(responseData.data.hits.hits)
           }

       })



    },[filters,searchString,type,tableMetaData]);

    const removeField = (index)=>{
        const currentCols = [...cols];
        currentCols.splice(index, 1);
        setCols(currentCols);
    }

    useEffect(()=>{
        Emitter.on('ADD_FIELD',(newValue)=>{
            const currentCols = [...cols, newValue.replace(".keyword","")];
            setCols(currentCols);
        })

        Emitter.on('SAVE_FILETRS',(newValue)=>{
            const tableData = {cols:cols}
            let id =0;
            if(newValue.id){
                id = newValue.id
            }
            let finalData = {id:id,queryName:newValue.filterName,tableMetaData:JSON.stringify(tableData),metaData:JSON.stringify(props)}
            histroicalService.saveFilters(finalData).then((responseData) =>{

            })
        })



        return () =>{
            Emitter.off('ADD_FIELD');
            Emitter.off('SAVE_FILETRS');

        }
    },[cols])
    return (
        <div className="table-responsive">
            <table className="table table--bordered">
                <thead>
                <tr>
                    <th></th>

                    <th>Time</th>
                    {cols.length ===0 && <th>Source</th>}
                    {cols.map((value,index)=>{
                        return <th>  {value} <span className="colDelete"> <HighlightOffIcon onClick={(e)=>removeField(index)} fontSize="small" /> </span>  </th>
                    })}
                </tr>
                </thead>
                <tbody>
                {rows.map((value)=>{
                    return <RenderRow cols = {cols} row = {value}/>



                })}
                </tbody>

            </table>
        </div>
        )

}

const RenderRow = (props) =>{

    const {row,cols} = props;

    const [expanded,setExpanded] = useState(false);
    const dispatch = useDispatch();
    const toggle = () =>{
        setExpanded(!expanded)
    }

    const applyFiltersClick = (fieldName,value) => {
        let data = {key:fieldName,operator:"=",value:value};
        dispatch(addFilters(data))


    }
    const removeFilters = (fieldName,value) =>{
        let data = {key:fieldName,operator:"!=",value:value};
        dispatch(addFilters(data))
    }

    return (<React.Fragment>
        <tr height="30px">
            <td className="filterRow">
                {expanded && <ExpandMoreIcon style={{cursor:'pointer'}} onClick={toggle}/>}
                {!expanded && <ArrowRightIcon style={{cursor:'pointer'}} onClick={toggle}/>}
            </td>
            <td style = {{width:'15%'}}>{row._source['@timestamp']}</td>
            {cols.length===0 &&
            <td className="source">
                <DisplaySourceData source  = {row._source}/>

            </td>
            }
            {cols.map((value,index)=>{
                return (<td className="filterRow"><div className="truncate-by-height">
                    {row._source[value]} <span>
                         <ZoomInIcon  color="primary" onClick={()=>applyFiltersClick(value,row._source[value])} fontSize = "small"/>
                         <ZoomOutIcon color="primary" onClick={()=>removeFilters(value,row._source[value])} fontSize = "small"/>

                        </span>
                </div>

                </td>)
            })}

        </tr>

        {expanded && <tr>
            <td colspan={3}>
                <h4 className="expandedDocumentTitle"><FolderIcon/> Expanded document </h4>

                <ExpandedViewDocument applyFiltersClick = {applyFiltersClick} removeFilters={removeFilters} row = {row._source}/>

            </td>
        </tr>}
    </React.Fragment> )
}

const ExpandedViewDocument = (props)=>{
    const {row,applyFiltersClick,removeFilters} = props;
    return (<table className="table">
        <tbody>
        {Object.keys(row).map((key)=>{
            return <tr className="documentTable">
                <td className = "documentTableButtons">
<span>
                    <ZoomInIcon color="primary" onClick={()=>applyFiltersClick(key,row[key])} fontSize = "small"/>
                    <ZoomOutIcon color="primary" onClick={()=>removeFilters(key,row[key])} fontSize = "small"/>
</span>
                </td>
                {key != 'rule_data' &&
                <React.Fragment>
                    <td className="documentTableKey">{key}</td>
                    <td>{row[key]}</td>
                </React.Fragment>
                }
                {key === 'rule_data' &&
                <React.Fragment>
                    <td className="documentTableKey">{key}</td>
                    <td> {JSON.stringify(row[key])}</td>
                </React.Fragment>
                }

            </tr>
        })}
        </tbody>
    </table>)

}

const DisplaySourceData = (props)=>{

    return (<div className="truncate-by-height">
        <dl className="source truncate-by-height">
            {Object.keys(props.source).map((key)=>{
               return ( <RenderKeyValue renderKey = {key} renderValue = {props.source[key]} />)

            })}
        </dl>
    </div>)

}

const RenderKeyValue = (props)=>{
    const {renderKey,renderValue} = props;

    if(renderKey!='rule_data'){
        return  <React.Fragment>
            <dt>{renderKey} :</dt> <dd>{renderValue}</dd>
        </React.Fragment>
    }else{
        return <div></div>
    }
}


export default HistoircalLayout;