
import React, {Component} from 'react'
import { Row, Container, Col,Card,CardBody,Button } from 'reactstrap';
//import diagramXML from '../../diagram.bpmn';

import PlayBookDetails from './Comment/PlayBookDetails'
import CreatePlayBook from './Comment/CreatePlayBook'
import playbookService from '../../api/playbookService';

class Playbook extends Component{


    constructor(){
        super();
        this.state = {
          createPlayBook: false,
          playbookDetails:[],
         
        };
        this.CreatePlayBook = this.CreatePlayBook.bind(this);
        this.refresh = this.refresh.bind(this);
        this.child = React.createRef();
    }



    CreatePlayBook = ()=>{
        this.setState({
            createPlayBook:!this.state.createPlayBook
        })
    }

    SavePlayBook = () => {
        this.child.current.getFormData();
    }

    refresh = ()=>{
        playbookService.getPlayBookDetails().then((responseData)=>{
            this.setState({
                 playbookDetails:responseData.data
            })
         })
    }

    componentDidMount(){
       this.refresh();
    }

    render(){
        return (<div style={{paddingTop:'70px'}}>
                <Container>


                   
                    <Row>
                      <Col xs = {12} md = {12} lg = {12}>
                      {!this.state.createPlayBook && <PlayBookDetails refresh = {this.refresh} toggle = {this.CreatePlayBook} rows = {this.state.playbookDetails}/> }
                       {this.state.createPlayBook && <CreatePlayBook  toggle = {this.CreatePlayBook} ref={this.child}/>}
                      </Col>  
                      
                    </Row>
                </Container>
            </div>)
    }

  
}


export default Playbook;