export const OPEN_CASE = 'OPEN_CASE';

export const OPEN_EVIDENCE = 'OPEN_EVIDENCE';

export const OPEN_PLAYBOOK = 'OPEN_PLAYBOOK';

export const CLEAR_CAESES = "CLEAR_CASE";

export const LOAD_ALL_CASES = "LOAD_ALL_CASES";

export const OPEN_ALL_CASES = "OPEN_ALL_CASES";

export const LOAD_PLAYBOOK = "LOAD_PLAYBOOK"

export const LOAD_FILTERED_CASES = "LOAD_FILTERED_CASES"

export const OPEN_FILTERED_CASES = "OPEN_FILTERED_CASES"

export const OPEN_PLAYBOOK_RESULT = "OPEN_PLAYBOOK_RESULT"

export function loadFilteredCases(action){
  return {
    type:LOAD_FILTERED_CASES,
    playload:action
  }
}

export function openFilteredCases(action){
  return {
    type:OPEN_FILTERED_CASES,
    playload:action
  }
}


export function loadPlaybook(data){
  return {
    type:LOAD_PLAYBOOK,
    playload:data
  }
}

export function loadAllCases(action){
  return {
    type:LOAD_ALL_CASES,
    payload:action
  }
}
export function openAllCases(action){
  return {
    type:OPEN_ALL_CASES,
    payload:action
  }
}

export function openCase(action) {
  return {
    type: OPEN_CASE,
    payload: action,
  };
  
}

export function openPlaybookSinglePlayBookResult(action){
  return {
    type: OPEN_PLAYBOOK_RESULT,
    payload: action,
  };
}

export function openPlaybookResults(action){
  return {
    type: OPEN_PLAYBOOK,
    payload: action,
  };
}

export function openEvidence(action) {
  return {
    type: OPEN_EVIDENCE,
    payload: action,
  };
}

export function clearCaseState(action){
  return {
    type: CLEAR_CAESES,
    
  };
}


