import React, { Component, Fragment } from 'react';
import {Button, ButtonToolbar, Card, CardBody, Col, Container, Row} from "reactstrap";

import {Field, reduxForm} from "redux-form";
import {withTranslation} from "react-i18next";
import EyeIcon from "mdi-react/EyeIcon";
import EmailIcon from "mdi-react/EmailIcon";
import AccountSearchIcon from "mdi-react/AccountSearchIcon";
import renderSelectField from "../../shared/components/form/Select";
import renderMultiSelectField from "../../shared/components/form/MultiSelect";
import renderFileInputField from "../../shared/components/form/FileInput";
import CreatableSelect from "react-select/creatable/dist/react-select.esm";
import {toast, ToastContainer} from "react-toastify";
import _ from "lodash"
import brandMointoring from "../../api/brandMointoringService";
import BarLoader from "react-spinners/BarLoader";
const customStyles = {
    container: provided => ({
        ...provided,
        width: '100%',

    }),
    control: base => ({
        ...base,
        height: 35,
        minHeight: 35
    })
};

const showError = (message) =>{
    toast.error(message, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
}

class BrandConfiguration  extends  Component{

        constructor() {
            super();
            this.state ={
                tagValue:[],
                selectLoading:false,
                showPassword:false,
                id:0,
                formData:{
                    id:0,
                    sources:[],
                    twitterConsumerKey:'',
                    twitterConsumerSecrete:'',
                    twitterAccessToken:'',
                    twitterAccessTokenSecrete:''

                }
            }

            this.showSuccess = this.showSuccess.bind(this);
        }


     handleChangeTag = (newValue,actionMeta) => {

        this.setState({tagValue:newValue});
    };
    showPassword = (e) => {
        e.preventDefault();
        this.setState(prevState => ({ showPassword: !prevState.showPassword }));
    };
     createOption = (label) => ({
        label,
        value: label.toLowerCase().replace(/\W/g, ''),
    });
    handleCreate = (inputValue) =>{
        const newOption = this.createOption(inputValue);
        let currentTags = this.state.tagValue;
        if(!currentTags){
            currentTags = [];
        }
        currentTags.push(newOption)
        //tags.push(newOption)
        //setTags(tags)
        this.setState({tagValue:currentTags})
        //setTagValue(tagValue)
    }

    submit = function(values){

        if(!this.state.tagValue){
            showError("Please select at least one keyword")
            return false;
        }

        if(this.state.tagValue && this.state.tagValue.length===0){
            showError("Please select at least one keyword")
            return false;
        }

        if(!this.state.formData.sources){
            showError("Please Select At least one source")
            return false;
        }

        if(this.state.formData.sources){
          let twiter =  _.find(this.state.formData.sources, {value:'Twitter'});
          if(twiter && twiter.label && !values.twitterConsumerKey){
              showError("Please Enter Valid Twitter Consumer Key")
              return false;
          }
          if(twiter && twiter.label && !this.state.formData.twitterConsumerSecrete){
              showError("Please Enter Valid Twitter Consumer Secrete")
                return false;
         }
         if(twiter && twiter.label && !this.state.formData.twitterAccessToken){
             showError("Please Enter Valid Twitter Access Token")
                return false;
         }
        if(twiter && twiter.label && !this.state.formData.twitterAccessTokenSecrete){
                showError("Please Enter Valid Twitter Access Secrete")
                return false;
         }


        }
        const keywords = this.state.tagValue.map(value=>{return value.value});
        const sources = this.state.formData.sources.map(value=>{return value.value});
        const twiterConfig = {
            twitterConsumerKey:this.state.formData.twitterConsumerKey,
            twitterConsumerSecrete:this.state.formData.twitterConsumerSecrete,
            twitterAccessToken:this.state.formData.twitterAccessToken,
            twitterAccessTokenSecrete:this.state.formData.twitterAccessTokenSecrete
        }
        const data = {id:this.state.formData.id,keywords:keywords.join(","),sources:sources.join(","),twiterConfiguration:JSON.stringify(twiterConfig)};
        this.setState({selectLoading:true})
        brandMointoring.saveBrandConfiguration(data).then((responseData) =>{
            this.setState({selectLoading:false})
            if(responseData.status === 200){
                this.showSuccess("Configuration was saved successfully.");
            }
        })
    }


     showSuccess = (message) =>{
        toast.success(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }

    componentDidMount() {
        const {initialize} = this.props;
        this.setState({loading:true})
        brandMointoring.getConfiguration().then((responseData)=>{
            this.setState({loading:false})
            if(responseData.data && responseData.data.sources){
                let keywords = responseData.data.keywords.split(",").map(value=>{
                    return {label:value,value:value}
                })
                let sources = responseData.data.sources.split(",").map(value=>{
                    return {label:value,value:value}
                })
                let tiwterConfiguration = JSON.parse(responseData.data.twiterConfiguration);
                let formData = {
                    id:responseData.data.id,
                    sources:sources,
                    twitterConsumerKey:tiwterConfiguration.twitterConsumerKey,
                    twitterConsumerSecrete:tiwterConfiguration.twitterConsumerSecrete,
                    twitterAccessToken:tiwterConfiguration.twitterAccessToken,
                    twitterAccessTokenSecrete:tiwterConfiguration.twitterAccessTokenSecrete,
                }
                initialize(formData);
                this.setState({
                    tagValue :keywords,
                    formData:formData
                })
            }
        })
    }

    onChangeSources = (value,lable) =>{
        let existingFormData = this.state.formData;
        if(value.target){
            existingFormData[lable] = value.target.value;
        }else{
            existingFormData[lable] = value;
        }


        this.setState({formData:existingFormData})
    }

    render() {
        const { showPassword } = this.state;
        const { error, handleSubmit, pristine, reset, submitting } = this.props
        return (<div style={{paddingTop:'50px'}}>
            {this.state.selectLoading &&  <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
            <ToastContainer
                position="top-right"
                hideProgressBar={false}
                autoClose={false}
                newestOnTop={true}
                closeOnClick={false}
                draggable={false}
                rtl={false}
            />

            <Container>
                <Row>
                    <Col md={12}>
                        <h3 className="page-title">Brand Configuration</h3>

                    </Col>
                </Row>
                <Row>
                    <Col md={12} lg={12}>
                        <Card>
                            <CardBody>
                                <div className="card__title">

                                </div>
                                <form className="form form--horizontal" onSubmit={handleSubmit(this.submit.bind(this))}>
                                    <div className="form__form-group">
                                        <span className="form__form-group-label">Tags</span>
                                        <div className="form__form-group-field">
                                            <CreatableSelect styles={customStyles}
                                                             isClearable
                                                             isMulti
                                                             onChange={this.handleChangeTag}
                                                             value = {this.state.tagValue}

                                                             isDisabled={this.state.selectLoading}
                                                             isLoading={this.state.selectLoading}
                                                             onCreateOption={this.handleCreate}



                                            />
                                        </div>
                                    </div>







                                    <div className="form__form-group">
                                        <span className="form__form-group-label">Sources</span>
                                        <div className="form__form-group-field">
                                            <Field
                                                name="sources" onChange={(value)=>this.onChangeSources(value,"sources")}
                                                component={renderMultiSelectField}
                                                options={[
                                                    { value: 'Twitter', label: 'Twitter' },
                                                    { value: 'Instagram', label: 'Instagram' },
                                                    { value: 'News', label: 'News' },
                                                    { value: 'Web', label: 'Web' },
                                                    { value: 'Blogs', label: 'Blogs' },
                                                    { value: 'Videos', label: 'Videos' },
                                                    { value: 'Forums', label: 'Forums' }

                                                ]}

                                            />
                                        </div>
                                    </div>

                                    <div className="card-title" style={{paddingTop:'14px',paddingBottom:'14px'}}>
                                        <h4>Twitter Configuration</h4>
                                    </div>



                                    <hr/>

                                    <div className="form__form-group">
                                        <span className="form__form-group-label">Consumer Key</span>
                                        <div className="form__form-group-field">
                                            <Field
                                                name="twitterConsumerKey"
                                                component="input"
                                                type="text"
                                                placeholder="Consumer Key"
                                                autoComplete="off"
                                                autoCorrect="off"
                                                spellCheck="off"
                                                onChange={(value)=>this.onChangeSources(value,"twitterConsumerKey")}
                                            />
                                        </div>
                                    </div>

                                    <div className="form__form-group">
                                        <span className="form__form-group-label">Consumer Secrete</span>
                                        <div className="form__form-group-field">
                                            <Field
                                                name="twitterConsumerSecrete"
                                                component="input"
                                                type={showPassword ? 'text' : 'password'}
                                                placeholder="Password"
                                                autoComplete="off"
                                                autoCorrect="off"
                                                spellCheck="off"
                                                onChange={(value)=>this.onChangeSources(value,"twitterConsumerSecrete")}
                                            />
                                            <button
                                                type="button"
                                                className={`form__form-group-button${showPassword ? ' active' : ''}`}
                                                onClick={e => this.showPassword(e)}
                                            ><EyeIcon />
                                            </button>
                                        </div>
                                    </div>

                                    <div className="form__form-group">
                                        <span className="form__form-group-label">Access Token</span>
                                        <div className="form__form-group-field">
                                            <Field
                                                name="twitterAccessToken"
                                                component="input"
                                                type="text"
                                                placeholder="Consumer Key"
                                                onChange={(value)=>this.onChangeSources(value,"twitterAccessToken")}
                                            />
                                        </div>
                                    </div>

                                    <div className="form__form-group">
                                        <span className="form__form-group-label">Access Token Secrete</span>
                                        <div className="form__form-group-field">
                                            <Field
                                                name="twitterAccessTokenSecrete"
                                                component="input"
                                                type={showPassword ? 'text' : 'password'}
                                                placeholder="Password"
                                                onChange={(value)=>this.onChangeSources(value,"twitterAccessTokenSecrete")}
                                            />
                                            <button
                                                type="button"
                                                className={`form__form-group-button${showPassword ? ' active' : ''}`}
                                                onClick={e => this.showPassword(e)}
                                            ><EyeIcon />
                                            </button>
                                        </div>
                                    </div>


                                    <ButtonToolbar className="form__button-toolbar">
                                        <Button color="primary" type="submit">Submit</Button>

                                    </ButtonToolbar>
                                </form>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                <Row>

                </Row>
            </Container>
        </div>);
    }
}

export default reduxForm({
    form: 'brand_configuration_form',
    enableReinitialize : true
})(withTranslation('common')(BrandConfiguration));
