import { takeEvery,put } from 'redux-saga/effects'
import caseService from '../../api/caseService'
import dashboardService from '../../api/dashboardService'
import userService from '../../api/userService'
import playbookService from '../../api/playbookService'
import * as ActionTypes from '../actions/casesAction';

function* getAllCases(action) {
    const casesResponse = yield caseService.getAllCases();
    const allFields =  yield dashboardService.getFieldsWithDataTypes();
    const allUsers = yield userService.getAccessList();
    let currentCase = {};
    if(action.payload && action.payload.id){
        currentCase = yield caseService.getSingleCase(action.payload.id);
    }else if(casesResponse.data.length>0){
        currentCase = yield caseService.getSingleCase(casesResponse.data[0].id);
    }   

    allUsers.data.push({name:'System',type:'user'})
    const finalData = {cases:casesResponse.data,logFields:allFields.data,users:allUsers.data,currentCase:currentCase.data}

    yield put({ type: ActionTypes.OPEN_ALL_CASES, payload:finalData});
   
}

function* getFilterCases(action) {
    const casesResponse = yield caseService.filterCase(action.playload);
    let currentCase = {};
    if(casesResponse.data.length>0){
        currentCase = yield caseService.getSingleCase(casesResponse.data[0].id);
    }   
    const finalData = {cases:casesResponse.data,currentCase:currentCase.data}

    yield put({ type: ActionTypes.OPEN_FILTERED_CASES, payload:finalData});
   
}

function* getPlaybook(action){
    if(action.playload && action.playload.alertName){
        const data = {alertName:action.playload.alertName,caseId:action.playload.caseId,alertId:action.playload.alertId}
        const playbookDetails = yield playbookService.getPlaybookBasedOnAlertname(data);
        if(playbookDetails.status === 200 && playbookDetails.data.playbookDetails && playbookDetails.data.playbookDetails.playbookXml){
            yield put({ type: ActionTypes.OPEN_PLAYBOOK, payload:playbookDetails.data});
        }else{
            //playbookResults: action.payload.playbookResults,playbookDetails : action.payload.playbookDetails
            const playbookData= {playbookResults:[],playbookDetails:[]}
            yield put({ type: ActionTypes.OPEN_PLAYBOOK, payload:playbookData});
        }
    }
   
}


export  function* fetchAllCasesWatcher(){
    yield takeEvery(ActionTypes.LOAD_ALL_CASES, getAllCases)
}

export function* fetchPlaybookWatcher(){
    yield takeEvery(ActionTypes.LOAD_PLAYBOOK, getPlaybook)
}

export function* fetchFilterCasesWatcher(){
    yield takeEvery(ActionTypes.LOAD_FILTERED_CASES, getFilterCases)
}


