import React,{Component} from 'react'
import { CardBody, Col, Container, Row,Card } from 'reactstrap'
import { connect } from 'react-redux';
import userService from '../../api/userService';
import DisplayChangePasswordModel from './components/ChangePassword'

import dashboardService from '../../api/dashboardService';
import {
  changeThemeToDark, changeThemeToLight,
} from '../../redux/actions/themeActions';
class  Profile extends   Component {
  
  constructor(props) {
    super(props);
    this.state = {
      isModelOpen:false,
      data:{},
      checked: false,
      dashboards:[],
      formData : {homeDashboard:'146040',theme:'light',actionType:''}
    }
    this.showChangePassword = this.showChangePassword.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(checked) {
    this.setState({ checked });
   
  }

  componentDidMount(){
   this.fetchCurrentUserDetails();
   this.fetchDashboardDetails();
  }

  fetchCurrentUserDetails = ()=>{
    userService.getCurrentLoginedUserDetails().then((responseData)=>{
      console.log(responseData);
      if(responseData.status === 200){
        const tempFormData = this.state.formData;
        tempFormData['theme'] = responseData.data.theme
        tempFormData['homeDashboard'] = responseData.data.dashboard;
        this.setState({
          data:responseData.data,
          formData:tempFormData
        })
      }
    }).catch(error => {
      console.log("test")
   })
  }

  changeDashboard  = (type,event)=>{
    const tempFormData = this.state.formData;
    tempFormData[event.target.name] = event.target.value;
    tempFormData['actionType'] = 'homeDashboard'
    this.setState({
      formData:tempFormData,
      
    })
    this.savePreferneces();
  }

  changeUIThemeHandler = (theme,event)=>{
    const { dispatch } = this.props;
    event.preventDefault();
    const tempFormData = this.state.formData;
    tempFormData['theme'] = theme;
    tempFormData['actionType'] = 'theme'
    this.setState({
      formData:tempFormData,
      
    })

    this.savePreferneces();
    if(theme === 'dark'){
      dispatch(changeThemeToDark());
    }else{
      dispatch(changeThemeToLight());
    }
   
    
  }

  savePreferneces = ()=>{
      userService.savePreferneces(this.state.formData).then((responseData)=>{
          console.log("")
      });
  }

  fetchDashboardDetails = ()=>{
    dashboardService.getAllDashboards().then((responseData)=>{
        if(responseData.status===200){
           
              
            
            this.setState({
              dashboards:responseData.data
            })
        }
    })
  }

  showChangePassword = (event)=>{
    event.preventDefault();
    this.setState({
      isModelOpen:!this.state.isModelOpen
    })
  }

  render(){
    return (<Container style = {{paddingTop:'80px'}}>
      
      <Row>
        <Col xs = {6} lg={8} sm={6} md={8}>
                   <div className="panel__title">
                       <h5 className="bold-text">Profile</h5>
                   </div>
               </Col>       
      </Row>
      <Row>
      <Col md={12} lg={12}>
        <Card>
          <CardBody className = "profile-view">
                <div className="card__title">
                
                </div>
                <Col md = {6} lg = {6}>
                    <div className = "account-section-container">
                        <div>
                        <span className = "field-label">
                           Organization ID:
                        </span>
                        <span className = "field-value">
                            {this.state.data.companyName}
                        </span>
                        </div>
                        <div>
                        <span className = "field-label">
                           Username:
                        </span>
                        <span className = "field-value">
                           {this.state.data.userName}
                        </span>
                        </div>
                        <div>
                        <span className = "field-label">
                           Password:
                        </span>
                        <span className = "field-value">
                            <a href = "#" onClick = {(event)=>this.showChangePassword(event)}  > Change Password</a> 
                        </span>
                        </div>
                        <div>
                        <span className = "field-label">
                            Roles:
                        </span>
                        <span className = "field-value">

                            {this.state.data.permissions && Object.keys(this.state.data.permissions).join(',')}
                        </span>
                        </div>
                        <div>
                        
                        </div>
                           <div>
                        </div>

                        
                        
                    </div>

                </Col>

                <br/>

                <h4 style= {{fontSize:'14px'}}>Preferences</h4>
                <form className="form" style={{display:'revert',paddingLeft:'33px',marginTop:'12px'}}>
                
                <div className="form__form-group">
                                <span className="form__form-group-label">UI Theme</span>
                                <div className="form__form-group-field">
                                <div className= "btn-toolbar">
                                        <div className = "btn-group btn-group--icons">
                                          {this.state.formData.theme === 'dark' && 
                                              <React.Fragment>
                                                   <button className = "btn btn-outline-primary" onClick = {event => this.changeUIThemeHandler('dark',event)}>Dark</button>
                                                 <button className = "btn btn-outline-secondary" onClick = {event => this.changeUIThemeHandler('light',event)}>light</button>
                                              </React.Fragment>
                                             
                                          }
                                          {this.state.formData.theme === 'light' && 
                                              <React.Fragment>
                                                   <button className = "btn btn-outline-secondary" onClick = {event => this.changeUIThemeHandler('dark',event)}>Dark</button>
                                                 <button className = "btn btn-outline-primary" onClick = {event => this.changeUIThemeHandler('light',event)}>light</button>
                                              </React.Fragment>
                                             
                                          }
                                            
                                            
                                        </div>

                                      </div>
                                </div>
                </div>
                
                 <div className="form__form-group">
                                <span className="form__form-group-label">Home Dashboard</span>
                                <div className="form__form-group-field">
                                      <select className = "form-control" value = {this.state.formData.homeDashboard} onChange = {event => this.changeDashboard('homeDashboard',event)}  style = {{width:'11%'}} name = "homeDashboard" >
                                        <option value = "0">Default</option>
                                        {this.state.dashboards && this.state.dashboards.map((value)=>{
                                           return <option value = {`${value.id}`}>{value.name}</option>
                                        })}
                                      </select>
                                </div>
               
                   </div>


                </form> 
                

                <DisplayChangePasswordModel toggle = {this.showChangePassword} isModelOpen = {this.state.isModelOpen}/>
        
                
            
            </CardBody>
            </Card>
        </Col>

      </Row>
        

    </Container>)
  }

}


export default connect()(Profile);
