
import React,{useState,useEffect} from 'react'
import { Col, Row,Card, CardBody, Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap';
import GetAppIcon from '@material-ui/icons/GetApp';
import OfflinePinIcon from '@material-ui/icons/OfflinePin';

import { useSelector,useDispatch,connect } from 'react-redux';
import BarLoader from "react-spinners/BarLoader";
import {saveIntegrations} from '../../../redux/actions/integrationsActions'
import { toast,ToastContainer } from 'react-toastify';
import _ from 'lodash'
const IntegationsView = (props)=>{
  

    const {data,loading,completedTask} = props;
   
    const [configuration,setConfiguration] = useState(false);
    
    const showMessages = (type,message)=>{
        if(type === 'error'){
            toast.error(message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
        }else{
            toast.success(message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
        }
    }
    if(completedTask && completedTask.data === 'Success'){
        showMessages('success','succesfully added configurations'); 
    }
    const openConfiguration = (name)=>{
        
        const currentConfiguration = Object.assign({},name);
        console.log()
        if(currentConfiguration && currentConfiguration.isModelOpen){
            currentConfiguration['isModelOpen'] = false;
        }else{
            currentConfiguration['isModelOpen'] = true;
        }
        
        setConfiguration(currentConfiguration);
    }
    const close = ()=>{
        setConfiguration(prevState => {
            return { ...prevState, isModelOpen: false }
          });
        
    }

    return <Row className = "integrationService">
        <ToastContainer
        position="top-right"
        hideProgressBar={false}
        autoClose={false}
        newestOnTop={true}
        closeOnClick={false}
        draggable={false}
        rtl={false}
      />
        {loading && 
            <BarLoader/>
        }
        
        <RenderItems list = {data.data} openConfiguration = {openConfiguration}/>
    
    {configuration && configuration.isModelOpen 
                   && <ConfigurationModel key = "configuration_model"  configuration = {configuration} close = {close}/>}
    </Row>
    

}

const MEMORender = ({ list,openConfiguration })=>{
    console.log('Render: List');
    return (
        <React.Fragment>
          {list.map((value)=>{
          return <Col key = {value.id} style = {{paddingBottom:'12px'}} xs = {3} lg = {3} md = {4}>
  
                  <Card style = {{height:'300px'}}>
  
                      <div className = "integrationTitle">
                          <div className = "integrationTitle-logo">
                               <img src = {value.siteIcon}/>
                          </div>
                      </div>
                     <hr/>
                     <CardBody>
                          <Row>
                              <Col xs = {10} lg = {10} md = {10}>
                                   <h4 className ="bold-text"> {value.integrationName}</h4>
                              </Col>
                              <Col xs = {2} lg = {2} md = {2}>
                                      {value.status && value.status === 'Active' &&  <span><OfflinePinIcon style = {{cursor:'pointer'}} onClick = {()=>openConfiguration(value)} color = "primary"/></span>}
                                      {!value.status &&   <span><GetAppIcon style = {{cursor:'pointer'}} onClick = {()=>openConfiguration(value)}/></span> }
                                   
                              </Col>
                          </Row>
                          <p className = "typography-message">{value.description}</p>
                     </CardBody>
                     
                     
  
                  </Card>
          </Col>
      })} 
        </React.Fragment>
      );
}

const areEqual = (prevProps, nextProps) => {
    return _.isEqual(prevProps.list, nextProps.list)
  };

const RenderItems = React.memo(MEMORender,areEqual);

const ConfigurationModel = (props)=>{
    const {configuration,close} = props;
    const dispatch = useDispatch();
    const [formData,setFormData] = useState([]);
    useEffect(() => {
        if(props.configuration.configuredData){
            setFormData(JSON.parse(props.configuration.configuredData));
        }else if(props.configuration.integrationData){
            setFormData(JSON.parse(props.configuration.integrationData));
        }
    
    }, [configuration.isModelOpen])
    const changeEventHandler = (index,event)=>{
        const existingData = [...formData];
        existingData[index][event.target.name] = event.target.value;
        setFormData(existingData)
    }
    const submitData = ()=>{
        
        const finalFormData  = {id:0,integrationId:configuration.id,integrationData:JSON.stringify(formData)}
        if(configuration.mappingId){
            finalFormData['id'] = configuration.mappingId
        }
        
        dispatch(saveIntegrations(finalFormData))
    }
    
    return  <Modal className="add-widget-model" isOpen={configuration.isModelOpen}  >
        <ModalHeader>Configure {configuration.integrationName}</ModalHeader>
        <ModalBody>
            <form className = "">
            {formData && formData.map((value,index)=>{

                return  <div key = {index} className="form__form-group">
                            <span className="form__form-group-label">{value.label}</span>
                            <div className="form__form-group-field">
                               <input className = "form-control" value = {value[value.name]} name = {value.name}  type = "text"  onChange = {(event)=>changeEventHandler(index,event)} />
                             </div>
                         </div>

            })} 
            </form>
         
        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick = {submitData}>Save</Button>{' '}
            <Button color="secondary" onClick = {close}>Cancel</Button>
        </ModalFooter>
    </Modal>

}

function mapStateToProps (state) {
    return {
      isLoading: state.integrationsReducer.loading,
      data: state.integrationsReducer,
      completedTask: state.integrationsReducer.completeTask
    }
  }

export default connect(mapStateToProps)(IntegationsView); ;