function getMaxValue(options){
    let maxValueArray = [];
    if(options.thresholds){

        for(var i = 0; i < options.thresholds.length; i++){  
                var fromValue = parseInt(options.thresholds[i].from); 
                maxValueArray.push(fromValue);
        } 
    }
    return Math.max.apply(Math,maxValueArray);
}
function bulidRange(options){

    var result = []; 
  
    if(options.thresholds){

        for(var i = 0; i < options.thresholds.length; i++){  
           
            let rangeMap = {range:[],color:''}
            
            if(options.thresholds[i+1]){
                var fromValue = parseInt(options.thresholds[i].from); 
                var toValue = parseInt(options.thresholds[i+1].from); 
                rangeMap.range = [fromValue,toValue];
                rangeMap.color = options.thresholds[i].color.hex;
                result.push(rangeMap);
            } 
          

        } 

       return result;
       
    }
}

function processTableData (esResponse)  {
    const csvContent =  constructCsvData(esResponse);
    const finalResponse = consvertCsvDataToRows(csvContent);
    const fields = findMetricFields(esResponse);
    const noMetricFields = findNonMetricFields(esResponse);
    const dv = new DataSet.DataView();
      dv.source(finalResponse).transform({
        type: 'fold',
        fields: fields,
         key: 'opinion',
         value: 'value',
        retains: noMetricFields
      });

    return dv.rows;
  }

  function covertMetricDataToArray(data){
      let tempArray = [];
      for(var i=0;i<data.length;i++){
          if(data[i][0]){
            tempArray.push(data[i][0]);
          }else{
            tempArray.push(0);
          }
        
      }
      return tempArray;
  }

  function findMetricFields  (esResponse) {
        
    let metricCols  = esResponse[0].columns.filter(function (singleMetric) {
           return singleMetric.text === 'Count' || singleMetric.text === 'Sum';
       }).map(function(sm){
           return sm.text;
     }); 

  return metricCols;
  }
  function findNonMetricFields  (esResponse) {
        
    let metricCols  = esResponse[0].columns.filter(function (singleMetric) {
       return singleMetric.text != 'Count' &&  singleMetric.text != 'Sum';
   }).map(function(sm){
      return sm.text;
   }); 

   return metricCols;

  
}

  function consvertCsvDataToRows  (csvContent) {
    const ds = new DataSet();
     let rows1 =   ds.createView().source(csvContent, {
        type: 'csv',
     });

      var finalObjects = [];
      var finalCsvdata = rows1.rows
     for(var i=0;i<finalCsvdata.length;i++){
           var object = new Object();
          Object.keys(finalCsvdata[i]).forEach(function(key) {
            if(!isNaN(finalCsvdata[i][key])){
                  object[key] = parseInt(finalCsvdata[i][key]);
            }else{
                  object[key] = finalCsvdata[i][key];
          }
      });
      finalObjects.push(object);
  }
  return finalObjects;
 

}

  function constructCsvData  (data) {
    let tableHeaders = data[0].columns
    let tableData = data[0].rows;
    let csvContent = "";
    var tempData = [];
    for(var i=0;i<tableHeaders.length;i++){
      tempData.push(tableHeaders[i].text);
    }
    csvContent+=tempData.join(',')+"\n"
    for(var i=0;i<tableData.length;i++){
      var tempData1 = [];
      for(var j=0;j<tableData[i].length;j++){
        if(!isNaN(tableData[i][j])){
          tempData1.push(parseInt(tableData[i][j]));
        }else{
          tempData1.push(tableData[i][j]);
        }
       
      }
      csvContent+=tempData1.join(',')+"\n"
    }
    
    return csvContent;

  }

function processTimeSeriesData (data){
  let finalArray = [];
  for(var i=0;i<data.length;i++){
     
      let dataPoints = data[i].datapoints;
      dataPoints.map(function(singlePoint){
        let object = new Object();
        object['label'] = singlePoint[1];
        object['value'] = singlePoint[0];
        object['metric'] = data[i].target;
        finalArray.push(object);
      });
     
  }
  return finalArray;
}

function normalizeData  (dvData) {
    let tempArray = [];
    for(var i=0;i<dvData.length;i++){
      let object = new Object();
      let cols = [];
      Object.keys(dvData[i]).map(function(key){
        if(key!='opinion' && key!='value'){
          cols.push(dvData[i][key]);
        }

      });
      object['label'] = cols[0];
      object['value'] = dvData[i].value;
      object['metric'] =  cols.join(',')+"("+dvData[i].opinion+")";
      tempArray.push(object);
    }
    return tempArray;

  }

  function checkForDirectiveAggregation(metrics,id){
    for(var i=0;i<metrics.length;i++){
        if(metrics[i].pipelineAgg.model && metrics[i].pipelineAgg.id === id){

            return {fieldName:metrics[i].field,index:i,type:metrics[i].type};
        }
    }
    return {};
  }



function getComponent(data,config,query,width,height) {
 
if(!height){
    height = 300
}

  console.log(config.thresholds);

  let finalData = [];

  
  
 let isTableView = false;
 let isDeltaViewRequried = false;
  
  if(data.length > 0  && !data[0].rows){
     finalData = processTimeSeriesData(data);
     
  }else if(data.length > 0  && data[0].rows){
    let tableData = processTableData(data);
    isTableView = true;   
    finalData = normalizeData(tableData);
  }

  

  let object = new Object();
  const color = bulidRange(config)
  const maxValue = getMaxValue(config);
  if(isTableView && data[0].rows[0] && data[0].rows[0].length<3){
    if(data.length>0){
        let rows = data[0].rows;
        for(var i=0;i<rows.length;i++){
            object[rows[i][0]] = {'numeric':0,'changedvalue':0};
            object[rows[i][0]].numeric = rows[i][1];
            object[rows[i][0]].finalFieldName = rows[i][0];
        }
    }
  }else{
    for(let i=0;i<data.length;i++){
        let metricType = data[i].metric;
        let fieldname = data[i].props.field;
        let target = data[i].target;
        let fieldProps =  data[i].props;
        let finalFieldName = "";
        for (let j = 0; j < Object.keys(fieldProps).length; j++) {
                if(Object.keys(fieldProps)[j] != 'field'){
                    finalFieldName = fieldProps[Object.keys(fieldProps)[j]];
                }
        }   
    
        
        if(metricType=== 'count' || metricType=== 'sum' || metricType=== 'avg' || metricType=== 'min' || metricType=== 'max' || metricType=== 'cardinality') {
           let data1 = _.find(data,function(fieldName){
                if(fieldName.metric === 'derivative' && fieldName.props.field == fieldname){
                    return fieldName;
                }
            });
            object[target] = {'value':0,'changedvalue':0};
            const changeValues = covertMetricDataToArray( data[i].datapoints);
            if(data1 && data1.datapoints){
                const changeValues1 = covertMetricDataToArray( data1.datapoints);
                object[target].changedvalue = Math.max.apply(Math,changeValues1);
                isDeltaViewRequried = true;
            }
            const timeseires = [];
    
            for(let k=0;k<data[i].datapoints.length;k++){
                timeseires.push(data[i].datapoints[k][1]);
            }
    
           
    
            const maxTime = Math.max.apply(Math,timeseires);
            const minTime = Math.min.apply(Math,timeseires);
    
            let value = 0;
            if(config.calculation === 'mean'){
                value = mean(changeValues);
            }else if(config.calculation ==='max' ){
                value = Math.max.apply(Math,changeValues);
            }else if(config.calculation ==='min'){
                value = Math.min.apply(Math,changeValues);
            }else{
                value = Math.min.apply(Math,changeValues);
            }
           
            const sparkLineData = {
                xMin: minTime,
                xMax: maxTime,
                data: data[i].datapoints,
              }
            object[target].text = value
            object[target].numeric = value
            object[target].color = color
            object[target].title = target;
            object[target].sparkLine = sparkLineData;
            object[target].finalFieldName = finalFieldName;
           // object[target].dataPoints = target;
           
        }   
      }
  }


  
  
  
 console.log(object);



 
  



  class RenderChart extends React.Component {

    
    componentDidMount() {
      
     
    }

   
    


    render() {
        let chartData = [];
        const itemSpacing = 3;

        const grid = calculateGridDimensions(width, height, 3, Object.keys(object).length);
        console.log(grid);

    let xGrid = 0;
    let yGrid = 0;
    let items : JSX.Element[] = [];
    
     

    for (let i = 0; i < Object.keys(object).length; i++) {
      const key = Object.keys(object)[i];
      
      const value = object[key];

    
      
      const isLastRow = yGrid === grid.yCount - 1;

      const itemWidth = isLastRow ? grid.widthOnLastRow : grid.width;
      const itemHeight = grid.height;

      const xPos = xGrid * itemWidth + itemSpacing * xGrid;
      const yPos = yGrid * itemHeight + itemSpacing * yGrid;
      let fontSize =  calculateFontSize(
        value.numeric,
        itemWidth,
        height * 0.2,
        1.2,
        30
      );


      const itemStyles = {
        position: 'absolute',
        left: xPos,
        top: yPos,
        width: `${itemWidth}px`,
        height: `${itemHeight}px`,
      };
      if(isDeltaViewRequried){
        items.push(
            <div key={i} style={itemStyles}>
            <Plot
            data={[
                {
                    type: "indicator",
                    mode: "gauge+number+delta",
                    value: value.numeric,
                    gauge: {
                        axis: { range: [null, value.numeric] },
                        steps: color
                        
                      },
                    delta: { position: "top", reference: 0 },
                    domain: { x: [0, 1], y: [0, 1] }
                  }
            ]}
            layout={{
               
                width: itemWidth,
                height: itemHeight,
                margin: { t: 30, b: 0, l: 20, r: 3 },title: value.finalFieldName,titlefont: {
                    "size": fontSize*0.4
                  },
              } }
          />
            </div>
          );
      }else{
        items.push(
            <div key={i} style={itemStyles}>
            <Plot
            data={[
                {
                    type: "indicator",
                    mode: "gauge+number",
                    value: value.numeric,
                    gauge: {
                        axis: { visible: false,range: [null, maxValue] },
                        steps: color
                        
                      },
                   
                    domain: { x: [0, 1], y: [0, 1] }
                  }
            ]}
            layout={{
               
                width: itemWidth,
                height: itemHeight,
                margin: { t: 30, b: 0, l: 20, r: 3 },title: value.finalFieldName,titlefont: {
                    "size": fontSize*0.4
                  },
              } }
          />
            </div>
          );
      }
     

      xGrid++;

      if (xGrid === grid.xCount) {
        xGrid = 0;
        yGrid++;
      }
    }

   

      return (
      
       
        <div style={{ position: 'relative' }}>{items}</div>
      
     
        
      );
    }
  }
  return RenderChart;
}


class Gauge extends React.Component {
  render() {
    const { width, height } = this.props
    console.log(height);
    const RenderChart = getComponent(this.props.data,this.props.chartConfig,this.props.query,width,height);
    return (
     
       <RenderChart />
      
    );
  }
}

export default  (Gauge);
