import React,{useEffect,useState} from 'react';
import { Button, Card, CardBody, Col, Container, Row } from 'reactstrap';
import { Field, reduxForm } from 'redux-form';
import Playbook from './Playbook'

import Modeler from 'bpmn-js/lib/Modeler';

import customModdleExtension from '../moddle/custom.json';
import 'bpmn-js/dist/assets/diagram-js.css';
import 'bpmn-js/dist/assets/bpmn-font/css/bpmn-embedded.css';
import 'bpmn-js-properties-panel/dist/assets/bpmn-js-properties-panel.css'
import {toast, ToastContainer} from "react-toastify";
import playbookService from '../../../api/playbookService';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';



var propertiesPanelModule = require('bpmn-js-properties-panel'),
    // providing camunda executable properties, too
    propertiesProviderModule = require('bpmn-js-properties-panel/lib/provider/camunda'),
    camundaModdleDescriptor = require('camunda-bpmn-moddle/resources/camunda');

let  modeler = {}

const CreatePlayBook = (props)=>{

    let { id } = useParams();
    const {initialize} = props;

    console.log(id);

     const [formData,setFormData] = useState({name:"",description:""});

     const changeFormData = (event)=>{
         let tempFormData = formData;
         tempFormData[event.target.name] = event.target.value;
        setFormData(tempFormData)
     }
    
    useEffect(() => {
        const $modelerContainer = document.querySelector('#modeler-container');
        const $propertiesContainer = document.querySelector('#properties-container');
         modeler = new Modeler({
            container: $modelerContainer,
            additionalModules: [
                propertiesPanelModule,
                propertiesProviderModule
                
            ],
            moddleExtensions: {
                camunda: camundaModdleDescriptor
            },
            keyboard: {
              bindTo: document.body
            },
            propertiesPanel: {
                parent: $propertiesContainer
              },
            
          });
        if(id){
            playbookService.getSinglePlaybook(id).then((responseData)=>{
                if(responseData.data.id){
                    const tempFormData = new Object();
                    tempFormData['name'] = responseData.data.playBookName
                    tempFormData['description'] = responseData.data.playBookDescription
                    
                    initialize(tempFormData);
                    setFormData(tempFormData);
                    console.log(props.enableReinitialize);
                    
                    modeler.importXML(responseData.data.playbookXml);
                }else{
                    alert("Requested Playbook was Available")
                    window.location.href = "/playbook"
                }
                
    
            })
        }else{

            const processId = "Process_"+Math.round(Math.random() * 11000);
           
            modeler.importXML('<?xml version="1.0" encoding="UTF-8"?> <bpmn:definitions xmlns:bpmn="http://www.omg.org/spec/BPMN/20100524/MODEL" xmlns:bpmndi="http://www.omg.org/spec/BPMN/20100524/DI" xmlns:dc="http://www.omg.org/spec/DD/20100524/DC" id="Definitions_1buzjmt" targetNamespace="http://bpmn.io/schema/bpmn" exporter="Camunda Modeler" exporterVersion="3.0.0-dev"> <bpmn:process id="'+processId+'" isExecutable="true"> <bpmn:startEvent id="StartEvent_1" /> </bpmn:process> <bpmndi:BPMNDiagram id="BPMNDiagram_1"> <bpmndi:BPMNPlane id="BPMNPlane_1" bpmnElement="Process_1buwy5e"> <bpmndi:BPMNShape id="_BPMNShape_StartEvent_2" bpmnElement="StartEvent_1"> <dc:Bounds x="179" y="159" width="36" height="36" /> </bpmndi:BPMNShape> </bpmndi:BPMNPlane> </bpmndi:BPMNDiagram> </bpmn:definitions> ');
    
        }

        
          
         
      
        },[id])

    

    const saveData = ()=>{
        if(formData.name === ''){
            toast.error("Please Enter Name", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return false;
        }
        if(formData.description === ''){
            toast.error("Please Enter Description", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return false;
        }
        modeler.saveXML().then((responseData)=>{
            const {xml} = responseData;
            const tempData = formData;
            if(id){
                tempData['id'] = parseInt(id);
            }
            tempData['playBookName'] = formData.name
            tempData['playBookDescription'] = formData.description
            tempData['playbookXml'] = xml;
            playbookService.savePlayBookDetails(tempData).then((response)=>{
                if(response.status === 201){
                    toast.success("Successfully Created Playbook", {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }else{
                    toast.error("Unable to create playbook", {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }
            })
        });
    }

    return  (
    <Container style = {{paddingTop:'70px'}}>
        <ToastContainer
                position="top-right"
                hideProgressBar={false}
                autoClose={false}
                newestOnTop={true}
                closeOnClick={false}
                draggable={false}
                rtl={false}
            />

        <Row >
                        <Col md={10}>
                            <h3 className="page-title">Playbooks</h3>
                        </Col>
                        <Col md={2}>
                             <Link className="btn btn-primary"  to ="/playbook">Back</Link> 
                             <Button color = "primary" onClick = {saveData}>Save</Button> 
                        </Col>
        </Row>

    <Card style = {{height:'100px'}}>
        <CardBody>
        <form className="form form--horizontal">
           
            <Col xs = {12} lg = {6} md = {6}>

                <div className="form__form-group">
                    <span className="form__form-group-label">Name</span>
                    <div className="form__form-group-field">
                    <Field 
                        onChange = {(event)=>changeFormData(event)}
                        name="name"
                        component="input"
                        type="text"
                        placeholder="Playbook name"
                    />
                    </div>
                </div>

            </Col>
            <Col xs = {12} lg = {6} md = {6}>
             <div className="form__form-group">
                    <span className="form__form-group-label">Description</span>
                    <div className="form__form-group-field">
                    <Field 
                        onChange = {(event)=>changeFormData(event)}
                        name="description"
                        component="input"
                        type="text"
                        placeholder="Playbook description"
                    />
                    </div>
                </div>

            </Col>

            
            

                

            </form>   
        </CardBody>
          

    </Card>
    <hr/>
    <Card>
        <CardBody>
            <Playbook modeler = {modeler}/>
        </CardBody>
    </Card>

    </Container>
    )
   
}

export default reduxForm({
    form: 'playbook_form', 
    enableReinitialize : true
    // a unique identifier for this form
})(CreatePlayBook);