import axiosInstance from '../middleware/Interceptor'


function getPlanDetails () {
    return axiosInstance.get('/siem-core/user/plan/get-plan-details')
}
function getUsageDetails(startDate){
    return axiosInstance.get('/siem-core/user/plan/get-usage-details?startDate='+startDate)
}
function getBillingMonths(){
    return axiosInstance.get('/siem-core/user/plan/get-billing-dates')
}
function getBillingHistory(startDate){
    return axiosInstance.get("/siem-core/user/plan/get-billing-details?startDate="+startDate)
}

function getInvoiceMonths(){
    return axiosInstance.get('/siem-core/user/plan/get-invoice-dates')
}

function getInvoiceDetails(startDate){
    return axiosInstance.get("/siem-core/user/plan/get-invoice-details?startDate="+startDate)
}


const billingService = {
    getPlanDetails,
    getUsageDetails,
    getBillingMonths,
    getBillingHistory,
    getInvoiceMonths,    
    getInvoiceDetails
}

export default billingService