
import React,{useState,useEffect    } from 'react';
import { Col, Container, Row,ButtonToolbar,Button,Card,CardBody,ButtonGroup,InputGroup,InputGroupAddon,Input } from 'reactstrap';
import { useSelector,useDispatch } from 'react-redux';

import {applySearch} from '../../../../redux/actions/dashboardActions'

export const Filter = (props) =>{

    return(
        <React.Fragment>
        <SearchBar/> 
        
        </React.Fragment>
       
    )
    

}


const SearchBar = () =>{

    const [searchText, setSearchText] = useState('')

    const dispatch = useDispatch();

    const changeHandler = (event) =>{
        if(event.target.value===''){
            
            dispatch(applySearch("*"));
        }else{
            setSearchText(event.target.value);
        }
    }

    const applySearchHandler = () => {
        dispatch(applySearch(searchText));
    }

    return (<InputGroup>
       
        <Input placeholder="search ...." onChange={changeHandler} />
           <InputGroupAddon addonType="append" style = {{height:"53px"}}>
            <Button color="primary" onClick = {applySearchHandler}>Search</Button>
           </InputGroupAddon>
        </InputGroup>);
}



export default Filter

