import React, { useEffect, useState } from "react";

import { useSelector, useDispatch } from "react-redux";
import {
  Col,
  Modal,
  ModalHeader,
  ModalBody,
  Button,
} from "reactstrap";
import { Field, reduxForm, SubmissionError, FieldArray } from "redux-form";
import dashboardService from "../../../api/dashboardService";
import _ from "lodash";
import { ToastContainer, toast } from "react-toastify";
import renderSelectField from "../../../shared/components/form/Select";
import BarLoader from "react-spinners/BarLoader";
import {closeEditDashboard} from "../../../redux/actions/dashboardActions";

const permissions = [
  { label: "Viewer", value: "viewer" },
  { label: "Admin", value: "admin" },
  { label: "Editor", value: "Editor" },
];

const EditDashboard = (props) => {
  const dashboardModel = useSelector((state) => state.dashboards.dashboard);
  
  const {
    error,
    handleSubmit,
    pristine,
    reset,
    submitting,
    initialize,
  } = props;

  return (
    <NewDashboardMemo
      dashboard={dashboardModel}
      error={error}
      handleSubmit={handleSubmit}
      pristine={pristine}
      reset={reset}
      submitting={submitting}
      initialize={initialize}
    />
  );
};

function NewDashboardPropsAreEqual(preProps, nextProps) {
  return (
    preProps.dashboard.isSidePanelOpen === nextProps.dashboard.isSidePanelOpen
  );
}

const NewDashboard = (props) => {
  console.log("new dashboad")
  const {
    dashboard,
    handleSubmit,
    submitting,
    initialize,
  } = props;

  const [filters, setFilters] = useState([]);
  const [loading, setLoading] = useState(false);
  const [enableReport, setEnableReport] = useState(false);
  const dispatch = useDispatch();

  const [frequency, setFrequency] = useState("");
  useEffect(() => {
    if (dashboard.id != 0) {
      let categoryLabel = {};
      if(dashboard.reportId){
        categoryLabel = _.find(dashboard.reportCategories, function (o) {
          return o.value === dashboard.category;
        });
      }else{
        categoryLabel = _.find(dashboard.dashboardCategories, function (o) {
          return o.value === dashboard.category;
        });
      }
      
      initialize({
        title: dashboard.title,
        description: dashboard.description,
        category: categoryLabel,
        members: dashboard.permissions,
        reportTitle: dashboard.scheduleName,
        emailAddress: dashboard.emailAddress,
        frequency: { label: dashboard.frequency, value: dashboard.frequency },
        frequencyType: {
          label: dashboard.frequencyType,
          value: dashboard.frequencyType,
        },
        hourOfDay: { label: dashboard.hourOfDay, value: dashboard.hourOfDay },
      });
      if(dashboard.filters){
        setFilters(dashboard.filters);
      }
      
      if (dashboard.reportId && dashboard.reportId != 0) {
        setEnableReport(true);
      }
    }
  }, [dashboard.isSidePanelOpen]);

  const submit = (values, formValues) => {
    if (!dashboard.title) {
      throw new SubmissionError({
        title: "Please Enter Valid Title",
      });
    }
    if (!dashboard.description) {
      throw new SubmissionError({
        description: "Please Enter Valid Description",
      });
    }
    if (!dashboard.category) {
      throw new SubmissionError({
        category: "Please Valid Category",
      });
    }

    if (dashboard.permissions.length == 0) {
      toast.error("Please Add Permission", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return false;
    }
    

    for(var i=0;i<dashboard.permissions.length;i++){
      if(!dashboard.permissions[i].permission){
        toast.error("Please Add Permission", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return false;
      }
      if(!dashboard.permissions[i].username){
        toast.error("Please Add User Name", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return false;
      }
    }
    //var isPermissionsNull = dashboard.permissions.permission.

    const data = {
      id: dashboard.id,
      title: dashboard.title,
      description: dashboard.description,
      config: JSON.stringify(dashboard.permissions),
      categoryId: dashboard.category,
      isReportConfigured: enableReport,
      scheduleName: dashboard.scheduleName,
      emailAddress: dashboard.emailAddress,
      frequency: dashboard.frequency,
      timeRange: dashboard.timeRange,
      frequencyType: dashboard.frequencyType,
      hourOfDay: dashboard.hourOfDay,
      format: "PDF",
      report_id: dashboard.report_id,
      dashboard_filters: JSON.stringify(filters),
    };

    setLoading(true);
    dashboardService.saveDashboard(data).then((response) => {
      setLoading(false);
      if (response.status === 201 || response.status === 200) {
        toast.success("Configuration was saved successfully", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      } else {
        toast.error("Unable to Save Configuration", {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    });
   
  };
  const onEabledReport = () => {
    setEnableReport(!enableReport);
  };
  const changeEventHanlder = (event) => {
    dashboard.updateNonSelectValue(event);
  };
  const changeCategory = (event) => {
    dashboard.updateCategory(event);
  };

  const onReportChangeHandler = (event, name) => {
    dashboard.updateReport(event, name);
    if (name === "frequency") {
      setFrequency(event.value);
    }
  };
  const updatePermissions = (index, event, name) => {
    dashboard.updatePermissions(index, event, name);
  };
  const addField = (fields) => {
    fields.push({});
    dashboard.addField();
  };

  const removeField = (fields, index) => {
    fields.remove(index);
    dashboard.removeField(index);
  };
  const addFilter = () => {
    setFilters((prevArray) => [
      ...prevArray,
      { key: "", operator: "", value: "" },
    ]);
  };

  const onFilterChange = (index, fieldName, event) => {
    let newArr = filters.map((item, i) => {
      if (index == i) {
        return { ...item, [fieldName]: event.target.value };
      } else {
        return item;
      }
    });

    setFilters(newArr);
  };
  const deleteFilter = (index) => {
    const temp = [...filters];

    // removing the element using splice
    temp.splice(index, 1);

    // updating the list
    setFilters(temp);
  };
  const closeEditModel = () =>{
    dispatch(closeEditDashboard(true))
}

  const RenderFilters = () => {
    console.log(filters);
    return (
      <React.Fragment>
        <Col md={12}>
          <Button
            style={{ float: "right" }}
            color="link"
            onClick={() => addFilter()}
          >
            <span class="lnr lnr-plus-circle"></span>
          </Button>
        </Col>

        <table className="table">
          <thead>
            <tr>
              <th>Field Name</th>
              <th>Operator</th>
              <th>Value</th>
              <th>Delete</th>
            </tr>
          </thead>
          <tbody>
            {filters && filters.map((value, index) => {
              return (
                <tr>
                  <td style={{ width: "30%" }}>
                    <select
                      onChange={(event) => onFilterChange(index, "key", event)}
                      value={value.key}
                      className="form-control"
                    >
                      {dashboard.logFields.map((field) => (
                        <option value={field.value}>{field.value}</option>
                      ))}
                    </select>
                  </td>

                  <td>
                    <select
                      onChange={(event) =>
                        onFilterChange(index, "operator", event)
                      }
                      value={value.operator}
                      className="form-control"
                    >
                      <option value="=">Equals</option>
                      <option value="!=">Not Equals</option>
                      <option value="<">Greater Than</option>
                      <option value=">">Less Than</option>
                      <option value="=~">Contains</option>
                      <option value="!~">Not Contains</option>
                    </select>
                  </td>

                  <td>
                    <input
                      onChange={(event) =>
                        onFilterChange(index, "value", event)
                      }
                      type="text"
                      value={value.value}
                      className="form-control"
                    />
                  </td>

                  <td>
                    <Button
                      style={{ float: "right" }}
                      color="link"
                      onClick={() => deleteFilter(index)}
                    >
                      <span class="lnr lnr-trash"></span>
                    </Button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </React.Fragment>
    );
  };

  const renderPermissions = ({ fields }) => (
    <React.Fragment>
      <Col md={12}>
        <Button
          style={{ float: "right" }}
          color="link"
          onClick={() => addField(fields)}
        >
          <span class="lnr lnr-plus-circle"></span>
        </Button>
      </Col>

      <table className="table">
        <thead>
          <tr>
            <th>Permission</th>
            <th>User</th>
            <th>Action</th>
          </tr>
        </thead>

        <tbody>
          {fields.map((member, index) => (
            <tr>
              <td>
                <Field
                  key={`${member}.permission-${index}`}
                  onChange={(target) =>
                    updatePermissions(index, target, "permission")
                  }
                  name={`${member}.permission`}
                  type="select"
                  options={permissions}
                  component={renderSelectField}
                />
              </td>
              <td>
                <Field
                  key={`${member}.username-${index}`}
                  onChange={(target) =>
                    updatePermissions(index, target, "username")
                  }
                  name={`${member}.username`}
                  options={dashboard.users}
                  type="text"
                  component={renderSelectField}
                />
              </td>
              <td>
                <Button
                  key={`${member}.button-${index}`}
                  style={{ float: "right" }}
                  color="link"
                  onClick={() => removeField(fields, index)}
                >
                  <span class="lnr lnr-trash"></span>
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </React.Fragment>
  );

  return (
    <Modal
      style={{ maxWidth: 800 }}
      size="lg"
      isOpen={dashboard.isSidePanelOpen}
    >
      {loading && <BarLoader color="#53c8b7" height={4} width="100%" />}
      <ModalHeader>New Dashboard</ModalHeader>
      <ModalBody>
        <ToastContainer
          position="top-right"
          hideProgressBar={false}
          autoClose={false}
          newestOnTop={true}
          closeOnClick={false}
          draggable={false}
          rtl={false}
        />

        <Col md={12} lg={12}>
          <form
            className="form form--horizontal"
            onSubmit={handleSubmit(submit)}
          >
            <div className="form__form-group">
              <span className="form__form-group-label">Title</span>
              <div className="form__form-group-field">
                <Field
                  name="title"
                  component={renderField}
                  type="text"
                  onChange={changeEventHanlder}
                />
              </div>
            </div>
            <div className="form__form-group">
              <span className="form__form-group-label">Description</span>
              <div className="form__form-group-field">
                <Field
                  name="description"
                  component={renderField}
                  type="text"
                  onChange={changeEventHanlder}
                />
              </div>
            </div>
            <div className="form__form-group">
              <span className="form__form-group-label">Enable Report</span>
              <div className="form__form-group-field">
                {enableReport && (
                  <input
                    checked
                    onChange={onEabledReport}
                    style={{ height: "12px", width: "12px" }}
                    name="enableReport"
                    type="checkbox"
                    className="form-control"
                  />
                )}
                {!enableReport && (
                  <input
                    onChange={onEabledReport}
                    style={{ height: "12px", width: "12px" }}
                    name="enableReport"
                    type="checkbox"
                    className="form-control"
                  />
                )}
              </div>
            </div>

            {!enableReport && (
              <div className="form__form-group">
                <span className="form__form-group-label">Category</span>
                <div className="form__form-group-field">
                  <Field
                    name="category"
                    component={renderSelectField}
                    options={dashboard.dashboardCategories}
                    onChange={changeCategory}
                  />
                </div>
              </div>
            )}

            {enableReport && (
              <div className="form__form-group">
                <span className="form__form-group-label">Report Category</span>
                <div className="form__form-group-field">
                  <Field
                    name="category"
                    component={renderSelectField}
                    options={dashboard.reportCategories}
                    onChange={changeCategory}
                  />
                </div>
              </div>
            )}

            {enableReport && (
              <React.Fragment>
                <div className="form__form-group">
                  <span className="form__form-group-label">Report Title</span>
                  <div className="form__form-group-field">
                    <Field
                      name="reportTitle"
                      component={renderField}
                      type="text"
                      onChange={(event) =>
                        onReportChangeHandler(event, "reportTitle")
                      }
                    />
                  </div>
                </div>

                <div className="form__form-group">
                  <span className="form__form-group-label">Frequency</span>
                  <div className="form__form-group-field">
                    <Field
                      name="frequency"
                      component={renderSelectField}
                      type="select"
                      onChange={(event) =>
                        onReportChangeHandler(event, "frequency")
                      }
                      options={[
                        { label: "Daily", value: "Daily" },
                        { label: "Weekly", value: "Weekly" },
                        { label: "Monthly", value: "Monthly" },
                      ]}
                    />
                  </div>
                </div>

                {frequency === "Weekly" && (
                  <div className="form__form-group">
                    <span className="form__form-group-label">Day of Week</span>
                    <div className="form__form-group-field">
                      <Field
                        name="frequencyType"
                        component={renderSelectField}
                        type="select"
                        onChange={(event) =>
                          onReportChangeHandler(event, "frequencyType")
                        }
                        options={[
                          { label: "Sun", value: "Sun" },
                          { label: "Mon", value: "Mon" },
                          { label: "TUE", value: "TUE" },
                          { label: "WED", value: "WED" },
                          { label: "THU", value: "THU" },
                          { label: "FRI", value: "FRI" },
                          { label: "SAT", value: "SAT" },
                        ]}
                      />
                    </div>
                  </div>
                )}
                {frequency === "Monthly" && (
                  <div className="form__form-group">
                    <span className="form__form-group-label">Week of Day</span>
                    <div className="form__form-group-field">
                      <Field
                        name="frequencyType"
                        component={renderSelectField}
                        type="select"
                        onChange={(event) =>
                          onReportChangeHandler(event, "frequencyType")
                        }
                        options={[
                          { label: "1", value: "1" },
                          { label: "2", value: "2" },
                          { label: "3", value: "3" },
                          { label: "4", value: "4" },
                          { label: "5", value: "5" },
                          { label: "6", value: "6" },
                          { label: "7", value: "7" },
                          { label: "8", value: "8" },
                          { label: "9", value: "9" },
                          { label: "10", value: "10" },
                          { label: "11", value: "11" },
                          { label: "12", value: "12" },
                          { label: "13", value: "13" },
                          { label: "14", value: "14" },
                          { label: "15", value: "15" },
                          { label: "16", value: "16" },
                          { label: "17", value: "17" },
                          { label: "18", value: "18" },
                          { label: "19", value: "19" },
                          { label: "20", value: "20" },
                          { label: "21", value: "21" },
                          { label: "22", value: "22" },
                          { label: "23", value: "23" },
                          { label: "24", value: "24" },
                          { label: "25", value: "25" },
                          { label: "26", value: "26" },
                          { label: "27", value: "27" },
                          { label: "28", value: "28" },
                          { label: "29", value: "29" },
                          { label: "30", value: "30" },
                        ]}
                      />
                    </div>
                  </div>
                )}

                <div className="form__form-group">
                  <span className="form__form-group-label">Hour of Day</span>
                  <div className="form__form-group-field">
                    <Field
                      name="hourOfDay"
                      component={renderSelectField}
                      type="select"
                      onChange={(event) =>
                        onReportChangeHandler(event, "hourOfDay")
                      }
                      options={[
                        { label: "0", value: "0" },
                        { label: "1", value: "1" },
                        { label: "2", value: "2" },
                        { label: "3", value: "3" },
                        { label: "4", value: "4" },
                        { label: "5", value: "5" },
                        { label: "6", value: "6" },
                        { label: "7", value: "7" },
                        { label: "8", value: "8" },
                        { label: "9", value: "9" },
                        { label: "10", value: "10" },
                        { label: "11", value: "11" },
                        { label: "12", value: "12" },
                        { label: "13", value: "13" },
                        { label: "14", value: "14" },
                        { label: "15", value: "15" },
                        { label: "16", value: "16" },
                        { label: "17", value: "17" },
                        { label: "18", value: "18" },
                        { label: "19", value: "19" },
                        { label: "20", value: "20" },
                        { label: "21", value: "21" },
                        { label: "22", value: "22" },
                        { label: "23", value: "23" },
                      ]}
                    />
                  </div>
                </div>

                <div className="form__form-group">
                  <span className="form__form-group-label">Email </span>
                  <div className="form__form-group-field">
                    <Field
                      name="emailAddress"
                      component={renderField}
                      type="text"
                      onChange={(event) =>
                        onReportChangeHandler(event, "emailAddress")
                      }
                    />
                  </div>
                </div>
              </React.Fragment>
            )}
            <div
              style={{
                borderBottom: "1px solid #c3bfbf",
                width: "100%",
                textAlign: "left",
              }}
            >
              Permissions
            </div>

            <FieldArray name="members" component={renderPermissions} />

            <hr />

            <div
              style={{
                borderBottom: "1px solid #c3bfbf",
                width: "100%",
                textAlign: "left",
              }}
            >
              Filters
            </div>
            <RenderFilters />
            <Col md={12}>
              <Button
                style={{ float: "right", marginLeft: "12px" }}
                color="primary"
                type="submit"
                disabled={submitting}
              >
                Submit
              </Button>
              <Button style = {{float: 'right'}} type="button" onClick = {closeEditModel} >
                  Close
                </Button>
            </Col>
          </form>
        </Col>
      </ModalBody>
    </Modal>
  );
};
const NewDashboardMemo = React.memo(NewDashboard, NewDashboardPropsAreEqual);

const renderField = ({
  input,
  placeholder,
  type,
  meta: { touched, error },
}) => (
  <div className="form__form-group-input-wrap">
    <input {...input} placeholder={placeholder} type={type} />
    {touched && error && (
      <span className="form__form-group-error">{error}</span>
    )}
  </div>
);

export default reduxForm({
  form: "edit_dashboard_form",
  enableReinitialize: true,
})(EditDashboard);
