import React from 'react'
import SplitterLayout from "react-splitter-layout";
import {Row,Col} from 'reactstrap'
import EntityType from "./EntityType";
import {connect, useSelector} from "react-redux";
import EntityQuickInfo from "./EntityQuickInfo";
import TimeLineView from "./TimeLineview";

const EntityLayout =(props)=>{

    const entity = useSelector((state) => state.threatExploreReducer.entity);


    console.log(entity)

    return (

            <Row>
                <Col xs= {12} md = {3} lg={3} sm={12} >
                    <EntityType entity = {entity}/>
                </Col>

                <Col xs= {12} md = {9} lg={9} sm={12} >
                    <EntityQuickInfo entity = {entity}/>
                    <TimeLineView />
                </Col>
            </Row>

       )
}
export default connect()(EntityLayout)
