import React,{useEffect,useState} from 'react';

import { Row,Col, Container } from 'reactstrap';
import dashboardService from '../../../api/dashboardService';
import RGL, { WidthProvider } from "react-grid-layout";
import '../../../scss/component/report.scss'
import {
 subDays,
} from 'date-fns';

import DashboardPanel from '../components/AddPanelWidget/DashboardPanel'
import Moment from 'react-moment';

const ReactGridLayout = WidthProvider(RGL);
 const DashboardPdf = (props)=>{
        const  [dashboardData,setDashboardData] = useState({});
        useEffect(() => {
          let search = window.location.search;
          const id = props.match.params.id;
          let params = new URLSearchParams(search);
          let token = params.get("token");
          dashboardService.getSingleDashboardForReport(id,token).then((responseData)=>{
            setDashboardData(responseData.data);
          })
        }, [])

       

        return (
            <React.Fragment>
              {dashboardData && Object.keys(dashboardData).length!=0 && 
                 <React.Fragment>
                   <RenderHeader dashboard = {dashboardData}/>
                   <RenderLayout dashboard = {dashboardData}/>
                   </React.Fragment>
              }
            
            </React.Fragment>
            
        
       );
}

const RenderLayout = (props) =>{
  const GRID_CELL_HEIGHT = 80;
  const GRID_CELL_VMARGIN = 8;
  const GRID_COLUMN_COUNT = 24;
  const REPEAT_DIR_VERTICAL = 'v';
 
  const {dashboard} = props;

  const buildLayout =() =>{
    const layout = [];
    const panelMap = {};
    if(Object.keys(dashboard).length!=0){
      console.log(dashboard);
           for (const panel of dashboard.panels) {
                      const stringId = panel.id.toString();
                      panelMap[stringId] = panel;

                      if (!panel.gridOptions) {
                        console.log('panel without gridpos');
                        continue;
                      }

                      const panelPos = {
                        i: stringId,
                        x: panel.gridOptions.x,
                        y: panel.gridOptions.y,
                        w: panel.gridOptions.w,
                        h: panel.gridOptions.h,
                      };

                      layout.push(panelPos);
                    }

    }


   // console.log(this.props.dashboard);

    return layout;

  }


  const renderPanels =()=> {
    const panelElements = [];

    
   // console.log("test")
   if(Object.keys(dashboard).length!=0){
         for (const panel of dashboard.panels) {
                    const id = panel.id.toString();
                     panelElements.push(
                           <div key={id}  id={'panel-' + id}>
                               {renderPanel(panel)}
                           </div>
                    );
                }

    }

    return panelElements;
  }

  const renderPanel = (panel)=> {
    
    let search = window.location.search;
    let params = new URLSearchParams(search);
    let startDate = params.get('startDate');
    
    let endDate = params.get('endDate');
    let token = params.get('token');

    const tempStartDate = new Date(parseInt(startDate));
    const tempEndDate = new Date(parseInt(endDate));
    
    console.log(new Date(parseInt(startDate))+">>>"+new Date(parseInt(startDate)));

    const filters = [];
    const fields = [];
    const date = [{startDate:tempStartDate,endDate:tempEndDate}]
    return (
      <DashboardPanel token = {token}  type = "report"   searchString = "*"  filters = {filters} date = {date} panel = {panel} fields = {filters} dashboard = {props.dashboard}/>


    );
  } 


  return (
  <Container>
    <Row>
      <Col lg= {12}>
      <ReactGridLayout
        layout={buildLayout()}
        width={1000}
      className="grid"
      isDraggable={false}
      isResizable={false}
      containerPadding={[0, 0]}
      useCSSTransforms={false}
      margin={[GRID_CELL_VMARGIN, GRID_CELL_VMARGIN]}
      cols={GRID_COLUMN_COUNT}
      rowHeight={GRID_CELL_HEIGHT}
      draggableHandle = ".panel-title-container"
      
     
      
        
      >
        {renderPanels()}
      </ReactGridLayout>
      </Col>

    </Row>
  

  </Container>
  
  );


}


const RenderHeader = (props)=>{

  let search = window.location.search;
    let params = new URLSearchParams(search);
     let startDate = params.get('startDate');
    
     let endDate = params.get('endDate');

     let generateBy = params.get('generateBy');

     const tempStartDate = new Date(parseInt(startDate));
     const tempEndDate = new Date(parseInt(endDate));

   return (<Row className = "header-container">
          <div className="col-md-2 log--container">
					<img className="logo"
						alt="https://obelus.us/images/logo.png"
						src="https://obelus.us/images/logo.png" />
				</div>
        <div className="col-md-10">

					<p className="text-main text-semibold report-title">{props.dashboard.name}</p>
					<p className="report-desc">{props.dashboard.description}</p>
          <p className="report-desc">Generated By <b>{generateBy}</b><span>Time Range :</span> <b> <Moment format="MM/DD/YYYY HH:mm:ss A">{tempStartDate}</Moment> </b> to <b> <Moment format="MM/DD/YYYY HH:mm:ss A">{tempEndDate}</Moment> </b> </p>
				</div>

    </Row>)
  
}

const RenderPdf =()=>{
    return (<div>Prasanth</div> )
}


export default DashboardPdf