import {

    OPEN_THERAT_EXPLORE_ENTITY,
    OPEN_THERAT_TIME_LINE
} from '../actions/threatExploreAction';


const initialState = {
    entity: {type:'user',entityData:{},startDate:0,endDate:0,entityValue:''},
    timeLine:{type:'user',entityData:[]},
};

const threatExploreReducer = (state = initialState, action) => {
    if(action.type === OPEN_THERAT_EXPLORE_ENTITY ){
        return Object.assign({}, state, { entity: action.payload  });
    }
    if(action.type === OPEN_THERAT_TIME_LINE ){
        return Object.assign({}, state, { timeLine: action.payload  });
    }
    return state;

};

export default threatExploreReducer;
