import React, { Component } from 'react';
import { render } from 'react-dom';
import { AgGridReact } from '@ag-grid-community/react';
import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';
import { RowGroupingModule } from '@ag-grid-enterprise/row-grouping';
import { MenuModule } from '@ag-grid-enterprise/menu';
import { ColumnsToolPanelModule } from '@ag-grid-enterprise/column-tool-panel';
import { SetFilterModule } from '@ag-grid-enterprise/set-filter';
import '@ag-grid-community/core/dist/styles/ag-grid.css';
import '@ag-grid-community/core/dist/styles/ag-theme-alpine.css';
import '@ag-grid-community/core/dist/styles/ag-theme-alpine-dark.css';
import SparkLine from '../BizCharts/SparkLine';

import sizeMe from 'react-sizeme'
import {  CardBody } from 'reactstrap';
import {addFilters} from '../../../redux/actions/dashboardActions'
import { connect } from 'react-redux';

import _ from 'lodash'
import {ChangeDetectionService} from "ag-grid-react/lib/changeDetectionService";

function getColor(options,value){

    var result = { value: 0,color:'' };

    if(options.thresholds){
        console.log(options)
        for(var i = 0; i < options.thresholds.length; i++){
            var currentValue = parseInt(options.thresholds[i]['from'+i]);
            if(currentValue > result.value && currentValue <= value){
                result.value = currentValue;
                result.color = options.thresholds[i].color.hex
            }

        }

        return result.color;

    }
}
var timer;
let clickCount
class TableChart extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      modules: [
        ClientSideRowModelModule,
        RowGroupingModule,
        MenuModule,
        ColumnsToolPanelModule,
        SetFilterModule,
      ],

      frameworkComponents: {
        sparkLine: SparkLine,
        numberParser:this.numberParser
      },
      defaultColDef: {
        flex: 1,
        minWidth: 100,
        filter: false,
        sortable: true,
        resizable: true,
        floatingFilter: true
      },
      autoGroupColumnDef: { minWidth: 200 },
      rowData: {tableheader:[],rowData:[]},
    };
  }

  prepareChartData = (data) =>{
    let tableHeaders = data[0].columns
    let tableData = data[0].rows;
    const finalTableHeaders = [];
    const rows = [];
    
    for(var i=0;i<tableHeaders.length;i++){
        if(tableHeaders[i].filterable){
            let fieldName = tableHeaders[i].text.replace("rule_data.event_data.","")

            finalTableHeaders.push(
                { field:fieldName.replace(".keyword",""),filter: 'agTextColumnFilter',valueParser:this.numberParser,cellStyle : this.cellStyle}
            );
        }

    }
      for(var i=0;i<tableHeaders.length;i++){
          if(!tableHeaders[i].filterable){
              let fieldName = tableHeaders[i].text.replace("rule_data.event_data.","")
              finalTableHeaders.push(
                  { field:fieldName.replace(".keyword",""),filter: 'agTextColumnFilter',valueParser:this.numberParser,cellStyle : this.cellStyle}
              );
          }

      }

    



    for(var i=0;i<tableData.length;i++){
        let object = new Object();
        for(var j=0;j<tableData[i].length;j++){
            let rowKey = finalTableHeaders[j]['field'];
            if(tableData[i][j] != ""){
              if(!isNaN(tableData[i][j]) && rowKey != '@timestamp'){
             
                object[rowKey] = parseInt(tableData[i][j]);
              }else if(rowKey === '@timestamp'){
                object[rowKey] = new Date(parseInt(tableData[i][j]));
               
              }else{
                object[rowKey] = tableData[i][j];
              }
             
            }
            object['sparkLine'] = [10,202,10,10,101,1,1,1];
            rows.push(object);
            }
         
        
      }
      let result = new Object();
      result['tableheader'] = finalTableHeaders;
      result['rows'] = rows;
      return result;
  }

     applyFiltersForSingleWidget = (event) =>{


        clickCount += 1;

        setTimeout(() => {
            if (clickCount === 1) this.onSingleClick(event);
            else if (clickCount === 2) this.onDoubleClick(event);

            clickCount = 0;
        }, 300);





    }

      onSingleClick = (e) => {

          const {dispatch} = this.props;

          const fieldValue = e.data[e.colDef.field];
          const fieldName = e.colDef.field;

          let fitlerData = {key:fieldName+".keyword",operator:"=",value:fieldValue};
          dispatch(addFilters(fitlerData))
    }
      onDoubleClick = (e) => {

        if(e.data){

            const currentFilters = [];


            const fieldValue = e.data[e.colDef.field];
            const fieldName = e.colDef.field;

            let fitlerData = {key:fieldName+".keyword",operator:"=",value:fieldValue};
            currentFilters.push(fitlerData)

            if(this.props.dashboardLink && this.props.dashboardLink!=''){
                const url = `${window.location.origin.toString()}/dashboards/`+this.props.dashboardLink+"?query="+JSON.stringify(currentFilters)+"&startDate="+this.props.date.startDate+"&endDate="+this.props.date.endDate;
                window.open(url,"_blank");
            }

        }


    }

  onCellClicked = (e) =>{
    const {dispatch} = this.props;

    const fieldValue = e.data[e.colDef.field];
    const fieldName = e.colDef.field;

    let fitlerData = {key:fieldName,operator:"=",value:fieldValue};
    dispatch(addFilters(fitlerData))

  }

  onGridReady = (params) => {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
 
  };
  componentDidUpdate(prevProps, prevState, snapshot) {
      if( this.gridApi){
          this.gridApi.refreshCells({force: true});
      }

  }

    cellStyle = (params) =>{

        if(!isNaN(params.value)){

            const color = getColor(this.props.chartConfig,params.value)
            return  { backgroundColor: color }
        }


    }



    numberParser (params) {
        var newValue = params.newValue;

        var valueAsNumber;
        if (newValue === null || newValue === undefined || newValue === '') {
            valueAsNumber = null;
        } else {
            valueAsNumber = parseFloat(params.newValue);
        }
        return valueAsNumber;
    }

  render() {
    const { width, height } = this.props.size;
    const finalHeight = height- 50;
    const finalWidth = width- 50;
    let chartData = this.props.data;
    let config = this.props.chartConfig;
    let rowData = new Object();
    if(chartData.length > 0  && chartData[0].rows){
        rowData = this.prepareChartData(chartData);

    }else{
      let finalRows = [];
      let tableHeaders = [];
        for(let i=0;i<chartData.length;i++){
            let fieldNames = [];
            let fieldProps =  chartData[i].props;
            for (let j = 0; j < Object.keys(fieldProps).length; j++) {
              if(Object.keys(fieldProps)[j] != 'field'){
                let rowData = new Object();
                rowData['fieldName'] = Object.keys(fieldProps)[j].replace(".keyword","");
                rowData['fieldValue'] = fieldProps[Object.keys(fieldProps)[j]];
                fieldNames.push(rowData);
            }
         } 
        let rowObject = new Object();
        for(let k =0;k<fieldNames.length;k++){
          rowObject[fieldNames[k].fieldName] = fieldNames[k].fieldValue;
        }
        var sparkLinesData = [];
        let count = 0;
        for(let j=0;j<chartData[i].datapoints.length;j++){
            sparkLinesData.push(chartData[i].datapoints[j][0]);
            count+= chartData[i].datapoints[j][0];
        }
        rowObject['sparkLine'] = sparkLinesData;
        rowObject[chartData[i].metric] = count;
        finalRows.push(rowObject)

      
    }
    

    var grouped = _.groupBy(finalRows, function(row) {
      let tempObjects = [];
      let keys = Object.keys(row);
      for(var i=0;i<keys.length;i++){
          if(keys[i]!='sum' && keys[i]!='count' && keys[i]!='avg' &&  keys[i]!='min' &&  keys[i]!='max' && keys[i]!='sparkLine' && keys[i].indexOf('.') ==-1 ){ 
            tempObjects.push(row[keys[i]]);
          }
      }

      return tempObjects.join("|");
    });
    
    console.log(grouped);

    let keys = Object.keys(grouped);
     finalRows = [];
    for(var i=0;i<keys.length;i++){
        let subObject = grouped[keys[i]];
        let finalObject = new Object();
        let subObjectkeys = Object.keys(subObject[0]);
        for(let j=0;j<subObjectkeys.length;j++){
          if(subObjectkeys[j]!='sum' && subObjectkeys[j]!='count' && subObjectkeys[j]!='avg' &&  subObjectkeys[j]!='min' &&  subObjectkeys[j]!='max' && keys[j]!='sparkLine' && subObjectkeys[j].indexOf('.') ==-1 ){ 
            finalObject[subObjectkeys[j]] = subObject[0][subObjectkeys[j]];
          }  
        }

        for(let j=0;j<subObject.length;j++){
            let aggKeys = Object.keys(subObject[j]);
         for(let k=0;k<aggKeys.length;k++){
          if(aggKeys[k]==='sum' || aggKeys[k]==='count' || aggKeys[k]==='avg' ||  aggKeys[k]==='min' ||  aggKeys[k]==='max' || aggKeys[k]==='sparkLine' || aggKeys[k].indexOf('.') !=-1 ){ 
            finalObject[aggKeys[k]] = subObject[j][aggKeys[k]];
          }
         }
        }
        
         finalRows.push(finalObject); 

    }



    if(finalRows.length>0){
      let singleRow = finalRows[0];
      
      for (let [key, value] of Object.entries(singleRow)) {
        if(key === 'sparkLine'){
          tableHeaders .push({ field:key.replace(".keyword",""),filter: 'agTextColumnFilter',cellRenderer: 'sparkLine'});
        }else{
          tableHeaders .push({ field:key.replace(".keyword",""),filter: 'agTextColumnFilter',valueParser:this.numberParser,cellStyle : this.cellStyle});
        }
        
      } 
     
  }

   // tableHeaders.push( { field:'sparkLine',filter: false, cellRenderer: 'sparkLine'})

    rowData['tableheader'] = tableHeaders;
    rowData['rows'] = finalRows;
    
    
  }

    
   let themeName = "ag-theme-alpine";
    if(this.props.themeName === 'theme-dark'){
      themeName = "ag-theme-alpine-dark";
    }
    return (
      <CardBody style = {{height:'100%'}}>
        <div
          id="myGrid"
          style={{
            height:finalHeight ,
            width: finalWidth,
          }}
          className={themeName}
        >
         {rowData.tableheader && <AgGridReact
             disableStaticMarkup={true}
            modules={this.state.modules}
            columnDefs={rowData.tableheader}
            defaultColDef={this.state.defaultColDef}
            autoGroupColumnDef={this.state.autoGroupColumnDef}
            enableRangeSelection={true}
            frameworkComponents={this.state.frameworkComponents}
            animateRows={true}
            onGridReady={this.onGridReady}
            rowData={rowData.rows}
            onCellClicked={this.applyFiltersForSingleWidget.bind(this)}

             rowDataChangeDetectionStrategy={ChangeDetectionService.NoCheck}
            columnDefsChangeDetectionStrategy={ChangeDetectionService.NoCheck}

          /> }
          </div>
        </CardBody>
    );
  }
}

export default connect(state => ({ themeName: state.theme.className }))(sizeMe({ monitorHeight: true })(TableChart));
