function processTableData (esResponse)  {
    const csvContent =  constructCsvData(esResponse);
    console.log(csvContent)
    const finalResponse = consvertCsvDataToRows(csvContent);
    const fields = findMetricFields(esResponse);
    const noMetricFields = findNonMetricFields(esResponse);
    const dv = new DataSet.DataView();
      dv.source(finalResponse).transform({
        type: 'fold',
        fields: fields,
         key: 'opinion',
         value: 'value',
        retains: noMetricFields
      });

    return dv.rows;
  }

  function findMetricFields  (esResponse) {
        
    let cols = esResponse[0].columns;
    let metricCols = [];
    for(let i=0;i<cols.length;i++){
      let singleMetric = cols[i];
      if(singleMetric.text.toLowerCase().includes('count') ||
      singleMetric.text.toLowerCase().includes('sum') ||
      singleMetric.text.toLowerCase().includes('min') || 
      singleMetric.text.toLowerCase().includes('max') ||
      singleMetric.text.toLowerCase().includes('average') || 
      singleMetric.text.toLowerCase().includes('.0')){
        metricCols.push(singleMetric.text);
      }
    }

  return metricCols;
  }
  function findNonMetricFields  (esResponse) {

    let cols = esResponse[0].columns;
    let metricCols = [];
    for(let i=0;i<cols.length;i++){
      let singleMetric = cols[i];
      if(!singleMetric.text.toLowerCase().includes('count') &&
      !singleMetric.text.toLowerCase().includes('sum') &&
      !singleMetric.text.toLowerCase().includes('min') && 
      !singleMetric.text.toLowerCase().includes('max') &&
      !singleMetric.text.toLowerCase().includes('average') && 
      !singleMetric.text.toLowerCase().includes('.0')){
        metricCols.push(singleMetric.text);
      }
    }
        
   

   return metricCols;

  
}

const  csvJSON = (csv) =>{

  var lines=csv.split("\n");

  var result = [];

  var headers=lines[0].split("|");

  for(var i=1;i<lines.length;i++){

	  var obj = {};
	  var currentline=lines[i].split("|");

	  for(var j=0;j<headers.length;j++){
      if(currentline[j]){
        obj[headers[j]] = currentline[j];
      }
		
	  }
    if(Object.keys.length!=0){
      result.push(obj);
    }
	  

  }
  
  //return result; //JavaScript object
  return result; //JSON
}


  function consvertCsvDataToRows  (csvContent) {
    const ds = new DataSet();
    
      var finalObjects = [];
      var finalCsvdata = csvJSON(csvContent);
     for(var i=0;i<finalCsvdata.length;i++){
           var object = new Object();
          Object.keys(finalCsvdata[i]).forEach(function(key) {
            if(!isNaN(finalCsvdata[i][key])){
                  object[key] = parseInt(finalCsvdata[i][key]);
            }else{
                  object[key] = finalCsvdata[i][key];
          }
      });
      if(Object.keys(object).length!=0){
        finalObjects.push(object);
      }
      
  }
  return finalObjects;
 

}

  function constructCsvData  (data) {
    let tableHeaders = data[0].columns
    let tableData = data[0].rows;
    let csvContent = "";
    var tempData = [];
    for(var i=0;i<tableHeaders.length;i++){
      tempData.push(tableHeaders[i].text);
    }
    csvContent+=tempData.join('|')+"\n"
    for(var i=0;i<tableData.length;i++){
      var tempData1 = [];
      for(var j=0;j<tableData[i].length;j++){
        if(tableData[i][j] != ""){
          if(!isNaN(tableData[i][j])){
            tempData1.push(parseInt(tableData[i][j]));
          }else{
            tempData1.push(tableData[i][j]);
          }
        }
        
       
      }
      if(tempData1.length!=0){
        csvContent+=tempData1.join('|')+"\n"
      }
    }
    
    return csvContent;

  }

function processTimeSeriesData (data){
  let finalArray = [];
  for(var i=0;i<data.length;i++){
      let dataPoints = data[i].datapoints;
      dataPoints.map(function(singlePoint){
        let object = new Object();
        object['label'] = singlePoint[1];
        object['value'] = singlePoint[0];
        object['metric'] = data[i].target.replace(".keyword","")
        var propsKeys = Object.keys(data[i].props);
        for(var j=0;j<propsKeys.length;j++){
          if(propsKeys[j]!='field'){
            object['field_name'] = propsKeys[j];
            object['field_value'] = data[i].props[propsKeys[j]];
          }
        }
        finalArray.push(object);
      });
     
  }
  return finalArray;
}


function normalizeData  (dvData) {
    let tempArray = [];
    for(var i=0;i<dvData.length;i++){
      let object = new Object();
      let cols = [];
      let fields = [];
      let counter = 0;
      Object.keys(dvData[i]).map(function(key){
        
          if(key!='opinion' && key!='value'){
            cols.push(dvData[i][key]);
            if(key.indexOf("Count")==-1 ){
              fields.push(key);
            }
          }
        
        counter++;
        
      });
      let fieldName = fields.join("|");
      
      const tempCols = [];
      for(var k=1;k<cols.length;k++){
        tempCols.push(cols[k]);
      }
      object['label'] = cols[0];
      object['value'] = dvData[i].value;
      if(tempCols.length==0){
        object['metric'] =  dvData[i].opinion.replace(".keyword","");
      }else{
        object['metric'] =  tempCols.join(',')+"("+dvData[i].opinion.replace(".keyword","")+")";
      }
      
      object['field_name'] = fieldName;
      object['field_value'] = cols.join("|");
      tempArray.push(object);
    }
    return tempArray;

  }

function formatTime(value,timeFormat){
  return moment(parseInt(value)).format(timeFormat); 
}

function getComponent(data,config,width,height,dispatch,timeFromat,dashboardLink,date) {
 

  let finalData = [];

  
  

  const barChartConfig = {
    data: finalData,
    isStack: true,
    xField: 'value',
    height:'800px',
    yField: 'label',
    animate:true,
    seriesField: 'metric',
    barWidthRatio: 0.5,
    interactions: [{ type: 'element-highlight-by-color' }, { type: 'element-link' }],
    theme:widgetHelper.themeOptions.theme
  };

  if(data.length > 0  && !data[0].rows){
     finalData = processTimeSeriesData(data);
     barChartConfig.data = finalData
     barChartConfig['yAxis']={
       
        label : {
          formatter:function formatter(v) { 
              return moment(parseInt(v)).format('DD/MM HH:mm')
          },
          
        },

        tickMethod:'time-cat',
        tickCount:12
     }
     barChartConfig['tooltip'] = {
      formatter:function formatter(v) { 
            if(v.value===null){
              return { title: moment(parseInt(v.label)).format('DD/MM HH:mm'), name: v.metric,value:0};
            }else{
              return { title: moment(parseInt(v.label)).format('DD/MM HH:mm'), name: v.metric,value:v.value};
            }
               
      }
     }
     //barChartConfig['tickMethod'] = 'time'
     //barChartConfig['tickCount'] =10;
     
  }else if(data.length > 0  && data[0].rows){
    let tableData = processTableData(data);
    
        
    finalData = normalizeData(tableData);
    barChartConfig.data = finalData;
   
  }
  

  

  if(finalData.length > 0 && config.stackType && config.stackType === 'percent'){
    barChartConfig.isStack = true;
    barChartConfig.isPercent = true;
    
       const currentData = finalData;
       const  dv  = new DataSet.DataView().source(currentData);
        dv.transform({
                type: 'percent',
                field: 'value',
                dimension: 'metric',
                groupBy: ['label'],
                as: 'percent',
        });
        finalData = [];
        finalData = dv.rows;
       
       
    }else if(config.stackType && config.stackType === 'group'){
      barChartConfig.isGroup = true;
      barChartConfig['groupField'] = "metric";
    }

    

  var timer;
let clickCount
  const applyFiltersForSingleWidget = (event) =>{


      clickCount += 1;

      setTimeout(() => {
          if (clickCount === 1) onSingleClick(event);
          else if (clickCount === 2) onDoubleClick(event);

          clickCount = 0;
      }, 300);





  }


    const  onSingleClick = (event) => {

        if(event.data){
            let fieldName = event.data.data.field_name.split("|");
            for(var i=0;i<fieldName.length;i++){
                let fitlerData = {key:fieldName[i],operator:"=",value:event.data.data.field_value.split("|")[i]};
                dispatch(addFilters(fitlerData))
            }

        }
    }
    const  onDoubleClick = (event) => {

        if(event.data){
            let fieldName = event.data.data.field_name.split("|");
            const currentFilters = [];
            for(var i=0;i<fieldName.length;i++){
                let fitlerData = {key:fieldName[i],operator:"=",value:event.data.data.field_value.split("|")[i]};
                currentFilters.push(fitlerData);
                //dispatch(addFilters(fitlerData))
            }

            if(dashboardLink && dashboardLink!=''){
                const url = `${window.location.origin.toString()}/dashboards/`+dashboardLink+"?query="+JSON.stringify(currentFilters)+"&startDate="+date.startDate+"&endDate="+date.endDate;
                window.open(url,"_blank");
            }

        }


    }


  class RenderChart extends React.Component {

    componentDidMount() {
      
     
    }
    registerEvent  =  ( chart ,  type )  =>  { 
      chart.on ('plot:click' ,  ev  =>  { 
          applyFiltersForSingleWidget(ev)
      }) ; 
    } ;

    render() {
        const {theme} = this.props;
        if(theme === 'theme-dark'){
          barChartConfig['legend'] = {
            itemName:{
              style:{
                fill:'white'
              }
            }
          }
          if(!barChartConfig.yAxis){
            barChartConfig['yAxis']={
              label: {
                style:{fill:'white'}
              },
            }
            barChartConfig.yAxis['grid']  ={ 
              line: {
                style: {
                  stroke: '#38373f',
                  lineWidth: 2,
                  cursor: 'pointer'
                }
              }
            }
          }
          if(!barChartConfig.xAxis){
            barChartConfig['xAxis']={
              label: {
                style:{fill:'white'}
              },
            }
            barChartConfig.xAxis['grid']  ={ 
              line: {
                style: {
                  stroke: '#38373f',
                  lineWidth: 2,
                  cursor: 'pointer'
                }
              }
            }
          }
          if(barChartConfig.yAxis){
            barChartConfig.yAxis.label['style'] = {fill:'white'};
            barChartConfig.yAxis['grid']  ={ 
              line: {
                style: {
                  stroke: '#38373f',
                  lineWidth: 2,
                  cursor: 'pointer'
                }
              }
            }
          }
          if(barChartConfig.xAxis){
            barChartConfig.xAxis.label['style'] = {fill:'white'};
            barChartConfig.xAxis['grid']  ={ 
              line: {
                style: {
                  stroke: '#38373f',
                  lineWidth: 2,
                  cursor: 'pointer'
                }
              }
            }
          }
          barChartConfig.theme = widgetHelper.darkThemOptions.theme
        }
      return (

          <CardBody style = {{height:'100%'}}>
         

          <Bar {...barChartConfig} onReady={colChart => this.registerEvent(colChart)} />
            

                 
            
          
     </CardBody>    
        
      
      
     
      );
    }
  }
  return RenderChart;
}


class BarChart extends React.Component {
  render() {
    const { dispatch,timeFromat,dashboardLink,date,themeName} = this.props
      const { width, height } = this.props.size;
      console.log(themeName);
      const finalWidth = width - 30;
      const finalHeight = height - 50;
    const RenderChart = getComponent(this.props.data,this.props.chartConfig,finalWidth,finalHeight,dispatch,timeFromat,dashboardLink,date);
    return (
      
       <RenderChart theme = {themeName} />
     
    );
  }
}

function getTickCount(width){
   return 20;
}
export default connect(state => ({ themeName: state.theme.className }))(sizeMe({ monitorHeight: true })(BarChart));


