
import React, {useEffect, useState} from 'react'
import {
    CardBody,
    Modal,
    ModalBody,
    ModalHeader,
    ModalFooter,
    Button,
    ListGroup,
    ListGroupItem,
    Col,
    Input, Row
} from "reactstrap";

import {useDispatch, useSelector} from "react-redux";
import {saveFilters,openSavedSearch} from '../../../redux/actions/historicalActions'
import histroicalService from "../../../api/histroicalService";
import {List} from 'react-virtualized';
import Emitter from "../../../events/Events";


const SaveFilter = (props) =>{
    const [rows,setRows] = useState([]);
    const [filter,setFilter] = useState("");
    const [filterFormData,setFilterFormData] = useState({id:0,filterName:''});
    const {viewType} = props;


    const [savedSearches,setSavedSearches] = useState([])
    const dispatch = useDispatch();
    const onChange = (event)=>{
        setFilter(event.target.value);
    }

    const setFormData = (data) =>{
        setFilterFormData(data);
        setFilter(data.queryName);
    }

    const filterData = (event) =>{

        if(event.target.value!=''){
            let searchString = event.target.value.toLowerCase();
            const filtered = rows.filter(entry => Object.values(entry).some(val => typeof val === "string" && val.toLowerCase().includes(searchString)));
            setRows(filtered);
        }else{
            setRows(savedSearches);
        }



        //console.log(event.target.value);
    }

    useEffect(() => {
        fetchSavedSearches();


    },[props.isSidePanelOpen]);

    const fetchSavedSearches = () =>{
        histroicalService.getSavedSearches().then((responseData) =>{

            setSavedSearches(prevSelected => {
                // props.handle_card_selected(props.id, !prevSelected)
                return responseData.data
            })
            setRows(prevSelected => {
                // props.handle_card_selected(props.id, !prevSelected)
                return responseData.data
            })



        })
    }

    const saveFiltersToDatabase = () =>{
        let filterData = {filterName:filter}
        if(filterFormData && filterFormData.id){
            filterData['id'] = filterFormData.id;
        }
        props.close();
        Emitter.emit('SAVE_FILETRS',filterData)

        //dispatch(saveFilters(filterData));
        //fetchSavedSearches()
        //

    }

    const saveAsNewFiltersToDatabase = () =>{
        let filterData = {filterName:filter}
        filterData['id'] = 0;
        props.close();
        Emitter.emit('SAVE_FILETRS',filterData)
        //fetchSavedSearches()
       
    }

    return( <Modal  style = {{maxWidth:800}} size = "lg"  isOpen={props.isSidePanelOpen} >

             <ModalHeader>Save Filters</ModalHeader>

            <ModalBody>
                <form className="form form--horizontal" >
                    {viewType && viewType=== 'save' && <div className="form__form-group">

                        <div className="form__form-group-field">


                            <input type = "text" value={filter} onChange={onChange} placeholder="Enter Filter Name"/>
                        </div>
                    </div>}

                    {viewType && viewType=== 'open' &&
                        <RenderSaveSearches setFormData = {setFormData} close = {props.close} filterData = {filterData} rows = {rows}/>
                    }

                </form>
            </ModalBody>

            <ModalFooter>
                {viewType && viewType=== 'save' &&
                    <Button disabled={filter===''} color="primary" onClick={saveFiltersToDatabase}>Save</Button>
                }
                {viewType && viewType=== 'save' && filterFormData.id!=0 &&
                     <Button color="primary" onClick={saveAsNewFiltersToDatabase}>Save As New</Button>
                }
                <Button color="default" onClick={props.close}>Close</Button>
            </ModalFooter>
    </Modal>)

}

const RenderSaveSearches = (props) =>{
    const {rows,close,setFormData} = props;
    const dispatch = useDispatch();
    const openSavedSearchFromIndex = function(search){
        dispatch(openSavedSearch(search))
        close();
        setFormData(search);
    }
    function rowRenderer({ key, index,  isScrolling, isVisible, style, }) {
        return (
            <ListGroup key={key} style={style}>
                <ListGroupItem  >
                  <span className = "widgetTitle">

                      <span onClick={() => openSavedSearchFromIndex(rows[index])} className = "widgetIconTitle " > {rows[index].queryName}</span>

                    </span>


                </ListGroupItem>
            </ListGroup>
        );
    }

    return (

        <React.Fragment>

            <Row style = {{width:'100%'}}>

                <Col md= {12} lg = {12} sm = {12}>
                    <Input type="text" placeholder = "Search...." name="search" onChange = {props.filterData}  />
                </Col>

            </Row>

            <List width={700} height={300}
                  rowCount={rows.length}
                  rowHeight={50}
                  rowRenderer={rowRenderer}/>

        </React.Fragment>


    )

}

export default SaveFilter;

