import axiosInstance from '../middleware/Interceptor'




function searchData (data) {
    return axiosInstance.post('http://192.168.1.57:8999/user/historical/',data)

}
function saveFilters (data){
    return axiosInstance.post('http://192.168.1.57:8999/user/historical/save_search',data)
}

function getSavedSearches(){
    return axiosInstance.get("http://192.168.1.57:8999/user/historical/get-save-searches")
}

function getThreatHuntingQuries(data){
    return axiosInstance.post("http://192.168.1.57:8999/user/historical/get-threat-hunting-quries",data)
}
function saveThreatHuntingQuries(data){
    return axiosInstance.post("http://192.168.1.57:8999/user/historical/save-hunting-quries",data)
}

function getThreatHuntingSaveQuries(){
    return axiosInstance.get("http://192.168.1.57:8999/user/historical/get-threat-hunting-quries")
}







const historicalService = {
    searchData,
    saveFilters,
    getSavedSearches,
    getThreatHuntingQuries,
    saveThreatHuntingQuries,
    getThreatHuntingSaveQuries

}

export default historicalService