import React from 'react';
import { useLocation} from "react-router-dom";
import {Row,Col } from 'reactstrap';
import {List} from 'react-virtualized';
import Moment from 'react-moment';
import { useSelector,useDispatch } from 'react-redux';
import {openAlert} from '../../../redux/actions/exploreActions' 

const AlertView = (props) =>{





   //const  {alertData,highlightNodes} = props;


   const alertData = useSelector((state) => state.exploreReducer.alerts);



    if(alertData.type === 'case' &&  alertData.alertEvidences){
        const rows = alertData.alertEvidences;
        return (

            <div className="cases-content__start">
             <EntityHeader type ={alertData.type}  alertData = {alertData}/>
            <div style = {{paddingTop:'12px'}} className="left-sidebar__field">
                   <div className = "case-queue-list">
                    
                     <AlertList rows = {rows} />

                   </div>
            </div>




    </div>

            
        );
    }else if(alertData.type === 'investigation' &&  alertData.alertData){

        console.log(alertData)

        const rows = alertData.alertData;
        return (

            <div className="cases-content__start">
                <EntityHeader type ={alertData.type} alertData = {alertData}/>

                <div className="left-sidebar__field">
                    <div className = "case-queue-list">

                        <AlertList type = {alertData.type}  rows = {rows} highlightNodes = {highlightNodes}/>

                    </div>
                </div>




            </div>




        );
    }else{
        return (<div>No Data</div>)
    }
    
}

function useQuery() {
    return new URLSearchParams(useLocation().search);
  }

const AlertList = (props) =>{
    const {rows,type} = props;
    
     const dispatch = useDispatch();
    
    const highlightNodes = (data)=>{
        let payloayd = {alertDetails:data,caseDetails:rows};
        dispatch(openAlert(payloayd));
    }


    function rowRenderer({ key, index,  isScrolling, isVisible, style, }) {
        return (

            <div onClick={ () => highlightNodes(rows[index]) }  className = "case-card" key={key} style={style}>

                        <div className={`status--${rows[index].alertPriority.toLowerCase()}`}>
                              <div className = "case-card__left">
                                    <div className = "case-card__left--top">
                                        <div class="case-card__name" tnautomationid="caseCardName" >{rows[index].alertName} </div>
                                           <div class="case-card__time-id font-regular-secondary">
                                           <span class="ng-star-inserted" style={{fontWeight: "bold"}}>Modified </span>
                                               <span tnautomationid="caseCardTime" class="ng-star-inserted"><Moment format="DD/MM/YYYY HH:mm:ss">{rows[index].createdDate}</Moment></span>

                                        </div>
                                    </div>

                                    </div>

                                 
                           </div>
                         
                    </div>

            
        );
      }

    function rowInvestigationRenderer({ key, index,  isScrolling, isVisible, style, }) {
        return (

            <div onClick={ () => highlightNodes(rows[index]) }  className = "case-card" key={key} style={style}>

                <div className={`status--${rows[index].priority.toLowerCase()}`}>

                    <div className = "case-card__left">
                        <div className = "case-card__left--top">
                            <div class="case-card__name" tnautomationid="caseCardName" >{rows[index].name} </div>
                            <div class="case-card__time-id font-regular-secondary">
                                <span class="ng-star-inserted" style={{fontWeight: "bold"}}>Modified </span>
                                <span tnautomationid="caseCardTime" class="ng-star-inserted"><Moment format="DD/MM/YYYY HH:mm:ss">{rows[index].createdDate}</Moment></span>

                            </div>
                        </div>

                    </div>


                </div>

            </div>


        );
    }

      if(type === 'investigation'){
          return ( <List  height={700} width = {250}
                          rowCount={rows.length}
                          rowHeight={70}
                          rowRenderer={rowInvestigationRenderer}/>

          )
      }else{
          return ( <List  height={700} width = {250}
                          rowCount={rows.length}
                          rowHeight={70}
                          rowRenderer={rowRenderer}/>

          )
      }


}

const EntityHeader = (props) =>{
   
    const {alertData,type} = props
    if(type === 'case'){
        return (<div className="case-investigation-header">
            <div className="case__status case-card__status 100">
                <div className="title-container">
                    <div className="case-investigation-header--title">
                        {alertData.title}
                    </div>
                    <div className="case-investigation-header--details font-regular-primary d-flex-align--center">
                        <Row>

                            <Col>
                        <span style={{marginLeft:"12px"}} className="">
                            <b>{alertData.status}</b>
                          </span>
                            </Col>
                            <Col>
                        <span className="">
                         <b>{alertData.priority}</b>
                          </span>
                            </Col>

                        </Row>
                    </div>

                </div>
            </div>
        </div>)
    }else{
        return (<div className="case-investigation-header">
            <div className="case__status case-card__status 100">
                <div className="title-container">
                    <div className="case-investigation-header--title">
                        {alertData.workBenchName}
                    </div>
                    <div className="case-investigation-header--details font-regular-primary d-flex-align--center">
                        <Row>
                            <Col>
                        <span style={{marginLeft:"12px"}} className="">
                            ID : {alertData.id}
                        </span>
                            </Col>
                            <Col>
                        <span className="">
                            <b>{alertData.status}</b>
                          </span>
                            </Col>
                            <Col>
                        <span className="">
                         <b>{alertData.priority}</b>
                          </span>
                            </Col>

                        </Row>
                    </div>

                </div>
            </div>
        </div>)
    }




}

export default AlertView;
