
import PanelModel from './PanelModel'
import _ from "lodash";


export default   class DashboardModel {

    id:any;
    uid:string;
    editable:any;
    time:any;
    refresh:any;
    panels:PanelMoel[]
    panelInEdit: PanelModel;
    panelInView: PanelModel;
    description:any;
    title:any;
    showCreatePanel:any;
    isEditDashboardOpen:any;
    category:any;
    permissions:any;
    isAdmin:any;
    isOwner:any;
    scheduleName:any;
    emailAddress:any;
    frequency:any;
    timeRange:any;
    frequencyType:any;
    hourOfDay:any;
    hourformatOfDay:any;
    isReportEnabled:any;
    reportId:any;
    dashboardCategories:any;
    reportCategories:any;
    users:any;
    isReport:any;
    scheduleName:any;
    logFields:any;
    isWidgetOpen:any;
    constructor(data){
        this.id = data.id;
        this.uid = data.uid;
        this.title = data.title;
        this.description = data.description
        this.time = data.time;
        this.refresh = data.refresh;
        this.panels =  data.panels;
        this.currentPanel = {};
        this.isSidePanelOpen = data.isSidePanelOpen;
        this.showCreatePanel = false;
        this.isEditDashboardOpen = false;
        this.category = data.category;
        this.permissions = data.permissions;
        this.isAdmin = data.isAdmin;
        this.isOwner = data.isOwner;
        this.scheduleName = data.scheduleName;
        this.emailAddress = data.emailAddress;
        this.frequency = data.frequency;
        this.timeRange = data.timeRange;
        this.frequencyType = data.frequencyType;
        this.hourOfDay = data.hourOfDay;
        this.format = data.format;
        this.isReportEnabled = data.isReportEnabled
        this.reportId=data.reportId
        this.filters = data.filters
        this.newDashboard = data.newDashboard;
        this.dashboardCategories = data.dashboardCategories;
        this.reportCategories= data.reportCategories;
        this.users = data.users;
        this.isReport = false;
        this.logFields = [];
        this.isWidgetOpen=false;

    }
    updateLogFields(logFields){
      this.logFields = logFields;
    }
    updateNonSelectValue(event){
      
      if(event.target &&  event.target.name === 'title'){
        this.title = event.target.value;
      }
      if(event.target &&  event.target.name === 'description'){
        this.description = event.target.value;
      }
      
     
    }

    updateReport(event,name){
      if(name === 'reportTitle'){
        this.scheduleName = event.target.value;
      }
      if(name === 'emailAddress'){
        this.emailAddress = event.target.value;
      }
      if(name === 'frequency'){
        this.frequency = event.value;
      }
      if(name === 'frequencyType'){
        this.frequencyType = event.value;
      }
      if(name === 'hourOfDay'){
        this.hourOfDay = event.value;
      }
      if(name === 'format'){
        this.format = event.target.value;
      }
    
    }

    updateEnableReport(value){
      this.isReport = value
    }

    updateDashboardCategories(categories){
      this.dashboardCategories = categories;
    }
    updateReportCategories(categories){
      this.reportCategories = categories;
    }

    updateUsers(users){
      this.users = users;
    }

    addPanels(panel){
        this.panels = panel;
        //this.panels.push(panel);
    }

    updatePermissions(index,event,name){
      this.permissions[index][name] = event
    }

    updateCategory(event){
      this.category = event.value
    }
    addField (){
      this.permissions.push({});
    }
    removeField(index){
      this.permissions.splice(index,1);
    }

    initViewPanel(panel: PanelModel) {
        this.panelInView = panel;
        panel.setIsViewing(true);
      }
    
    exitViewPanel(panel: PanelModel) {
        this.panelInView = undefined;
        panel.setIsViewing(false);
    }

    getNextPanelId() {
        let max = 0;
    
        for (const panel of this.panels) {
          if (panel.id > max) {
            max = panel.id;
          }
    
          if (panel.collapsed) {
            for (const rowPanel of panel.panels) {
              if (rowPanel.id > max) {
                max = rowPanel.id;
              }
            }
          }
        }
    
        return max + 1;
      }

      getPanelById(id: number): PanelModel | null {
        if (this.panelInEdit && this.panelInEdit.id === id) {
          return this.panelInEdit;
        }
    
        for (const panel of this.panels) {
          if (panel.id === id) {
            return panel;
          }
        }
    
        return null;
      }

      getPannels(){
          return this.panels;
      }

      addPanel(panelData: any) {
        panelData.id = this.getNextPanelId();
    
        const panel = new PanelModel(panelData);
    
        this.panels.unshift(panel);
    
        this.sortPanelsByGridPos();
        this.currentPanel = panel;
       
      }
      getCurrentPanel(){
        return this.currentPanel;
      }
      sortPanelsByGridPos() {
        this.panels.sort((panelA, panelB) => {
          if (panelA.gridOptions.y === panelB.gridOptions.y) {
            return panelA.gridOptions.x - panelB.gridOptions.x;
          } else {
            return panelA.gridOptions.y - panelB.gridOptions.y;
          }
        });
      }


}