
import {
    GET_INTEGRATIONS,
    PUT_INTEGRATIONS,
    SAVE_INTEGRATIONS,
    COMPLETE_TASK,
    SAVE_COMPLETED

} from '../actions/integrationsActions';
const initalState = {
    data:[],
    loading:false,
    completeTask:{},
    
}

const integrationsReducer = (state = initalState, action) => {

    if(action.type === GET_INTEGRATIONS){
        return { ...state, loading: true };
    }
    if(action.type === PUT_INTEGRATIONS){
        return { ...state,
            data:action.json, 
            loading: false };
    }
    if(action.type === SAVE_INTEGRATIONS){
        return { 
            ...state, 
            loading: true,
            completeTask:{data:'Waiting'}
        };
    }
    if(action.type === COMPLETE_TASK){
        return { 
            ...state, 
            loading: true,
            completeTask:{data:action.json} 
        };
    }
    if(action.type === SAVE_COMPLETED){
        return { 
            ...state, 
            loading: true,
            completeTask:{data:''} 
        };
    }

    return state;
}   

export  default integrationsReducer;