import React, {Component} from 'react'
import {Container} from 'reactstrap';
import {RuleHeader,RuleBody} from '../RuleEditor/components/RuleHeader'
import {connect} from 'react-redux';
import ruleService from "../../api/ruleService";
import {viewRules} from '../../redux/actions/ruleActions'
class RuleEditor extends  Component{

    constructor(props) {
        super(props);
        this.state = {showViewPage:true,showNewPage:false};
    }
    componentDidMount() {
        this.fetchAllEvents();
    }



    fetchAllEvents = () =>{
        const {dispatch} = this.props;
        const rules = [];
        ruleService.getAllEvents().then((responseData)=>{
            for(let i=0;i<responseData.data.custom.length;i++){
                if(responseData.data.custom[i].eventName!=null){
                    rules.push(responseData.data.custom[i])
                }
            }
            dispatch(viewRules(rules));
        })
    }

    render() {
        return (<Container>
            <div style={{paddingTop:'80px'}}>
                <div className="panel card">
                    <div className="panel__body card-body">
                        <RuleHeader/>
                        <RuleBody/>
                    </div>
                </div>
            </div>
        </Container>)
   }
}

export default connect()(RuleEditor);

