import React from 'react';
import {Col, Container, Row} from "reactstrap";
import {BrandHeader} from '../Analytics/Header'
import ChartContainer from '../Analytics/ChartContainer'
import Mentions from "./Mentions";
import {TopInFluencers} from './Mentions'
import brandMointoring from "../../../api/brandMointoringService";
class Analytics extends  React.Component{

    constructor() {
        super();
        this.state = {
            date:{startDate:1609521040000,endDate:1610077167000},
            searchString:'*',
            category:'all',
            keywords:[],
        }
        this.changeDateSettings = this.changeDateSettings.bind(this);
        this.applySearchFilter = this.applySearchFilter.bind(this);
        this.selectCategory = this.selectCategory.bind(this);

    }
    changeDateSettings = function (date){
       this.setState({date:date})
    }
    applySearchFilter = function (searchString){
        this.setState({searchString:searchString})
    }

    selectCategory = function(searchString){
        this.setState({category:searchString})
    }

    componentDidMount() {
        brandMointoring.getConfiguration().then((responseData)=> {
            this.setState({loading: false})
            if (responseData.data && responseData.data.sources) {
                let keywords = responseData.data.keywords.split(",")
                this.setState({keywords:keywords})
            }
        });
    }

    render() {
        return(
            <div style={{paddingTop:'50px'}}>

                <Container>

                    <BrandHeader keywords = {this.state.keywords} applySearchFilter = {this.applySearchFilter} date={this.state.date} changeDateSettings = {this.changeDateSettings}/>

                    <ChartContainer category = {this.state.category} searchString = {this.state.searchString} date={this.state.date}/>

                    <Row>
                        <Col md={8} lg={8} sm={12} >
                            <Mentions keywords = {this.state.keywords} category = {this.state.category}  selectCategory = {this.selectCategory} searchString = {this.state.searchString} date={this.state.date}/>
                        </Col>

                        <Col md = {4} lg = {4} sm={12}>
                            <TopInFluencers category = {this.state.category} searchString = {this.state.searchString} date={this.state.date}/>
                        </Col>

                    </Row>


                </Container>

            </div>
        )
    }

}

export  default  Analytics