import React,{useEffect,useState} from 'react'
import playbookService from '../../../api/playbookService'
import { DataGrid } from '@material-ui/data-grid';
import { Button, Col, Row } from 'reactstrap';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { Link } from 'react-router-dom';
 const  PlayBookDetails = (props) =>{

    

    const columns = [
        { field: 'id', headerName: 'ID', width: 100 },
        { field: 'playBookName', headerName: 'Playbook Name', width: 200 },
        { field: 'playBookDescription', headerName: 'Description',width: 300 },
        { field: 'createdDate', headerName: 'Created Date', type: 'date',width: 200},
        {  field: 'updateDate', headerName: 'Updated Date', type: 'date',width: 200},
        { field: 'createdBy', headerName: 'Created BY',width: 200},
        { field: 'updatedBy',  headerName: 'Updated BY',width: 200},
        { field: 'Action',  headerName: 'Action',width: 200,
    
        renderCell: (params) => (
            <React.Fragment>
                 <DeleteIcon onClick = {(event)=>cellClick(params.row.id)} style = {{cursor:'pointer'}} fontSize = "small" color = "error"/>
                 <Link to = {`/playbook/edit/${params.row.id}`}> <EditIcon  style = {{cursor:'pointer'}} fontSize = "small" /></Link>
            </React.Fragment>  
           
          ),
        },
       
      ];

      const cellClick = (id)=>{

        playbookService.deletePlaybook(id).then((responseData)=>{
            props.refresh();
        })

      }
   

    return (
                <React.Fragment>
                    <Row>
                        <Col md={10}>
                            <h3 className="page-title">Playbooks</h3>
                        </Col>
                        <Col md={2}>
                             <Link className="btn btn-primary" to = "/playbook/new">Create Playbook</Link> 
                        </Col>
                    </Row>

                     <Col md={12} lg={12}>
                         <div style = {{height:"500px"}}>

                             <DataGrid rows={props.rows} columns={columns} pageSize={5}  />

                         </div>

                     </Col>
         </React.Fragment>
        )
    

}

export default PlayBookDetails
