import {
    OPEN_ALERT,
    OPEN_ENTITY,
    LOAD_EXPLORE_DATA,
    OPEN_EXPLORE_DATA,
    LOAD_ENTITY_VIEW
  } from '../actions/exploreActions';
  
  //alerts: [],graphData : {},alertDetails:{}
  const initialState = {
    alertDetails : {},
    entityDetails : {},
    loading:false,
    alerts:{type:'case'},
    graphData:[],
    
  };
  
  const exploreReducer = (state = initialState, action) => {

    if(action.type === LOAD_EXPLORE_DATA ){
      return Object.assign({}, state, { loading:true  });
    }

    if(action.type === OPEN_EXPLORE_DATA ){
      return Object.assign({}, state, { 
        loading:false ,
        alerts:action.payload.alerts,
        alertDetails:{alertDetails:action.payload.alerts.alertEvidences[0],caseDetails:action.payload.alerts},
       
        graphData:action.payload.graphData
      });
    }
    if(action.type === LOAD_ENTITY_VIEW){
       return Object.assign({}, state, { loading:true  });
    }
    
    if(action.type === OPEN_ALERT ){
      return Object.assign({}, state, { alertDetails: action.payload  });
    }

    if(action.type === OPEN_ENTITY ){
      return Object.assign({}, state, { loading:false,entityDetails: action.payload  });
    }
    return state;




  };
  
  export default exploreReducer;
  