import React from 'react'
import { Col, Row } from 'reactstrap';
import DashboardTree from '../../../Dashboards/Tree/DashboardTree';


function RenderSideBarPropsAreEqual(currentProps,nextProps){
    return currentProps.title === nextProps.title;
}


const RenderSideBar = (props)=>{

    const openDashboardsOrReports = (type)=>{
        props.openDashboardsOrReports(type);
    }
   
    return (
    <Row>
            <Col xs = {1} md = {1} lg = {1}>
                <Aside openDashboardsOrReports = {openDashboardsOrReports} title = 'Dashboards'/>
            </Col>
            <Col xs = {10} md = {10} lg = {10}>

                <DashboardTree type = {props.type} addDashboardToLayout = {props.addDashboardToLayout} />
            </Col>
    </Row>
    )
}

const Aside =(props)=>{

    return <div className = "csstransforms">
                <aside>
                    <div className = "sideContainer">
                         <h3 onClick = {(event)=>props.openDashboardsOrReports('dashboards')}>Dashboards</h3>
                     </div> 
                     <div className = "sideContainer" style = {{marginTop:'81px'}}>
                         <h3 onClick = {(event)=>props.openDashboardsOrReports('reports')}>Reports</h3>
                     </div>   

                   
                </aside>
    </div> 

}

export default React.memo(RenderSideBar,RenderSideBarPropsAreEqual);