

export const FILTER_HISTORICAL = 'FILTER_HISTORICAL';
export const APPLY_DATE_FILTER = 'APPLY_DATE_FILTER';
export const DELETE_FILTERS = 'DELETE_FILTERS'
export const APPLY_SEARCH_STRING = 'APPLY_SEARCH_STRING';
export const SAVE_FILETRS = 'SAVE_FILETRS'
export const OPEN_SAVED_SEARCH = 'OPEN_SAVED_SEARCH'
export const APPLY_INDEX_TYPE = 'APPLY_INDEX_TYPE'
export const SHOW_HISTORICAL_EVENT = 'SHOW_HISTORICAL_EVENT'

export function showSingleHistoricalEvent(action){
    return {
        type: SHOW_HISTORICAL_EVENT,
        payload: action,
    };
}
export function applyIndexType(action){
    return {
        type: APPLY_INDEX_TYPE,
        payload: action,
    };
}

export function saveFilters(action){
    return {
        type: SAVE_FILETRS,
        payload: action,
    };
}

export function openSavedSearch(action){
    return {
        type: OPEN_SAVED_SEARCH,
        payload: action,
    };
}

export function addFilters(action) {
    return {
        type: FILTER_HISTORICAL,
        payload: action,
    };
}
export function deleteFilters(index){
    return {
        type: DELETE_FILTERS,
        index:index
    };
}

export function applyFilter(action) {
    return {
        type: APPLY_DATE_FILTER,
        payload: action,
    };
}
export function applySearchString(action) {
    return {
        type: APPLY_SEARCH_STRING,
        payload: action,
    };
}




