import React,{ PureComponent } from 'react';

import {Container, Card, Row, Col} from 'reactstrap'
import Filter from '../Histroical/components/Filter'
import  HistoricalTimeLine from '../Histroical/components/HistoricalTimeLine'
import histroicalService from "../../api/histroicalService";
import FilterBar from "./components/FilterBar";
import dashboardService from "../../api/dashboardService";

import HistoricalTable from "./components/HistoricalTable";
import {connect} from "react-redux";
import {ElasticQueryBuilder} from "../../utils/ElasticQueryBulider";
import * as widgetHelper from "../../shared/helpers/widget_helper";
import {ElasticResponse} from "../../shared/helpers/elastic_reponse";
import _ from 'lodash';

import SaveFilter  from "./components/SaveFilter";
import HistoircalLayout from "./components/HistoricalTable";
import FieldChooser from "./components/FieldChooser";
import SplitterLayout from 'react-splitter-layout';

function difference(object, base) {
    function changes(object, base) {
        return _.transform(object, function(result, value, key) {
            if (!_.isEqual(value, base[key])) {
                result[key] = (_.isObject(value) && _.isObject(base[key])) ? changes(value, base[key]) : value;
            }
        });
    }
    return changes(object, base);
}

class Historical extends  PureComponent {


    constructor(props) {
        super(props);
        this.state = {
            fields:[],
            autoCompeleteFields:[],
            chartData:[],

        }
    }



    componentDidMount() {
        this.fetchFields();
    }


    componentDidUpdate(prevProps, prevState, snapshot) {
        let difference1 = difference(prevProps.filterData,this.props.filterData)
        if(Object.keys(difference1).length!=0){
            this.fetchChartData([],[],this.props.filterData)
        }

    }

    fetchChartData (fields,autocompleteFields,difference1){
        const {filters,type,searchString,date} = difference1;
        const metrics = [{metric:[{field: 'event_name.keyword',type:'count',id:"1"}],bucket:[{type:'date_histogram',field:'@timestamp',id:"1"}]}]
        const queryBuilder = new ElasticQueryBuilder({
            timeField: '@timestamp',
            esVersion: '7.9',
        });

        let mergedFilters = [];

        if(type && type === 'alerts' ){
            let tempFilters = [];
            for(let i=0;i<filters.length;i++){
                let key = filters[i].key;
                if(!key.startsWith("rule_data.event_data.")){
                    tempFilters.push({key:"rule_data.event_data."+filters[i].key,operator:filters[i].operator,value:filters[i].value})
                }else{
                    tempFilters.push(filters[i])
                }
            }

            mergedFilters = tempFilters;
        }else{
            let tempFilters = [];
            for(let i=0;i<filters.length;i++){
                let key = filters[i].key;
                if(key.startsWith("rule_data.event_data.")){
                    tempFilters.push({key:filters[i].key.replace("rule_data.event_data.",""),operator:filters[i].operator,value:filters[i].value})
                }else{
                    tempFilters.push(filters[i])
                }
            }

            mergedFilters = tempFilters;
        }
        var query = queryBuilder.build(metrics[0],mergedFilters,searchString);
        let payload = JSON.stringify(query);
        let duration = widgetHelper.calculateTimeRange( this.props.filterData.date[0].startDate.getTime(),this.props.filterData.date[0].endDate.getTime());
        let finalData =  payload.replaceAll("$timeFrom",this.props.filterData.date[0].startDate.getTime());
        let fainlData1 = finalData.replaceAll("$timeTo", this.props.filterData.date[0].endDate.getTime());
        let finalData2 = fainlData1.replaceAll("$__interval",duration)
        let panelJson = {};
        panelJson['elsQuery'] = finalData2;
        panelJson['type'] = type
        let data = {'panelData':JSON.stringify(panelJson)};


        dashboardService.showPanelPreivew(data ).then((response) => {
            var responses = {responses:[response.data.data]}

            const er = new ElasticResponse(metrics, responses);


            let esResponse =  er.getTimeSeries('NA');
            if(fields.length>0 && autocompleteFields.length>0){
                this.setState({
                    fields:fields,
                    autoCompeleteFields:autocompleteFields,
                    chartData : esResponse
                });
            }else{
                this.setState({

                    chartData : esResponse
                });
            }


        });
    }

    fetchFields (){

        dashboardService.getFields() .then((response) => {
            const fields = [];
            const tempFields = [];
            for (const [key, value] of Object.entries(response.data)) {
                fields.push({label:key,value:key})
                tempFields.push({name:key,type:'string',enumerations:null})
            }
            this.fetchChartData(fields,tempFields,this.props.filterData)

        });

    }


    render() {
        console.log(this.props.filterData.date)
        return(<div style={{paddingTop:'60px'}}>
          <Container>
              <Filter finalSearchString = {this.props.filterData.searchString} fields = {this.state.autoCompeleteFields} type = {this.props.filterData.type} />
                 <FilterBar fields = {this.state.fields}/>
                    <div className="historicalContainer">

                        <SplitterLayout primaryIndex={0} percentage = {20} secondaryInitialSize = {80}>
                            <FieldChooser  fields = {this.state.fields}/>
                            <div>
                                <HistoricalTimeLine startDate = {this.props.filterData.date[0].startDate.getTime()} endDate = {this.props.filterData.date[0].endDate.getTime()} chartData = {this.state.chartData} />

                                <Card style = {{marginTop:'12px',height:'90%'}}>
                                    <div className="ag-theme-alpine" >
                                        <HistoricalTable  filters= {this.props.filterData.filters}
                                                          searchString = {this.props.filterData.searchString}
                                                          type = {this.props.filterData.type}
                                                          startDate = {this.props.filterData.date[0].startDate.getTime()}
                                                          endDate = {this.props.filterData.date[0].endDate.getTime()}
                                                          tableState =  {this.props.filterData.tableState}
                                        />
                                    </div>
                                </Card>
                            </div>


                        </SplitterLayout>



                    </div>









          </Container>
        </div>)
    }

}

const mapStateToProps = (state) => {
    return {
        filterData: state.historicalReducer.historicalModel,

    }
}


export default connect(mapStateToProps, null)(Historical)
