import React,{useState,useEffect    } from 'react';
import _ from 'lodash';

import { Button, Modal, ModalHeader, ModalBody, ModalFooter,Container, Row, Col, CardBody, Card, Input } from 'reactstrap';
import Collapse from '../../../../shared/components/Collapse';
import Visualization from "../AddPanelWidget/Visualization"
import { Field, reduxForm } from 'redux-form';
import renderSelectField from '../../../../shared/components/form/Select';
import * as widgetHelper  from "../../../../shared/helpers/widget_helper"

import 'react-toastify/dist/ReactToastify.css';
import InputColor from 'react-input-color';
import dashboardService from "../../../../api/dashboardService";





const WidgetConfiguration = (props) =>{

    
    const {panel,initialize} = props;

    const [dashboards,setDashboards] = useState([]);
    

    useEffect(() => {
      let object = new Object();
      object['panelTitle'] = panel.panelTitle;
      object['panelDescription'] = panel.panelDescription;
      object['type'] = {value:panel.type,label:panel.type}
      for(var i=0;i<panel.query.length;i++){
       let metrics =  panel.query[i].metric ;
        for(let j=0;j<metrics.length;j++){
          object[panel.query[i].metric[j].metricName] = panel.query[i].metric[j].field;
         
        }
      }
      let chartType = panel.chartConfig.chartType
      let chartConfig = panel.chartConfig[chartType];
      if(chartType=== 'bar' || chartType=== 'column'){
        if(chartConfig.stackType === 'normal'){
          object['stackType'] = {label:'Normal',value:chartConfig.stackType};
        }
        if(chartConfig.stackType === 'percent'){
          object['stackType'] = {label:'Percent',value:'percent'};
        }
        if(chartConfig.stackType === 'group'){
          object['stackType'] = {label:'Group',value:'group'};
        }
        
      }
      if(chartType=== 'line' ){
     
       let scale = _.find(widgetHelper.scaleOptions, { value: chartConfig.scale });
       object['scale'] = scale;

       object['lineWidth'] = chartConfig.lineWidth;

       let lineStyle = _.find(widgetHelper.lineTypes, { value: chartConfig.shape });
       object['shape'] = lineStyle;
        
      }
      if(chartType=== 'pie' ){
     
        
        object['showLables'] = chartConfig.showLables;
 
        object['pieType'] = chartConfig.pieType;
 
      
         
       }

      

    

      
     
      initialize(object);
      fetchDashboards();
    }, []);

    const fetchDashboards = () =>{
        dashboardService.getAllDashboards().then((responseData) =>{
           const dashboardData = responseData.data;
           const tempData = [];
           for(let i=0;i<dashboardData.length;i++){
               tempData.push({label:dashboardData[i].name,value:dashboardData[i].id.toString()})
           }
            setDashboards(tempData)
        })
    }


    return (<form className="form">
    <Card>
      <CardBody>
         <Collapse title="Settings" className="with-shadow">
           <div className="form__form-group">
              <span className="form__form-group-label">Panel Title</span>
               <div className="form__form-group-field">
             <Field  name="panelTitle" onChange={(event) => props.changeFormValue(event,'panelTitle')} component="input" type="text"  placeholder="Panel Title"/>
         </div>
       </div>

      <div className="form__form-group">
        <span className="form__form-group-label">Description</span>
        <div className="form__form-group-field">
          <Field  name="panelDescription" onChange={(event) => props.changeFormValue(event,'panelDescription')}   component="input" type="text"  placeholder="Panel Title"/>
      </div>
    </div>

    <div className="form__form-group">
        <span className="form__form-group-label">Type</span>
        <div className="form__form-group-field">
          <Field  name="type" onChange={(event) => props.changeFormValue(event,'type')}     component={renderSelectField} type="select" options = {[
            {label:'Events',value:'events'},
            {label:'Alerts',value:'alerts'},
            
            ]}/>
      </div>
    </div>

             <div className="form__form-group">
                 <span className="form__form-group-label">Dashboard Link</span>
                 <div className="form__form-group-field">
                     <Field  name="dashboardLink" onChange = {(event)=>props.changeFormValue(event,'dashboardLink')}   component={renderSelectField} type="select" options = {dashboards}/>
                 </div>
             </div>

</Collapse>

<Collapse title="Visualization" className="with-shadow">
    <Visualization changeChartType = {props.changeChartType} panel={panel}/>
</Collapse>

<Collapse title="Display" className="with-shadow">


{panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&  (panel.chartConfig.chartType  == 'line' ||  panel.chartConfig.chartType  == 'area')  && 
   <div>
   
  
   <div className="form__form-group">
    <span className="form__form-group-label">Scale</span>
      <div className="form__form-group-field">
      
      <Field  name = "scale" onChange={(target) => props.changeChartConfig(target,'scale')} component={renderSelectField} options={widgetHelper.scaleOptions} />

       
     </div>
    </div>
   <div className="form__form-group">
   <span className="form__form-group-label">Line Width</span>
     <div className="form__form-group-field">
      
     <Field  name = "lineWidth" onChange={(target) => props.changeChartConfig(target,'lineWidth')}  type="text" component="input" />

      
        
    </div>
   </div>
   <div className="form__form-group">
   <span className="form__form-group-label">Line Style</span>
     <div className="form__form-group-field">
     
     <Field  name = "shape" onChange={(target) => props.changeChartConfig(target,'shape')} component={renderSelectField} options={widgetHelper.lineTypes} />

     
    
    
    </div>
   </div>

       <a href="#" className='' onClick={props.addAnotations.bind(this)}>Add threshold</a>

       { panel.chartConfig.chartType  == 'line' &&  panel.chartConfig.line.thresholds && panel.chartConfig.line.thresholds.map((range,index) =>
           <div className="form__form-group">
               <Row>
                   <Col>
                       <Field type = "number"  name = {"from"+index}   onChange={(target) => props.updateThreshold(index,target,"from"+index)}  component="input"  />
                   </Col>
                   <Col>
                       <Field type = "select"  name = {"color"+index}   onChange={(target) => props.updateThreshold(index,target,"color"+index)}  component={renderSelectField}
                        options={[
                            {label:'Critical',value:'Critical'},
                            {label:'High',value:'High'},
                            {label:'Medium',value:'Medium'},
                            {label:'Low',value:'Low'}
                            ]}
                       />
                   </Col>

                   <Col>
                       <Field type = "text"  name = {"label"+index}   onChange={(target) => props.updateThreshold(index,target,"label"+index)}  component="input"

                       />
                   </Col>

                   <Col>
                       <span className=" lnr lnr-trash" onClick = {props.deleteThresholdShold.bind(this,index)}></span>
                   </Col>

               </Row>
           </div>
       )}



           </div>

}
{panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&    panel.chartConfig.chartType  == 'area'  && 
    <div className="form__form-group">
    <span className="form__form-group-label">Area Type</span>
      <div className="form__form-group-field">
      
      <Field  name = "areaType" onChange={(target) => props.changeChartConfig(target,'areaType')} component={renderSelectField} options={[
      { value: 'normal', label: 'Normal' },
      { value: 'percent', label: 'Percent' },
    ]} />


      
      
     
     </div>
    </div>
}

{ panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&   panel.chartConfig.chartType  == 'stats'  && 
    <div>
    <div className="form__form-group">
    <span className="form__form-group-label">Calculation</span>
      <div className="form__form-group-field">
      
      <Field  name = "calculation" onChange={(target) => props.changeChartConfig(target,'stats')} component={renderSelectField} options={[
      { value: 'mean', label: 'Mean' },
      { value: 'Min', label: 'Min' },
      { value: 'Max', label: 'Max' },
      { value: 'Count', label: 'Max' },
    ]} />
      </div>
   </div>
    <div className="form__form-group">
    <span className="form__form-group-label">Orientation</span>
      <div className="form__form-group-field">
      
      <Field  name = "orientation" onChange={(target) => props.changeChartConfig(target,'orientation')} component={renderSelectField} options={[
      { value: 'auto', label: 'Auto' },
      { value: 'horizontal', label: 'horizontal' },
      { value: 'vertical', label: 'Vertical' }
      
    ]} />
      </div>
   </div>

   <div className="form__form-group">
    <span className="form__form-group-label">Graph Mode</span>
      <div className="form__form-group-field">
      
      <Field  name = "graphMode" onChange={(target) => props.changeChartConfig(target,'graphMode')} component={renderSelectField} options={[
      { value: 'none', label: 'None' },
      { value: 'area', label: 'Area' }
      
      
    ]} />
      </div>
   </div>

   <div className="form__form-group">
    <span className="form__form-group-label">Color Mode</span>
      <div className="form__form-group-field">
      
      <Field  name = "colorMode" onChange={(target) => props.changeChartConfig(target,'colorMode')} component={renderSelectField} options={[
      { value: 'value', label: 'Value' },
      { value: 'background', label: 'Background' }
      
      
    ]} />
      </div>
   </div>

   <a class = ''  onClick={props.addThreshold.bind(this)}>Add threshold</a>







       { panel.chartConfig.stats.thresholds.map((range,index) =>
      <div className="form__form-group">
        <Row>
          <Col> 
            <Field type = "number"  name = {"from"+index}   onChange={(target) => props.updateThreshold(index,target,"from"+index)}  component="input"  />
          </Col>
          <Col>
              <InputColor  initialValue="#5e72e4"  onChange={props.onChangeColor.bind(this,index)}  placement="right" />
          </Col>

          <Col>
            <span className=" lnr lnr-trash" onClick = {props.deleteThresholdShold.bind(this,index)}></span>
          </Col>
        
        </Row>
        </div>
      )}
    </div>

}

{panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&   panel.chartConfig.chartType  == 'table'  && 
<React.Fragment>

    <div className="form__form-group">
        <span className="form__form-group-label">Require SparkLine</span>
        <div className="form__form-group-field">

            <Field  name = "showSparkLine" onChange={(target) => props.changeChartConfig(target,'showSparkLine')} component={renderSelectField} options={[
                { value: 'true', label: 'true' },
                { value: 'false', label: 'False' }

            ]} />
        </div>
    </div>

    <a class = ''  onClick={props.addThreshold.bind(this)}>Add threshold</a>

    { panel.chartConfig.table.thresholds.map((range,index) =>
        <div className="form__form-group">
            <Row>
                <Col>
                    <Field type = "number"  name = {"from"+index}   onChange={(target) => props.updateThreshold(index,target,"from"+index)}  component="input"  />
                </Col>
                <Col>
                    <InputColor  initialValue="#5e72e4"  onChange={props.onChangeColor.bind(this,index)}  placement="right" />
                </Col>

                <Col>
                    <span className=" lnr lnr-trash" onClick = {props.deleteThresholdShold.bind(this,index)}></span>
                </Col>

            </Row>
        </div>
    )}
</React.Fragment>


}

{panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&   panel.chartConfig.chartType  == 'gauge'  && 
    <div>
    <div className="form__form-group">
    <span className="form__form-group-label">Calculation</span>
      <div className="form__form-group-field">
      
      <Field  name = "calculation" onChange={(target) => props.changeChartConfig(target,'stats')} component={renderSelectField} options={[
      { value: 'mean', label: 'Mean' },
      { value: 'Min', label: 'Min' },
      { value: 'Max', label: 'Max' },
      { value: 'Count', label: 'Max' },
    ]} />
      </div>
   </div>
   <div className="form__form-group">
    <span className="form__form-group-label">Type</span>
      <div className="form__form-group-field">
      
      <Field  name = "type" onChange={(target) => props.changeChartConfig(target,'type')} component={renderSelectField} options={[
      { value: 'Single', label: 'Single' },
      { value: 'Multi', label: 'Multi' }
      
    ]} />
      </div>
   </div>
    <div className="form__form-group">
    <span className="form__form-group-label">Orientation</span>
      <div className="form__form-group-field">
      
      <Field  name = "orientation" onChange={(target) => props.changeChartConfig(target,'orientation')} component={renderSelectField} options={[
      { value: 'auto', label: 'Auto' },
      { value: 'horizontal', label: 'horizontal' },
      { value: 'vertical', label: 'Vertical' }
      
    ]} />
      </div>
   </div>

   <div className="form__form-group">
    <span className="form__form-group-label">Graph Mode</span>
      <div className="form__form-group-field">
      
      <Field  name = "graphMode" onChange={(target) => props.changeChartConfig(target,'graphMode')} component={renderSelectField} options={[
      { value: 'none', label: 'None' },
      { value: 'area', label: 'Area' }
      
      
    ]} />
      </div>
   </div>

   <div className="form__form-group">
    <span className="form__form-group-label">Color Mode</span>
      <div className="form__form-group-field">
      
      <Field  name = "colorMode" onChange={(target) => props.changeChartConfig(target,'colorMode')} component={renderSelectField} options={[
      { value: 'value', label: 'Value' },
      { value: 'background', label: 'Background' }
      
      
    ]} />
      </div>
   </div>

   <a class = ''  onClick={props.addThreshold.bind(this)}>Add threshold</a>

   

   
    
      { panel.chartConfig.gauge.thresholds.map((range,index) => 
      <div className="form__form-group">
        <Row>
          <Col> 
            <Field type = "number"  name = {"from"+index}   onChange={(target) => props.updateThreshold(index,target)}  component="input"  />
          </Col>
          <Col>
              <InputColor  initialValue="#5e72e4"  onChange={props.onChangeColor.bind(this,index)}  placement="right" />
          </Col>

          <Col>
            <span className=" lnr lnr-trash" onClick = {props.deleteThresholdShold.bind(this,index)}></span>
          </Col>
        
        </Row>
        </div>
      )}

       
    </div>

}

{panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&    (panel.chartConfig.chartType  == 'bar'  || panel.chartConfig.chartType  == 'column'   ) && 
 <div>
 <div className="form__form-group">
    <span className="form__form-group-label">Stack Type</span>
      <div className="form__form-group-field">
     
      <Field  name = "stackType" onChange={(target) => props.changeChartConfig(target,'stackType')} component={renderSelectField} options={[
      { value: 'normal', label: 'Normal' },
      { value: 'percent', label: 'Percent' },
      { value: 'group', label: 'Group' },
    ]} />
     
     
     
     </div>
    </div>

<div className="form__form-group">
<span className="form__form-group-label">Chart Type</span>
<div className="form__form-group-field">

<Field  name = "subChartType" onChange={(target) => props.changeChartConfig(target,'subChartType')} component={renderSelectField} options={[
      { value: 'normal', label: 'Normal' },
      { value: 'polar', label: 'Percent' },
     
    ]} />



</div>
</div>

     {panel.chartConfig.chartType  == 'column' && <a href="#" className='' onClick={props.addAnotations.bind(this)}>Add threshold</a>}

     { panel.chartConfig.chartType  == 'column' && panel.chartConfig.column.thresholds && panel.chartConfig.column.thresholds.map((range,index) =>

         <div className="form__form-group">
             <Row>
                 <Col>
                     <Field type = "number"  name = {"from"+index}   onChange={(target) => props.updateThreshold(index,target,"from"+index)}  component="input"  />
                 </Col>
                 <Col>
                     <Field type = "select"  name = {"color"+index}   onChange={(target) => props.updateThreshold(index,target,"color"+index)}  component={renderSelectField}
                            options={[
                                {label:'Critical',value:'Critical'},
                                {label:'High',value:'High'},
                                {label:'Medium',value:'Medium'},
                                {label:'Low',value:'Low'}
                            ]}
                     />
                 </Col>

                 <Col>
                     <Field type = "text"  name = {"label"+index}   onChange={(target) => props.updateThreshold(index,target,"label"+index)}  component="input"

                     />
                 </Col>

                 <Col>
                     <span className=" lnr lnr-trash" onClick = {props.deleteThresholdShold.bind(this,index)}></span>
                 </Col>

             </Row>
         </div>
     )}
</div>
}

{panel && panel.chartData && panel.chartData.length>0  && panel.chartConfig &&    (panel.chartConfig.chartType  == 'pie'   ) && 
 <div>
 <div className="form__form-group">
    <span className="form__form-group-label">Show Labels</span>
      <div className="form__form-group-field">
      
      <Field  name = "showLables" onChange={(target) => props.changeChartConfig(target,'showLables')} component={renderSelectField} options={[
      { value: 'true', label: 'Yes' },
      { value: 'false', label: 'No' },
     
    ]} />
      
      
     
     </div>
    </div>

<div className="form__form-group">
<span className="form__form-group-label">Chart Type</span>
<div className="form__form-group-field">

<Field  name = "pieType" onChange={(target) => props.changeChartConfig(target,'pieType')} component={renderSelectField} options={[
      { value: 'pie', label: 'Pie' },
      { value: 'donut', label: 'Donut' },
      { value: 'rose', label: 'Rose' },
     
    ]} />



</div>
</div>
</div>
}

    <div className="form__form-group">
        <span className="form__form-group-label">Units</span>
        <div className="form__form-group-field">

            <Field  name = "units" onChange={(target) => props.changeChartConfig(target,'units')} component={renderSelectField} options={widgetHelper.units} />


        </div>
    </div>

</Collapse>   

<Collapse title="Filters" className="with-shadow">
          <Button color="link" onClick = {props.addFitler} >Add</Button>
      {panel.filters && panel.filters.map((filter,index) => 
           <div className="form__form-group">
           <Row>
           //fieldName,index,event
             <Col> 
               <Field onChange={(target) => props.updateFilterField('key',index,target)} type = "select"   name = {"fitler.fieldname"+index}   component={renderSelectField}
                options = {props.fields}
               />
             </Col>
             <Col>
              <Field  onChange={(target) => props.updateFilterField('operator',index,target)}   name = {"fitler.operator"+index}   component={renderSelectField}
                options = {[
                  { value: '=', label: 'Equals' },
                  { value: '!=', label: 'Not Equals' },
                  { value: '<', label: 'Greater Than' },
                  { value: '>', label: 'Less Than' },
                  { value: '=~', label: 'Contains' },
                  { value: '!~', label: 'Not Contains' },
                ]

                }
               />
             </Col>
             <Col>
              <Field type = "text" onChange={(target) => props.updateFilterField('value',index,target)}  name = {"fitler.value"+index}     component="input"  />
             </Col>
   
             <Col>
               <span className=" lnr lnr-trash" onClick = {props.deleteFilters.bind(this,index)}></span>
             </Col>
           
           </Row>
           </div>

       
      ) 
      
    }


</Collapse>  

</CardBody>
</Card>
</form>);


}

export default reduxForm({
    form: 'widgetConfiguration', 
    enableReinitialize: true, 
    
})(WidgetConfiguration)
