const changeChartType = (chartType) =>{
 
  let tempChartType = panel.chartType;
  panel.chartType = chartType;
  panel.chartConfig.chartType = chartType;
  dispatch(updateChartType(chartType))
  

}

const addThreshold = ()=>{
  
  let chartType = panel.chartConfig.chartType;
  if(panel.chartConfig[chartType]['thresholds']){
      panel.chartConfig[chartType]['thresholds'].push({});
  }else{
      panel.chartConfig[chartType]['thresholds'] = [];
      panel.chartConfig[chartType]['thresholds'].push({});
  }


    dispatch(addThreadSholds(chartType,panel.chartConfig[chartType].thresholds))

  //this.forceUpdate();
  
}

const addAnotations = (event) =>{
      event.preventDefault();
      let chartType = panel.chartConfig.chartType
    if(panel.chartConfig[chartType]['thresholds']){
        panel.chartConfig[chartType]['thresholds'].push({});
    }else{
        panel.chartConfig[chartType]['thresholds'] = [];
        panel.chartConfig[chartType]['thresholds'].push({});
    }
    //panel.updateChartConfig(chartType,'thresholds',panel.chartConfig.thresholds);
    //this.forceUpdate();
    dispatch(addThreadSholds(chartType,panel.chartConfig[chartType].thresholds))
}


const updateFilterField = (fieldName,index,event) =>{
    if(event.target){
       let payload = {fieldName:fieldName,index:index,value:event.target.value}
       dispatch(updatePanelFilter(payload))
    }else{
         let payload = {fieldName:fieldName,index:index,value:event.value}
         dispatch(updatePanelFilter(payload))
    }

}

const updateThreshold = (index,target,fieldName) =>{

  
  let chartType = panel.chartConfig.chartType
  let chartConfig = panel.chartConfig[chartType];
  if(target.target){
      if(!isNaN(target.target.value)){
          chartConfig['thresholds'][index][fieldName] = parseInt(target.target.value);
      }else{
          chartConfig['thresholds'][index][fieldName] = target.target.value;
      }

  }else{
      chartConfig['thresholds'][index][fieldName] = target.value;
  }

  //panel.updateChartConfig(chartConfig);
  dispatch(addThreadSholds(chartType,panel.chartConfig[chartType].thresholds))
}

