const ShowViewWidgets = (props) => {

  const dispatch = useDispatch()

  const {rows} = props;
  const createNewPanel = () =>{

    const gridOptions1 =   { x: 0, y: 0, h: 3, w: 6 };
    const {dashboard} = props;

    const panel = {id:'',panelTitle:'',panelDescription:'',gridOptions:gridOptions1,query:'',configuration:'',chartType:'line',initialModalState:true};
    let queryData = {collapse:true,metric:[{metricName:'metric.0',fieldName:'field.0',type:'avg',field:'event_name.keyword'}],bucket:[{bucketName:'bucket.0',bucketField:'bucketfield.0',type:'count',field:'event_category.keyword',settings:{size:10}}]}
    panel['query'] = [queryData];
    panel['chartConfig'] = widgetHelper.chartOptions;
    panel['filters'] = [];
    dispatch(addCreatedPanel(panel))
    //dashboard.addPanel(panel);
    //dispatch(createPanel(dashboard))
    //dispatch(createPanel(dashboard.getCurrentPanel()));
}



  function rowRenderer({ key, index,  isScrolling, isVisible, style, }) {
    return (
        <ListGroup key={key} style={style}>
              <ListGroupItem  >
                  <span className = "widgetTitle">
                      <img className = "widgetIcon" src = {widgetHelper.chartMap[rows[index].chartType]}></img> 
                      <span className = "widgetIconTitle " onClick={ () => dispatch(addCreatedPanelToDashboard(rows[index])) }> {rows[index].panelTitle} - {rows[index].panelDescription}</span> 
                      <span className = "widgetDelete  lnr lnr-trash" onClick={ () => props.deleteSingleWidget(rows[index]) }></span> 
                    </span>

             
              </ListGroupItem>
        </ListGroup>
    );
  }

  return (
  <div>
  <Row>
   
    <Col md= {8}>
        <Input type="text" placeholder = "Search...." name="search" onChange = {props.filterData}  />
    </Col>
    <Col md= {3}>
        <Button onClick = {createNewPanel.bind(this)}> Create New </Button>
    </Col>
  </Row>
  
  

  
  <List width={700} height={300}
    rowCount={rows.length}
    rowHeight={50}
    rowRenderer={rowRenderer}/>
    </div>
    );

}



const ModelSidePanle = (props) =>{

  const [rows,setRows] = useState([]);

  const [originalData,setOriginalData] = useState([]);

    const [loading,setLoading] = useState(false);

    const filterData = (event) =>{
   
      if(event.target.value!=''){
        let searchString = event.target.value.toLowerCase();
        const filtered = rows.filter(entry => Object.values(entry).some(val => typeof val === "string" && val.toLowerCase().includes(searchString)));
        setRows(filtered);
      }else{
        setRows(originalData);
      }
     
      
  
      //console.log(event.target.value);
    }
    
    const deleteSingleWidget = (panelDetails) =>{
      setLoading(true);
      dashboardService.deleteWidget(panelDetails.id).then((response) => {
          if(response.status === 200){
            toast.success('Panel was deleted succesfully', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
            fetchPanelData();
            setLoading(false);
          }else{
            toast.error('Unable to delete Panel', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
            setLoading(false);
          }
    });
     
  }

    const dispatch = useDispatch()

    useEffect(() => {
        fetchPanelData();
      }, [props.showCreatePanel]);
   
   const  fetchPanelData = () =>{
        dashboardService.getPanels().then((response) => {
          setOriginalData(response.data)
          setRows(response.data);
        });
    }
    

    let className = "";
    if(props.showCreatePanel){
      className = "add-widget-model";
    }
    
    const getChartData =() => {

     //startDate:moment().subtract(24, 'hours').valueOf(),endDate:moment().valueOf(),label:"Past 1 Day"
      let startDate = moment().subtract(30, 'days').valueOf();
      let endDate = moment().valueOf();
      const {panel} = props//this.props;
     
      let queryString =  "*"
     let queryBuilder = new ElasticQueryBuilder({
        timeField: '@timestamp',
        esVersion: '7.9',
      })

      let panelQuery = panel.query;
      if(panel.type && panel.type === 'alerts'){
        let tempArray = {};
        for(var i=0;i<panelQuery.length;i++){
         let metrics = panelQuery[i].metric;
         let buckets = panelQuery[i].bucket;
         for(let j=0;j<metrics.length;j++){
             if(!widgetHelper.alertMetaData.includes(metrics[j].field)){
                 let fieldName = metrics[j].field.replaceAll("rule_data.event_data.","")
                 metrics[j].field = "";
                 metrics[j].field = "rule_data.event_data."+fieldName;
             }

         }
         for(let j=0;j<buckets.length;j++){
             if(!widgetHelper.alertMetaData.includes(buckets[j].field)){
                 let fieldName = buckets[j].field.replaceAll("rule_data.event_data.","")
                 buckets[j].field = "";
                 buckets[j].field = "rule_data.event_data."+fieldName;
             }

         }
         tempArray['metric'] = metrics;
         tempArray['metrics'] = metrics;
         tempArray['bucketAggs'] = buckets;
         tempArray['bucket'] = buckets;
        }
        panelQuery = [];
        panelQuery.push(tempArray);
      }

      let duration = widgetHelper.calculateTimeRange( startDate,endDate);

      var query = queryBuilder.build(panelQuery[0],panel.filters,queryString);
      let payload = JSON.stringify(query);
      let finalData =  payload.replaceAll("$timeFrom", startDate);
      let fainlData1 = finalData.replaceAll("$timeTo", endDate);
      let finalData2 = fainlData1.replaceAll("$__interval",duration)
      let panelJson = panel;
      panelJson['elsQuery'] = finalData2;
      var data = {'panelData':JSON.stringify(panelJson)};
      dashboardService.showPanelPreivew(data).then((response) => {
       if(response.data.data && response.data.data.status === 400){
         toast.error('Invalid Query Selected.', {
           position: "top-right",
           autoClose: 5000,
           hideProgressBar: false,
           closeOnClick: true,
           pauseOnHover: true,
           draggable: true,
           progress: undefined,
         });
       }else{
           var responses = {responses:[response.data.data]} 
    
           const er = new ElasticResponse(panelQuery, responses);
           if(panel.chartConfig.chartType === 'table' && panel.chartConfig.table.showSparkLine!='true'){
            
            let esResponse =  er.getTimeSeries('table');
            dispatch(updatePanel(esResponse.data));
            //panel.updateChartData(esResponse.data);
             
           }else{
             let esResponse =  er.getTimeSeries('NA');
             dispatch(updatePanel(esResponse.data));
             //panel.updateChartData(esResponse.data);
            
           }
          
           
          
           //this.forceUpdate();
       }
      
      
      
      
       
       
     });
    
     
    
    
    }

    const savePanel = () =>{

      
    
      const {panel} = props//this.props;


        let queryString =  "*"
        let queryBuilder = new ElasticQueryBuilder({
          timeField: '@timestamp',
          esVersion: '7.9',
        })
        var query = queryBuilder.build(panel.query[0],'',queryString);
    
       
      
       
       
    
        let payload = JSON.stringify(query);
        let finalData =  payload.replaceAll("$timeFrom", "1604059820000");
        let fainlData1 = finalData.replaceAll("$timeTo", "1604203820000");
        let fainlData2 = fainlData1.replaceAll("$__interval","30m")
        let panelJson = panel;
        panelJson['elsQuery'] = fainlData2;
        var data = {'panelData':JSON.stringify(panelJson)};

        dashboardService.showPanelPreivew(data).then((response) => {
          if(response.data.data && response.data.data.status === 400){
            toast.error('Invalid Query Selected.', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }else{
            panel['operationType'] = 'insert';
            if(panel.id === ""){
              panel.id = 0;
            }
            dashboardService.savePanel(panel).then((response) => {
              if(response.data.data && response.data.data.status === 400){
                toast.error('Unable to Save Data', {
                  position: "top-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
                });
              }else{
                toast.success('Sucessfully Save Widget', {
                  position: "top-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true,
                  progress: undefined,
                });
              }
          });
    
        }
    
    
      });
    
    }
    
  
      
    

    return(
        <div>
          
        <Modal className = {className} style = {{maxWidth:'none',left:'18px',marginRight:'35px'}} size = "lg"  isOpen={props.isSidePanelOpen} >
        {loading && <BarLoader color= "#53c8b7" height ={4} width = {800}/>}
        <ModalHeader>Add Panels</ModalHeader>
        <ModalBody>

       
        <Col md={12} lg={12}>
        <Card>
          <CardBody>
            
          {!props.showCreatePanel && 
            <ShowViewWidgets filterData = {filterData} deleteSingleWidget = {deleteSingleWidget} rows = {rows} dashboard = {props.dashboard}/>
          }

           {props.showCreatePanel &&   
           
           <CreatePanelModel panel = {props.panel}/>}

          </CardBody>
          <ToastContainer
        position="top-right"
        hideProgressBar={false}
        autoClose={false}
        newestOnTop={true}
        closeOnClick={false}
        draggable={false}
        rtl={false}
      />
        </Card>
      </Col>
        
         </ModalBody>
         <ModalFooter>
           
         {props.showCreatePanel && 
          <React.Fragment>
              <Button color='primary' onClick={savePanel.bind(this,props.dashboard)}>Save </Button>
              <Button color='primary' onClick={getChartData.bind(this)}>Show Data</Button>
              <Button color='secondary' onClick={ () => dispatch(hideCreatePanel(props))}>Back</Button>
          </React.Fragment>
         }
          {!props.showCreatePanel && 
        
            <Button color='secondary' onClick={ () => dispatch(closeSideBar())}>Cancel</Button>
          
         }
           

            
          </ModalFooter>
      </Modal> 

        

        </div>
       

    
    );

}

export default SidePanel