import React from 'react';
import {
  Chart,
  Geom,
  Axis,
  Tooltip,
  Label,
  Legend
} from 'bizcharts';
import _ from 'lodash';
import DataSet from "@antv/data-set";

import sizeMe from 'react-sizeme'
import {  CardBody } from 'reactstrap';
import {addFilters} from '../../../redux/actions/dashboardActions'
import { connect } from 'react-redux';

let getRowElements = function (row, elmIdxs) {
    var arr = new Array();
    for (var j = 0; j < elmIdxs.length; j++) {
        var elm = row[elmIdxs[j]];
        arr.push(elm);
    }
    return arr;
};

let castSeriesData = function (index,series, group, castedKeys, newData, iterator) {
    
        for (var i = 0; i < castedKeys.length; i++) {
            iterator(newData[group][index]['count'][castedKeys[i]], i)
        }
   
};

let processTableData = function(data1){
    var joinedKeys = {};
    var newData = {};
    var castedGroups = new Array();
    var castedKeys = new Array();
    var joinedGroups = {};
    const data = [];
    var finalData = data1[0].rows;
        
    for(let i=0;i<finalData.length;i++){
        let newKey = finalData[i][0];
        let jk = newKey;
        if (_.isUndefined(joinedKeys[jk])) {
            castedKeys.push(newKey);
            joinedKeys[jk] = true;
        }

        var group =  finalData[i][1];
		var newGroup = group;
		if (_.isUndefined(joinedGroups[newGroup])) {
				castedGroups.push(group);
				joinedGroups[newGroup] = true;
        }
        if (_.isUndefined(newData[newGroup])) {
            newData[newGroup] = {};
        }
        if (_.isUndefined(newData[newGroup][0])) {
            newData[newGroup][0] = {};
        }
        if (_.isUndefined(newData[newGroup][0]['count'])) {
            newData[newGroup][0]['count'] = {};
        }
        
        newData[newGroup][0]['count'][jk] = parseFloat(finalData[i][2]);


    }

    var aliasData = new Array();

    _.each(castedGroups, function (group,index) {
        castSeriesData(0,{}, group, castedKeys, newData, function (castedData, keyIdx) {
            if (!aliasData[index]) {
                aliasData[index] = new Array();
            }
            // Only format decimal
            aliasData[index][keyIdx] = castedData;
        });
    });
    for (var i in aliasData) {
        for (var j in aliasData[i]) {
            var value = isNaN(aliasData[i][j]) ? 0 : parseFloat(aliasData[i][j]);
          
            if(value != 0){
                data.push([parseInt(j), parseInt(i), value]);
            }

        }
    }
    return data;
}

let processHexBinData = function(data1){
    var finalData = data1[0].rows;
    let binArray = [];
    for(let i=0;i<finalData.length;i++){
        let object = new Object();
        object['name'] = finalData[i][finalData[i].length-2];
        object['day'] = finalData[i][finalData[i].length-1];
        binArray.push(object);
    }  
    const ds = new DataSet({
        state: {
          sizeEncoding: false
        }
    });
    
    const dv = ds
    .createView()
    .source(binArray)
    .transform({
      sizeByCount: "$state.sizeEncoding",
      
      type: "bin.hexagon",
      fields: ["name", "day"],
     
      bins: [10, 5]
    });
    return dv;
}

let processTimeSeriesData = function(data1){
    const finalData = [];
    for(var i=0;i<data1.length;i++){
        for(var j=0;j<data1[i].datapoints.length;j++){
            if(data1[i].datapoints[j][0] != 0){
                finalData.push([parseInt(j), parseInt(i), data1[i].datapoints[j][0]]);
            }
           
        }
    }
    return finalData;
}
let getSeriesData = function(data1,cols){
    let xAxis = new Set()
    let yAxis = new Set()
    if(data1.length > 0 && data1[0].rows){
        
        for (var i in data1[0].rows) {
            xAxis.add(data1[0].rows[i][0]);
            yAxis.add(data1[0].rows[i][1]);
       
        } 
        let colmap = data1[0].columnMap;

        let finalColsMap = [];
        for (let value of Object.values(colmap)) {
            if(!value.text.includes("Count")){
              finalColsMap.push(value.text);
            }
        } 


        cols.name.fields = finalColsMap[0];
        cols.day.fields = finalColsMap[1];
        cols.name.values = Array.from(xAxis);
        cols.day.values = Array.from(yAxis);

    }
    if(data1.length > 0 && data1[0].datapoints){
        for(let i in data1){
            yAxis.add(data1[i].target)
        }
        for(let j in data1[0].datapoints){
            xAxis.add(data1[0].datapoints[j][1]);
        }
        cols.name.values = Array.from(xAxis);
        cols.day.values = Array.from(yAxis);
        cols.name.type = 'timeCat';
    } 
    return cols;
}


function getComponent(data1,config,width,height,dispatch,timeFromat,dashboardLink,date) {
 
    let data = [];

    const source = [];

    

    let cols = {
      name: {
        type: 'cat',
        values: [],
      },
      day: {
        type: 'cat',
        values: [],
      },
      sales:{
        nice:true
      }
    };
    
    if(config.type === 'square'){
        if(data1.length > 0 && data1[0].rows){
            data = processTableData(data1);
           
        }else if(data1.length > 0 && data1[0].datapoints){
            data = processTimeSeriesData(data1);
        }
    }else{
           data = processHexBinData(data1);
    }
 
   

   
    
    const finalCols = getSeriesData(data1,cols);

    console.log(data);            

    for (let i = 0; i < data.length; i++) {
        const item = data[i];
        const obj = {};
        
        obj.name = item[0];
        obj.day = item[1];
        obj.sales = item[2];

        obj.nameFieldName = finalCols.name.fields
        obj.nameFieldValue = finalCols.name.values[item[0]]
        obj.nameDayName = finalCols.day.fields
        obj.nameDayValue = finalCols.day.values[item[1]]
        source.push(obj);
      }
    var timer;
    let clickCount
    const applyFiltersForSingleWidget = (event) =>{


        clickCount += 1;

        setTimeout(() => {
            if (clickCount === 1) onSingleClick(event);
            else if (clickCount === 2) onDoubleClick(event);

            clickCount = 0;
        }, 300);





    }

     const onSingleClick = (event) =>{


        if(event.data){
          let fieldName = event.data.data.nameFieldName;
          let nameFieldValue = event.data.data.nameFieldValue;
          
          let fieldName1 = event.data.data.nameDayName;
          let nameFieldValue1 = event.data.data.nameDayValue;
          
          
          let fitlerData = {key:fieldName,operator:"=",value:nameFieldValue};
  
          let fitlerData1 = {key:fieldName1,operator:"=",value:nameFieldValue1};
  
          dispatch(addFilters(fitlerData))
          dispatch(addFilters(fitlerData1))
          
         
        }
    }

    const  onDoubleClick = (event) => {

        if(event.data){
            const currentFilters = [];
            let fieldName = event.data.data.nameFieldName;
            let nameFieldValue = event.data.data.nameFieldValue;

            let fieldName1 = event.data.data.nameDayName;
            let nameFieldValue1 = event.data.data.nameDayValue;


            let fitlerData = {key:fieldName,operator:"=",value:nameFieldValue};

            let fitlerData1 = {key:fieldName1,operator:"=",value:nameFieldValue1};

            currentFilters.push(fitlerData);
            currentFilters.push(fitlerData1);

            if(dashboardLink && dashboardLink!=''){
                const url = `${window.location.origin.toString()}/dashboards/`+dashboardLink+"?query="+JSON.stringify(currentFilters)+"&startDate="+date.startDate+"&endDate="+date.endDate;
                window.open(url,"_blank");
            }

            //dispatch(addFilters(fitlerData))
            //dispatch(addFilters(fitlerData1))


        }

    }

  
  let chart;

  class RenderChart extends React.Component {
   
   


    render() {
      return (
        <CardBody>
       
       {config.type && config.type ==='square' && <Chart
          height={height}
          width = {width}
          data={source}
          scale={finalCols}
          onClick={(e: IEvent, chart: G2Instance) => {
            applyFiltersForSingleWidget(e);
        }}
          forceFit
        >
          <Axis
            name="name"
            grid={{
              align: 'center',
              lineStyle: {
                lineWidth: 1,
                lineDash: null,
                stroke: '#f0f0f0',
              },
              showFirstLine: true,
            }}
          />
          <Axis
            name="day"
            grid={{
              align: 'center',
              lineStyle: {
                lineWidth: 1,
                lineDash: null,
                stroke: '#f0f0f0',
              },
            }}
          />
          <Tooltip />
          <Geom
            type="polygon"
            position="name*day"
            color={['sales', '#BAE7FF-#1890FF-#0050B3']}
            style={{
              stroke: '#fff',
              lineWidth: 1,
            }}
          >
            <Label
              content="sales"
              offset={-2}
              textStyle={{
                fill: '#fff',
                fontWeight: 'bold',
                shadowBlur: 2,
                shadowColor: 'rgba(0, 0, 0, .45)',
              }}
            />
          </Geom>
        </Chart>}

        {config.type && config.type ==='hexbin' && <Chart height={400} data={data} forceFit>
          <Tooltip showTitle={false} crosshairs={false} />
          <Axis
            name="x"
            grid={{
              lineStyle: {
                stroke: "#d9d9d9",
                lineWidth: 1,
                lineDash: [2, 2]
              }
            }}
          />
          <Legend offset={40} />
          <Geom
            type="polygon"
            position="x*y"
            style={{
              lineWidth: 1,
              stroke: "#fff"
            }}
            color={["count", "#BAE7FF-#1890FF-#0050B3"]}
          />
        </Chart>}

      </CardBody>
      );
    }
  }
  return RenderChart;
}  

class HeatMap extends React.Component {

    render() {
        const { width, height,dispatch,timeFromat,dashboardLink,date} = this.props

        const RenderChart = getComponent(this.props.data,this.props.chartConfig,width,height,dispatch,timeFromat,dashboardLink,date);
        
        return (
          
            <RenderChart />
          
        );
      }
}
export default connect()(sizeMe({ monitorHeight: true })(HeatMap));

