import { takeEvery,put } from 'redux-saga/effects'
import integrationService from '../../api/integrationService'
import * as ActionTypes from '../actions/integrationsActions';

function* fetchIntegrations() {
    const json = yield integrationService.getAllIntegrations();  
    yield put({ type: ActionTypes.PUT_INTEGRATIONS, json: json.data });
}

function* callSaveIntegrations(action){
    try{
       const data = yield integrationService.enableIntegrationForCompany(action.payload);  
        yield put({ type: ActionTypes.COMPLETE_TASK,json:'Success' });
        yield put({ type: ActionTypes.SAVE_COMPLETED});
    }catch(err){
        yield put({ type: ActionTypes.COMPLETE_TASK, json: {data:"unable to load data"} });
    }
   
}

export  function* fetchIntergrationsWatcher(){
     yield takeEvery(ActionTypes.GET_INTEGRATIONS, fetchIntegrations)
}

export  function* saveIntegrations(){
    yield takeEvery(ActionTypes.SAVE_INTEGRATIONS, callSaveIntegrations)
}


  