import React, {useEffect, useState} from 'react'
import Panel from "../../../shared/components/Panel";
import {Card, CardBody, Col, Row} from "reactstrap";
import {ElasticQueryBuilder} from "../../../utils/ElasticQueryBulider";
import dashboardService from "../../../api/dashboardService";
import {ElasticResponse} from "../../../shared/helpers/elastic_reponse";
import BarChart from "../../Charts/BizCharts/BarChart";
import * as widgetHelper  from "../../../shared/helpers/widget_helper"
import ColumnChart from "../../Charts/BizCharts/ColumnChart";
import LineChart from "../../Charts/BizCharts/LineChart";
import AreaChart from "../../Charts/BizCharts/AreaChart";
const ChartContainer = (props) =>{

    const {date} = props;
    const anaytics = [
        "Mentions Over Time By Source",
        "Mentions Over Time By Category",
        "Sentiment Analysis Over Time",
        "Top Sources",
        "Social Reach",
        "NON Social Reach"

    ]

    const listItems = anaytics.map((number) =>
        <DisplaySingleChart category = {props.category}  searchString={props.searchString}  date = {date} title = {number}/>
    );

    return (<Row>

        {listItems}

    </Row>)




}



export const DisplaySingleChart = (props) => {

    const {date,searchString,category} = props

    const [chartData,setChartData] = useState([])
    let query = [];
    let filterQuery = [];
    if(category!='all'){
        filterQuery.push({key:'category',operator:'=',value:category})
    }
    if(props.title === 'Mentions Over Time By Source'){
        query = [{
            metric:[{type:'count',field:'source.keyword',id:0}],
            bucket:[{id:"0",type:'terms',field:'source.keyword',settings:{size:1000}},{type:'date_histogram',field:'@timestamp',id:"1"}]
        }]
    }
    if(props.title === 'Mentions Over Time By Category'){
        query = [{
            metric:[{type:'count',field:'category.keyword',id:0}],
            bucket:[{id:"0",type:'terms',field:'category.keyword',settings:{size:1000}},{type:'date_histogram',field:'@timestamp',id:"1"}]
        }]
    }
    if(props.title === 'Sentiment Analysis Over Time'){
        query = [{
            metric:[{type:'count',field:'statementCss.keyword',id:0}],
            bucket:[{id:"0",type:'terms',field:'statementCss.keyword',settings:{size:1000}},{type:'date_histogram',field:'@timestamp',id:"1"}]
        }]
    }
    if(props.title === 'Top Sources'){
        query = [{
            metric:[{type:'count',field:'source.keyword',id:0}],
            bucket:[{id:"0",type:'terms',field:'source.keyword',settings:{size:10}}]
        }]
    }
    if(props.title === 'Social Reach'){
        query = [{
            metric:[{type:'sum',field:'socialReach',id:0}],
            bucket:[{type:'date_histogram',field:'@timestamp',id:"1"}]
        }]
    }

    if(props.title === 'NON Social Reach'){
        query = [{
            metric:[{type:'count',field:'source.keyword',id:0}],
            bucket:[{type:'date_histogram',field:'@timestamp',id:"1"}]
        }]
        filterQuery.push({key:'socialReach',operator:'notexists',value:''})

    }




    let queryBuilder = new ElasticQueryBuilder({
        timeField: '@timestamp',
        esVersion: '7.9',
    })

    var finalQuery = queryBuilder.build(query[0],filterQuery,searchString);

    let payload = JSON.stringify(finalQuery);
    let finalData =  payload.replaceAll("$timeFrom", date.startDate);
    let fainlData1 = finalData.replaceAll("$timeTo", date.endDate);
    let finalData2 = fainlData1.replaceAll("$__interval",'1d')
    let panelJson = {};
    panelJson['elsQuery'] = finalData2;
    panelJson['type'] = 'brand';
    var data = {'panelData':JSON.stringify(panelJson)};

    useEffect(() => {
        dashboardService.showPanelPreivew(data).then((response) => {
            if (response.data.data) {
                var responses = {responses:[response.data.data]}

                const er = new ElasticResponse(query, responses);
                let esResponse =  er.getTimeSeries('NA');
                setChartData(esResponse.data);
            }
        });
    },[props.title,date,searchString,category])



    return (
     <Col xs = {12} md = {4} xl={4} style = {{marginBottom :'12px'}}>
         <Card style = {{height:'300px'}}>
             <h4 className="card-title" style={{marginTop:'11px',fontSize:'15px',fontWeight:'bold',marginLeft:'12px'}}>{props.title}</h4>

                 {props.title && props.title === 'Mentions Over Time By Source' &&
                    <LineChart  date = "" timeFromat = {'DD/MM'} data = {chartData }   chartConfig = {widgetHelper.chartOptions.line} height = {200} width = {450}/>
                 }
                 {props.title && props.title === 'Mentions Over Time By Category' &&
                    <AreaChart  date = "" timeFromat = {'DD/MM'} data = {chartData }   chartConfig = {widgetHelper.chartOptions.area} height = {130} width = {650}/>
                 }
                 {props.title && props.title === 'Sentiment Analysis Over Time' &&
                    <ColumnChart  date = "" timeFromat = {'DD/MM'} data = {chartData }   chartConfig = {widgetHelper.chartOptions.column} height = {180} width = {450}/>
                 }
                 {props.title && props.title === 'Top Sources' &&
                     <BarChart  date = "" timeFromat = {'DD/MM'} data = {chartData }   chartConfig = {widgetHelper.chartOptions.bar} height = {120} width = {450}/>
                 }
                 {props.title && props.title === 'Social Reach' &&
                      <LineChart  date = "" timeFromat = {'DD/MM'} data = {chartData }   chartConfig = {widgetHelper.chartOptions.line} height = {180} width = {450}/>
                 }

             {props.title && props.title === 'NON Social Reach' &&
                 <LineChart  date = "" timeFromat = {'DD/MM'} data = {chartData }   chartConfig = {widgetHelper.chartOptions.line} height = {180} width = {450}/>
             }



         </Card>

     </Col>

    )
}

export default  ChartContainer;