export function addPanelFilter() {
  return {
    type: ADD_PANEL_FILTER,
   
  };
}

export function changeSize(event,metric,currnetIndex,parentIndex,metricType,aggType){
  return {
    type: CHANGE_SIZE,
    event:event,
    metric: metric,
    currnetIndex: currnetIndex,
    parentIndex: parentIndex,
    metricType:metricType,
    aggType:aggType
  };
}

export function updatePanelFilter(payload){
    return {
        type: UPATE_PANEL_FILTER,
        payload:payload
      };
}

export function deletePanelFilters(index){
    return {
        type: DELETE_PANEL_FILTER,
        payload: index
      };
}

export function updatePanel(payload) {
  return {
    type: UPDATE_PANEL,
    panel: payload,
  };
}

export function updateChartConfig(chartType,name,value){
  return {
    type: UPDATE_CHART_CONFIG,
    chartType: chartType,
    name:name,
    value:value
  };
}

export function addThreadSholds(chartType,value){
    return {
        type: ADD_THREASHOLD,
        chartType: chartType,
        value:value
    };
}



