export const  calculateTimeFormat =(startDate,endDate) =>{

    var a = moment(startDate);
    var b = moment(endDate);

    var diff = b.diff(a,'days');
    if(diff==0){
        return "hh:mm";
    }else if(diff>=1 && diff<=30){
        return "MM/DD hh:mm";
    }else if(diff>=30 && diff<=365){
        return "MM/DD";
    }
    else if(diff>=365){
        return "yyyy/MM/DD";
    }

}

export const getFormatter = (value,units) =>{
    if(units && units === 'Bytes'){
        return convert(value).from('b').to('b').toFixed(2)
    }
    if(units && units === 'KB'){
        return convert(value).from('b').to('Kb').toFixed(2)
    }
    if(units && units === 'MB'){
        return convert(value).from('b').to('Mb').toFixed(2)
    }
    if(units &&  units === 'GB'){
        return convert(value).from('b').to('Gb').toFixed(2)
    }
    if(units && units === 'Number'){
        return numeral(value).format('0,0')
    }
    return value;

}

export const getTextColor = (theme) =>{
    if(theme === 'theme-dark'){
        return 'white';
    }else{
        return 'black';
    }
}

export const textTruncate =(text,number)=>{

    if (text.length <= number) {
        return text
    }

    return text.slice(0, number) + '...'

}
