import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SidebarLink from './SidebarLink';
import SidebarCategory from './SidebarCategory';
import DashboardTree from '../../Dashboards/Tree/DashboardTree'
class SidebarContent extends Component {
  static propTypes = {
    changeToDark: PropTypes.func.isRequired,
    changeToLight: PropTypes.func.isRequired,
    onClick: PropTypes.func.isRequired,
    sidebarCollapse: PropTypes.bool,
  };

  static defaultProps = {
    sidebarCollapse: false,
  }

  hideSidebar = () => {
    const { onClick } = this.props;
    onClick();
  };

  render() {
    const { changeToLight, changeToDark, sidebarCollapse } = this.props;

    return (
      <div className="sidebar__content">
        
        <DashboardTree/>

        <ul className="sidebar__block">
          
          <SidebarLink title="Dashboard Default" icon="home" route="/dashboard_default" onClick={this.hideSidebar} />

        </ul>


        <ul className="sidebar__block">
          <SidebarLink title="Log Out" icon="exit" route="/log_in" />
        </ul>

      </div>
    );
  }
}

export default SidebarContent;
