import React, {useEffect, useState} from 'react';
import {
    Button,
    Popover,
    PopoverHeader,
    PopoverBody,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem, ButtonGroup, Col, Row
} from 'reactstrap';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import Emitter from "../../../events/Events";
import {applyIndexType} from "../../../redux/actions/historicalActions";
import {useDispatch} from "react-redux";
const FieldChooser = (props) =>{
    const [indexChooser, setIndexChooser] = useState(false);

    const [indexType,setIndexType] = useState('Events')

    const [finalFields,setFinalFields] = useState([])
    const dispatch = useDispatch();
    useEffect(()=>{
        setFinalFields(props.fields);
    },[props.fields])

    const addFieldToTable = (value) =>{
        Emitter.emit('ADD_FIELD',value);
    }
    const searchFields = (event)=>{

        const tempFields = props.fields.filter(entry => Object.values(entry).some(val => typeof val === "string" && val.includes(event.target.value)));
        setFinalFields(tempFields)
    }
    const setType = (type) =>{
        setIndexType(type)
        dispatch(applyIndexType(type))
    }
    const toggle = () => setIndexChooser(!indexChooser);
    return (<div className="sideBar">


            <div className="dscFieldChooser">
                <div>
                    <Dropdown className="dropDownButtonWidth" isOpen={indexChooser} toggle={toggle}>
                        <DropdownToggle caret>
                            {indexType}
                        </DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem onClick = {()=>setType('Raw')}>Raw</DropdownItem>
                            <DropdownItem onClick = {()=>setType('Events')}>Events</DropdownItem>
                            <DropdownItem onClick = {()=>setType('Alerts')}>Alerts</DropdownItem>

                        </DropdownMenu>
                    </Dropdown>



                </div>

                <div className="searchContainer">
                    <div className="form__form-group-field">


                        <input type = "text" className="form-control" onChange = {searchFields} placeholder="Search field names"/>
                     </div>
                </div>
                <div className="availableTitleHeader">
                    <h4>Available fields</h4>
                </div>

                <div className="availableFieldsContainer">
                        <DisplayFields addFieldToTable = {addFieldToTable} fields = {finalFields}/>
                </div>
            </div>




    </div>)
}

const DisplayFields = (props) =>{
    const {fields,addFieldToTable} = props;
    return <ul className="dscFieldList">

        {fields.map((value)=>{
            return (<li className="fieldli">
              <Row>
                  <Col md = {9}>
                      <span className="fieldName" style={{width:'80%'}}>{value.value.replace(".keyword","")}</span>
                  </Col>
                  <Col md = {3} className="actionContainer" >

                          <AddCircleIcon fontSize="small" onClick={()=>addFieldToTable(value.value)} color="primary"/>


                  </Col>
              </Row>

            </li>)
        })}

    </ul>
}

export default  FieldChooser;