    translateAlong =(path)=> {
        var l = path.getTotalLength();
        return function(i) {
            return function(t) {
                // Put in try/catch because sometimes floating point is stupid..
                try {
                    var p = path.getPointAtLength(t*l);
                    return "translate(" + p.x + "," + p.y + ")";
                } catch(err){
                    console.log("Caught exception.");
                    return "ERROR";
                }
            }
        }
    }

     handleParticle = (msg, srcPoint) =>{
        var i = 0;
        var x = srcPoint['x'];
        var y = srcPoint['y'];

        svg.append('circle')
            .attr('cx', x)
            .attr('cy', y)
            .attr('r', 1e-6)
            .style('fill', 'none')
            //.style('stroke', d3.hsl((i = (i + 1) % 360), 1, .5))
            .style('stroke', msg.color)
            .style('stroke-opacity', 1)
            .transition()
            .duration(2000)
            .ease(Math.sqrt)
            .attr('r', 35)
            .style('stroke-opacity', 1e-6)
            .remove();

        //d3.event.preventDefault();
    }

     calcMidpoint = (x1, y1, x2, y2, bend) => {
        if(y2<y1 && x2<x1) {
            var tmpy = y2;
            var tmpx = x2;
            x2 = x1;
            y2 = y1;
            x1 = tmpx;
            y1 = tmpy;
        }
        else if(y2<y1) {
            y1 = y2 + (y2=y1, 0);
        }
        else if(x2<x1) {
            x1 = x2 + (x2=x1, 0);
        }

        var radian = Math.atan(-((y2-y1)/(x2-x1)));
        var r = Math.sqrt(x2-x1) + Math.sqrt(y2-y1);
        var m1 = (x1+x2)/2;
        var m2 = (y1+y2)/2;

        var min = 2.5, max = 7.5;
        //var min = 1, max = 7;
        var arcIntensity = parseFloat((Math.random() * (max - min) + min).toFixed(2));

        if (bend === true) {
            var a = Math.floor(m1 - r * arcIntensity * Math.sin(radian));
            var b = Math.floor(m2 - r * arcIntensity * Math.cos(radian));
        } else {
            var a = Math.floor(m1 + r * arcIntensity * Math.sin(radian));
            var b = Math.floor(m2 + r * arcIntensity * Math.cos(radian));
        }

        return {"x":a, "y":b};
    }

     handleTraffic =(msg, srcPoint, hqPoint) => {
        var fromX = srcPoint['x'];
        var fromY = srcPoint['y'];
        var toX = hqPoint['x'];
        var toY = hqPoint['y'];
        var bendArray = [true, false];
        var bend = bendArray[Math.floor(Math.random() * bendArray.length)];

        var lineData = [srcPoint, this.calcMidpoint(fromX, fromY, toX, toY, bend), hqPoint]
        var lineFunction = d3.svg.line()
            .interpolate("basis")
            .x(function(d) {return d.x;})
            .y(function(d) {return d.y;});

        var lineGraph = svg.append('path')
            .attr('d', lineFunction(lineData))
            .attr('opacity', 0.8)
            .attr('stroke', msg.color)
            .attr('stroke-width', 4)
            .attr('fill', 'none');

        if (this.translateAlong(lineGraph.node()) === 'ERROR') {
            console.log('translateAlong ERROR')
            return;
        }

        var circleRadius = 6

        // Circle follows the line
        var dot = svg.append('circle')
            .attr('r', circleRadius)
            .attr('fill', msg.color)
            .transition()
            .duration(700)
            .ease('ease-in')
            .attrTween('transform', this.translateAlong(lineGraph.node()))
            .each('end', function() {
                d3.select(this)
                    .transition()
                    .duration(500)
                    .attr('r', circleRadius * 2.5)
                    .style('opacity', 0)
                    .remove();
            });

        var length = lineGraph.node().getTotalLength();
        lineGraph.attr('stroke-dasharray', length + ' ' + length)
            .attr('stroke-dashoffset', length)
            .transition()
            .duration(700)
            .ease('ease-in')
            .attr('stroke-dashoffset', 0)
            .each('end', function() {
                d3.select(this)
                    .transition()
                    .duration(100)
                    .style('opacity', 0)
                    .remove();
            });
    }

     addCircle = (msg,srcLatLng) => {

       let  circleCount = cricles.getLayers().length;
       let circleArray = cricles.getLayers();

        // Only allow 50 circles to be on the map at a time
        if (circleCount >= 50) {
            cricles.removeLayer(circleArray[0]);
        }

         var circle =  L.circle(srcLatLng, 50000, {
             color: msg.color,
             fillColor: msg.color,
             fillOpacity: 0.2,
         }).addTo(cricles);

         var str = [];
         str.push(msg.source_country_name);
         str.push(msg.destination_country_name);
         str.push(msg.source_ip);
         str.push(msg.destination_ip);
         circle.bindPopup(str.join(','));
         //circle.on('click', getInfoContet );





    }

    handleMessage = (msg) =>{
        var srcLatLng = new L.LatLng(msg.source_latitude, msg.source_longitude);
        var hqPoint = new L.LatLng(msg.destination_latitude, msg.destination_longitude);
        var srcPoint = map.latLngToLayerPoint(srcLatLng);
        var hqPoint1 = map.latLngToLayerPoint(hqPoint);

        this.addCircle(msg, srcLatLng);
        this.handleParticle(msg, srcPoint);
        this.handleTraffic(msg, srcPoint, hqPoint1, srcLatLng);
        let tors = [...this.state.tors];
        let attacks = [...this.state.attacks];
        let services = [...this.state.services];
        let sourceCountries = [...this.state.sourceCountries];
        let destinationCountries = [...this.state.destinationCountries];
        let threatData = [...this.state.threatData]
        tors.unshift({torIp:msg.destination_ip,isp:msg.ISP,fraudScore:msg.fraud_score})
        if(tors.length>50){
           tors.pop();
        }
        if(attacks.length>50){
            attacks.pop();
        }
        if(services.length>50){
            services.pop();
        }
        if(sourceCountries.length>50){
            sourceCountries.pop();
        }
        if(destinationCountries.length>50){
            destinationCountries.pop();
        }
        if(threatData.length>50){
            threatData.pop();
        }

        let serviceCount = this.state.services.find(e=> e.app === msg.app )

        if(serviceCount != undefined){
            serviceCount.count+=1;
        }else{
            services.unshift({count:1,color:msg.color,app:msg.app,port:msg.source_port});
        }

        let sourceCountry  = this.state.sourceCountries.find(e=> e.country_name === msg.source_country_name )
        let destCountry  =  this.state.destinationCountries.find(e=> e.country_name === msg.destination_country_name )

        if(sourceCountry!=undefined){
            sourceCountry.count+=1;
        }else{
            sourceCountries.unshift({path:"/obelus/img/flags/"+msg.source_country_code+".png",country_name:msg.source_country_name,count:1})
        }
        if(destCountry!=undefined){
            destCountry.count+=1;
        }else{
            destinationCountries.unshift({path:"/obelus/img/flags/"+msg.destination_country_code+".png",country_name:msg.destination_country_name,count:1})
        }

        threatData.unshift(msg)
        attacks.unshift({attack:msg.attack})
        this.setState({ tors:tors });
        this.setState({ attacks:attacks });
        this.setState({ services:services });
        this.setState({ sourceCountries:sourceCountries });
        this.setState({ destinationCountries:destinationCountries });
        this.setState({ threatData:threatData });
    }

