import React,{useState,useEffect} from 'react';
import { Col, Container, Row,ButtonToolbar,Button,Card,CardBody } from 'reactstrap';
import { withTranslation } from 'react-i18next';
import { Table } from "react-fluid-table";
import { connect } from 'react-redux';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from 'mdi-react/AddIcon';
import _ from "lodash";
import MagnifyIcon from 'mdi-react/MagnifyIcon';
import dashboardService from '../../../api/dashboardService'
import  EditDashboard from '../components/EditDashboard';
import { useDispatch, useSelector } from 'react-redux';
import {openDashboardModel} from '../../../redux/actions/dashboardActions'
import {loadFetchLayout} from '../../../redux/actions/dashboardLayoutActions'
import { ToastContainer, toast } from 'react-toastify';
import {Link, useHistory} from 'react-router-dom'
import DashboardModel from '../../../models/DashboardModel';


const ViewDashboards = () => {

  
  
  const dispatch = useDispatch()

  const openCreatePanel = () =>{
    const data = {
      id: 0,
      uid: "",
      title: "dashboarTitle",
      time: "",
      refresh: false,
      isSidePanelOpen: true,
      panels: [],
      isEditDashboardOpen: true,
      currentPanelId: 0,
      newDashboard:false,
      dashboardCategories:[],
      reportCategories:[],
      permissions:[]
      
    };
    const dashboardModel = new DashboardModel(data);
    dispatch(openDashboardModel(dashboardModel));
  } 

  
 return (
 <Container className="dashboard" style = {{paddingTop:'100px'}}>
   <Row xs="2">
          <Col>
              <h3 className="page-title">Dashboards</h3>
          </Col>
          <Col>
                <Button onClick = {openCreatePanel} style = {{float:'right'}} color="primary">New Dashboard <AddIcon/> </Button>
          </Col>
    </Row>

    <Row>
          <DashboardsList/>
    </Row>
     


  </Container>
 )}

 

 const DashboardsList = (props) =>{
  const dispatch = useDispatch()
  const history = useHistory();
  const openDashbaord = (event,currentDashboard)=>{
    event.preventDefault();
    //const data = {id:currentDashboard.id}
    //dispatch(loadFetchLayout(data));
    history.push("/dashboard/"+currentDashboard.id);
   }

  const columns = [
    {
      key: "id",
      header: "ID",
      sortable: true,
      
    },
    {
      key: "name",
      header: "Name",
      sortable: true,
      content: ({ row }) => <a href = "#" onClick = {(event)=>{openDashbaord(event,row)}} > {row.name}</a>
    
    },
    {
      key: "description",
      header: "Description",
      sortable: true,
      
    },
    {
      key: "updatedAt",
      header: "Modified",
      sortable: true,
      
    },
    {
      key: "categoryName",
      header: "Category",
      sortable: true,
      
    },
    {
      key: "delete",
      header: "Delete",
      content: ({ row }) => <DeleteIcon style = {{cursor:'pointer'}} color = {"error"} onClick = {()=>deleteDshboard(row.id)}/>
      
    },
   
  ];
  const deleteDshboard = (id) =>{
    dashboardService.deleteDashboard(id).then((responseData)=>{
        if(responseData.status === 200){
          toast.success('Dashboard was deleted successfully', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          refreshView();
        }
      }).catch((error) => {
        toast.error(error.data, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  }
 

  const [rowData,setRowData] = useState([]);

  const [originalData,setOriginalData] = useState([]);
  

    useEffect(() => {
        fetchAllDashboardsData();
     }, []);

    const refreshView = () =>{
      fetchAllDashboardsData();
    } 

  const filterData = (event) =>{
   
    if(event.target.value!=''){
      let searchString = event.target.value.toLowerCase();
      const filtered = rowData.filter(entry => Object.values(entry).some(val => typeof val === "string" && val.toLowerCase().includes(searchString)));
      setRowData(filtered);
    }else{
      setRowData(originalData);
    }
   
    

    //console.log(event.target.value);
  }

const  fetchAllDashboardsData = () =>{
    
    dashboardService.getAllDashboards().then((response) => {
        setRowData(response.data);
        setOriginalData(response.data);
    });
}

    return (
      <Col md={12} sm={12}>
<ToastContainer
      position="top-right"
      hideProgressBar={false}
      autoClose={false}
      newestOnTop={true}
      closeOnClick={false}
      draggable={false}
      rtl={false}
    />
      <EditDashboard refreshView = {refreshView}/>

      <Card>
          <SearchBar filterData = {filterData}/>    
          <Table
      data={rowData}
      columns={columns}
      itemKey={row => row.id}
      rowHeight={50}
      tableHeight={600}
     
      sortColumn="name"
      sortDirection="ASC"
    />

      </Card> 
      </Col>
    );
 }

 const SearchBar = (props) =>{

    return (<div style = {{paddingTop:'12px',paddingLeft:'12px',paddingBottom:'12px'}}>
              <div className="inbox__emails-control-search">
                <input placeholder="Search" onChange = {props.filterData}/>
                  <div className="inbox__emails-control-search-icon"><MagnifyIcon /></div>
                </div>
            </div>
          )

 }

 const TableHeader = (props) =>{

    return (<thead>
      <tr>
     {props.cols.map(name => (
      
     <th>{name}</th>
   
     ))} 
     </tr>
   </thead>);

 }



export default connect(state => ({
  rtl: state.rtl,
}))(withTranslation('common')(ViewDashboards));
