import React, {useEffect, useState} from 'react'
import {useDispatch, useSelector} from "react-redux";
import { DateRangePicker } from 'react-date-range';
import {
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    Row,
    Col,
    Input,
    InputGroupAddon,
    Button,
    InputGroup,
    Badge
} from 'reactstrap'
import {

    isSameDay,

    subDays,
    subMinutes,
    subHours
} from 'date-fns';
import {changeDateSettings} from "../../../redux/actions/dashboardActions";
import  moment from 'moment'
const staticRangeHandler = {
    range: {},
    isSelected(range) {
        const definedRange = this.range();
        return (
            isSameDay(range.startDate, definedRange.startDate) &&
            isSameDay(range.endDate, definedRange.endDate)
        );
    },
};

export function createStaticRanges(ranges) {
    return ranges.map(range => ({ ...staticRangeHandler, ...range }));
}


export const defaultStaticRanges = createStaticRanges([

    {
        label: 'Today',
        range: () => ({
            startDate: subHours(new Date(),24 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Yesterday',
        range: () => ({
            startDate: subDays(new Date(),2 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 7 Days',
        range: () => ({
            startDate: subDays(new Date(),7 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 30 Days',
        range: () => ({
            startDate: subDays(new Date(),30 ),
            endDate:new Date()
        }),
    },

]);
export const BrandHeader = (props) =>{

    const [currentData,setCurrentData] = useState([]);
    const [timeRangeDropDown, setTimeRangeDropDown] = useState(false);

    const [searchQuery,setSearchQuery] = useState("*");

    const toggle1 = () => setTimeRangeDropDown(prevState => !prevState);
    const [label,setLabel] = useState("")
    const changeDateHandler =  (start, end,label)=>{

        const date = {startDate:start.selection.startDate.getTime(),endDate:start.selection.endDate.getTime()}
        const lable = moment(start.selection.startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(start.selection.endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
        setLabel(lable)
        props.changeDateSettings(date);

        const finalDateObject = {
            startDate: start.selection.startDate,
            endDate:  start.selection.endDate,
            key: 'selection'
        }

        setCurrentData([finalDateObject]);
    }

    useEffect(() => {
        if(currentData.length!=0){
            const lable = moment(currentData[0].startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(currentData[0].endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
            setLabel(lable)
        }else{
            const finalDateObject = {
                startDate: new Date(props.date.startDate),
                endDate:  new Date(props.date.endDate),
                key: 'selection'
            }
            setCurrentData([finalDateObject]);
        }

    },[currentData]);

    const changeHandler = (event)=>{
        setSearchQuery(event.target.value);
    }
    const applySearchHandler = ()=>{
        props.applySearchFilter(searchQuery);
    }

    return (

        <Row style={{paddingTop:'14px'}}>
            <Col xl={4} sm={4} md={8} lg={4}>
                <h3 className="page-title">Brand Monitoring

                    {props.keywords.map((value)=>{
                       return (<Badge color="success" style={{marginLeft:'12px'}}> {value}</Badge> )
                    })}

                </h3>
            </Col>

            <Col xl={4} sm={4} md={8} lg={4}>
                <InputGroup>

                    <Input placeholder="search ...." onChange={changeHandler} />
                    <InputGroupAddon addonType="append" style = {{height:"53px"}}>
                        <Button color="primary" onClick = {applySearchHandler}>Search</Button>
                    </InputGroupAddon>
                </InputGroup>
            </Col>


            <Col xl={4} sm={4} md={4} lg={4}>
                <div style={{float:'right'}}>
                    <Dropdown isOpen={timeRangeDropDown} toggle={toggle1}>
                        <DropdownToggle caret>
                            {label}
                        </DropdownToggle>
                        <DropdownMenu style = {{width:"600px",marginLeft: '-85%',top:'35px'}}>
                            <DateRangePicker
                                onChange={(item,label) => changeDateHandler(item,label)}
                                showSelectionPreview={true}
                                moveRangeOnFirstSelection={false}

                                ranges={currentData}
                                direction="vertical"
                                scroll={{ enabled: true }}
                                staticRanges={defaultStaticRanges}
                            />
                        </DropdownMenu>
                    </Dropdown>
                </div>
            </Col>
        </Row>


       )
}

