import React, {useState,useEffect} from 'react';
import {Card, CardBody, Col, Container, Row,Button} from "reactstrap";
import {Field, reduxForm} from "redux-form";
import { AsyncTypeahead } from 'react-bootstrap-typeahead';
import moment from "moment";
import DateRangePicker from "react-bootstrap-daterangepicker";
import renderSelectField from "../../../shared/components/form/Select";
import '../../../../node_modules/react-bootstrap-typeahead/css/Typeahead.css';
import caseService from '../../../api/caseService'
import {useDispatch} from "react-redux";
import {openEntity} from '../../../redux/actions/threatExploreAction'
import queryString from 'query-string';
const Header = (props) =>{


    const currenData = new Date();
    const startDate = currenData.setDate(currenData.getDate()-90)
     
    const filterBy = () => true;

    const dispatch = useDispatch();

    const [isLoading, setIsLoading] = useState(false);
    const [options, setOptions] = useState([]);

    const [malwareSearchType,setMalwareSearchType] = useState("");

    const [entityType,setEntityType] = useState("");

    const [malwareCategories,setMalwareCategories] = useState([])

    const [fieldVale,setFieldValue] = useState("");

    const [date,setDate] = useState({startDate:startDate,endDate:new Date().getTime(),label:'Past 30 Days'})

    const onEntityChangeHandler = (event) =>{
        console.log(event.value);
        setEntityType(event.value);
    }
    useEffect(() => {
        let params = queryString.parse(window.location.search);
        if(params.entityType && params.entityValue){
            setEntityType(params.entityType)
            setFieldValue(params.entityValue)

            var object = new Object();
            object['entityType'] = {label:params.entityType,value:params.entityType};
            object['entityType'] = {label:params.entityType,value:params.entityType};
            props.initialize(object);
            
            const data = {fieldType:params.entityType,fieldValue:params.entityValue,startDate:date.startDate,endDate:date.endDate,malwareSearchTye:malwareSearchType}

            caseService.getInfo(data).then((responseData) =>{
                const threatExploreData = {type:params.entityType,entityData:responseData.data,startDate:date.startDate,endDate:date.endDate,entityValue:params.entityValue }
                dispatch(openEntity(threatExploreData))
            })

        }
        
        console.log(params);
    }, [])

    const onMalwareSearchTypeChangeHandler = (event) =>{

        setMalwareSearchType(event.value);
        if(event.value === 'malwareCategory' && malwareCategories.length === 0){
            caseService.loadMalwareCategories().then((responseData)=>{
                const tempData = [];
                for(let i=0;i<responseData.data.length;i++){
                    tempData.push({label:responseData.data[i],value:responseData.data[i]});
                }
                setMalwareCategories(tempData)

            })
        }

    }

    const typeHeadChanged = (event)=>{
            if(event.length!=0){
                setFieldValue(event[0]);
            }
    }

    const onDateChange = (start,end,label) =>{
        //const {dispatch} = this.props;
        //dispatch(changeDateSettings(start.valueOf(),end.valueOf(),label))
        setDate({startDate:start.valueOf(),endDate:end.valueOf(),label:label})
    }


    const handleSearch = (fieldName,query) => {
        setIsLoading(true);
        const data = {fieldType:fieldName,fieldValue:query}
        
        caseService.autocomplete(data).then((responseData)=>{
            setOptions(responseData.data)
            setIsLoading(false);
        })
    };

    const explore = () =>{

        const data = {fieldType:entityType,fieldValue:fieldVale,startDate:date.startDate,endDate:date.endDate,malwareSearchTye:malwareSearchType}

        caseService.getInfo(data).then((responseData) =>{
            const threatExploreData = {type:entityType,entityData:responseData.data,startDate:date.startDate,endDate:date.endDate,entityValue:fieldVale }
            dispatch(openEntity(threatExploreData))
        })

    }

    return (

        <Row className="topContainer">
            <Card >
                <form className="form" >
                    <Col xs= {3}>
                        <div className="form__form-group">
                            <span className="form__form-group-label">Time Range</span>
                            <div className="form__form-group-field">
                        <DateRangePicker
                            initialSettings={{

                                ranges: {
                                    "Past 5 Minutes": [moment().subtract(5,"minutes").toDate(), moment().toDate()],
                                    "Past 15 Minutes": [moment().subtract(15,"minutes").toDate(), moment().toDate()],
                                    "Past 30 Minutes": [moment().subtract(30,"minutes").toDate(), moment().toDate()],
                                    "Past 1 Hour": [moment().subtract(60,"minutes").toDate(), moment().toDate()],
                                    "Past 4 Hour": [moment().subtract(4,"hours").toDate(), moment().toDate()],
                                    "Past 1 Day": [moment().subtract(24,"hours").toDate(), moment().toDate()],
                                    "Past 2 Days": [moment().subtract(48,"hours").toDate(), moment().toDate()],
                                    "Past 7 Days": [moment().subtract(7,"days").toDate(), moment().toDate()],
                                    "Past 30 Days": [moment().subtract(30,"days").toDate(), moment().toDate()],
                                },
                            }}
                            onCallback = {onDateChange.bind(this)}
                        >
                            <div
                                id="reportrange"
                                className="col-12"
                                style={{

                                    cursor: 'pointer',
                                    padding: '5px 10px',
                                    border: '1px solid #ccc',
                                    width: '100%',
                                }}
                            >
                                <i className="fa fa-calendar"></i>&nbsp;
                                <span>{date.label}</span> <i className="fa fa-caret-down"></i>
                            </div>
                        </DateRangePicker>
                            </div>
                        </div>


                    </Col>

                    <Col xs= {3}>

                        <div className="form__form-group">
                            <span className="form__form-group-label">Entity Type</span>
                            <div className="form__form-group-field">
                                <Field
                                    onChange={onEntityChangeHandler}
                                    name="entityType"
                                    component={renderSelectField}
                                    options={[
                                        { value: 'User', label: 'User' },
                                        { value: 'Device', label: 'Device' },
                                        { value: 'IP', label: 'IP' },
                                        { value: 'Malware', label: 'Malware' },
                                        { value: 'Threat_IOC_IP', label: 'Threat_IOC_IP' },
                                        { value: 'Threat_IOC_DOMAIN', label: 'Threat_IOC_DOMAIN' },
                                        
                                    ]}
                                />
                            </div>
                        </div>


                    </Col>

                    {entityType && entityType==='Malware' &&
                    <Col xs= {3}>
                    <div className="form__form-group">
                        <span className="form__form-group-label">Malware Search Type </span>
                        <div className="form__form-group-field">
                            <Field
                                onChange={onMalwareSearchTypeChangeHandler}
                                name="malwareSearchType"
                                component={renderSelectField}
                                options={[
                                    { value: 'malwareCategory', label: 'Malware Category' },
                                    { value: 'malwareValue', label: 'Malware Hash' }

                                ]}
                            />
                        </div>
                    </div>
                    </Col>

                    }
                    {malwareSearchType && malwareSearchType==='malwareCategory' &&
                         <Col  xs= {3}>
                             <div className="form__form-group">
                                 <span className="form__form-group-label">Value</span>
                                 <div className="form__form-group-field">
                                     <Field

                                         name="malwareCategoryName"
                                         component={renderSelectField}
                                         options={malwareCategories}
                                     />


                                 </div>
                             </div>

                        </Col>
                    }

                    {entityType && entityType != 'Malware' &&

                    <Col xs={3}>
                        <div className="form__form-group">
                            <span className="form__form-group-label">Value</span>
                            <div className="form__form-group-field">
                                <AsyncTypeahead
                                    filterBy={filterBy}
                                    id="async-example"
                                    isLoading={isLoading}
                                    labelKey="login"
                                    onChange={
                                        typeHeadChanged
                                    }
                                    minLength={3}
                                    onSearch={(target) => handleSearch(entityType, target)}
                                    options={options}
                                    placeholder={`Search ${entityType}`}
                                    renderMenuItemChildren={(option, props) => (

                                        <span>{option}</span>

                                    )}
                                />


                            </div>
                        </div>


                    </Col>
                    }

                    {entityType && entityType === 'Malware' &&

                    <Col xs={3}>
                        <div className="form__form-group">
                            <span className="form__form-group-label">Value</span>
                            <div className="form__form-group-field">
                                <AsyncTypeahead
                                    filterBy={filterBy}
                                    id="async-example"
                                    isLoading={isLoading}
                                    labelKey="login"
                                    minLength={3}
                                    onChange={
                                        typeHeadChanged
                                    }
                                    onSearch={(target) => handleSearch(entityType, target)}
                                    options={options}
                                    placeholder={`Search ${entityType}`}
                                    renderMenuItemChildren={(option, props) => (

                                        <span>{option}</span>

                                    )}
                                />


                            </div>
                        </div>


                    </Col>
                    }


                    <Col  xs= {3}>
                        <Button style = {{marginTop:'23px'}} onClick={explore} color="primary">Explore</Button>
                    </Col>

                </form>
            </Card>

        </Row>



    );

}

export default reduxForm({
    form: 'explore_filter_form', 
    enableReinitialize:true
})(Header);
