class Stats extends React.Component {
  render() {
    const { width, height } = this.props.size
    const finalHeight = height- 50;
    const finalWidth = width- 50;
    const history = this.props.history;
    const RenderChart = getComponent(this.props.data,this.props.chartConfig,this.props.query,finalWidth,finalHeight,this.props.filters,this.props.dashboardLink,history,this.props.dispatch);
    return (
     
       <RenderChart />
      
    );
  }
}

export default withRouter(connect()(sizeMe({ monitorHeight: true })(Stats)));