
import React, { useState,useRef,useEffect } from 'react';
import { render } from 'react-dom';
import { AgGridReact, AgGridColumn,ChangeDetectionStrategyType } from '@ag-grid-community/react';
import { ServerSideRowModelModule } from '@ag-grid-enterprise/server-side-row-model';
import { RowGroupingModule } from '@ag-grid-enterprise/row-grouping';
import { MenuModule } from '@ag-grid-enterprise/menu';
import { ColumnsToolPanelModule } from '@ag-grid-enterprise/column-tool-panel';
import { AllModules } from '@ag-grid-enterprise/all-modules';
import dashboardService from '../../api/dashboardService'
import { useDispatch, useSelector } from 'react-redux';
import {showSingleEvent} from '../../redux/actions/panelActions'
import {Field} from "redux-form";
import {CardBody,Row,Col,Card} from "reactstrap";
import Emitter from "../../events/Events";
import '@ag-grid-community/core/dist/styles/ag-grid.css';
import '@ag-grid-community/core/dist/styles/ag-theme-alpine.css';
import '@ag-grid-community/core/dist/styles/ag-theme-alpine-dark.css';
const getColDefs = (props,type) =>{
    const {fields} = props;
    const fieldsNames = ['event_name','event_description','log_type','log_device'];
    const columns = [];

    if(type === 'events'){
        columns.push({headerName:"@timestamp", field: "@timestamp", width: 200,});
        columns.push({
            headerName:"Event Name", 
            field: "event_name",
            width: 200,
            enableRowGroup: true,
            filter: 'agTextColumnFilter'});
        //columns.push({headerName:"event_category", field: "event_category",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter'});
        //columns.push({headerName:"log_type", field: "log_type",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter'});
        //columns.push({headerName:"log_device", field: "log_device",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter'});
    }
    if(type === 'alerts'){
        columns.push({headerName:"@timestamp", field: "@timestamp", width: 200,headerCheckboxSelection: true,  headerCheckboxSelectionFilteredOnly: true,checkboxSelection: true});
        columns.push({headerName:"Alert Name", field: "name", hide:true, width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',rowGroup: true,filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Priority", field: "priority",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Category", field: "rule_category",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Case", field: "case_name", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Event Name",hide:true,field: "event_name", width: 200,enableRowGroup: false,rowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});


        columns.push({headerName:"Assignee",hide:true,field: "assignee", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Status",hide:true,field: "alert_status", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Investigation",field: "investigation_name", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Investigation ID",hide:true, field: "investigation_id", width: 200,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Case ID",hide:true,field: "case_id", width: 200,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
    }

    const tempFiels = fields.filter((value)=>{
        return (value.fieldName.replace(".keyword","")!='event_name' && 
         value.fieldName.replace(".keyword","")!='event_description' &&
         value.fieldName.replace(".keyword","")!='log_type' &&
         value.fieldName.replace(".keyword","")!='log_device')
        
    })


    for(var i=0;i<tempFiels.length;i++){
        let field = tempFiels[i];
        if(!fieldsNames.includes(field.fieldName.replace(".keyword",""))){
            fieldsNames.push(field.fieldName.replace(".keyword",""));
            let hide = true;
            if(field.fieldName.replace(".keyword","") === 'event_category'){
                hide= false;
            }
            if(type === 'alerts'){
                columns.push({
                    headerName:field.fieldName.replace(".keyword",""),
                    field:"rule_data.event_data."+field.fieldName.replace(".keyword",""),
                    hide:hide,
                    width:150,
                    filter: 'agTextColumnFilter',
                    enableRowGroup: true
                })
            }else{
                columns.push({
                    headerName:field.fieldName.replace(".keyword",""),
                    field:field.fieldName.replace(".keyword",""),
                    hide:hide,
                    width:150,
                    filter: 'agTextColumnFilter',
                    enableRowGroup: true
                })
            }

        }
    }

    console.log(fieldsNames)


    

    return columns;
}




const getDefaultColDef = () =>{
    const def = {
        flex: 1,
        minWidth: 100,
        filter: false,
        sortable: true,
        resizable: true,
        floatingFilter: true
    }
      return def;
}
const getSideBar = () =>{
 
}




const LogViewer = (props) =>{

    const {type} = props;

    const date = useSelector((state) => state.dashboards.date);
    const themeName = useSelector((state) => state.theme.className);
   
    let finalTheme = "ag-theme-alpine"
    let selectionType = "single";
    if(type === 'alerts'){
        selectionType = 'multiple'
    }

    if(themeName === 'theme-dark'){
        finalTheme  = "ag-theme-alpine-dark";
    }

   

    const [gridApi, setGridApi] = useState(null);
    const [gridColumnApi, setGridColumnApi] = useState(null);
    const cols = getColDefs(props,type)
    const defaultColDef = getDefaultColDef();
    const sideBar = getSideBar();
    const dispatch = useDispatch();
    const inputEl = useRef(null);


    

    
    const prevCountRef = useRef();
    useEffect(() => {
        prevCountRef.current = date[0].startDate.getTime();




    },[date[0].startDate.getTime()]);

    useEffect(()=>{
        Emitter.on('UPDATE_CASE',(newValue)=>{
            console.log(gridApi)
            if(gridApi){
                for(let i=0;i<gridApi.getSelectedRows().length;i++){
                    const singleRow = gridApi.getSelectedRows()[i];
                    const rowNode = gridApi.getRowNode(singleRow.id);
                    if(newValue.type === 'case'){
                        rowNode.setDataValue('case_name', newValue.data);
                        rowNode.setDataValue('case_id',newValue.id);
                        rowNode.setSelected(false);
                    }else{
                        rowNode.setDataValue('investigation_name', newValue.data);
                        rowNode.setDataValue('investigation_id',newValue.id);
                        rowNode.setSelected(false);
                    }

                }
            }

        })
        return () =>{
            Emitter.off('UPDATE_CASE');
        }
    },[gridApi])

    //console.log(prevCountRef.current.startDate);






    const onGridReady = (params) => {
        setGridApi(params.api);
        setGridColumnApi(params.columnApi);

        var columnToolPanel = params.api.getToolPanelInstance('columns');
        columnToolPanel.setPivotModeSectionVisible(false);
        columnToolPanel.setRowGroupsSectionVisible(false);
        columnToolPanel.setValuesSectionVisible(false);
        params.api.closeToolPanel();
        var datasource =  ServerSideDatasource();
        params.api.setServerSideDatasource(datasource);
       
    }
    const ServerSideDatasource  = () => {
        return {
         getRows :function(params) {
             console.log(JSON.stringify(params.request, null, 1));
            
             params.request['startDate'] = date[0].startDate.getTime()
             params.request['endDate'] = date[0].endDate.getTime();
             params.request['type'] = type;
             params.request['companyName'] = 'technominds';
             dashboardService.loadLogviewerData(params.request).then(response => {

                 params.successCallback(response.data.data, response.data.lastRow);
                 if(response.data.data.length>0){
                     const data = {data:response.data.data[0],type:type};
                     dispatch(showSingleEvent(data))
                 }


             })
         }
        }
     };
    const onSelectionChanged = () =>{
        let selectedRows = gridApi.getSelectedRows();
        console.log(selectedRows);
        if(selectedRows.length!=0){
            const data = {data:selectedRows[selectedRows.length - 1],type:type,selectedRows:selectedRows};
            dispatch(showSingleEvent(data))
        }

    }
    
    if(gridApi && prevCountRef.current && prevCountRef.current != date[0].startDate.getTime() ){
        gridApi.purgeServerSideCache([]);
        var datasource =  ServerSideDatasource();
        gridApi.setServerSideDatasource(datasource);
    }


    return (

        <React.Fragment>


            <div
                ref={inputEl}
                id="myGrid"
                style={{
                    height: '1000px',
                    width: '100%',
                }}
                className={finalTheme}
            >
                <AgGridReact
                    rowHeight={30}
                    rowSelection={selectionType}

                    onSelectionChanged={onSelectionChanged}
                    sideBar={{
                        toolPanels: [
                            {
                                id: 'columns',
                                labelDefault: 'Columns',
                                labelKey: 'columns',
                                iconKey: 'columns',
                                toolPanel: 'agColumnsToolPanel',
                                toolPanelParams: {
                                    suppressRowGroups: true,
                                    suppressValues: true,
                                    suppressPivots: true,
                                    suppressPivotMode: true,
                                    suppressSideButtons: true,
                                    suppressColumnFilter: true,
                                    suppressColumnSelectAll: true,
                                    suppressColumnExpandAll: true,
                                },
                            },
                        ],
                        defaultToolPanel: 'columns',
                    }}
                    sideBar={true}
                    modules={[ServerSideRowModelModule,RowGroupingModule,MenuModule,ColumnsToolPanelModule]}
                    columnDefs={cols}
                    pivotMode={false}
                    rowGroupPanelShow= {'always'}
                    defaultColDef={defaultColDef}
                    rowModelType={'serverSide'}
                    animateRows={true}
                    onGridReady={onGridReady}
                    
                    rowDataChangeDetectionStrategy={ChangeDetectionStrategyType.NONE}

                /></div>

        </React.Fragment>

         )
    
    


}




export default LogViewer;