import React, {useEffect, useState} from 'react'

import detectionsService from "../../../api/detectionsService";
import _ from 'lodash'
import DataSet from "@antv/data-set";
import {
    G2,
    Chart,
    Geom,
    Axis,
    Tooltip,
    Coord,
    Label,
    Legend,
    View,
    Guide,
    Shape,
    Facet,
    Util, Annotation,LineAdvance
} from "bizcharts";
import {Card, CardBody, Container, Row, Col, Badge, DropdownToggle, DropdownMenu, Dropdown, Button} from "reactstrap";
import Moment from "react-moment";
import {DataGrid} from "@material-ui/data-grid";
import {DateRangePicker} from "react-date-range";
import {defaultStaticRanges} from "../../Dashboards/components/DateRangeFilter";
import moment from "moment";
import {subHours} from "date-fns";
import * as widgetHelper from '../../../shared/helpers/widget_helper'
import { ElasticQueryBuilder } from '../../../utils/ElasticQueryBulider';
import dashboardService from '../../../api/dashboardService';
import { ElasticResponse } from '../../../shared/helpers/elastic_reponse';



class DetectionsResults extends React.Component{
    state = {dv: [],information:{name:''},
        currentState:'',
        numberOfAnomalies:0,
        aGrade:0,
        confidence:0,
        lastDetectionDate:0,
        firstDetectionDate:0,
        anomalies:[],
        features:{},
        startDate:null,
        endDate:null,
    }
    constructor() {
        super();

    }

    getMetrics = (startDate,endDate) =>{

        const { match: { params } } = this.props;

        const data = {id:params.id,startDate:startDate,endDate:endDate};
        this.fetchData(data);
        this.setState({
            startDate:startDate,
            endDate:endDate
        })
    }

     stopOrStateDetector = (startDate,endDate) =>{
         const { match: { params } } = this.props;
         const data = {id:params.id,state:this.state.currentState};
         detectionsService.startOrStop(data).then((responseData)=>{
             if(responseData.status === 200){
                 const data = {id:params.id,startDate:startDate,endDate:endDate};
                 this.fetchData(data);
             }
         })
     }

     deleteDetector = () =>{
        const { match: { params } } = this.props;
        detectionsService.deleteDetector(params.id).then((responseData)=>{
            if(responseData.status === 200){
                this.props.history.push('/detections');
            }
        }) 
     }

    fetchData = (data) =>{

        this.setState({
            startDate:data.startDate,
            endDate:data.endDate,
        })

        detectionsService.getDetectionMetrics(data).then((responseData)=>{
            const data = responseData.data.results;

            const anaomlies = [];
            for(var i=0;i<data.length;i++){
                if(data[i].anomalyGrade>0){
                    anaomlies.push(data[i]);
                }

            }


            const finalData = _.orderBy(data, ['startTime'],['asc']);
            const anaomliesData  = _.orderBy(anaomlies, ['anomalyGrade'],['asc']);
            const soretedAnaomliesData  = _.orderBy(anaomlies, ['startTime'],['desc']);
            const confidenceData  = _.orderBy(anaomlies, ['confidence'],['desc']);

            if(anaomliesData[0]){
                const  minValue = anaomliesData[0].anomalyGrade;
                const  earilesDate = soretedAnaomliesData[0].startTime;
                const  firstSeen = soretedAnaomliesData[soretedAnaomliesData.length-1].startTime;
                console.log(minValue);
                console.log(earilesDate);
                const ds = new DataSet();
                const dv = ds.createView().source(finalData);
                dv.transform({
                    type: "fold",
                    fields: ["anomalyGrade", "confidence"],
                    key: "key",
                    value: "value"
                });
                this.setState({
                    dv:dv,
                    information:responseData.data.information,
                    currentState:responseData.data.state,
                    numberOfAnomalies:anaomliesData.length,
                    anomalies:anaomliesData,
                    aGrade:minValue+"-1.00",
                    lastDetectionDate:earilesDate,
                    firstDetectionDate:firstSeen,
                    confidence:confidenceData[0].confidence+"-"+confidenceData[confidenceData.length-1].confidence,
                    features:responseData.data.features

                })
                console.log(dv);
            }else{
                this.setState({
                    dv:[],
                  
                    numberOfAnomalies:0,
                    anomalies:[],
                    aGrade:0,
                    lastDetectionDate:0,
                    firstDetectionDate:0,
                    confidence:0,
                    features:responseData.data.features,
                    information:responseData.data.information,
                    currentState:responseData.data.state,
                    

                })
            }



        })
    }

    componentDidMount() {
        const { match: { params } } = this.props;
        console.log(params.id);
        const data = {id:params.id,startDate:subHours(new Date(),24 ).getTime(),endDate:new Date().getTime()};
        this.fetchData(data);
    }

    render() {

        const cols = {
            startTime: {
                type: "timeCat",
                tickCount:10,
                mask:widgetHelper.calculateTimeFormat(this.state.startDate,this.state.endDate)
            }
        };

        return (<Container style = {{paddingTop:'60px'}}>



                    <Header name = {this.state.information}
                            state = {this.state.currentState}
                            date = {this.state.information}
                            interval = {this.state.information.anomaly_detector}
                            getMetrics = {this.getMetrics}
                            stopOrStateDetector = {this.stopOrStateDetector}
                            deleteDetector = {this.deleteDetector}
                    />



            <RenderSummary
                numberOfAnomalies = {this.state.numberOfAnomalies}
                aGrade = {this.state.aGrade}
                confidence = {this.state.confidence}
                lastDetectionDate = {this.state.lastDetectionDate}
                firstDetectionDate = {this.state.firstDetectionDate}

            />



            <Row>
                <Col xs = {12} lg={12} md = {12} >

                    {this.state.dv  &&
                    <Card style={{backgroundColor:'transparent'}} >
                        <CardBody>
                            <Chart height={200}   data={this.state.dv} scale={cols}  autoFit>
                                <Legend />
                                <Axis name="startTime" />
                                <Axis name="value" />
                                

                                <LineAdvance
                // area
                                     point={{ size: 3 }}
								   
                                    tooltip={["value"]}
                                    position="startTime*value"
                                    size={2}
                                    color={"key"}
                            />
                                
                            </Chart>
                        </CardBody>
                    </Card>

                    }
                </Col>

            </Row>

            <RenderOccurrences rows = {this.state.anomalies}/>
            {this.state.features && Object.keys(this.state.features).map((key)=>{
                return <RenderFeatures 
                startDate = {this.state.startDate} 
                endDate = {this.state.endDate} 
                anomalies = {this.state.anomalies} 
                currentFeatureId = {key} 
                features = {this.state.information.anomaly_detector.feature_attributes}  
                key = {key} 
                rows = {this.state.features[key]}/>
            })}





        </Container>)
    }

}

const RenderFeatures = (props)=>{

    const {rows,features,currentFeatureId,anomalies} = props;

    const [merticData,setMetricData] = useState([]);

    const currentFeature = _.find(features, function(o) { return o.feature_id === currentFeatureId; });

    

    if(currentFeature){
        const featureName = Object.keys(currentFeature.aggregation_query).join('')

        const aggType = Object.keys(currentFeature.aggregation_query[featureName]).join('')

        const field =  currentFeature.aggregation_query[featureName][aggType]

        useEffect(()=>{
            const metrics = {
                metric:[{field:field.field,type:aggType,id:"0"}],
                bucket:[
                    
                    {field:field.field,type:"terms",id:"0"},
                    {field:'@timestamp',type:"date_histogram",id:"1"},
                
                ]
            }
    
            const queryBuilder = new ElasticQueryBuilder({
                timeField: '@timestamp',
                esVersion: '7.9',
            });
            const query =   queryBuilder.build(metrics,[],'*')
            query.aggs[0].aggs[1].date_histogram.min_doc_count = 1;
          
          let payload = JSON.stringify(query);
          let finalData =  payload.replaceAll("$timeFrom", props.startDate);
          let fainlData1 = finalData.replaceAll("$timeTo", props.endDate);
          let finalData2 = fainlData1.replaceAll("$__interval",10+"m")
    
          let panelJson = {};
          panelJson['elsQuery'] = finalData2;
          panelJson['type'] = 'events';
          var data = {'panelData':JSON.stringify(panelJson)};
         const finalQuery = [metrics];
          dashboardService.showPanelPreivew(data).then((response) => {
            if(response.data.data && response.data.data.status != 400){
                var responses = {responses:[response.data.data]} 
        
                const er = new ElasticResponse(finalQuery, responses);
                console.log( er.getTimeSeries('table'));
                const metricData = er.getTimeSeries('table');
                const tempData  = [];
                if(metricData && metricData.data[0] ){
                    for(var i=0;i<metricData.data[0].rows.length;i++){
                        const object = new Object();
                        object.count = metricData.data[0].rows[i][2];
                        object.startTime =  metricData.data[0].rows[i][1];
                        object[field.field.replace('.keyword','')] = metricData.data[0].rows[i][0];
                        object.id = i;
                        tempData.push(object); 
                    }
                        setMetricData(tempData)
                }
               
              }
          
          });
    
        },[])
    

       
        const finalDatarows = _.orderBy(rows, ['startTime'],['asc']);
        const ds = new DataSet();
        const dv = ds.createView().source(finalDatarows);
        const cols = {
            startTime: {
                type: "timeCat",
                tickCount:10,
                mask:widgetHelper.calculateTimeFormat(props.startDate,props.endDate)
            }
        };
        dv.transform({
            type: "fold",
            fields: ["data"],
            key: "key",
            value: "value"
        });
        console.log(dv);

        return (
            <Row>
<Col xs ={12} lg={6} md = {6}>
                <Card style={{backgroundColor:'transparent'}}>


    <CardBody className="panel-body">
    <div className="panel__title">
        <h5>{currentFeature.feature_name} </h5>
        <h5 className="subhead">
            <span style={{color:'black',paddingRight:'12px'}}>Field : {field.field}</span>
            <span style={{color:'black',paddingRight:'12px'}}>Aggregation method: {aggType}</span>
            <span style={{color:'black',paddingRight:'12px'}}>
                {currentFeature.feature_enabled && `State : Enabled` }
                {!currentFeature.feature_enabled && `State : Disabled` }
            </span>
        </h5>
    </div>
    <br/>
    <div  style={{ height: 200, width: '100%' }}>
        <Chart height={200}   data={dv} scale={cols}  autoFit>
            <Legend/>
            <Axis name="startTime" />
            <Axis name="value" />
            <Tooltip>
            {(title,items) => {
                const color = items[0].data.startTime;
                const value = items[0].data.value;   
                const confidence = items[0].data.confidence
                const endTime = items[0].data.endTime
                return checkIfAnomlyExists(title,anomalies,color,value,confidence,endTime)
             }} 
            </Tooltip>   
            <Geom
                type="line"
                position="startTime*value"

                color={"key"}

            />
            {anomalies && anomalies.map((value)=>{
               return  <Annotation.Region 
               start={[value.startTime, 'min']} 
               end={[value.endTime,'max']} 
               
               />
               
              
            })


            }



        </Chart>
    </div>

        </CardBody> 
    </Card>
</Col>
        <Col xs = {12} md= {6} lg={6}>
            <Card style={{backgroundColor:'transparent'}}>
                <CardBody className="panel-body">
                <div className="panel__title">
                     <h5>{currentFeature.feature_name} - Detailed View </h5>
                     <h5 className="subhead">
                         <span style={{color:'black',paddingRight:'12px'}}>Field : {field.field}</span>
                         <span style={{color:'black',paddingRight:'12px'}}>Aggregation method: {aggType}</span>
                         <span style={{color:'black',paddingRight:'12px'}}>
                             {currentFeature.feature_enabled && `State : Enabled` }
                              {!currentFeature.feature_enabled && `State : Disabled` }
                        </span>
                        </h5>
                </div>
                <div  style={{ height: 300, width: '100%' }}>
                    <RenderDetailedView rows = {merticData} field = {field.field.replace(".keyword","")}/>

                </div>
                
                   

                </CardBody>
            </Card>
        </Col>
</Row>

        
)
    }else{
        return (<div></div>)
    }


}

const RenderDetailedView = (props) =>{

    const columns = [
        { field: 'id', headerName: 'ID',flex: 0.3,hide: true},
        { field: 'startTime', headerName: 'Time',flex: 0.3,renderCell: (params) =>(
                <Moment  date={params.getValue('startTime')} format="MM/DD/YYYY hh:mm:ss A"/>),},
       
        { field: props.field, headerName: props.field,flex: 0.3},
        { field: 'count', headerName: 'count',flex: 0.3, },
    ];

    return <DataGrid rows={props.rows} columns={columns} pageSize={5} rowHeight = {40} checkboxSelection={false} />

}

const checkIfAnomlyExists = (title,data,startTime,value,confidence,endTime)=>{
     const  exists = _.find(data, function(o) { return o.startTime === startTime; });
     if(exists){
            return <div style ={{height:'50px',width:'300px',marginTop:'12px'}}>
                 
                    <span style ={{marginTop:'12px',fontWeight:'bold'}}>There is an anomaly with confidence  {exists.confidence} between from <Moment  date={startTime} format="MM/DD/YYYY hh:mm:ss A"/>  to <Moment  date={endTime} format="MM/DD/YYYY hh:mm:ss A"/></span>
                 
                 </div>
     }else{
        return <div style ={{height:'35px',width:'100px',marginTop:'5px'}}>
            <div>
                         <span>{title}</span><br/>
            </div>

            <div style ={{marginTop:'5px'}}>
                    <span style ={{marginTop:'12px'}}>Value: {value}</span>
                
            </div>

            </div>
            
            
                    
                    
     }
}

const RenderOccurrences = (props) =>{

    const columns = [
        { field: 'id', headerName: 'ID',flex: 0.3,hide: true},
        { field: 'startTime', headerName: 'Start Time',flex: 0.3,renderCell: (params) =>(
                <Moment  date={params.getValue('startTime')} format="MM/DD/YYYY hh:mm:ss A"/>),},
        { field: 'endTime', headerName: 'End Time',flex: 0.3,renderCell: (params) =>(
                <Moment  date={params.getValue('endTime')} format="MM/DD/YYYY hh:mm:ss A"/>),},
        { field: 'confidence', headerName: 'Data confidence',flex: 0.3},
        { field: 'anomalyGrade', headerName: 'Anomaly grade',flex: 0.3,


        },


    ];
    console.log("rows")
    console.log(props.rows)
    return ( <Card style={{backgroundColor:'transparent'}} className="panel">

         <CardBody className="panel-body">
            <div className="panel__title">
                <h5>Anomaly occurrences</h5>
            </div>
             <br/>
            <div  style={{ height: 300, width: '100%' }}>
                <DataGrid rows={props.rows} columns={columns} pageSize={5} rowHeight = {40} checkboxSelection={false} />
            </div>

        </CardBody>

    </Card>)
}

const RenderSummary = (props) =>{

    return (<Row>

        <Col xs= {2} md= {2} lg = {2}>
            <Card style={{backgroundColor:'transparent'}} className="panel">
              <CardBody className="panel-body">
                <div className="panel__title">
                    <h5>Anomaly occurrences</h5>
                </div>
                <h4 className="anomily-titles">{props.numberOfAnomalies}</h4>
            </CardBody>
        </Card>

    </Col>
        <Col xs= {2} md= {2} lg = {2}>
            <Card style={{backgroundColor:'transparent'}} className="panel">


                <CardBody className="panel-body">
                    <div className="panel__title">
                        <h5>Anomaly grade</h5>
                    </div>
                    <h4 className="anomily-titles">{props.aGrade}</h4>
                </CardBody>
            </Card>

        </Col>



        <Col xs= {2} md= {2} lg = {2}>
            <Card style={{backgroundColor:'transparent'}} className="panel">


                <CardBody className="panel-body">
                    <div className="panel__title">
                        <h5>Confidence</h5>
                    </div>
                    <h4 className="anomily-titles">{props.confidence}</h4>
                </CardBody>
            </Card>

        </Col>
        <Col xs= {2} md= {2} lg = {2}>
            <Card style={{backgroundColor:'transparent'}} className="panel">


                <CardBody className="panel-body">
                    <div className="panel__title">
                        <h5>First anomaly occurrence</h5>
                    </div>
                    <h4 className="anomily-titles">
                        <Moment  date={props.firstDetectionDate} format="MM/DD/YYYY hh:mm:ss A"/> </h4>
                </CardBody>
            </Card>

        </Col>
        <Col xs= {2} md= {2} lg = {2}>
            <Card style={{backgroundColor:'transparent'}} className="panel">


                <CardBody className="panel-body">
                    <div className="panel__title">
                        <h5>Last anomaly occurrence</h5>
                    </div>
                    <h4 className="anomily-titles">
                        <Moment  date={props.lastDetectionDate} format="MM/DD/YYYY hh:mm:ss A"/> </h4>
                </CardBody>
            </Card>

        </Col>
        <Col xs= {2} md= {2} lg = {2}>
            <Card style={{backgroundColor:'transparent'}} className="panel">


                <CardBody className="panel-body">
                    <div className="panel__title">
                        <h5>Alerts</h5>
                    </div>
                    <h4 className="anomily-titles">
                        0 </h4>
                </CardBody>
            </Card>

        </Col>




    </Row>);
}

const Header = (props) =>{

    const [isOpen,setIsOpen] = useState(false);

    const [currentData,setCurrentData] = useState([
        {
            startDate: subHours(new Date(),24 ),
            endDate: new Date(),
            key: 'selection'
        }
    ])

    useEffect(()=>{
        if(currentData.length!=0){
            const lable = moment(currentData[0].startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(currentData[0].endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
            setLabel(lable)
        }
    },currentData)



    const toggle1 = () => setIsOpen(prevState => !prevState);



    const [label,setLabel] = useState(moment(currentData[0].startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(currentData[0].endDate.getTime()).format('MM/DD/YYYY h:mm:ss'));

    const changeDateHandler =  (start, end,label)=>{

        const lable = moment(start.selection.startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(start.selection.endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
        setLabel(lable)
        props.getMetrics(start.selection.startDate.getTime(),start.selection.endDate.getTime())
        setCurrentData([start.selection])
        //dispatch(changeDateSettings(start))
    }

    return (<Row>
        {props.name.anomaly_detector &&
        <React.Fragment>
        <Col xs = {6} md = {6} lg = {6}>
            <h3 className="page-title">
                {props.name.anomaly_detector.name} <span>
                {props.state === 'RUNNING' &&
                <React.Fragment>
                        <span style = {{fontSize: '16px',
                            paddingLeft: '12px'}}>
                            <Badge  color = "success">Running</Badge></span>
                             {props.date.anomaly_detector && <span style = {{fontSize: '12px',
                            paddingLeft: '12px'}}>
                           Since <Moment  date={props.date.anomaly_detector_job.disabled_time} format="MM/DD/YYYY hh:mm:ss A"/>
                       </span>}
                </React.Fragment>
                }
                {props.state !== 'RUNNING' &&
                <React.Fragment>
                        <span style = {{fontSize: '16px',
                            paddingLeft: '12px'}}>
                            <Badge  color = "warning">{props.state}</Badge></span>
                    {props.date.anomaly_detector_job &&
                        <span style = {{fontSize: '12px',
                        paddingLeft: '12px'}}>
                           On <Moment  date={props.date.anomaly_detector_job.disabled_time} format="MM/DD/YYYY hh:mm:ss A"/>
                       </span>
                    }

                </React.Fragment>

                }
                </span>
                {props.interval.detection_interval &&  <span style = {{fontSize: '14px',paddingLeft: '12px'}}>
                    Detector interval : {props.interval.detection_interval.period.interval} {props.interval.detection_interval.period.unit}
                </span>}

            </h3>

        </Col>
            <Col xs = {3} md = {3} lg = {3}>
            <Dropdown isOpen={isOpen} toggle={toggle1}>
            <DropdownToggle caret>
        {label}
            </DropdownToggle>
            <DropdownMenu style = {{width:"600px",marginLeft: '-85%',top:'35px'}}>
            <DateRangePicker
            onChange={(item,label) => changeDateHandler(item,label)}
            showSelectionPreview={true}
            moveRangeOnFirstSelection={false}

            ranges={currentData}
            direction="vertical"
            scroll={{ enabled: true }}
            staticRanges={defaultStaticRanges}
            />
            </DropdownMenu>
            </Dropdown>
            </Col>

            <Col xs = {3} md = {3} lg = {3}>
                {props.state === 'RUNNING' && <Button onClick={()=>props.stopOrStateDetector(currentData[0].startDate.getTime(),currentData[0].endDate.getTime())} color = "danger">Stop detector</Button> }
                {props.state != 'RUNNING' && props.state != 'INIT'  && 
                    <React.Fragment>
                    <Button onClick={()=>props.stopOrStateDetector(currentData[0].startDate.getTime(),currentData[0].endDate.getTime())} color = "primary">Start detector</Button>
                    <Button onClick={()=>props.deleteDetector()} color = "danger">Delete</Button>
                    </React.Fragment>
                   
                 }

                <Button href = {`/create-ad/${props.name._id}`} color = "primary">View Details</Button> 
            </Col>

            
                
                
            

        </React.Fragment>
        }



    </Row>)

}

export default  DetectionsResults;