export const GET_INTEGRATIONS = 'GET_INTEGRATIONS';
export const PUT_INTEGRATIONS = 'PUT_INTEGRATIONS';
export const SAVE_INTEGRATIONS = 'SAVE_INTEGRATIONS';
export const COMPLETE_TASK = 'COMPLETE_TASK'
export const SAVE_COMPLETED = 'SAVE_COMPLETED'

export function getIntergrations(){
    return {
        type: GET_INTEGRATIONS
    };
}

export function putIntergrations(){
    return {
        type: PUT_INTEGRATIONS
    };
}
export function saveIntegrations(action){
    return {
        type: SAVE_INTEGRATIONS,
        payload:action
    };
}
export function completeTaks(action){
    return {
        type: COMPLETE_TASK,
        payload:action.payload
    };
}
export function saveCompleted(action){
    return {
        type: SAVE_COMPLETED,
        payload:action.payload
    };
}