const HistoricalColumChart = (props) =>{
    const chartData = processTimeSeriesData(props.data);

    const themeName = useSelector((state)=>state.theme.className);

    const {chartConfig} = props
    let totalHits = 0;
    for(var i=0;i<chartData.length;i++){
        totalHits+=chartData[i].value
    }
    console.log(totalHits)
    const format = calculateTimeRange(props.startDate,props.endDate);

    chartConfig['mask'] = format;
    chartConfig['tickCount'] = widgetHelper.calculateTick(props.startDate,props.endDate);

    let position = "label*value";
    const dispatch = useDispatch();
    const triggerEvent = (event,finalData) =>{


        if(event.filteredData.length!=chartData.length){

            const dataArray = [];
            let sum =0;
            for(var i=0;i<event.filteredData.length;i++){
                dataArray.push(event.filteredData[i].label);

            }

            const minValue =  Math.min.apply(Math, dataArray);
            const maxValue =  Math.max.apply(Math, dataArray);
            let object = {strat:minValue,end:maxValue,label:""}

            setTimeout(function(){

                if(_.isFinite(this.strat) && _.isFinite(this.end)) {
                    const dateObject = {startDate:this.strat,endDate:this.end,label:"test"}

                    dispatch(applyFilter(dateObject));
                }

            }.bind(object),1000);



            //return true;

        }

    }

    const scale  = {
        label:{
            type: "timeCat",
            mask: chartConfig.mask,
            tickInterval:10
        }


    }

    return (<CardBody>

            <NumberFormat value={totalHits} displayType={'text'} thousandSeparator={true}  renderText={value => <div> <h4 style={{textAlign:'center'}}><b>{value}</b> Hits</h4></div>} />
            <Chart  onAfterrender={(e, chart) => {
                triggerEvent(chart,chartData)
            }} scale={scale} height={250} width = '98%' data={chartData} autoFit >

                <Legend

                        itemName={{style:{fill:widgetHelper.getTextColor(themeName)}}}

                />

                <Axis
                    name="label"

                    label = {{
                        autoRotate:false,
                        style:{
                            fill:widgetHelper.getTextColor(themeName),
                            
                        }
                        
                    }}
                />
                <Interaction type = "brush"/>
                <Interval

                    position={position}
                    color={"metric"}


                />
            </Chart>
        </CardBody>
        )

}

const  processTimeSeriesData = (data) =>{
    let finalArray = [];
    for(var i=0;i<data.length;i++){

        let dataPoints = data[i].datapoints;
        dataPoints.map(function(singlePoint){
            let object = new Object();
            object['label'] = singlePoint[1];
            object['value'] = singlePoint[0];
            object['metric'] = data[i].target
            finalArray.push(object);
        });

    }
    return finalArray;
}

const  calculateTimeRange =(startDate,endDate) =>{

    var a = moment(startDate);
    var b = moment(endDate);

    var diff = b.diff(a,'days');
    if(diff==0){
        return "hh:mm";
    }else if(diff>=1 && diff<=30){
        return "MM-DD hh:mm";
    }else if(diff>=30 && diff<=365){
        return "MM-DD hh:mm";
    }
    else if(diff>=365){
        return "yyyy/MM/DD";
    }

}

export default HistoricalTimeLine;