const  calculateTimeRange =(startDate,endDate) =>{

    var a = moment(startDate);
    var b = moment(endDate);

    var diff = b.diff(a,'days');
    if(diff==0){
        return "hh:mm";
    }else if(diff>=1 && diff<=30){
        return "MM/DD hh:mm";
    }else if(diff>=30 && diff<=365){
        return "MM/DD";
    }
    else if(diff>=365){
        return "yyyy/MM/DD";
    }

}




