import { AppBar, Box, Tab, Tabs, Typography } from '@material-ui/core';
import React from 'react'
import { ReflexContainer, ReflexElement, ReflexSplitter } from 'react-reflex';
import { Col, Row } from 'reactstrap';
import LogDetailView from '../../../LogViewer/LogDetailView'
import LogViewer from '../../../LogViewer/LogViewer'
const RenderBottom =(props)=>{

   const {toggleBottom,status,fieldMaps} = props;



    return <div>
            <Row>
            <Col xs = {12} md = {12} lg = {12} style = {{height: '36px', position: 'fixed',marginTop: '-23px'}}>
                    {status && <div onClick = {toggleBottom} className = "logButton">Logs</div>}
                    {!status && <div onClick = {toggleBottom} className = "logButton">Logs</div>}
                    
                </Col>
            </Row>
            <Row>
                <Col xs = {12} md = {12} lg = {12}>
                    <RenderTabs fieldMaps= {fieldMaps} fields = {props.fields}/>
                </Col>
                

            </Row>
            
    </div>
}

const RenderTabs = (props)=>{
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
            setValue(newValue);
    };
    return (<div>
            <AppBar  position="static" color = "default">
                <Tabs indicatorColor="primary" textColor="primary"  variant="scrollable" scrollButtons="auto" style = {{minHeight:'20px'}} value={value} onChange={handleChange} >
                <Tab style = {{minHeight:'30px'}} label={ <span style = {{fontSize:'12px'}} > {" "} Events </span>   } {...a11yProps(0)} />
                <Tab style = {{minHeight:'30px'}} label={ <span style = {{fontSize:'12px'}} > {" "} Alerts </span>   } {...a11yProps(1)} />
        
            </Tabs>
         </AppBar>
            <TabPanel  value={value} index={0}>
            <ReflexContainer orientation="vertical">
                <ReflexElement    className="left-pane">
                    <LogViewer type = "events"   fields = {props.fields}/>
                </ReflexElement>   
                <ReflexSplitter style = {{height:'300px'}} propagate={true}/>
                <ReflexElement size = {400}  className="right-pane">
                    <LogDetailView fields = {props.fieldMaps}/>     
                </ReflexElement>
            </ReflexContainer>   
               
            </TabPanel>
            <TabPanel value={value} index={1}>
            <ReflexContainer orientation="vertical">
                <ReflexElement    className="left-pane">
                  <LogViewer type = "alerts"   fields = {props.fields}/>
                </ReflexElement>   
                <ReflexSplitter style = {{height:'300px'}} propagate={true}/>
                <ReflexElement size = {400}  className="right-pane">
                    <LogDetailView fields = {props.fieldMaps}/>     
                </ReflexElement>
            </ReflexContainer>
                
            </TabPanel>
    
</div>);

}

function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
           <div>{children}</div>
        )}
      </div>
    );
  }

export default RenderBottom;