import React from 'react'
import { Col, Container, Row } from 'reactstrap';

import { connect } from 'react-redux';
import {bindActionCreators} from 'redux'
import IntegationsView from './components/IntegrationView'
import {getIntergrations} from '../../redux/actions/integrationsActions';
class Integration extends React.Component{

    constructor(props){
        super(props);
        this.state={
            data:[]
        }
    }

    componentDidMount(){
        const { dispatch} = this.props;
        dispatch(getIntergrations());
    }

    render(){
        return <Container style = {{paddingTop:'80px'}}>
                <Row>
                    <Col xs = {12} md = {12} lg = {12} >
                        <h3 className = "page-title"> Integrations </h3>
                    </Col>
                    
                </Row>
                <IntegationsView key = "integration_view" data = {this.state.data}/>
        </Container>
    }

}



export default connect()(Integration)