import axiosInstance from '../middleware/Interceptor'


function getDetections () {
    return axiosInstance.get('/siem-core/users/detectors/')
}
function getDetectionMetrics(data){
    return axiosInstance.post('/siem-core/users/detectors/get-metrics',data)

}

function startOrStop(data){
    return axiosInstance.post('/siem-core/users/detectors/stop-detectors',data)
}
function createAd(data){
    return axiosInstance.post('/siem-core/users/detectors/create-ad',data)

}
function getSingleDetector(id){
    return axiosInstance.get('/siem-core/users/detectors/detection/'+id)
}

function deleteDetector(id){
    return axiosInstance.delete('/siem-core/users/detectors/delete-detectors/'+id)
}





const detectionsService = {
    getDetections,
    getDetectionMetrics,
    startOrStop,
    createAd,
    getSingleDetector,
    deleteDetector

}

export default detectionsService