import React from 'react';
import CaseLayout from '../Cases/components/CaseLayout'
import queryString from 'query-string';
import {loadAllCases} from '../../redux/actions/casesAction'
import { connect } from 'react-redux';

class CasesView extends React.Component {

    constructor(props) {
        super(props);
    }

    componentDidMount(){
        let params = queryString.parse(this.props.location.search)
        
        const {dispatch} = this.props
        if(params){
            const action = {id:params.id}
            dispatch (loadAllCases(action))
        }else{
            dispatch (loadAllCases())
        }
        
    }

    render() {
        let params = queryString.parse(this.props.location.search)
        if(params){
            return (<CaseLayout caseId = {params.id}/>)
        }else{
            return (<CaseLayout/>)
        }

    }

}

export default connect()(CasesView);