import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import dashboardService from '../../../api/dashboardService'
import TreeView from '../Tree/Tree'
import {saveDashboardLayout} from '../../../redux/actions/dashboardLayoutActions'
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import { Button, Col, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import { BarLoader } from 'react-spinners';

const Sierra = `${process.env.PUBLIC_URL}/img/folder.png`;
const bar = `${process.env.PUBLIC_URL}/img/bar-active.png`;

const reportTreeConfig = {

    core: {
      data: [],
      "check_callback": true
    },
    contextmenu : {
        'items' : []
    },
    types : {
        "default" : {
            "icon" : Sierra
        }, 
        'Dashboard' : {
            "icon" : bar,
        },
        'Report' :{
            "icon" : bar,
        }
    },
    plugins : ['types',"contextmenu","search","sort"]
  }

const treeConfig = {

    core: {
      data: [],
      "check_callback": true
    },
    contextmenu : {
        'items' : []
    },
    types : {
        "default" : {
            "icon" : Sierra
        }, 
        'Dashboard' : {
            "icon" : bar,
        },
        'Report' :{
            "icon" : bar,
        }
    },
    plugins : ['types',"contextmenu","search","sort"]
  }

const DashboardTree = (props) =>{

   
    const [treeData,setTreeData] = useState(treeConfig);
    const [reportTree,setReportTree] = useState(reportTreeConfig);
    const [isOpen,setIsOpen] = useState(false);
    const [loading,setLoading] = useState(false);

    const saveCategoryData = (formData)=>{
        setLoading(true);
         dashboardService.saveCategoryData(formData).then((responseData)=>{
            if(responseData.status == 201){
                fetchTreeData();
            }
         })
    }



    const toggle = ()=>{
        setIsOpen(!isOpen);
    }
   

    const addThisDashboardToLayout = (event) =>{
        
            const currentDashboard = {
                "type": "tab",
                "id": event.id,
                "name": event.text,
                "component": "dashboard"
            }
            const data = {type:"add",dashboard:currentDashboard}
            //dispatch(saveDashboardLayout(data))
            props.addDashboardToLayout(currentDashboard);
            //dispatch(addDasboard(currentDashboard));
        
        
    }   

    useEffect(() => {
        fetchTreeData();
      }, []);
   
   const  fetchTreeData = () =>{
        dashboardService.getAllDashboardsWithCategoris('dashboard').then((response) => {
            const tempTreeData = Object.assign({},treeData);
            tempTreeData.core.data = response.data
            setTreeData(tempTreeData)
            setLoading(false);
        });
        dashboardService.getAllDashboardsWithCategoris('reports').then((response) => {
            const tempTreeData = Object.assign({},reportTree);
            tempTreeData.core.data = response.data
            setReportTree(tempTreeData)
            setLoading(false);
        });
    }

    const deleteCategory = (id)=>{
        setLoading(true);
        dashboardService.deleteCategory(id).then((responseData)=>{
            fetchTreeData();
            
        })
    }

    const changeCategory = (e,data)=>{
        console.log(data);
        const finalData = {id:parseInt(data.node.id), categoryName:data.text,parentId:parseInt(data.node.parent)}
        saveCategoryData(finalData);  
    }



     


    return (
         <React.Fragment>
             <Row>

                    <Col xs = {10} lg = {10} md = {10} >

                    </Col>
                    <Col xs = {2} lg = {2} md = {2}>
                            <ControlPointIcon onClick = {toggle} title = "Create Category" style = {{cursor:'pointer'}} color = "primary" fontSize = "small"/>
                    </Col>
             </Row>
                {loading && <BarLoader color = "#0066CC" width = "100%"/> }
                 {props.type && props.type === 'dashboards' &&  <TreeView openDashboard = {addThisDashboardToLayout}  changeCategory = {changeCategory}  deleteCategory = {deleteCategory} treeData={treeData} treeSearchData ={treeData} />}
                 {props.type && props.type === 'reports' &&  <TreeView openDashboard = {addThisDashboardToLayout}  changeCategory = {changeCategory}  deleteCategory = {deleteCategory} treeData={reportTree} treeSearchData ={reportTree} />}
                
                 {isOpen && <DisplayModel saveCategoryData = {saveCategoryData} data = {treeData} isOpen = {isOpen} toggle = {toggle}/> }
                 
         </React.Fragment>
        
    
    )


}

const DisplayModel = (props)=>{
    const {toggle,isOpen} = props
    const [formData,setFormData] = useState({categoryName:'',parentCategoryName:'0'});
    const [categories,setCategories] = useState(false);
   
    useEffect(() => {
        fetchPanelData();
       
    }, [categories.length]);
    
    const  fetchPanelData = () =>{
       
        dashboardService.getDashboardCategories().then((response) => {
            const data = response.data.map(function(value,index){
                return {label:value.name,value:value.id}
            });
            
            setCategories(data);
        });
    }

    const saveData = ()=>{
        const finalData = {categoryName:formData.categoryName,parentId:parseInt(formData.parentCategoryName)}
        props.saveCategoryData(finalData);
        props.toggle();
    }

   const  onChnageHandler = (event)=>{
        let tempFormData = Object.assign({},formData);
         tempFormData[event.target.name] = event.target.value;
         setFormData(tempFormData)
   }

    return  (
            
            <Modal size = "lg" isOpen={isOpen} toggle={toggle} >
               
            <ModalHeader toggle={toggle}>Create Category</ModalHeader>
            <ModalBody>

            <form className="form form--horizontal" >
              <div className="form__form-group">
                <span className="form__form-group-label">Category Name</span>
                <div className="form__form-group-field">
                    <input  onChange = {onChnageHandler} name = "categoryName" type = "text" className = "form-control" value={formData.categoryName}/>
                </div>
              </div>
              <div className="form__form-group">
                <span className="form__form-group-label">Parent Category</span>
                <div className="form__form-group-field">
                    <select onChange = {onChnageHandler} className = "form-control" name = "parentCategoryName" value={formData.parentCategoryName} >
                        <option value = "0">Select Category</option>
                        {categories &&  categories.map((value)=>{
                           return <option value = {value.value}>{value.label}</option>
                        })}
                    </select>
                </div>
              </div>
             </form> 
            
            </ModalBody>
            <ModalFooter>
                 <Button color="primary" onClick={saveData}>Save</Button>{' '}
                 <Button color="secondary" onClick={toggle}>Cancel</Button>
            </ModalFooter>
    </Modal>);
}

export default DashboardTree;
