import {
  ADD_DASHBOARD,
  UPDATE_LAYOUT,
  FETCH_LAYOUT,
  LOAD_LAYOUT,
  CHANGE_DASHBOARD,
  DELETE_DASHBOARD,
  SAVE_DASHBOARD_LAYOUT,
} from "../actions/dashboardLayoutActions";
import _ from "lodash";
import cloneDeep from "lodash/cloneDeep";

const initialState = {
  layout: [],
  loading: false,
  index: 0,
  value: 0,
  fields: [],
};

const dashboardLayoutReducer = (state = initialState, action) => {
  if (action.type === FETCH_LAYOUT) {
    return Object.assign({}, state, {
      loading: true,
      layout: [],
      index: 0,
      value: 0,
      fields: [],
    });
  }
  if (action.type === CHANGE_DASHBOARD) {
    return Object.assign({}, state, {
      index: action.payload,
      value: action.payload,
    });
  }
  if (action.type === DELETE_DASHBOARD) {
	  //filterData:filterData,value:filterData.length-1,index:filterData.length-1
    return Object.assign({}, state, {
      layout: action.payload.filterData,
	  loading:false,
	  index: action.payload.index,
      value: action.payload.value,
    });
  }

  if (action.type === LOAD_LAYOUT) {
    return Object.assign({}, state, {
      loading: false,
      layout: action.payload.dashboardLayout,
      index: action.payload.index,
      value: action.payload.value,
      fields: action.payload.fields,
    });
  }

  if (action.type === SAVE_DASHBOARD_LAYOUT) {
	  
    return Object.assign({}, state, {
      loading: true
    });
  }

  if (action.type === ADD_DASHBOARD) {
    const currentLayout = cloneDeep(state.layout);
    currentLayout["layout"]["children"][0]["children"].push(action.layout);
    return Object.assign({}, state, { layout: currentLayout });
  }

  if (action.type === UPDATE_LAYOUT) {
    return Object.assign({}, state, { layout: action.layout });
  }

  return state;
};
export default dashboardLayoutReducer;
