const CaseAlert = (props) =>{

    const caseDetails = useSelector((state) => state.cases.caseDetails);
    const users = useSelector((state) => state.cases.users);
    const loading = useSelector((state) => state.cases.loading);

    return (<React.Fragment>
        {loading && <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
        {caseDetails && caseDetails.id &&  
        <React.Fragment>
            <CaseHeader users = {users} caseDetails = {caseDetails}/>
            <CaseBody caseDetails = {caseDetails}/>    

        </React.Fragment>}

        {caseDetails && !caseDetails.id &&
        <React.Fragment>
           <div style = {{fontSize:'14px', fontWeight:'bold',textAlign:'center'}}>No Cases Found</div>  

        </React.Fragment>}
        

    </React.Fragment>)

}

