const AlertCard = (props) =>{
    const {alertData} = props
     
    return (<Col onClick={ () => props.openEntityView(alertData) } xs="3" sm="3" md ={3} className = "alertEvidenceCard">
            <Card  className = {`alert-card-container alert-card-container-${alertData.alertPriority.toLowerCase()}`}>
                <CardBody className = 'alert-card-body-container'>
                    <CardTitle className = "alert-card__content__name" ><span className = "u-ellipsis">{alertData.alertName}</span></CardTitle>
                    <CardTitle  className = "alert-card__content_sub"  ><Moment format="MM/DD/YYYY HH:mm:ss">{alertData.createdDate}</Moment></CardTitle>
                    
                    <CardTitle  className = "alert-card__content_sub">{alertData.eventCount} Events</CardTitle>
                </CardBody>
            </Card>
    </Col>);

}

const CasesTab = (props) =>{
    const [activeTab,setActiveTab] = useState('1');
    const [open,setOpen] = useState(false);
    const [comment,setComment] = useState("");
    const [currentAlertDetails,setCurrentAlertDetails] = useState({});
    const [chartData,setChartData] = useState([])
    const dispatch = useDispatch();
    const {caseDetails} = props;

    const openEntityView = (alertData) =>{
      setCurrentAlertDetails(alertData)
      //const data = {alertName:action.alertName,caseId:action.caseId,action:props.alertId}
      var actionData = {alertName:alertData.alertName,caseId:caseDetails.id,alertId:alertData.alertId}
      dispatch(loadPlaybook(actionData))
      dispatch(openEvidence(alertData))
    }

   


    const toggle = (tab) =>{
        if (activeTab !== tab) {
            setActiveTab(tab)
        }
    
    }

    const onRichTextEditChange = (event)=> {
        setComment(event);
    }

    const addComment = () =>{
        if(caseDetails){

                const  caseData = {id:caseDetails.id,note:comment}
                            caseService.addNotesToCase(caseData).then((response)=>{
                                openCurrentCase();
                                setComment(""); 
                            })



        }

    }
    const showPlaybookResults = (data)=>{
        dispatch(openPlaybookSinglePlayBookResult(data));
    }

    const openCurrentCase = () =>{

        caseService.getSingleCase(caseDetails.id) .then((response) => {
            if(response.status===200 && response.data){
                dispatch(openCase(response.data));
            }
        })



    }

    
    useEffect(()=>{
        if(caseDetails && caseDetails.alertEvidences){
            setChartData([])
            setCurrentAlertDetails(caseDetails.alertEvidences[0])
            openEntityView(caseDetails.alertEvidences[0]);
            const tempChartData = [];
            for(var i=0;i<caseDetails.alertEvidences.length;i++){
                let object = new Object();
                object['date'] = moment(caseDetails.alertEvidences[i].createdDate).format('HH:mm:ss');
                object['count'] = caseDetails.alertEvidences[i].eventCount
                tempChartData.push(object);
              }
              setChartData(tempChartData);
              console.log(chartData);
        }
       
    },[caseDetails])

    

    const openUpload = () =>{
        setOpen(!open);
    }

    return (<div className="tabs tabs--bordered-bottom">
            <Model close = {openUpload} openCurrentCase = {openCurrentCase} caseId = {caseDetails.id} open = {open}/>
                <div className="tabs__wrap"> 
                <Nav tabs>
                  <NavItem>
                    <NavLink
                      className={classnames({ active: activeTab === '1' })}
                      onClick={() => {
                        toggle('1');
                      }}
                    >
                      Overview
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={classnames({ active: activeTab === '2' })}
                      onClick={() => {
                        toggle('2');
                      }}
                    >
                      History
                    </NavLink>
                  </NavItem>


                    <NavItem>
                        <NavLink
                            className={classnames({ active: activeTab === '4' })}
                            onClick={() => {
                                toggle('4');
                            }}
                        >
                            Evidence
                        </NavLink>
                    </NavItem>
              
                </Nav>
                <TabContent activeTab={activeTab}>
                  <TabPane tabId="1">
                      <div className = "alertContainer">
                         <CardTitle className = "alert-title"><span class="lnr lnr-flag"></span> Alerts</CardTitle>

                      
                      
                      {caseDetails && caseDetails.alertEvidences && 
                            <React.Fragment>
                                <Row>
                                    {caseDetails.alertEvidences.map((value, index) => {

                                    return (<AlertCard alertData = {value} openEntityView  = {openEntityView}/>)
        
                                    })}
                              </Row> 
                                 
                            </React.Fragment>
                           
                        
                            
                          
                       
                      }
                      </div>
                      <hr/>
                      <CardTitle style = {{paddingLeft:'12px'}} className = "alert-title"><span class="lnr lnr-flag"></span>Alert Actvity</CardTitle>
                      
                      <div className = "chart">
                      <Chart height={100} autoFit data={chartData} >
                         <Point position="date*count"  />
                      </Chart>
                      </div>

                    
                          <PlayBook alertName = {currentAlertDetails.alertName}  caseId = {caseDetails.id} alertId = {currentAlertDetails.alertId}  showPlaybookResults = {showPlaybookResults}/>
                    
                     
                  </TabPane>
                  <TabPane tabId="2">
                  <div className = "wall-case-container__case-comments">
                  {caseDetails && caseDetails.caseActivities && caseDetails.caseActivities.map((value, index) => {

                      return <div className = "case-wall-list--item">
                           <div className = "activity-icon-container">
                           <div className = "smp-icon-size--small">
                           <svg width="100%" height="100%" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill="currentColor"><path d="M19 4.3H5a1.4 1.4 0 00-1.4 1.4v9.1A1.4 1.4 0 005 16.2h2.1v4.2l5.6-4.2H19a1.4 1.4 0 001.4-1.4V5.7A1.4 1.4 0 0019 4.3z"></path></svg>
                           </div>
                           </div>
                          <div className = "action-comment-container">
                           <div className = "item-comment-container">  
                                 <p class="u-word-break--word u-white-space--pre-wrap item-comment item-comment--text underline-links font-regular-primary show-more ng-star-inserted" dir="ltr">{value.activity}</p>
                           </div>
                          </div>

                          <div className = "user-timestamp-container">
                               <div className = "activity-user-box">

                                 <div class="time"> <Moment format="MM/DD/YYYY HH:mm:ss">{value.activityDate}</Moment></div>
                               </div>
                          </div>
                          
                       </div>
                    
                 
                    })}
                     </div>
                     

                  </TabPane>

                  <TabPane tabId="4">

                      <Card>
                          <CardBody>

                              <div style={{height:'300px',overflowY:'auto'}}>
                                    {caseDetails.notes && caseDetails.notes.map((value, index) => {
                                       return (
                                           <Row className= "evidenBorder">
                                                <Col xs={8} style = {{marginBottom:'12px'}}>
                                                    <div style = {{paddingTop:'12px'}}>
                                                        {parse(value.notes)}
                                                    </div>

                                                </Col>

                                               <Col xs={4}>
                                                 <div style = {{paddingTop:'12px'}}>
                                                   <p  className="todo__due-date">Created By <b>{value.createdBy}</b></p>
                                                </div>
                                               </Col>

                                           </Row>
                                           )
                                    })}
                                  {caseDetails.files && caseDetails.files.map((value, index) => {
                                      return (
                                          <Row style = {{borderBottom:'1px solid #efe3e3'}}>
                                              <Col xs={8} style = {{marginBottom:'12px'}}>
                                                  <p style={{paddingTop:'12px'}}><a href = {`http://localhost:8899/user/case/downloadFile/${value.fileName}`} target={"_blank"}>{value.fileName}</a> was uploaded by <b>{value.fileUploadedBy}</b> on <Moment format="DD/MM/YYYY HH:mm:ss"><b>{value.fileCreatedDate}</b></Moment> </p>
                                              </Col>



                                          </Row>
                                      )
                                  })}

                              </div>
                              <div style={{marginTop:'10px'}}>
                                  <TextEditor onChange={onRichTextEditChange} />
                              </div>

                          </CardBody>



                      </Card>
                      <Button onClick={addComment} color = "primary" style = {{float: "right",
                          marginRight: '10px'}}>
                          Comment
                      </Button>

                      <Button onClick={openUpload} color = "primary" style = {{float: "right",
                          marginRight: '10px'}}>
                          Upload
                      </Button>





                  </TabPane>
                </TabContent>
                </div>
            </div>         
    );

    
}

const ExternalConnectionsModal = (props) =>{

    const {isPanelModel,closeModel,caseId,openCurrentCase} = props;
    const [externalConnections,setExternalConnections] = useState([])
    const [currentConnectionName,setCurrentConnectionName] = useState("")
    useEffect(()=>{
        fetchExternalCases();
    },[isPanelModel])

    const fetchExternalCases =() =>{
        caseService.getExternalConnections().then((responseData) =>{
            setExternalConnections(responseData.data);
        })
    }

    const submitData = ()=>{
       const data = {caseId:caseId,externalId:currentConnectionName.toString()};
       if(caseId && caseId!= '' && currentConnectionName && currentConnectionName!=''){
           caseService.createCaseInExternal(data).then((responseData) =>{
               if(responseData.status === 200){
                   console.log("created");
                   props.closeModel();
                   props.openCurrentCase();
               }
           })
       }

    }

    return (<Modal isOpen={isPanelModel}  >
        <ModalHeader>Create Ticket in External</ModalHeader>
        <ModalBody>
            <select className="form-control" onChange={(event)=> setCurrentConnectionName(event.target.value)} >
                {externalConnections.map((value)=> {
                    return (<option value={value.id}>{value.connectionName}</option>)
                })}


            </select>
        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={submitData}>Create Ticket</Button>{' '}
            <Button color="secondary">Cancel</Button>
        </ModalFooter>
    </Modal>);

}

const PlayBook = (props)=>{
   
    const playbookDetails = useSelector((state) => state.cases.playbookDetails);
    const playbookResults = useSelector((state) => state.cases.playbookResults);
   
    const showPlaybookResults = (data)=>{
        props.showPlaybookResults(data);
    }

    setTimeout(function(){ 
        if(playbookDetails && playbookDetails.playbookXml){
            const $modelerContainer = document.querySelector('#modeler-container');
            if($modelerContainer){
                $modelerContainer.innerHTML = "";
            }
                const modeler = new Modeler({
                    container: $modelerContainer,
                    keyboard: {
                        bindTo: document.body
                      },
                });
                modeler.importXML(playbookDetails.playbookXml).then((response)=>{
                    var canvas = modeler.get('canvas');
                    var eventBus = modeler.get('eventBus');
                    var events = ['element.click'];
    
                    events.forEach(function(event) {
    
                        eventBus.on(event, function(e) {
                          // e.element = the model element
                          // e.gfx = the graphical element
                          for(var i=0;i<playbookResults.length;i++){
                              if(playbookResults[i].actionId === e.element.id){
                                showPlaybookResults(playbookResults[i]);
                              }
                          }
                          
                        });
                      });
    
                    for(var i=0;i<playbookResults.length;i++){
                        if(playbookResults[i].runStatus === 'Success'){
                            canvas.addMarker(playbookResults[i].actionId, 'highlight');
                        }else{
                            canvas.addMarker(playbookResults[i].actionId, 'fail');
                        }
                        
                    }
                    
                });
      
            }

     }, 3000);

    
    
    const runPlaybook  = ()=>{
        const data = {alertName:props.alertName,caseId:props.caseId,alertId:props.alertId}
        playbookService.runPlaybook(data).then((responseData)=>{
                
        })
    }

    return (
       <div className = "playbookContainer">
         <Row>
           <Col xs = {12} md = {12} lg = {12}>
                <Card>
                       {playbookDetails && playbookDetails.playBookName   && <React.Fragment>

                        <Row>
                            <Col xs = {11} lg = {11} md = {11}>
                                <div className = "panel__title">
                                    {playbookDetails && <h5 className = "bold-text">Playbook - {playbookDetails.playBookName}</h5>}
                                </div>
                            </Col>
                            <Col xs = {1} lg = {1} md = {1}>
                                <Button color = "primary" onClick = {runPlaybook} style = {{float:"left"}}>Run</Button>
                            </Col>
                        </Row>
                        <hr/>
                        
                        <div id = "modeler-container" style = {{height:'500px'}}>

                        </div>

                           </React.Fragment>
                        }
                        
                        
                        {playbookDetails && !playbookDetails.playBookName && <React.Fragment>

                                <div style = {{fontSize:'14px', fontWeight:'bold',textAlign:'center'}}>No Playbook Found</div>  

                            </React.Fragment>
                        }

                </Card> 
           </Col>
            
       </Row> 
       </div>
      
      
     );
}

const Model = (props) =>{

    const {caseId,close,openCurrentCase} = props;
    const [file,setFile] = useState();
    const onFileChange = (event) =>{
        setFile(event.target.files[0])
    }
    const uploadFile = ()=>{
        const formData = new FormData();

        // Update the formData object
        formData.append(
            "file",
            file

        );




        caseService.addFileToCase(caseId,formData).then((response) => {
            if(response.status === 200){
                props.close();
                props.openCurrentCase();
            }
        });

    }

    return (<Modal isOpen={props.open}  >
        <ModalHeader>Upload File</ModalHeader>
        <ModalBody>
            <input type="file" onChange={onFileChange} />
        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={uploadFile}>Upload</Button>{' '}
            <Button color="secondary">Cancel</Button>
        </ModalFooter>
    </Modal>);

}

const CaseBody = (props) =>{
    const {caseDetails} = props;
    return (
    
    <div className = "main-content__left" >
        <CasesTab caseDetails = {caseDetails}/>



        <div className = "case-cli-cnt">

        </div>
    </div>
    
    )
}

export default CaseAlert;