import axiosInstance from '../middleware/Interceptor'


function getPlayBookDetails () {
    return axiosInstance.get('/siem-core/user/playbook/')
}
function savePlayBookDetails(data){
    return axiosInstance.post('/siem-core/user/playbook/save-playbook',data)
}
function deletePlaybook(id){
    return axiosInstance.delete('/siem-core/user/playbook/'+id)
}

function getSinglePlaybook(id){
    return axiosInstance.get('/siem-core/user/playbook/'+id)
}
function getNotificationDetails(){
    return axiosInstance.get('/siem-core/user/notifications/')
}
function approveOrReject(data){
    return axiosInstance.post('/siem-core/user/notifications/approve-or-reject',data)
}
function getPlaybookBasedOnAlertname(data){

    return axiosInstance.post('/siem-core/user/playbook/get-playbook-by-alert-name',data)
}
function runPlaybook(data){
    return axiosInstance.post('/siem-core/user/playbook/run-playbook',data)
}

const playbookService = {
    getPlayBookDetails,
    savePlayBookDetails,
    deletePlaybook,
    getSinglePlaybook,
    getNotificationDetails,
    approveOrReject,
    getPlaybookBasedOnAlertname,
    runPlaybook
  
   
}

export default playbookService;