import {
    FILTER_HISTORICAL,
    APPLY_DATE_FILTER,
    APPLY_SEARCH_STRING,
    SAVE_FILETRS,
    OPEN_SAVED_SEARCH,
    APPLY_INDEX_TYPE,
    SHOW_HISTORICAL_EVENT
} from '../actions/historicalActions';
import moment from "moment";
import cloneDeep from 'lodash/cloneDeep';
import {subHours} from "date-fns";

const initialState = {
    historicalModel: {
        date: [
            {
                startDate: subHours(new Date(),24 ),
                endDate: new Date(),
                key: 'selection'
            }
        ],
        filters: [],
        searchString: '*',
        type: 'events',
        id :0,
        filterName:'',
        tableState:{},
        eventData:{}

    },
    saveFilters : {id:0,filterName:''}

}

const historicalReducer = (state = initialState, action) => {

    if(action.type === FILTER_HISTORICAL ){
        const currentFilters = cloneDeep(state.historicalModel);
        currentFilters.filters.push(action.payload);
        return Object.assign({}, state, { historicalModel: currentFilters  });
    }
    if(action.type === SHOW_HISTORICAL_EVENT){
        const currentFilters = cloneDeep(state.historicalModel);
        currentFilters.eventData = action.payload
        return Object.assign({}, state, { historicalModel: currentFilters  });
    }



    if(action.type === APPLY_DATE_FILTER ){
        const currentFilters = cloneDeep(state.historicalModel);
        currentFilters.date = [action.payload.selection];
        return Object.assign({}, state, { historicalModel: currentFilters  });
    }
    if(action.type === 'DELETE_FILTERS'){
        const revmoedFilters = cloneDeep(state.historicalModel);
        revmoedFilters.filters.splice(action.index,1);
        return Object.assign({}, state, { historicalModel: revmoedFilters  });
    }
    if(action.type === APPLY_SEARCH_STRING){
        const currentFilters = cloneDeep(state.historicalModel);
        currentFilters.searchString = action.payload;
        return Object.assign({}, state, { historicalModel: currentFilters  });
    }
    if(action.type === SAVE_FILETRS){
        const currentFilters = cloneDeep(state.saveFilters);

        return Object.assign({}, state, { saveFilters: action.payload  });
    }
    if(action.type === APPLY_INDEX_TYPE){
        const currentFilters = cloneDeep(state.historicalModel);
        currentFilters.type = action.payload.toLowerCase();
        return Object.assign({}, state, { historicalModel: currentFilters  });
    }
    if(action.type === OPEN_SAVED_SEARCH){
        const currentFilters = cloneDeep(state.historicalModel);
        currentFilters.id = action.payload.id;
        currentFilters.filterName = action.payload.filterName;
        let metaData = JSON.parse(action.payload.metaData);
        let tableMeta = JSON.parse(action.payload.tableMetaData);
        currentFilters.type = metaData.type
        currentFilters.date = [
            {
                startDate: new Date(metaData.startDate),
                endDate: new Date(metaData.endDate),
                key: 'selection'
            }
        ]
        currentFilters.filters = metaData.filters;
        currentFilters.searchString = metaData.searchString;
        currentFilters.tableState = tableMeta;
        return Object.assign({}, state, { historicalModel: currentFilters  });
        //currentFilters.date =
    }

    return state;

};

export default historicalReducer;
