import axiosInstance from '../middleware/Interceptor'


function getAllCases () {
    return axiosInstance.get('/case-management/user/case/getAllCases')
}
function getSingleCase(id){
    return axiosInstance.get('/case-management/user/case/get-case/'+id)
}

function getSingleAlert(id){
    ///alert-history
    return axiosInstance.get('/case-management/user/alerts/alert-history/'+id)
}

function changeAssinee(data){
    return axiosInstance.post('/case-management/user/case/change-assignee',data)
}

function assingToMe(data){
    return axiosInstance.post('/case-management/user/case/assing-to-me',data)
}

function changeCaseState(data){
    return axiosInstance.post('/case-management/user/case/change-status',data)
}
function changePr(data){
    return axiosInstance.post('/case-management/user/case/change-priorty',data)
}

function getGraphData(id,type){
    return axiosInstance.get('/siem-core/user/alerts/get-case-relations/'+id+"?type="+type);
}

function getEntityInfo(data){
    return axiosInstance.post('/siem-core/user/explore/get-bulk-time-line',data)
}
function addNotesToCase(data){
    return axiosInstance.post('/case-management/user/case/add-notes',data)

}
function filterCase(data){

    return axiosInstance.post('/case-management/user/case/filter-cases',data)
}
function addFileToCase(id,data){

    const config = {
        headers: {
            'content-type': 'multipart/form-data'
        }
    }

    return axiosInstance.post('/case-management/user/case/uploadFileToCase/'+id,data,config)

}

function autocomplete (data){
    return axiosInstance.post('/siem-core/user/explore/auto-completed',data);
}

function loadMalwareCategories (){

    return axiosInstance.get('/siem-core/user/integrations/misp/load-malwarecategories');
}

function getInfo(data){
    return axiosInstance.post('/siem-core/user/explore/get-info',data);
}
function getTimeLine(data){
    return axiosInstance.post('/siem-core/user/explore/get-time-line',data);
}

function expandNode(data){
    return axiosInstance.post('/siem-core/user/explore/get-graph',data);
}

function createCase(data){
    return axiosInstance.post('/case-management/user/case/create-case',data);
}

function  attachCaseToAlerts(data){
    return axiosInstance.post('/siem-core/user/alerts/alert-history/update-cases',data);
}
function createInvestigation(data){
    return axiosInstance.post('/case-management/user/investigation/workbench/save',data);
}
function updateInvestigationWithAlerts(data){
    return axiosInstance.post('/case-management/user/alerts/alert-history/update-investigations',data);
}

function getExternalConnections(){
    return axiosInstance.get("/case-management/user/integrations/jira/");
}
function createCaseInExternal(data){
    return axiosInstance.post("/case-management/user/case/move-case-to-external",data)
}

function getInvestigationById(id){
    return axiosInstance.get("/case-management/user/investigation/get-investigations-by-id/"+id);
}

function getGraphDataByInvestigationName(id,type){
    return axiosInstance.get("/siem-core/user/alerts/get-case-relations-by-investiation/"+id+"?type=alert");
}


const caseService = {
    getAllCases,
    getSingleCase,
    changeAssinee,
    assingToMe,
    changeCaseState,
    changePr,
    getGraphData,
    getSingleAlert,
    getEntityInfo,
    addNotesToCase,
    addFileToCase,
    filterCase,
    autocomplete,
    loadMalwareCategories,
    getInfo,
    getTimeLine,
    expandNode,
    createCase,
    attachCaseToAlerts,
    createInvestigation,
    updateInvestigationWithAlerts,
    getExternalConnections,
    createCaseInExternal,
    getInvestigationById,
    getGraphDataByInvestigationName


}

export default caseService