import { combineReducers, createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import { reducer as reduxFormReducer } from 'redux-form';
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from 'redux-saga'
import {
  cryptoTableReducer,
  newOrderTableReducer,
  sidebarReducer,
  themeReducer,
  customizerReducer,
  todoReducer,
  rtlReducer,
  authReducer,
  dashboardReducer,
  panelReducer,
  dashboardLayoutReducer,
  casesReducer,
  exploreReducer,
  threatExploreReducer,
  historicalReducer,
    ruleReducer,
    integrationsReducer
} from '../../redux/reducers/index';

import sagas from '../../redux/saga';
const sagaMiddleware = createSagaMiddleware()
const reducer = combineReducers({
  form: reduxFormReducer, // mounted under "form",
  theme: themeReducer,
  rtl: rtlReducer,
  sidebar: sidebarReducer,
  cryptoTable: cryptoTableReducer,
  newOrder: newOrderTableReducer,
  customizer: customizerReducer,
  todos: todoReducer,
  user: authReducer,
  
  dashboards: dashboardReducer,
  panelReducer:panelReducer,
  dashboardLayoutReducer:dashboardLayoutReducer,
  cases:casesReducer,
  exploreReducer:exploreReducer,
  threatExploreReducer:threatExploreReducer,
  historicalReducer:historicalReducer,
  ruleReducer:ruleReducer,
  integrationsReducer:integrationsReducer
});
const store = createStore(reducer, composeWithDevTools(applyMiddleware(sagaMiddleware)));
sagaMiddleware.run(sagas);

export default store;
