import React from 'react';
import {Badge} from 'reactstrap'
import {useSelector} from "react-redux";

const TimeLineView = ()=>{

    const entity = useSelector((state) => state.threatExploreReducer.timeLine);
    if(entity && entity.type === 'Alerts'){
        return (<AlertView entity = {entity}/>);
    }
    if(entity && entity.type === 'Events'){
        return (<EventsView entity = {entity}/>);
    }
    if(entity && entity.type === 'loginEvents'){
        return (<LoginEventsView entity = {entity}/>);
    }
    if(entity && entity.type === 'Process'){
        return (<ProcessView entity = {entity}/>);
    }
    if(entity && entity.type === 'Malware Devices'){
        return (<ProcessView entity = {entity}/>);
    }
    if(entity && entity.type === 'userAccess'){
        return (<LoginEventsView entity = {entity}/>);
    }
    if(entity && entity.type === 'Malware'){
        return (<LoginEventsView entity = {entity}/>);
    }
    return (<div></div>)


}

const ProcessView =(props) =>{
    const {entity} = props;
    return (<div className="timeline-container">
        <div className="timeline">
            {entity && entity.entityData.map((value,index) =>{
                return(
                    <React.Fragment>
                        <div className="timeline__item">
                            <div className="timeline__icon">
                                <span>{index+1}</span>
                            </div>

                            <div className={`timeline__content card-body`}>

                                <h6 className="subhead timeline__date">{value['Date']}</h6>
                                <p> <b>{value['user_name']}</b> was executed command <b>{value['command_line']}</b>  on  <b>{value['log_source_device_name']}</b>  </p>



                            </div>
                        </div>
                    </React.Fragment>
                )})}

        </div>




    </div>);
}

const LoginEventsView =(props) =>{
    const {entity} = props;
    return (<div className="timeline-container">
        <div className="timeline">
            {entity && entity.entityData.map((value,index) =>{
                return(
                    <React.Fragment>
                        <div className="timeline__item">
                            <div className="timeline__icon">
                                <span>{index+1}</span>
                            </div>

                            <div className={`timeline__content card-body`}>

                                <h6 className="subhead timeline__date">{value['Date']}</h6>
                                <p>Logged on to {value['name']} from {value['source_ip']}</p>



                            </div>
                        </div>
                    </React.Fragment>
                )})}

        </div>




    </div>);
}

const EventsView =(props) =>{
    const {entity} = props;
    return (<div className="timeline-container">
        <div className="timeline">
            {entity && entity.entityData.map((value,index) =>{
                return(
                    <React.Fragment>
                        <div className="timeline__item">
                            <div className="timeline__icon">
                                <span>{index+1}</span>
                            </div>

                            <div className={`timeline__content card-body`}>
                                <h5 className="timeline__title">{value['name']}</h5>
                                <h6 className="subhead timeline__date">{value['Date']}</h6>
                                <p>{value['event_description.keyword']}</p>

                                <p> {value['event_category.keyword']}</p>

                            </div>
                        </div>
                    </React.Fragment>
                )})}

        </div>




    </div>);
}

const AlertView =(props) =>{
    const {entity} = props;
    return (<div className="timeline-container">
        <div className="timeline">
            {entity && entity.entityData.map((value,index) =>{
                return(
                    <React.Fragment>
                        <div className="timeline__item">
                            <div className="timeline__icon">
                                <span>{index+1}</span>
                            </div>

                            <div className={`timeline__content card-body timeline__content__${value['priority.keyword']}`}>
                                <h5 className="timeline__title">{value['name']}</h5>
                                <h6 className="subhead timeline__date">{value['Date']}</h6>
                                <p>{value['description.keyword']}</p>

                                <p>
                                    {value['alert_status.keyword']  === 'Open'  &&
                                    <Badge color="primary">Open</Badge>
                                    }
                                    {value['alert_status.keyword']  === 'In Progress'  &&
                                    <Badge color="warning">In Progress</Badge>
                                    }
                                    {value['alert_status.keyword']  === 'Close'  &&
                                    <Badge color="primary">In Progress</Badge>
                                    }




                                </p>

                            </div>
                        </div>
                    </React.Fragment>
                )})}

        </div>




    </div>);
}


export default  TimeLineView;