import axiosInstance from '../middleware/Interceptor'


function getAllEvents () {
    return axiosInstance.get('/siem-core/user/events/events')
}

function getAllCategories(){
    return axiosInstance.get('/siem-core/user/events/groups/')
}
function getTatics(){
    return axiosInstance.get('/siem-core/user/miter/load-all-techniques')
}


function createRule(data){
    return axiosInstance.post('/siem-core/user/events/create',data)
}

function deleteRules(data){
    return axiosInstance.post("/siem-core/user/events/deleteMultiple",data);
}
function loadSingleRule(id){
    return axiosInstance.get("/siem-core/user/events/"+id);
}

function getTags(){
    return axiosInstance.get("/siem-core/user/tags/")
}

function createTag(data){
    return axiosInstance.post("/siem-core/user/tags/create",data)
}

function getLookupDetails(){
    return axiosInstance.get("/siem-core/user/reference-set/");
}


const ruleService = {
    getAllEvents,
    getAllCategories,
    getTatics,
    createRule,
    deleteRules,
    loadSingleRule,
    getTags,
    createTag,
    getLookupDetails


}

export default ruleService

