import {

    VIEW_RULES,
    CREATE_RULES,
    BACK_TO_RULES,
    ADD_SELECTED_ROWS,
    OPEN_EDIT_VIEW
} from '../actions/ruleActions';
import isEqual from 'lodash.isequal'

const initialState = {
    ruleData: [],
    open: {isOpen:false,isView:true},
    selectedRows :[],
    formData:{}
};

const ruleReducer = (state = initialState, action) => {

    if(action.type === VIEW_RULES){

        const updatedState =     Object.assign({}, state, { ruleData: action.payload  })
        console.log( isEqual(updatedState, state))
        return isEqual(updatedState, state) ? state : updatedState

    }
    if(action.type === BACK_TO_RULES){
        const data = {isOpen:false,isView: true}
        const updatedState =     Object.assign({}, state, { open: data  })

        return isEqual(updatedState, state) ? state : updatedState
    }

    if(action.type === CREATE_RULES){
        const data = {isOpen:true,isView: false}
        const updatedState =     Object.assign({}, state, { open: data,formData:{}  })

        return isEqual(updatedState, state) ? state : updatedState

    }
    if(action.type === ADD_SELECTED_ROWS){

        const updatedState =     Object.assign({}, state, { selectedRows: action.payload  })

        return isEqual(updatedState, state) ? state : updatedState
    }

    if(action.type === OPEN_EDIT_VIEW){
        const data = {isOpen:true,isView: false}
        const updatedState =     Object.assign({}, state, { formData: action.payload,open: data  })

        return isEqual(updatedState, state) ? state : updatedState
    }


    return state;

};

export default ruleReducer;
