import React, {useEffect, useState} from 'react';
import {Card, CardBody, Row, Col, Badge} from "reactstrap";

import LanguageIcon from '@material-ui/icons/Language';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import NoteIcon from '@material-ui/icons/Note';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import RssFeedIcon from '@material-ui/icons/RssFeed';
import ShareIcon from '@material-ui/icons/Share';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import brandMointoringService from "../../../api/brandMointoringService";
import NewspaperIcon from "mdi-react/NewspaperIcon";
import InfiniteScroll from "react-infinite-scroll-component";
import Highlighter from "react-highlight-words";
 const Mentions = (props) =>{
     const {date,searchString,category} = props;


     const [categoryData,setCategoryData] = useState({news:0,twitter:0,videos:0,all:0})
     useEffect(()=>{
         fetchCountByCategory()
     },[date,searchString])

     const fetchCountByCategory = () =>{
         const data = {startDate:date.startDate,endDate:date.endDate,searchString:searchString,category:category}
         brandMointoringService.getCategoryByCount(data).then((responseData)=>{
             if(responseData.data){
                 let counter = responseData.data.news+ responseData.data.twitter+responseData.data.videos
                 responseData.data['all'] = counter
                 setCategoryData(responseData.data);
             }

         })
     }

     return (
         <React.Fragment>
             <Card style={{height:'100px',marginBottom:'12px'}}>
                 <CardBody style = {{paddingTop:'10px'}}>
                     <Row>
                         <div className="container">
                             <Row>
                                 <Col lg={1} onClick={()=>props.selectCategory('all')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <LanguageIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>All</p>
                                         <p>{categoryData.all}</p>
                                     </div>


                                 </Col>
                                 <Col lg={2} onClick={()=>props.selectCategory('twitter')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <TwitterIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>Twitter</p>
                                         <p>{categoryData.twitter}</p>
                                     </div>

                                 </Col>
                                 <Col lg={2} onClick={()=>props.selectCategory('facebook')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <FacebookIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>Facebook</p>
                                         <p>0</p>
                                     </div>

                                 </Col>
                                 <Col lg={2} onClick={()=>props.selectCategory('news')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <NoteIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>News</p>
                                         <p>{categoryData.news}</p>
                                     </div>

                                 </Col>
                                 <Col lg={2} onClick={()=>props.selectCategory('videos')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <PlayArrowIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>Video</p>
                                         <p>{categoryData.videos}</p>
                                     </div>

                                 </Col>
                                 <Col lg={2} onClick={()=>props.selectCategory('blogs')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <RssFeedIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>Blogs</p>
                                         <p>0</p>
                                     </div>

                                 </Col>
                                 <Col lg={1} onClick={()=>props.selectCategory('web')}>
                                     <div style={{textAlign:'center',cursor:'pointer'}}>
                                         <ShareIcon style={{color:'red'}}/>
                                         <p style={{paddingLeft:'4px'}}>Web</p>
                                         <p>0</p>
                                     </div>

                                 </Col>
                             </Row>

                         </div>

                     </Row>
                 </CardBody>


             </Card>
             <BrandStream keywords = {props.keywords} category = {category} searchString = {searchString} date = {date}/>
         </React.Fragment>
         )
}

export const TopInFluencers = (props) =>{
     const {date,searchString} = props;

    const [topInfuleners,setTopInfuleners] = useState([]);

    useEffect(()=>{
        fetchTopInfluencers();
    },[date,searchString])

    const fetchTopInfluencers = () =>{
        const data = {startDate:date.startDate,endDate:date.endDate,from:0,searchString:searchString}
        brandMointoringService.getTopInfluencers(data).then((responseData)=>{
            if(responseData.data){

                setTopInfuleners(responseData.data)

            }

        })
    }

    return (<Card style={{marginBottom:'12px'}}>
        <h4 className="card-title" style = {{paddingTop:'12px',paddingLeft:'13px',paddingBottom:'10px'}}>Top Influencers </h4>
       <CardBody style={{height:'412px',overflowY:'auto'}}>
           {topInfuleners.map((value)=>{
               return ( <div className="profile__activity">
                   <div className="profile__activity-avatar" style = {{width:'52px',height:'45px',minWidth:'47px'}}>
                       {value.image && <img src={value.image} alt="" /> }

                   </div>
                   <div style={{width:'100%'}}>

                       <Row>
                           <Col xs = {6} lg= {10} >
                               {value.source === 'twitter' &&  <p className="profile__activity-name" style = {{paddingTop:'12px'}}><strong>{value.profile}</strong> | <span style = {{fontWeight:'400'}}>{value.socialReach} followers </span></p>}
                               {value.source !== 'twitter' &&  <p className="profile__activity-name" style = {{paddingTop:'12px'}}><strong>{value.profile}</strong> | <span style = {{fontWeight:'400'}}>{value.socialReach} likes </span> </p>}
                           </Col>

                       </Row>


                   </div>
               </div>)
           })}
       </CardBody>


    </Card>)


}

const BrandStream = (props) =>{
    const {date,searchString,category,keywords} = props;
    const [hasMore,setHasMore] = useState(true);
    const [streamData,setStreamData] = useState([]);

     useEffect(()=>{
         fetchBrandStream();
     },[date,searchString,category])

    const fetchBrandStream = () =>{
        const data = {startDate:date.startDate,endDate:date.endDate,from:streamData.length,searchString:searchString,category:category}
        brandMointoringService.getBrandFeed(data).then((responseData)=>{
            if(responseData.data){
                console.log(data);
                if(responseData.data.length==0){
                    setHasMore(false);
                }else{
                    const currentItems = streamData.concat(responseData.data);
                    setStreamData(currentItems)
                }
            }else{
                setStreamData([])
            }

        })
    }


     return (
            <div style={{paddingTop:'12px',paddingBottom:'12px'}}>
                <h4>Activity</h4>
                <div style={{paddingTop:'12px'}}>

                    <InfiniteScroll
                        dataLength={streamData.length}
                        next={fetchBrandStream}
                        hasMore={hasMore}
                        loader={<h4>Loading...</h4>}
                        height={400}
                    >
                        {streamData.map((data)=>{

                            return <StreamAcitivity keywords = {keywords} data = {data}/>

                        })}
                    </InfiniteScroll>


                </div>

            </div>



         )
}

const StreamAcitivity = (props)=>{


     return (
            <Card style={{marginBottom:'12px',height:'100px'}}>
                <CardBody>
                    <div className="profile__activity">
                        <div className="profile__activity-avatar" >
                            {props.data.image && <img src={props.data.image} alt="" /> }
                            {!props.data.image && props.data.category === 'news' && <NewspaperIcon style = {{height:'64px',width:'64px'}}/> }
                            {!props.data.image && props.data.category === 'twitter' && <img src="data:image/png;base64," alt="" /> }
                        </div>
                        <div style={{width:'100%'}}>

                            <Row>
                                <Col xs = {6} lg= {9} >
                                    {props.data.category === 'twitter' &&  <p className="profile__activity-name">{props.data.profileName} | {props.data.socialReach} followers</p>}
                                    {props.data.category != 'twitter' &&  <p className="profile__activity-name">{props.data.title}</p>}
                                </Col>
                                <Col xs = {6} lg= {3} >
                                    <span className="profile__activity-date" ><CalendarTodayIcon style={{fontSize:'13px',color:'red'}}/> {props.data['@timestamp']}</span>
                                </Col>
                            </Row>
                             <Row>
                                 <Col xs = {1} lg= {12} >

                                     <p  className="profile__activity-name">
                                         <a style={{color:'#0066CC',width:'50px'}} href={props.data.profileLink} target="_blank">{props.data.source}</a>

                                         <a style={{marginLeft:'12px'}} href={props.data.profileLink} target="_blank">
                                             {props.data.statementCss ==='Neutral' && <Badge color="warning"> {props.data.statementCss}</Badge>}
                                             {props.data.statementCss ==='Very Negative' && <Badge color="danger"> {props.data.statementCss}</Badge>}
                                             {props.data.statementCss ==='Negative' && <Badge color="danger"> {props.data.statementCss}</Badge>}
                                             {props.data.statementCss ==='Positive' && <Badge color="success"> {props.data.statementCss}</Badge>}
                                             {props.data.statementCss ==='Very Positive' && <Badge color="success"> {props.data.statementCss}</Badge>}
                                         </a>


                                     </p>

                                 </Col>

                             </Row>
                            <Row>
                                <Col xs = {1} lg= {12} >




                                    <Highlighter
                                        highlightClassName="YourHighlightClass"
                                        searchWords={props.keywords}
                                        autoEscape={true}
                                        textToHighlight={props.data.text}
                                        caseSensitive={false}
                                        highlightStyle={{color:'#34a951',background:'transparent'}}
                                    />


                                </Col>
                            </Row>
                        </div>
                    </div>
                </CardBody>
            </Card>



         )

}

export default Mentions;