import { takeLatest,put } from 'redux-saga/effects'
import dashboardService from '../../api/dashboardService'
import userService from '../../api/userService'
import * as ActionTypes from '../actions/dashboardActions';
import PanelModel from '../../models/PanelModel'
import DashboardModel from '../../models/DashboardModel'

function* openDashboardModel(action){
  const categoriesResponse =  yield dashboardService.getDashboardCategories();
  const fieldsResponse = yield dashboardService.getAllLogFields()
  if(categoriesResponse.status === 200){
      const finalCategories = categoriesResponse.data.map((value)=>{
        return {label:value.name,value:value.id}
      })
      const tempDashboardCategories = finalCategories.filter(function(e){
        return e.label.toLowerCase().startsWith("dashboard");
      })
      const reportCategroies = finalCategories.filter(function(e){
        return !e.label.toLowerCase().startsWith("dashboard");
      })

      const tempFields = fieldsResponse.data.map((value)=>{
        return {label:value.fieldName,value:value.fieldName}
      })
      const usersData =  yield   userService.getAccessList();

      const tempUsers = usersData.data.map((value)=>{
        return {label:value.name,value:value.name}
      })

      const finalData = action.payload;
      const dashboardModel = new DashboardModel(finalData);
      dashboardModel.updateDashboardCategories(tempDashboardCategories);
      dashboardModel.updateReportCategories(reportCategroies);
      dashboardModel.updateUsers(tempUsers);
      dashboardModel.updateLogFields(tempFields)

      yield put({ type: ActionTypes.CREATE_DASHBOARD, payload:dashboardModel});
  }
  
}




function* fetchSingleDashboard(action) {
    
    const reponseData = yield dashboardService.getSingleDashboard(action.id);
    const dashboarData = reponseData.data;
    if(!dashboarData.permissionError){
        let existingPermissions = []
        if(dashboarData.config.config){
          existingPermissions = JSON.parse(dashboarData.config.config);
        }
        const existingPanels = dashboarData.panels;
        let data = {}
        const tempPanels = [];
         for(var j=0;j<existingPanels.length;j++){
           
           const singlePanel = new PanelModel(existingPanels[j]);
           tempPanels.push(singlePanel)
         }
        if(dashboarData.reportId){
          data =  {
            id:dashboarData.id,
            uid:'',
            title:dashboarData.name,
            time:'',
            refresh:false,
            panels :tempPanels,
            description:dashboarData.description,
            category:dashboarData.category,
            permissions:existingPermissions,
            isAdmin:  dashboarData.isAdmin,
            isOwner:  dashboarData.isOwner,
            isReportEnabled:true,
            scheduleName:dashboarData.scheduleName,
            emailAddress:dashboarData.emailAddress,
            frequency:dashboarData.frequency,
            timeRange:dashboarData.frequency,
            frequencyType:dashboarData.frequencyType,
            hourOfDay:dashboarData.config.hourOfDay,
            reportId:dashboarData.reportId,
            filters:JSON.parse(dashboarData.filters),
            isSidePanelOpen:false
          }

        }else{
          data =  {
            id:dashboarData.id,
            uid:'',
            title:dashboarData.name,
            time:'',
            refresh:false,
            panels :tempPanels,
            description:dashboarData.description,
            category:dashboarData.category,
            permissions:existingPermissions,
            isAdmin:  dashboarData.isAdmin,
            isOwner:  dashboarData.isOwner,
            filters:JSON.parse(dashboarData.filters),
            isSidePanelOpen:false
          }
        }
        yield put({ type: ActionTypes.OPEN_SIGNLE_DAHSBOARD, payload:data});
    }   

   
}


export  function* fetchDashboardWatcher(){
     yield takeLatest(ActionTypes.FETECH_SINGLE_DASHBOARD, fetchSingleDashboard)
}

export function* openDashboardWatcher(){
  yield takeLatest(ActionTypes.OPEN_DASHBOARD_MODAL, openDashboardModel)
}

