import 'react-toastify/dist/ReactToastify.css';
import WidgetPannel from '../AddPanelWidget/WidgetPannel'
import moment from "moment";
import WidgetPanelTitle from './WidgetPanelTitle';
import { BarLoader } from 'react-spinners';

interface Props{
    panel:PanelModel
}

class DashboardPanel  extends React.Component<Props>{
    
    constructor(props) {
        super(props);
        this.state = { data: {data:[]},loading:false };
        
        this.fetchPanelData = this.fetchPanelData.bind(this);
    }

    queryAndFtechData = (queryData,panel,type) =>{
        if(type  && type === 'report'){

            dashboardService.showPanelPreivewForReport(queryData).then((response) => {
                var responses = {responses:[response.data.data]}
                let finalQuery  =  JSON.parse(panel.query)
                const er = new ElasticResponse(finalQuery, responses);
    
                if(panel.chartConfig.chartType === 'table' && panel.chartConfig.table.showSparkLine!='true'){
                    let esResponse =  er.getTimeSeries('table');
                    this.setState(function(state, props) {
                        return {
                            data: esResponse,
                            loading:false
                        };
                    });
                }else{
                    let esResponse =  er.getTimeSeries('NA');
                    this.setState(function(state, props) {
                        return {
                            data: esResponse,
                            loading:false
                        };
                    });
                }
    
            });
        }else{
            dashboardService.showPanelPreivew(queryData).then((response) => {
                var responses = {responses:[response.data.data]}
                let finalQuery  =  JSON.parse(panel.query)
                const er = new ElasticResponse(finalQuery, responses);
    
                if(panel.chartConfig.chartType === 'table' && panel.chartConfig.table.showSparkLine!='true'){
                    let esResponse =  er.getTimeSeries('table');
                    this.setState(function(state, props) {
                        return {
                            data: esResponse,
                            loading:false
                        };
                    });
                }else{
                    let esResponse =  er.getTimeSeries('NA');
                    this.setState(function(state, props) {
                        return {
                            data: esResponse,
                            loading:false
                        };
                    });
                }
    
            });
        }
        

    }

    fetchPanelData = () =>{
        const {panel,filters,date,type} = this.props;
        const startDate = date[0].startDate.getTime()
        const endDate = date[0].endDate.getTime()
        const finalDate = {startDate:startDate,endDate:endDate};
        this.setState({
            loading:true
        })
        if(type && type === 'report'){
            dashboardService.getSingleWidgetForReport(panel.id,this.props.token,this.props.dashboard.id).then((responseData)=>{

                const fetchedPanel = new PanelModel(responseData.data);
    
                const finalFilters = this.getFilteredData(fetchedPanel,filters);
                const queryData = this.getQuery(fetchedPanel,finalDate,finalFilters,this.props.searchString);
                queryData['token'] = this.props.token;
                queryData['id'] =this.props.dashboard.id.toString();
                this.queryAndFtechData(queryData,fetchedPanel,type);
            })
           
        }else{
            //id,token,dashboardId
            
            dashboardService.getSingleWidget(panel.id).then((responseData)=>{

                const fetchedPanel = new PanelModel(responseData.data);
    
                const finalFilters = this.getFilteredData(fetchedPanel,filters);
                const queryData = this.getQuery(fetchedPanel,finalDate,finalFilters,this.props.searchString);
                
                this.queryAndFtechData(queryData,fetchedPanel,type);
            })
        }
       
    }

     getQuery = (panel,date,filters,searchString) =>{
        const queryBuilder = new ElasticQueryBuilder({
            timeField: '@timestamp',
            esVersion: '7.9',
        });
        let queryString = searchString
         let panelQuery = JSON.parse(panel.query);

         let mergedFilters = [];

          if(panel.type && panel.type === 'alerts' ){
            let tempFilters = [];
            if(filters){
                for(let i=0;i<filters.length;i++){
                    let key = filters[i].key;
                    let fieldName = key.replaceAll("rule_data.event_data.","")
                    if(widgetHelper.alertMetaData.includes(fieldName)){

                        tempFilters.push({key:fieldName,operator:filters[i].operator,value:filters[i].value})
                    }else{
                       tempFilters.push(filters[i])
                    }
                    mergedFilters = tempFilters;
                }
            }
              
            }else{
                mergedFilters = filters;
            }


         if(panel.type && panel.type === 'alerts'){
             let tempArray = {};
             for(var i=0;i<panelQuery.length;i++){
                 let metrics = panelQuery[i].metric;
                 let buckets = panelQuery[i].bucket;
                 for(let j=0;j<metrics.length;j++){
                     let fieldName = metrics[j].field.replaceAll("rule_data.event_data.","")
                     if(!widgetHelper.alertMetaData.includes(metrics[j].field)){

                         metrics[j].field = "";
                         metrics[j].field = "rule_data.event_data."+fieldName;
                     }

                 }
                 for(let j=0;j<buckets.length;j++){
                     let fieldName = buckets[j].field.replaceAll("rule_data.event_data.","")
                     if(!widgetHelper.alertMetaData.includes(buckets[j].field)){

                         buckets[j].field = "";
                         buckets[j].field = "rule_data.event_data."+fieldName;
                     }

                 }
                 tempArray['metric'] = metrics;
                 tempArray['metrics'] = metrics;
                 tempArray['bucketAggs'] = buckets;
                 tempArray['bucket'] = buckets;
             }
             panelQuery = [];
             panelQuery.push(tempArray);
         }



        var query = queryBuilder.build(panelQuery[0],mergedFilters,queryString);
        let payload = JSON.stringify(query);
        let duration = widgetHelper.calculateTimeRange( date.startDate,date.endDate);
        console.log(duration);
        let finalData =  payload.replaceAll("$timeFrom", date.startDate);
        let fainlData1 = finalData.replaceAll("$timeTo", date.endDate);
        let finalData2 = fainlData1.replaceAll("$__interval",duration)
        let panelJson = panel;
        panelJson['elsQuery'] = finalData2;
        let data = {'panelData':JSON.stringify(panelJson)}; 

        return data;

    }

    getFilteredData = (panel,filters) =>{

        let mergedFilters = [];
        if(panel.type && panel.type === 'alerts' ){
            let tempFilters = [];
            if(filters){
                for(let i=0;i<filters.length;i++){
                    let key = filters[i].key;
                    if(!key.startsWith("rule_data.event_data.")){
                        tempFilters.push({key:"rule_data.event_data."+filters[i].key,operator:filters[i].operator,value:filters[i].value})
                    }else{
                        tempFilters.push(filters[i])
                    }
                }
            }
            
            for(let i=0;i<panel.filters.length;i++){
                let key = panel.filters[i].key;
                if(!key.startsWith("rule_data.event_data.")){
                    tempFilters.push({key:"rule_data.event_data."+panel.filters[i].key,operator:panel.filters[i].operator,value:panel.filters[i].value})
                }else{
                    tempFilters.push(panel.filters[i])
                }
            }



            mergedFilters = tempFilters;
        }else{
            let tempFilters = [];
            if(filters){
                for(let i=0;i<filters.length;i++){
                    let key = filters[i].key;
                    if(key.startsWith("rule_data.event_data.")){
                        tempFilters.push({key:filters[i].key.replace("rule_data.event_data.",""),operator:filters[i].operator,value:filters[i].value})
                    }else{
                        tempFilters.push(filters[i])
                    }
                }
            }
           
            for(let i=0;i<panel.filters.length;i++){
                let key = panel.filters[i].key;
                if(key.startsWith("rule_data.event_data.")){
                    tempFilters.push({key:panel.filters[i].key.replace("rule_data.event_data.",""),operator:panel.filters[i].operator,value:panel.filters[i].value})
                }else{
                    tempFilters.push(panel.filters[i])
                }
            }
            mergedFilters = tempFilters;
        }
        return mergedFilters;
    }

    componentDidUpdate(prevProps) {
        // Typical usage (don't forget to compare props):
        
        let isPropsEqual =   _.isEqual(this.props, prevProps);
        if(!isPropsEqual){
            
            this.fetchPanelData();
        }

      }
      shouldComponentUpdate(nextProps, nextState){
          
        let isPropsEqual =   _.isEqual(this.props.filters, nextProps.filters);
        
        if(this.props.date[0].startDate != nextProps.date[0].startDate || !isPropsEqual || this.props.searchString!=nextProps.searchString ){
            this.setState(function(state, props) {
                return {
                  data: {data:[]}
                };
          });
          return true;
        }else{
            let isPropsEqual1 =   _.isEqual(this.state.data.data, nextState.data.data);
            if(!isPropsEqual1){
                return true;
            }
        }
        return false;
      }

     



    openSettings = (panel) => {
        
        const {dispatch} = this.props;
        let query = [];
        if(Array.isArray(panel.query)){
            query = panel.query;
        }else{
            query = JSON.parse(panel.query)
        }
         
        panel.query = query;
        dispatch(editPanel(panel))
        dispatch(addCreatedPanel(panel))
    }

    componentDidMount(){
        this.fetchPanelData();
    }

    render() {
       
        const {panel} = this.props;

        return (
           <div style = {{display: 'block', height: '100%'}}>

            <Card  style = {{paddingBottom:"0px"}} >

                
              
                {(this.props.dashboard.isAdmin || this.props.dashboard.isOwner) && this.props.deletePanel &&


                <div className="panel__btns">
                    <button className="panel__btn" type="button" onClick={this.openSettings.bind(this, panel)}>
                        <SettingsIcon/></button>
                    <button className="panel__btn" type="button" onClick={this.props.deletePanel.bind(this, panel.id)}>
                        <CloseIcon/></button>
                </div>
                }
              {this.state.data.data.length>0 &&
                 <WidgetPannel type = {this.props.type} panelData = {this.state.data} filters = {this.props.filters} date = {this.props.date} panel = {panel}/>

              }
               { this.state.data.data.length===0 &&
                <React.Fragment>
                <WidgetPanelTitle panel = {panel}/>
                    <div style = {{padding:"70px 0",textAlign:'center'}}> <h4> No Data</h4></div>    
                </React.Fragment>
              }
              
      </Card>


           

           

                  

            </div>
          
           
           
          
        );  

    }

}



export default connect()(DashboardPanel);